<template>
    <div class="contact-form">
        <form @submit.prevent="SubmitLoginFrom">
            <div class="form-group">
                <input class="form-control" placeholder="Email Address" type="text" v-model.trim="LoginData.email"
                    @input="$v.LoginData.email.$touch()" />
                <div v-if="$v.LoginData.email.$dirty">
                    <p class="text-danger" v-if="!$v.LoginData.email.required">Please enter your email address.</p>
                    <p class="text-danger" v-if="!$v.LoginData.email.email">Please enter valid email address.</p>
                </div>
            </div>
            <div class="form-group">
                <input class="form-control" placeholder="Password" type="password" v-model.trim="LoginData.password"
                    @input="$v.LoginData.password.$touch()" />
                <div v-if="$v.LoginData.password.$dirty">
                    <p class="text-danger" v-if="!$v.LoginData.password.required">Please enter your street address.</p>
                </div>
            </div>
            <div class="forgot-username">
                Forgot Username or Password?
                <router-link :to="{ name: 'forgotPassword' }">Click here</router-link>
            </div>
            <div v-if="error" v-html="error">{{error}}</div>
            <!-- @click="$v.LoginData.$touch"-->
            <button class="btn send-btn" :disabled="$v.$invalid">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>LOGIN
            </button>
        </form>
    </div>
</template>

<script>
/* eslint-disable */
const $ = window.jQuery = require('jquery');
import {
    required,
    email
} from "vuelidate/lib/validators";
import * as fb from '../../firebaseConfig'
export default {
    data() {
        return {
            LoginData: {
                email: null,
                password: null,
            },
            spinnerShow: false,
            error: null,
        };
    },
    validations: {
        LoginData: {
            email: {
                required,
                email,
            },
            password: {
                required,
            },
        },
    },
    methods: {

        async SubmitLoginFrom() {

            this.error = null;
            this.spinnerShow = true;
            $('.btn').prop("disabled", true);
            if (!this.$v.$invalid) {
                await fb.auth.signInWithEmailAndPassword(this.LoginData.email, this.LoginData.password)
                    .then((succee) => {
                        fb.usersCollection.doc(fb.auth.currentUser.uid).get().then((doc) => {
                            if (doc.exists) {
                                var response = doc.data()
                                if (response.activated_at == true) {
                                    if (response.role == "Dispensary") {
                                        this.$router.push({ name: "dispensaryOderList" })
                                    }
                                    if (response.role == "Customer") {
                                        this.$router.push({ name: "customerDashboard" })
                                    }
                                    if (response.role == "Driver") {
                                        this.$router.push({ name: "pendingOrders" })
                                    }
                                    if (response.role == "Admin") {
                                        this.$router.push({ name: "adminDashboard" })
                                    }
                                } else {
                                    fb.auth.signOut()
                                    $('.btn').prop("disabled", false);
                                    this.spinnerShow = false;
                                    this.flashError('You are disabled now. Please contact our team.', {
                                        timeout: 15000,
                                    });
                                    return false
                                }
                                $('.btn').prop("disabled", false);
                                return false;
                            } else {
                                fb.auth.signOut()
                                $('.btn').prop("disabled", false);
                                this.spinnerShow = false;
                                this.flashError('Your email address and/or password are invalid. Please contact our support team.', {
                                    timeout: 15000,
                                });

                            }
                        })

                    })
                    .catch((err) => {
                        $('.btn').prop("disabled", false);
                        this.error = "<p class='alert alert-danger'>Your email address and/or password are invalid. Please try again.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                        this.spinnerShow = false;
                    });
            } else {
                $('.btn').prop("disabled", false);
                this.spinnerShow = false;
            }
        },
    },
};
</script>
