<template>
  <div>
      <div :class="{ loader:dataLoader }"></div>
    <div class="shopping-cart tab-space">
    <flash-message></flash-message>
    <div v-if="cartEmptyError" v-html="cartEmptyError">{{cartEmptyError}}</div>
      <div role="tabpanel" class="tab-pane active border-bottom" id="account-setting">
        <div class="tab-table">
          <div class="table-responsive" v-if="cartProductData.length>0">
            <table class="table">

              <thead>
              <tr class="table-title">
                <th>PRODUCT</th>
                <th></th>
                <th>PRICE</th>
                <th>Quantity</th>
                <th>Total</th>
              </tr>
              </thead>

              <tbody>
              <tr v-for="(cartProduct, index) in cartProductData" :key="index">
                <td style="width: 10%">
                  <img :src="cartProduct.images[0]" class="img-responsive productImg-cart">
                </td>
                <td class="top-margin-td">{{cartProduct.name}}</td>
                <td class="top-margin-td">${{cartProduct.sale_price | currency}}</td>
                <td class="top-margin-td">
                  <div class="wish-list-count">
                    <div class="input-group">
                      <span class="input-group-btn">
                        <button type="button" class="btn btn-default btn-number"  @click.prevent="changeQuantity(cartProduct.id,cartProduct.quantity,cartProduct.sale_price,'decrease')" :disabled="cartProduct.quantity <= 1 ? true : false" >
                          <span class="glyphicon glyphicon-minus"></span>
                        </button>
                      </span>
                      <input name="quant[1]" class="form-control input-number" :value="cartProduct.quantity" min="1" max="100" type="text">
                      <span class="input-group-btn">
                        <button type="button" class="btn btn-default btn-number" @click.prevent="changeQuantity(cartProduct.id,cartProduct.quantity,cartProduct.sale_price,'increase')" :disabled="cartProduct.quantity >= 5 ? true : false">
                          <span class="glyphicon glyphicon-plus"></span>
                        </button>
                      </span>
                    </div>
                    <div class="remove-item"><a href="#" @click.prevent="removeCart(cartProduct.id)"> Remove</a></div>
                  </div>
                </td>
                <td class="top-margin-td">

                  <div class="cart-btn">
                    ${{cartProduct.total_count | currency}}
                  </div>

                </td>
              </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div class="row margin-top " v-if="cartProductData.length>0">
        <div class="col-sm-8">

        </div>
        <div class="col-sm-4">
          <div class="left-space-new margin-top">
            <div class="title "><h2>Order Summary</h2></div>
            <div class="order-table margin-top">
              <div class="table-responsive">
                <table class="table">
                  <thead>
                  <tr>
                    <th style="width: 50%">SUBTOTAL</th>
                    <th style="width: 50%" class="text-center">${{subTotalAmount | currency}}</th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr v-if="totalDiscount">
                    <td>DISCOUNT</td>
                    <td class="text-center">- ${{totalDiscount | currency}}</td>
                  </tr>

                <!--  <tr>
                    <td> DELIVERY FEE</td>
                    <td class="text-center">${{productOrderListData.delivery_fee | currency}}</td>
                  </tr>
               
                  <tr>
                    <td>SALES TAX</td>
                    <td class="text-center">${{taxTotalAmount | currency}}</td>
                  </tr>
                   -->
                  <tr>
                    <td class="total-text">TOTAL</td>
                    <td class="text-center total-text">${{totalAmount | currency}}</td>
                  </tr>

                  </tbody>
                </table>

              </div>
              <button class="btn send-btn checkout-btn" @click.prevent="checkOut">CHECKOUT</button>
              
            </div>

            <div class="white-box-border" v-if="totalDiscount<=0">
              <p>APPLY A PROMO CODE</p>
              <div class="contact-form">
                <div class="form-group">
                  <input type="text" @input="$v.applyPromoCode.$touch()" v-model.trim="applyPromoCode" class="form-control"  placeholder="Enter Promo Code">
                  
                  <div v-if="$v.applyPromoCode.$dirty">
                    <p class="text-danger" v-if="!$v.applyPromoCode.required">
                      Please enter valid promo code.
                    </p>
                  </div>
                  
                </div>
                  <div v-if="error" v-html="error">{{error}}</div>
              </div>

              <button class="btn app-btn" :disabled="$v.$invalid"  @click.prevent="checkCouponCode">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>APPLY</button>

            </div>

            <div class="white-box-border" v-else>
              <p class="text-center">APPLIED YOUR PROMO CODE</p>
              <div class="contact-form">
                <div class="text-center form-group applied-discount">
                  {{applyPromoCode}}
                </div>
              </div>
              <button class="btn btn-applied-discount"  @click.prevent="cancelDiscount">CANCEL</button>

            </div>
          </div>
        </div>
      </div>
    </div>

  </div>  
</template>

<script>
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import { required  } from "vuelidate/lib/validators";
import evenBus from "../../../event-bus";
import axios from "axios";
export default {
  props:{
    productOrderListData:{
      type: Object,
      required: true,
    },
  },
  data() {
    return {
        url:this.$store.state.API_URL_ROOT,
        dataLoader:true,
      cartProductData:[],
      subTotalAmount:null,
    //  taxTotalAmount:0,
      totalAmount:null,
      applyPromoCode:null,
      dispensaryUserId:null,
      totalDiscount:0,
      UsedCouponId: null,
      spinnerShow: false,
      error: null,
      cartEmptyError:null
    };
  },
  validations: {
    applyPromoCode: {
      required,
    },
  },
  created() {
    this.getCartData()
  },
  mounted(){
	  $(".flash__wrapper").empty();
  },
  methods: {

    async checkOut(){
      var vueInstance = this;
      $(".flash__wrapper").empty();
      vueInstance.error = null;
      vueInstance.spinnerShow = true;
      $('.btn').prop("disabled", true);

      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      await vueInstance.$store.dispatch("getCustomerUser");
      let checkExipreDocumentData = await vueInstance.$store.state.customerData;
 
      if(checkExipreDocumentData.document_info.expiration_date <= currentTime){
        evenBus.$emit("openExipreDocumentModal");
        $('.btn').prop("disabled", false);
        vueInstance.spinnerShow = false;
        return false;
      }
        vueInstance.$router.push({ name: "cartReviewProductView"})
        $('.btn').prop("disabled", false);
        vueInstance.spinnerShow = false;
    },

    async cancelDiscount(){
      let removeAlreadyCouponCode = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_orderList');

      removeAlreadyCouponCode.update({
        used_coupon_id: firebase.firestore.FieldValue.delete(),
        total_discount: firebase.firestore.FieldValue.delete(),
        coupon_code_name : firebase.firestore.FieldValue.delete(),
      })
      this.productOrderListData.used_coupon_id = null
      this.productOrderListData.total_discount = null
      this.getCartData();
      this.totalDiscount= 0;
    },

    async checkCouponCode(){
      var vueInstance = this;
      $(".flash__wrapper").empty();
      vueInstance.error = null;
      vueInstance.spinnerShow = true;
      $('.btn').prop("disabled", true);

      let removeAlreadyCouponCode = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_orderList');

      removeAlreadyCouponCode.update({
        used_coupon_id: firebase.firestore.FieldValue.delete(),
        total_discount: firebase.firestore.FieldValue.delete(),
        coupon_code_name : firebase.firestore.FieldValue.delete(),
      }) 
      
      if (!this.$v.$invalid) {

        let currentTime = firebase.firestore.Timestamp.now().toMillis();
        let getCouponCode = await fb.couponCollection.where("coupon_code_name", "==", vueInstance.applyPromoCode).where("expiration_date",">=",currentTime).where("isPublic","==",true);

        getCouponCode.get().then(function(doc)  {  
          if (!doc.empty) { 
            var data = doc.docs[0].data()
                
            fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_coupon').collection('usedCoupon').doc(data.id).get().then((doc)=> { 
              if(doc.exists){ 
                var usedLimit = doc.data()
                if(data.usage_limit_per_user <= usedLimit.used_limit){ 
                    $('.btn').prop("disabled", false);
                    vueInstance.spinnerShow = false;
                    vueInstance.error ="<p class='alert alert-danger'>You have already used this coupon please try another with one.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                    return false
                  }else{ 
                    vueInstance.couponCode()
                  }
                
              }else{ 
                vueInstance.couponCode()
              }
            }).catch(function(error) { console.log(error);});  
        
          } else {
              vueInstance.error ="<p class='alert alert-danger'>Coupon code is  not exist .<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
              vueInstance.spinnerShow = false;
              $('.btn').prop("disabled", false);
              return false;
            }
        }).catch(function(error) { console.log(error);});  

      }
    },

    async couponCode(){ 
      var vueInstance = this;
      vueInstance.totalDiscount= 0;

      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      let getCouponCode = await fb.couponCollection.where("coupon_code_name", "==", vueInstance.applyPromoCode).where("expiration_date",">=",currentTime).where("isPublic","==",true);

        getCouponCode.get().then(function(doc)  {
          if (!doc.empty) { 
              var data = doc.docs[0].data()
              if(data.dispensary_user_id != vueInstance.productOrderListData.dispensary_user_id){
            
                vueInstance.error ="<p class='alert alert-danger'>Coupon code is  not exist in the dispensary.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                vueInstance.spinnerShow = false;
                $('.btn').prop("disabled", false);
                return false;
              }else{

                try{
                    //Check product is exist into applied this coupon code
                  var isProductExist= false;
                  vueInstance.cartProductData.map(function(value, key) {
                  if (data.product.some(productId => productId.product_id === value.product_id)){
                    
                    isProductExist= true ;  

                      let totalProductPrice = value.quantity * value.sale_price
                      let discount;
                      if(data.discount_type=="percentage"){
                        discount = totalProductPrice/100 * data.percentage;
                        let totalCartAmount= vueInstance.totalAmount - parseFloat(discount.toFixed(2))
                        vueInstance.totalAmount = parseFloat(totalCartAmount.toFixed(2))
                        vueInstance.totalDiscount += parseFloat(discount.toFixed(2))
                      }

                    vueInstance.UsedCouponId = data.id

                    let UsedCouponInsert = fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_orderList');

                    UsedCouponInsert.set({
                      used_coupon_id:vueInstance.UsedCouponId,
                      total_discount:vueInstance.totalDiscount,
                      coupon_code_name:vueInstance.applyPromoCode,
                    },{ merge: true }) 
                  }
                });
                $('.btn').prop("disabled", false);
                vueInstance.spinnerShow = false;
              }catch(err){
                vueInstance.error ="<p class='alert alert-danger'>Coupon code is  not exist in the category.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                vueInstance.spinnerShow = false;
                $('.btn').prop("disabled", false);
                return false;
              }
              if(isProductExist == false){
                vueInstance.error ="<p class='alert alert-danger'>Coupon code is  not exist in the category.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                vueInstance.spinnerShow = false;
                $('.btn').prop("disabled", false);
                return false;
            }

            }
          } else {
              vueInstance.error ="<p class='alert alert-danger'>Coupon code is  not exist .<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
              vueInstance.spinnerShow = false;
              $('.btn').prop("disabled", false);
              return false;
            }

        }).catch(function(error) {
            console.log("Error getting document:", error);
        });

      },

    async removeCart(cartId){
       var vueInstance = this;
      if(confirm("Do you want to remove this product from your cart?")){
        let removeFromCart = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_orderList');
          removeFromCart.collection('orderList').doc(cartId).delete();

        vueInstance.flashSuccess("You have successfully removed the product from your cart.", {
          timeout: 8000,
        });
        removeFromCart.collection('orderList').get().then((getProduct)=> {
            
          if (getProduct.size <= 0) {
              removeFromCart.delete()
            return false
          }
        })
      }
    },

    async changeQuantity(id,currentQuantity,salePrice,actionType){ 
      var vueInstance = this;
      var changeQuantity;
      var totalCount;
      let updateQunatity = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_orderList');
      if(actionType ==="increase"){

        changeQuantity = currentQuantity + 1
        totalCount = changeQuantity * salePrice
        if(changeQuantity > 5){
          vueInstance.flashError("You can't select more than 5 quantities.", {
            timeout: 5000,
          });
          return false;
        }
         updateQunatity.collection('orderList').doc(id).update({quantity: changeQuantity,total_count: totalCount})
      }else{
        changeQuantity = currentQuantity - 1
        totalCount = changeQuantity * salePrice
        if(changeQuantity < 1){
          vueInstance.flashError("You can't select less than 1 quantity.", {
            timeout: 5000,
          });
          return false;
        }
        updateQunatity.collection('orderList').doc(id).update({quantity: changeQuantity,total_count: totalCount})
      } 
      return true
    },
    

    async getCartData(){ 
        var vueInstance = this;
      
      
//return false
      vueInstance.cartEmptyError= null
      vueInstance.error= null
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      await vueInstance.$store.dispatch("getCustomerUser");
      let checkExipreDocumentData = await vueInstance.$store.state.customerData;
 
      if(checkExipreDocumentData.document_info.expiration_date <= currentTime){
        evenBus.$emit("openExipreDocumentModal");
      }


      let getAddedOrderData = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_orderList');

              getAddedOrderData.collection('orderList').onSnapshot((getProduct)=> {
                var totalAddedProductCount = getProduct.size;
                if (totalAddedProductCount > 0) {
                  vueInstance.totalDiscount= 0;
                  let allAddedProdoctData =[];
                  let subTotalAmountData =0;
                  let totalAmountData =0;
                //  let taxAmountData =0;

                  getProduct.forEach(function(docOrder) { 
                    var getData = docOrder.data ();

                      allAddedProdoctData.push(getData);
                      subTotalAmountData += getData.quantity * getData.sale_price
                    //  taxAmountData += getData.tax_rate 
                      totalAmountData += getData.quantity * getData.sale_price
                      vueInstance.dispensaryUserId= getData.dispensary_user_id

                  });

                  vueInstance.cartProductData = allAddedProdoctData;
                  vueInstance.subTotalAmount = subTotalAmountData;
               //   vueInstance.taxTotalAmount = taxAmountData;
                //  let totalCartAmount = totalAmountData + vueInstance.productOrderListData.delivery_fee + taxAmountData ;
               //   let totalCartAmount = totalAmountData + taxAmountData ;
                  vueInstance.totalAmount = parseFloat(totalAmountData.toFixed(2))
                
                   if(vueInstance.productOrderListData.total_discount){ 
                     vueInstance.applyPromoCode = vueInstance.productOrderListData.coupon_code_name;
                     vueInstance.checkCouponCode()
                   }
                   vueInstance.dataLoader=false
                
                } else {
                  vueInstance.cartProductData=[];
                  vueInstance.cartEmptyError ="<p class='alert alert-danger'>Your Cart is empty.Please add product into your cart.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                  vueInstance.dataLoader=false
                    
                }
              })
      },
  },
};
</script>

<style scoped>
.productImg-cart {
  max-width: 120px;
}
.applied-discount{
  color: #8dc73f;
  border-bottom: 0;
  font-weight: bold;
}
.btn-applied-discount{ 
  display: block;
  background: #c72e2e;
  padding: 15px 80px;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  border-radius: 0;
  margin: 0 auto;
  }
.text-danger{
  color: #c72e2e;
}
.checkout-btn {
	padding: 15px 106px;
}  
</style>