<template>
  <div>
    <div class="col-sm-9"  v-if="productDetials">
      <div class="row" >
      <flash-message></flash-message>
        <div class="col-sm-8">
          <div class="product-slider">
            <div id="carousel-example-generic" class="carousel slide" data-ride="carousel">

              <!-- Wrapper for slides -->
              <div class="carousel-inner" role="listbox">
                <div class="item active">
                  <img :src="productDetials.feature_image" class="img-responsive productImg">
                </div>
                <div class="item" v-for="(productGalleryImage, index) in productDetials.product_gallery" :key="index">
                  <img :src="productGalleryImage" class="img-responsive productImg">

                </div>
              </div>

              <!-- Controls -->
              <a class="left carousel-control" href="#carousel-example-generic" role="button" data-slide="prev">
                <span class="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
                <span class="sr-only">Previous</span>
              </a>
              <a class="right carousel-control" href="#carousel-example-generic" role="button" data-slide="next">
                <span class="glyphicon glyphicon-chevron-right" aria-hidden="true"></span>
                <span class="sr-only">Next</span>
              </a>

              <!-- Indicators -->
              <ol class="carousel-indicators">
                <li data-target="#carousel-example-generic" data-slide-to="0" class="active"><img :src="productDetials.feature_image" class="img-responsive productImg"> </li>
                <li data-target="#carousel-example-generic" v-for="(productGalleryImage, index) in productDetials.product_gallery" :key="index" :data-slide-to="index+1">	<img :src="productGalleryImage" class="img-responsive productImg"></li>
              </ol>

            </div>
          </div>
        </div>
        <div class="col-sm-4">
          <div class="prod-sell">
            <div class="title ">
              <h2>{{productDetials.name}}</h2>
              <div class="star">
                <star-rating :max="5" :current="reviewCount.rating"></star-rating>
                <div class="reviews">({{reviewCount.count}} Reviews)</div>
              </div>
            </div>
            <div class="border-bottom"></div>
            <div class="contact-form">
              <select class="form-control" v-model.trim="productWeight">
                <option v-for="(weight, index) in prdouctWeights" :key="index" :value="weight">{{weight.value}} {{weight.name}}</option>
              </select>
              <div v-if="$v.productWeight.$dirty">
                  <p class="text-danger" v-if="!$v.productWeight.required">
                    Please select product weight.
                  </p>
                  
                </div>
            </div>
            <div class="title top-spce">
              <h2>${{totalSalePrice | currency}}</h2>
            </div>
            <div class="quantity-count">
              <div class="input-group">
                <span class="input-group-btn">
                  <button type="button" class="btn btn-default btn-number" @click.prevent="quantityMinus">
                    <span class="glyphicon glyphicon-minus"></span>
                  </button>
                </span>
                <input type="text" @input="$v.quantity.$touch()" v-model.trim="quantity" class="form-control input-number">
                <span class="input-group-btn">
                  <button type="button" class="btn btn-default btn-number"  @click.prevent="quantityAdd">
                    <span class="glyphicon glyphicon-plus"></span>
                  </button>
                </span>
              </div>
            </div>
            <div v-if="$v.quantity.$dirty">
                  <p class="text-danger" v-if="!$v.quantity.required">
                    Please add product quantity.
                  </p>
                   <p class="text-danger" v-if="!$v.quantity.numeric">
                     Please add product quantity as numeric.
                  </p>
                   <p class="text-danger" v-if="!$v.quantity.between">
                     Enter quantity Must be between {{$v.quantity.$params.between.min}} to {{$v.quantity.$params.between.max}} digit's
                  </p>
                </div>
            <div class="cart-btn">
              
              <button class="search-green-btn btn" :disabled="$v.$invalid"  @click.prevent="addProduct">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>
                 <i class="fa fa-shopping-bag" aria-hidden="true"></i>
                 ADD TO CART  
              </button>
            </div>
       
            <div class="border-bottom"></div>
            <div class="share-links">
              <ul>
                <li>SKU:</li>
                <li class="color-font">{{productDetials.sku}} </li>

              </ul>

              <div class="category-tag">
                <ul>
                  <li>Categories:</li>
                  <li  v-for="(category, index) in productDetials.categories" :key="index">
                    <span class="categoryComma" v-if="index!=0">,</span>
                    <a href="#">{{category.category_name}}</a>
                  </li>
                  
                </ul>

              </div>
            </div>
          </div>

        </div>
      </div>
      <div class="tab-section animated bounceIn go" data-id="1">
        <!-- Nav tabs -->
        <ul class="nav nav-tabs nav-justified table-border-bottom " role="tablist">

          <li role="presentation" class="active"><a href="#profile-new" aria-controls="profile-new" role="tab" data-toggle="tab" aria-expanded="false">

              <div class="tab-link">DESCRIPTION</div>
            </a></li>
          <li role="presentation" class=""><a href="#messages-new" aria-controls="messages-new" role="tab" data-toggle="tab" aria-expanded="false">

              <div class="tab-link"> STRAIN ATTRIBUTES</div>
            </a></li>
          <li role="presentation" class=""><a href="#deals-ads-new" aria-controls="deals-ads-new" role="tab" data-toggle="tab" aria-expanded="true">
              <div class="tab-link"> REVIEWS</div>
            </a></li>

        </ul>
        <!-- Tab panes -->
        <div class="tab-content tab-space ">

          <div role="tabpanel" class="tab-pane active" id="profile-new">

            <div class="strain-content">
              {{productDetials.description}}
             </div>

          </div>
          <div role="tabpanel" class="tab-pane" id="messages-new">
            <div class="strain-content">
              <div class="row">
                <div class="col-sm-4">
                  <div class="progress-effect">
                    <h3>Effects</h3>

                    <div class="progress-green-bar"  v-for="(stainAttributeVal, stainAttributeText) in productDetials.stain_attributes.effects" :key="stainAttributeText">
                      <span class="capitalize">{{stainAttributeText}}</span>
                      <div class="progress">

                        <div class="progress-bar" role="progressbar" :style="{ width: stainAttributeVal*20 + '%' }">
                         <!-- <span class="sr-only">60% Complete</span>-->
                        </div>
                      </div>
                    </div>
    
                  </div>

                </div>
                <div class="col-sm-4">

                  <div class="progress-effect">
                    <h3>Medical Benefits</h3> 
                    <span v-for="(medicalBenefit, index) in productDetials.stain_attributes.medical_benefits" :key="index"><span v-if="index!=0">,</span> {{medicalBenefit}}</span>
    
                  </div>

                </div>
                <div class="col-sm-4">

                  <div class="progress-effect">
                    <h3>Negatives</h3>
                    <span v-for="(negative, index) in productDetials.stain_attributes.negative" :key="index"><span v-if="index!=0">,</span> {{negative}}</span>
          
                  </div>

                </div>
              </div>

            </div>

          </div>
          <div role="tabpanel" class="tab-pane " id="deals-ads-new">
            <div class="review-section">

              <div class="client-reviews" v-for="(customerReview, index) in ProductReviewList" :key="index">
                <div class="row">
                  <div class="col-sm-9">
                    <h5>{{customerReview.user_name}}</h5>
                    <div class="star-icon"><star-rating :max="5" :current="customerReview.rating"></star-rating></div>

                  </div>
                  <div class="col-sm-3">
                    <div class="post-time">
                      {{ customerReview.created_at | timeFormateAgo}}
                    </div>
                  </div>
                </div>
                <p> {{customerReview.comment}}</p>

              </div>
              <div class="client-reviews" v-if="emptyReviewDataVal == 0 && ProductReviewList.length <=0">
                Product review is not available now.
              </div>
              
              <div class="pagination-review">
                <div class="row">
                  <div class="col-sm-6">
                    <div class="top-spce hidden-xs"></div>
                  </div>
              
                  <div class="text-right col-sm-6">
                    <nav aria-label="Page navigation">
                      <ul class="pagination">
                        <li>
                          <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
                            <span aria-hidden="true"></span>
                          </a>
                        </li>
                        <li><a
                              href="#"
                              v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
                              :key="index"
                              @click.prevent="page = pageNumber"
                            >{{pageNumber}}</a></li>
                        <li>
                          <a href="#" @click.prevent="page++" v-if="page < pages.length"  aria-label="Next">
                            <span aria-hidden="true"></span>
                          </a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>


    <div class="pre-product-slider" v-if="relatedProducts.length > 0">
      <div class="featured-product-slider" v-if="checkTrue">
        <div class="row">
          <div class="col-sm-3"></div>
            <div class="col-sm-9">
              <div class="title related">
              <h2>Related Products</h2>
              </div>

            <carousel v-if="relatedProducts.length > 0" :autoplay="true" :dots="false" :margin=10>

              <div class="item" v-for="(relatedProduct, index) in relatedProducts" :key="index">
                <div class="prod-slider-pro">
                  <a href="#"  @click.prevent="productDetail(relatedProduct.product_id)"><img :src="relatedProduct.product_image" class="img-responsive related-img"></a>

                  <div class="text-center">
                    <div class="hover-block">
                      <a href="#"> <h3>{{relatedProduct.product_name}}</h3></a>
                      <p>Starting at ${{relatedProduct.product_sale_price  | currency}}</p>

                    </div>
                  </div>
                </div>
              </div>
            </carousel>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

</script>
<script>
import carousel from 'vue-owl-carousel'
import * as fb from "../../../../firebaseConfig";
import starRating from "../../../../components/backEnd/RatingComponent";
import { required, numeric, between  } from "vuelidate/lib/validators";
export default {
  props: {
    productId: {
      type: String,
      required: true,
    },
    dispnesaryData:{
      required: true,
    }
  },
  data() {
    return {
        checkTrue: false,
      getProductId:this.productId,
      
      productDetials : null,
      productWeight:{},
      reviewCount : {},
      prdouctWeights : [],
      relatedProducts : [],
      
      page : 1,
      perPage : 5,
      pages : [],
      productReview : [],
      emptyReviewDataVal: false,

      quantity : 1,
      salePrice : null,
      totalSalePrice : null,
      spinnerShow: false,
    };
  },

 components: {
    starRating,
    carousel
  },

  validations: {
    quantity: {
      required,
      numeric,
      between: between(1, 5)
    },
    productWeight: {
      required,
    },
  },

  filters: {
    timeFormateAgo(date) {  
      const intervals = [
        { label: 'year', seconds: 31536000 },
        { label: 'month', seconds: 2592000 },
        { label: 'day', seconds: 86400 },
        { label: 'hour', seconds: 3600 },
        { label: 'minute', seconds: 60 },
        { label: 'second', seconds: 0 }
      ];
      const seconds = Math.floor((new Date().getTime() - date) / 1000);
      const interval = intervals.find(i => i.seconds < seconds);
      const count = Math.floor(seconds / interval.seconds);
      if(count == "Infinity") return `Few seconds ago`;
      return `${count} ${interval.label}${count !== 1 ? 's' : ''} ago`;
    }
  },

//   mounted() {
 
//     var owl = $('.owl-carousel');
//     owl.owlCarousel({
//       margin: 10,
//       loop: false,
//     })

//     $('.owl-prev').html('<i class="fa fa-angle-left" aria-hidden="true"></i>');
//     $('.owl-next').html('<i class="fa fa-angle-right" aria-hidden="true"></i>');
//   },

  created() {
      $('.flash__wrapper').empty();
    this.productDetailsData();
    this.getReviewData();
    this.totalWeight();
    this.relatedProductData();
  },
  computed: {
    ProductReviewList() {
      return this.paginate(this.productReview);
    },

  },
  watch: {
    productReview() {
      this.page = 1;
      this.perPage= 5;
      this.pages= [];
      this.setPages();
    },
  },
  methods: { 

    quantityAdd(){
      if(this.quantity <5){
        this.quantity +=1;
        this.totalSalePrice = this.salePrice*this.quantity
        return;
      }
      return false;
    },
    quantityMinus(){
      if(this.quantity >1){
        this.quantity -=1;
        this.totalSalePrice = this.salePrice*this.quantity
        return;
      }
      return false;
    },

    async relatedProductData(){
      var vueInstance = this;
      vueInstance.relatedProducts = [];
      vueInstance.checkTrue= false
      //var allRelatedProduct = [];
			try {
			 await fb.productCollection.doc(this.getProductId).collection('related_products').get().then(function(querySnapshot) { 
       
          querySnapshot.forEach((doc) => { 
            var dataProduct=[];
            dataProduct=doc.data();
            
            fb.productCollection.doc(doc.data().product_id).get().then((productPrice) => {
              if (productPrice.exists) {
                dataProduct.product_sale_price = productPrice.data().sale_price
                vueInstance.relatedProducts.push(dataProduct);
               
              } 
            })
          }); 
          //vueInstance.relatedProducts= 
          setTimeout(function(){  vueInstance.checkTrue= true }, 4000);
			})
			.catch(function(error) {
				console.log("Error getting documents: ", error);
			});
			} catch (error) {}
    },

    async totalWeight(){
      var vueInstance = this;
      var allProductWeight = [];
			try {
			  fb.productCollection.doc(this.getProductId).collection('weights').get().then(function(querySnapshot) { 
          querySnapshot.forEach((doc) => { 
            allProductWeight.push(doc.data());
            vueInstance.productWeight= doc.data()
          }); 
          vueInstance.prdouctWeights= allProductWeight
			})
			.catch(function(error) {
				console.log("Error getting documents: ", error);
			});
			} catch (error) {}
    },

    setPages() {
      let numberOfPages = Math.ceil(this.productReview.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },

    async getReviewData(){
      var vueInstance = this;
      var allReviewData = [];
			var ratingCount = 0;
			try {
			  fb.productCollection.doc(this.getProductId).collection('reviews').orderBy('created_at', 'desc').get().then(function(querySnapshot) { 
				  var totalCount = querySnapshot.size;
				  if(totalCount >0){
				
            querySnapshot.forEach(function(docreview) {
              ratingCount += parseInt(docreview.data().rating);
               allReviewData.push(docreview.data());
            });
            vueInstance.reviewCount={
              count:totalCount,
              rating:ratingCount/totalCount
            };
					ratingCount =0;

          vueInstance.productReview = allReviewData;
				}else{
					vueInstance.reviewCount={
						count:0,
						rating:ratingCount
					};
				}
				
			})
			.catch(function(error) {
				console.log("Error getting documents: ", error);
			});
			} catch (error) {}
      setTimeout(()=>{
          if(allReviewData.length <= 0)
            vueInstance.emptyReviewDataVal = 0;
        }, 20000);
    },

    async productDetailsData(){ 
      var vueInstance = this;
      vueInstance.productDetials=null;
      let productCodeQuery  = await fb.productCollection.doc(this.getProductId);

        productCodeQuery.get().then(docData => {
          if (docData.exists) {
            vueInstance.productDetials = docData.data();
            vueInstance.salePrice = docData.data().sale_price;
            vueInstance.totalSalePrice = docData.data().sale_price;
          } else {
              // docData.data() will be undefined in this case
              console.log("No such document!");
          }
      }).catch(function(error) {
          console.log("Error getting document:", error);
      });
    
    },

    async addProduct(){
      if (!this.$v.$invalid) {
        var vueInstance = this;
        vueInstance.spinnerShow = true; 
        $('.btn').prop("disabled", true);
        let dispensaryId = vueInstance.productDetials.dispensary_id;
        let productId = vueInstance.productDetials.id;
        let dataInsert = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_orderList');

          dataInsert.get().then((doc) => {
              if (doc.exists) {

                  if (doc.data().dispensary_user_id == dispensaryId){
                    dataInsert.collection('orderList').where("product_id", "==", productId).get().then(function(getProduct) {
                      if (getProduct.empty) {
                          vueInstance.addInotOrderList()
                      } else {
                          $('.btn').prop("disabled", false);
                          vueInstance.spinnerShow = false;
                          vueInstance.flashError("You have already added this product.", {
                            timeout: 10000,
                          });
                      }
                    })
                  }else{
                     $('.btn').prop("disabled", false);
                     vueInstance.spinnerShow = false;
                      vueInstance.flashError("You can't add product with this dispensary.", {
                        timeout: 10000,
                      });
                  }
              } else {
                  vueInstance.addInotOrderList()
              }
          }).catch(function(error) {
              $('.btn').prop("disabled", false);
              vueInstance.spinnerShow = false;
              vueInstance.flashError('There is some went wrong. Please try again later.', {
                timeout: 10000,
              });
          });
       
        }
      },
        
      async addInotOrderList(){
        var vueInstance = this;
        let dispensaryId = vueInstance.productDetials.dispensary_id;
        let productId = vueInstance.productDetials.id;
        let dataInsert = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_orderList');

        dataInsert.collection('orderList').add({
        
          busniess_name:vueInstance.dispnesaryData.busniess_name,
          description:vueInstance.productDetials.description,
          dispensary_user_id : dispensaryId,
          dispensary_id : vueInstance.dispnesaryData.id,
          images:vueInstance.productDetials.product_gallery,
          name:vueInstance.productDetials.name,
          product_id:productId,
          quantity:vueInstance.quantity,
          regular_price:vueInstance.productDetials.regular_price,
          sale_price:vueInstance.productDetials.sale_price,
        //  tax_rate:vueInstance.productDetials.tax_rate,
          total_count:vueInstance.totalSalePrice,
          weight:vueInstance.productWeight.value +" "+vueInstance.productWeight.name,
        }).then(function (docRef) { 

          let updateId = dataInsert.collection('orderList').doc(docRef.id)
          updateId.set({id: docRef.id},{ merge: true }) 
            
          dataInsert.set({

          //  delivery_fee:50,
            dispensary_user_id : dispensaryId,
            dispensary_id : vueInstance.dispnesaryData.id,

          }).then(function () {
              
            $('.btn').prop("disabled", false);
            vueInstance.spinnerShow = false;
            vueInstance.flashSuccess('You have successfully added this product to your cart.', {
              timeout: 10000,
            });
            })
        }).catch((err) => {
            $('.btn').prop("disabled", false);
            vueInstance.spinnerShow = false;
            vueInstance.flashError('There is some went wrong. Please try again later.', {
              timeout: 10000,
            });
            
          });
      
    },

    productDetail(producAutotId){ 
      this.getProductId = producAutotId
      this.productDetailsData();
      this.getReviewData();
      this.totalWeight();
      this.relatedProductData();
    },

  },
};
</script>

<style scoped>
.related-img{
  max-height:170px;
  min-height: 170px;
}
.productImg {
  max-height: 192px;
  min-height:192px;
}
.categoryComma{
  margin-top: 10px;
}
.capitalize { text-transform: capitalize; }

.search-green-btn {
	background: #8dc73f;
	padding: 12px 52px;
	color: #fff;
	text-align: center;
	text-transform: uppercase;
	border: none;
	border: 0;
	margin-top: 10px;
}
.cart-btn .fa {
	color: #fff;
	position: absolute;
	top: 21px;
	left: 15px;
	font-size: 21px;
}
.related{
  padding-bottom: 20px;
}
</style>