<template>
  <div>
    <div class="tab-title">
      <h3 class="text-center"><span>Step 3:</span> Documents</h3>
    </div>
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
      <form @submit.prevent="SubmitDocumentForm">
        <div class="right-title margin-top">
          <h2>Identification Information</h2>
        </div>
        <div class="contact-form">
          <div class="form-group">
            <select class="form-control" v-model.trim="DocumnetFormData.identification">
              <option value="" selected>Select Type of Identification*</option>
              <option value="Driver License">Drivers License</option>
              <option value="State ID">State ID</option>
            </select>
            <div v-if="$v.DocumnetFormData.identification.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.identification.required">Please select type of identification.</p>
            </div>
          </div>
          <div class="form-group">
            <select class="form-control" v-model.trim="DocumnetFormData.issuingState">
              <option value="">Issuing State *</option>
              <option v-for="(state, index) in getStateData" :key="index" :value="state">{{ state }}</option>
            </select>
             <div v-if="$v.DocumnetFormData.issuingState.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.issuingState.required">Please select issuing state.</p>
            </div>
          </div>
          <div class="form-group">
            <input v-model.trim="DocumnetFormData.driverLicenseNo" @input="$v.DocumnetFormData.driverLicenseNo.$touch()"
              class="form-control"
              placeholder="Drivers License Number *"
              type="text"
            />
             <div v-if="$v.DocumnetFormData.driverLicenseNo.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.driverLicenseNo.required">Please enter your driver license number.</p>
            </div>
          </div>
          <div class="input-group">
            <input v-model.trim="DocumnetFormData.expirationDate" @input="$v.DocumnetFormData.expirationDate.$touch()"
              class="form-control calender-date"
              aria-describedby="basic-addon2"
              type="text"
              placeholder="Expiration Date (MM/DD/YYYY) *"
              id="expirationDate"
              name="expirationDate"
            />
            <span class="input-group-addon cuorser" id="basic-addon2"
              ><i class="fa fa-calendar" aria-hidden="true"></i
            ></span>
             
          </div>
          <div v-if="$v.DocumnetFormData.expirationDate.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.expirationDate.required">Please select expiration date.</p>
            </div>
        </div>

        <div class="right-title margin-top spaceing">
          <h2>Medical Information</h2>
        </div>
        <div class="contact-form">
          <div class="form-group">
            <input v-model.trim="DocumnetFormData.patientId" @input="$v.DocumnetFormData.patientId.$touch()"
              class="form-control"
              placeholder="Patient ID Number *"
              type="text"
            />
             <div v-if="$v.DocumnetFormData.patientId.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.patientId.required">Please enter patient id.</p>
            </div>
          </div>
          <div class="input-group">
            <input v-model.trim="DocumnetFormData.recommendationDate" @input="$v.DocumnetFormData.recommendationDate.$touch()"
              class="form-control calender-date"
              aria-describedby="basic-addon2"
              type="text"
              placeholder="Doctor's Recommendation Expiration Date"
              id="recommendationDate"
              name="recommendationDate"
            />
            <span class="input-group-addon cuorser" id="basic-addon2"
              ><i class="fa fa-calendar" aria-hidden="true"></i
            ></span>
             
          </div>
          <div v-if="$v.DocumnetFormData.recommendationDate.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.recommendationDate.required">Please select recommendation date*.</p>
            </div>
        </div>

        <div class="right-title margin-top spaceing">
          <h2>Upload Photos</h2>
        </div>
        <div class="contact-form">
          <div class="upload-text">
            <p>Photo of Doctor's Recommendation</p>
          </div>
          <p class="doct-content">
            Please upload a photo of your <span>Doctor's Recommendation*</span>.
            Please <br class="hidden-xs" />
            make sure we can read all the letters and numbers on the license. *
          </p>

          <div class="upload-file">
            <div class="upload-photo">
              <a href="javascript:void(0)" @click="recommendationImage">
                <i class="fa fa-upload" aria-hidden="true"></i>
                CHOOSE FILE
              </a>
              <input type="file" accept="image/*" @change="onFileChangeRecommendation" id="recommendationImageUpload" style="display:none" />
              <span id="fileNameRecomendaion">No File Chosen</span>
            </div>
          </div>

          <div class="upload-text">
            <p>PHOTO OF Government Issued IDENTIFICATION*</p>
          </div>
          <p class="doct-content">
            Please upload a photo of your
            <span>GoVERNMEnt ISSUED Photo ID</span>. Please
            <br class="hidden-xs" />
            make sure we can read all the letters and numbers on the license. *
          </p>
          <div class="upload-file">
            <div class="upload-photo">
              <a href="javascript:void(0)" @click="govermentIdImage">
                <i class="fa fa-upload" aria-hidden="true"></i>
                CHOOSE FILE
              </a>
              <input type="file" accept="image/*" @change="onFileChangeGovermentId" id="govermentIdImageUpload" style="display:none" />
              <span id="fileNameGovermentId">No File Chosen</span>
            </div>
          </div>

          <div class="upload-text green-title-new">
            <p>DELIVERING TO AN ADDRESS OTHER THAN THE ONE ON ID?</p>
          </div>
          <p class="doct-content">
            Delivering to a different address than the one shown on your ID?
            Please upload a photo of a
            <span
              >document with your name and correct delivery address on it</span
            >
            (ex. utility bill, lease, hotel receipt). Please make sure we can
            read all the letters and numbers on the license.
          </p>
          <div class="upload-file">
            <div class="upload-photo">
              <a href="javascript:void(0)" @click="deliveringImage">
                <i class="fa fa-upload" aria-hidden="true"></i>
                CHOOSE FILE
              </a>
              <input type="file" accept="image/*" @change="onFileChangeDelivering" id="deliveringImageUpload" style="display:none" />
              <span id="fileNameDelivering">No File Chosen</span>
            </div>
          </div>

          <div class="border-bottom"></div>
        </div>
        <div class="text-center">
        <div v-if="error" v-html="error">{{error}}</div>
          <button class="btn send-btn save-btn" :disabled="$v.$invalid">
              <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>Save & Continue
            <i class="fa fa-long-arrow-right" aria-hidden="true"></i>
          </button>
        </div>
      </form>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
const $ = window.jQuery = require('jquery'); 
import { required, minLength } from "vuelidate/lib/validators";
export default {
  data() {
    return {
      getStateData: [],
      image:null,
      DocumnetFormData:{
        identification:"",
        issuingState:"",
        driverLicenseNo:null,
        expirationDate:null,
        patientId:null,
        recommendationDate:null,
      },
      spinnerShow: false,
      error: null,
      recommendationFile:null,
      govermentIdFile:null,
      deliveringFile:null,
    };
  },
  validations: {
    DocumnetFormData: {
      identification: {
        required,
      },
      issuingState: {
        required,
      },
      driverLicenseNo: {
        required,
      },
      expirationDate: {
        required,
      },
      patientId: {
        required,
      },
      recommendationDate: {
        required,
      },
    },
    recommendationFile: {
      required,
    },
    govermentIdFile: {
      required,
    },
    deliveringFile: {
      required,
    },
    
  },
  mounted() {
    this.fbState();
    var vm = this;
    // $("input[name=expirationDate]").datepicker({ 
    //   changeMonth: true,
    //   changeYear: true,
    //   minDate: 0,
    //   onSelect: (dateForm) => {
    //     vm.DocumnetFormData.expirationDate = dateForm;
    //   },
    // });
    // $("input[name=recommendationDate]").datepicker({ 
    //   changeMonth: true,
    //   changeYear: true,
    //   minDate: 0,
    //   onSelect: (dateForm) => {
    //     vm.DocumnetFormData.recommendationDate = dateForm;
    //   },
    // });
  },

  methods: {
    recommendationImage() {
        $("#recommendationImageUpload").trigger("click");
    },
    onFileChangeRecommendation(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length) return;
        $('#fileNameRecomendaion').html(files[0].name);
        this.recommendationFile=files[0].name;
        this.createImage(files[0], 'recommendationImage');
    },
    createImage(file, ketName) {
        var image = new Image();
        var reader = new FileReader();
        var vm = this;

        reader.onload = (e) => {
            let image = e.target.result;
            Vue.set(this.$store.state, ketName, image);
           // localStorage.setItem(ketName, image);
        };
        reader.readAsDataURL(file);
    },

    govermentIdImage() {
        $("#govermentIdImageUpload").trigger("click");
    },
    onFileChangeGovermentId(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length) return;
        $('#fileNameGovermentId').html(files[0].name);
        this.govermentIdFile=files[0].name;
        this.createImage(files[0], 'govermentIdImage');
    },
    
    deliveringImage() {
        $("#deliveringImageUpload").trigger("click");
    },
    onFileChangeDelivering(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length) return;
        $('#fileNameDelivering').html(files[0].name);
        this.deliveringFile=files[0].name;
        this.createImage(files[0], 'deliveringImage');
    },

    async fbState() {
        await this.$store.dispatch("getAllState");
        this.getStateData = this.$store.state.GetState[0].state;
    },
    
    async SubmitDocumentForm() {
      this.error = null;

      this.spinnerShow = true;

      if (!this.$v.$invalid) {
        localStorage.setItem('DocumnetFormData', JSON.stringify(this.DocumnetFormData))
        $(".nav-tabs li a[href='#settings']").tab("show");
      } else {
        this.spinnerShow = false;
      }
    },
  },
};
</script>
