<template>
  <div>
    <div :class="{ loader:dataLoader }"></div>
    <section id="banner-section">
      <div class="container">
        <div class="row">
          <div class="col-lg-10 col-lg-offset-1">
            <div class=" animatedParent" data-sequence='100'>
              <div class="main-page-title animated bounceIn go" data-id="1">
                <div class="form-menu contact-form">
                  <div class="back-menu"><i class="fa fa-long-arrow-left" aria-hidden="true"></i><a href="#">Back to ORDERS</a> </div>
                </div>
                <div class="row">
                  <!--<div class="col-sm-6"-->
                    <div class="col-sm-8">
                    <div class="row">
                      <div class="col-sm-8">
                        <div class="title">
                          <h2>ORDER #{{ orderData.order_no}}</h2>
                        </div>
                      </div>
                      <div class="col-sm-4">

                      </div>
                    </div>
                    <div class="order-number margin-top">Order placed on: <span>{{ orderData.created_at | longDate}}</span></div>
                    <div class="order-number ">Order status: <span>{{ orderData.status}}</span></div>
                    <div class="order-number"  v-show="orderData.confirmation_code">Confirmation Code: <span> {{ orderData.confirmation_code}}</span></div>
                    <div class="order-table">
                      <div class="signature-section">ORDER DETAILS</div>

                      <div class="table-responsive">
                        <table class="table">
                          <thead>
                          <tr>
                            <th style="width: 50%" >PRODUCT</th>
                            <th style="width: 50%" class="text-right">TOTAL</th>
                          </tr>
                          </thead>
                          <tbody>
                          <tr v-for="(iteam, index) in orderData.items" :key="index">
                            <td>{{ iteam.name}}</td>
                            <td class="text-right">${{ iteam.total_count.toFixed(2) | currency}}</td>
                          </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                    <div class="left-space">
                      <div class="order-table">
                        <div class="table-responsive">
                          <table class="table">
                            <thead>
                            <tr>
                              <th style="width: 50%">SUBTOTAL</th>
                              <th style="width: 50%" class="text-right">${{ orderData.sub_total | currency}}</th>
                            </tr>
                            </thead>
                            <tbody>
                              <tr v-if="orderData.discount_amount > 0">
                                <td>DISCOUNT</td>
                                <td class="text-right">- ${{ orderData.discount_amount.toFixed(2) | currency}}</td>
                              </tr>
                              <tr>
                                <td>DELIVERY FEE</td>
                                <td class="text-right">${{ orderData.delivery_fee.toFixed(2) | currency}}</td>
                              </tr>
                              <tr v-if="orderData.sales_tax > 0">
                                <td>SALES TAX</td>
                                <td class="text-right">${{ orderData.sales_tax.toFixed(2) | currency}}</td>
                              </tr>

                              <tr>
                                <td class="total-text">TOTAL</td>
                                <td class="total-text text-right">${{ orderData.grand_total.toFixed(2) | currency}}</td>
                              </tr>
                            </tbody>
                          </table>

                        </div>
                      </div>
                      <div class="row bottom-space">
                        <div class="col-sm-6">
                          <div class="type-sub-btn report-btn new-space">
                            <a href="#" @click.prevent="invoice">DOWNLOAD INVOICE</a>
                          </div>
                        </div>

                        <div class="col-sm-6">
                          <div class="type-sub-btn ">
                            <a href="#" @click.prevent="invoice">DOWNLOAD ORDER REPORT</a>
                          </div>
                        </div>
                      </div>
                    </div>

                  </div>
                  <div class="col-sm-1"></div>
                </div>

                <div class="signature-section">PICKUP DETAILS</div>
                <div class="row">
                  <div class="col-sm-5">
                    <div class="pickup-loac-map">
                      <GmapMap
                        :center="dispesaryStartLocation"
                        :zoom="15"
                        map-type-id="terrain"
                        style="width: 375px; height: 300px"
                        >
                        <gmap-info-window :options="dispesaryStartLocation"  :position="dispesaryStartLocation" 
                          :opened="infoOpenedDispesary" @closeclick="infoOpenedDispesary=false">
                          <p v-html=" getAddress(orderData.dispensary_data.address)"></p>
                        </gmap-info-window>
                        <gmap-marker :position="dispesaryStartLocation" :clickable="true" :draggable="true" @click="getCustomerPositionInfo('Dispesary')"/>
                        </GmapMap>
                    </div>
                  </div>
                  <div class="col-sm-4">
                    <div class="pickup-address">
                      <h3>PICKUP LOCATION</h3>
                      <div class="row">
                        <div class="col-sm-4">
                          <div class="light-gray-font">
                            Dispensary
                          </div>
                        </div>
                        <div class="col-sm-8">
                          <p class="dark-sub-font">{{ orderData.busniess_name}}</p>
                        </div>

                      </div>
                      <div class="row">
                        <div class="col-sm-4">
                          <div class="light-gray-font">
                            Email:
                          </div>
                        </div>
                        <div class="col-sm-8">
                          <p class="dark-sub-font">
                             <a v-bind:href="'mailto:' + orderData.dispensary_data.address.name_of_contact">{{ orderData.dispensary_data.address.name_of_contact }}</a>  
                          </p>
                        </div>

                      </div>
                      <div class="row">
                        <div class="col-sm-4">
                          <div class="light-gray-font">
                            Phone:
                          </div>
                        </div>
                        <div class="col-sm-8">
                          <p class="dark-sub-font">{{ orderData.dispensary_data.address.phone_number | phoneNoFormate}}</p>
                        </div>

                      </div>
                      <div class="row">
                        <div class="col-sm-4">
                          <div class="light-gray-font">
                            Address:
                          </div>
                        </div>
                        <div class="col-sm-8">
                          <p class="dark-sub-font" v-html=" getAddress(orderData.dispensary_data.address)">
                          </p>


                          <div class="direction-btn" v-if="htmlHideDivOder">
                            <i class="fa fa-map-marker" aria-hidden="true"></i>

                            <a href="#" v-if="directionShowMap" @click.prevent="getDirection('Dispesary')">SHOW DIRECTIONS</a>
                          </div>
                        </div>

                      </div>


                    </div>

                  </div>
                  <div class="col-sm-3"></div>
                </div>

                <div class="acc-top-space"></div>
                <div class="signature-section">DROPOFF DETAILS</div>
                <div class="row bottom-space">
                  <div class="col-sm-5">
                    <div class="pickup-loac-map">
                      <GmapMap
                        :center="customerStartLocation"
                        :zoom="15"
                        map-type-id="terrain"
                        style="width: 375px; height: 300px"
                        >
                        <gmap-info-window :options="customerStartLocation"  :position="customerStartLocation" 
                          :opened="infoOpenedCustomer" @closeclick="infoOpenedCustomer=false">
                          <p v-html=" customerFullAddress"></p>
                        </gmap-info-window>
                        <gmap-marker :position="customerStartLocation" :clickable="true" :draggable="true" 	@click="getCustomerPositionInfo('Customer')"/>
                      </GmapMap>
                        
                      
                    </div>
                  </div>
                  <div class="col-sm-5">
                    <div class="pickup-address">
                      <h3>DROPOFF LOCATION</h3>
                      <div class="row">
                        <div class="col-sm-4">
                          <div class="light-gray-font">
                            Customer:
                          </div>
                        </div>
                        <div class="col-sm-8">
                          <p class="dark-sub-font">{{ orderData.customer_name}}</p>
                        </div>

                      </div>
                      <div class="row">
                        <div class="col-sm-4">
                          <div class="light-gray-font">
                            Email:
                          </div>
                        </div>
                        <div class="col-sm-8">
                          <p class="dark-sub-font">
                            <a v-bind:href="'mailto:' + this.orderData.customer_email">{{ this.orderData.customer_email }}</a>
                          </p>
                        </div>

                      </div>
                      <div class="row">
                        <div class="col-sm-4">
                          <div class="light-gray-font">
                            Phone:
                          </div>
                        </div>
                        <div class="col-sm-8">
                          <p class="dark-sub-font">{{ customerPhoneNo | phoneNoFormate }}</p>
                        </div>

                      </div>
                      <div class="row">
                        <div class="col-sm-4">
                          <div class="light-gray-font">
                            Patient ID Number:
                          </div>
                        </div>
                        <div class="col-sm-8">
                          <p class="dark-sub-font">{{ orderData.patient_id }}</p>
                        </div>

                      </div>
                      <div class="row" v-if="htmlHideDivOder">
                        <div class="col-sm-4">
                          <div class="light-gray-font">
                            Experation Date:
                          </div>
                        </div>
                        <div class="col-sm-8">
                          <p class="dark-sub-font">{{ orderData.updated_at | longDate}}</p>
                        </div>

                      </div>

                      <div class="row">
                        <div class="col-sm-4">
                          <div class="light-gray-font">
                            Address:
                          </div>
                        </div>
                        <div class="col-sm-8">
                          <p class="dark-sub-font" v-html="customerFullAddress"></p> 
                          

                          <div class="direction-btn" v-if="htmlHideDivOder">
                            <i class="fa fa-map-marker" aria-hidden="true"></i>
                            <a href="#" v-if="directionShowMap" @click.prevent="getDirection('Customer')">SHOW DIRECTIONS</a>
                            <span v-else >NEED THE BROWSER PERMISSION</span>
                          </div>
                        </div>

                      </div>
                    </div>

                  </div>
                  <div class="col-sm-2"></div>
                </div>

              </div>

            </div>
          </div>
        </div>

      </div>
    </section>

    <div v-if="htmlHideDivOder" class="modal fade" id="directionModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">Direction Map</h4>
          </div>
          <div class="modal-body">
           <GmapMap ref="map"
              :center="customerStartLocation"
              :zoom="15"
              map-type-id="terrain"
              style="width: 800px; height: 375px"
            ></GmapMap>
          </div>
          
        </div>
      </div>
    </div>
    <div v-if="htmlHideDivOder" class="modal fade" id="DispensaryDirectionModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">Direction Map</h4>
          </div>
          <div class="modal-body">
           <GmapMap ref="DispensaryDirectionModal"
              :center="customerStartLocation"
              :zoom="15"
              map-type-id="terrain"
              style="width: 800px; height: 375px"
            ></GmapMap>
          </div>
          
        </div>
      </div>
    </div>
    <orderInovice :id="this.dataId.id"/>
  </div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import evenBus from "../../../event-bus";
import orderInovice from "./OrderInvoiceComponent";
import orderStatus from "../../../order-status";

export default {
  props: {
    orderData: {
      required: true,
    },
  },
  data() {
    return {
      dataLoader:true,
      error: null,
      spinnerShow: false,
      hideDiv: [orderStatus.complate,orderStatus.delivaryFalied,orderStatus.returnComplate,orderStatus.cancel],
      dataId: {
        id: this.orderData.id,
      },
      customerFullAddress: null,
      customerStartLocation:{},
      dispesaryStartLocation:{
        lat:this.orderData.dispensary_data.address.latitude,
        lng:this.orderData.dispensary_data.address.longitude
      },
      infoOpenedCustomer: false,
      infoOpenedDispesary: false,
      customerPhoneNo:null,
      processingError:null,
      htmlHideDivOder:true,

      directionShowMap: true,
      currentPosition: {},

    };
  },
  
  computed:{
    getAddress: (app)=> (fullAddress)=> {

        let street2 = '';
        if(fullAddress.street2){
          street2 = fullAddress.street2 +' '
        }
        return `${fullAddress.street1 +' ' + street2 +'<br>' + fullAddress.city +', '  + fullAddress.state +', ' + fullAddress.zip_code}`
    }
  },

  mounted() {
    const plugin = document.createElement("script");
    plugin.setAttribute(
      "src",
      "//cdnjs.cloudflare.com/ajax/libs/jspdf/1.0.272/jspdf.debug.js"
    );
    plugin.async = true;
    document.head.appendChild(plugin);
  },
  components: {
      orderInovice,
  },
  created() {
    
    this.getOrderDetail();
  },
  methods: {
    
    async getDirection(clickForm) { 
      
      var vueInstance = this;
       
      var directionsService = new google.maps.DirectionsService;
      var directionsDisplay = new google.maps.DirectionsRenderer;
      directionsDisplay.setMap(vueInstance.$refs.map.$mapObject);
      //google maps API's direction service
      let destination = '';
      if(clickForm == "Customer"){
        $('#directionModal').modal('show');
        directionsDisplay.setMap(vueInstance.$refs.map.$mapObject);
        destination =  vueInstance.customerStartLocation
      }
      else{
        $('#DispensaryDirectionModal').modal('show');
        directionsDisplay.setMap(vueInstance.$refs.DispensaryDirectionModal.$mapObject);  
        destination =  vueInstance.dispesaryStartLocation 
      }
     
        let start =  vueInstance.currentPosition
        directionsService.route({
          origin: start,
          destination: destination,
          travelMode: 'DRIVING'
        }, function(response, status) {
          if (status === 'OK') {
            vueInstance.directionShowMap = true
            directionsDisplay.setDirections(response);
          } else {
            window.alert('Directions request failed due to ' + status);
          }
        });   
    },
    
    async getCustomerPositionInfo(opnedFrom) {
      if(opnedFrom == 'Customer'){
        this.infoOpenedCustomer = true
      }else{
        this.infoOpenedDispesary = true
      }
      
    },

    async getOrderDetail() { 
      
      let dataValues = null;
      var vueInstance = this;
      if(vueInstance.hideDiv.includes(vueInstance.orderData.status)){ 
        vueInstance.htmlHideDivOder = false;
      }
      vueInstance.directionShowMap = false
      navigator.geolocation.getCurrentPosition(
        position => { 
          vueInstance.currentPosition={
            lat : position.coords.latitude,
            lng : position.coords.longitude
          }
          vueInstance.directionShowMap = true 
        },
        error => {
          console.log(error.message);
        },
      )
      
      var customerAddress = vueInstance.orderData.addresses;
      var customerAddressIndex = customerAddress.findIndex(item => item.type != 'BILLING');
      vueInstance.customerPhoneNo = customerAddress[customerAddressIndex].phone_number

      let street2 = '';
      if(customerAddress[customerAddressIndex].street_2){
        street2 = customerAddress[customerAddressIndex].street_2+' '
      }
      vueInstance.customerFullAddress= customerAddress[customerAddressIndex].street_1 +' '+ street2 + '<br>' + customerAddress[customerAddressIndex].city +', ' +customerAddress[customerAddressIndex].state +', ' +customerAddress[customerAddressIndex].zip_code   
      vueInstance.customerStartLocation ={
        lat : parseFloat(customerAddress[customerAddressIndex].latitude),
        lng : parseFloat(customerAddress[customerAddressIndex].longitude)
      }
      vueInstance.dataLoader=false
    },

    invoice() {
      evenBus.$emit("getInvoice");
    },
    orderListBack() {
      evenBus.$emit("backToList");
    },
  },
};
</script>

<style scoped>
.write-review a {
  background: #8dc73f;
  padding: 15px 18px;
  text-align: center;
  color: #fff;
  text-decoration: none;
  display: block;
}
.modal-dialog {
  width: 60%;
  height: 60%;
  padding: 0;
}

.modal-content {
  height: 100%;
  border-radius: 0;
}
</style>