<template>
  <div class="footer" id="footer-section">
    <div class="container">
      <div class="row">
        <div class="col-lg-10 col-lg-offset-1">
          <div class="row animatedParent" data-sequence="200">
            <div class="col-sm-4 animated fadeInLeftShort" data-id="1">
              <div class="contact hidden-xs">
                <img :src="footerLogo" class="img-responsive" />
              </div>
            </div>
            <div class="col-sm-4 animated fadeInLeftShort" data-id="2">
              <div class="contact">
                <ul class="col-sm-6 white-text">
                  <li>
                    <router-link  v-if="role=='Customer'" :to="{ name: 'myOrdersSetting' }">
                      MY ORDERS
                    </router-link>
                    <router-link  v-else-if="role=='Dispensary'" :to="{ name: 'home' }">
                        
