                    </router-link>
                  </li>
                  <li>
                    <router-link  v-if="role=='Customer'" :to="{ name: 'customerProfile' }">
                       MY PROFILE
                    </router-link>
                    <router-link  v-else-if="role=='Dispensary'" :to="{ name: 'home' }">
                        
                    </router-link>
