                    </router-link>
                  </li>

                  <li>
                    <router-link  v-if="role=='Customer'" :to="{ name: 'dispensarySearch' }">
                      NEAR BY DISPENSARY
                    </router-link>
                    <router-link  v-else-if="role=='Dispensary'" :to="{ name: 'home' }">
                        
                    </router-link>
                    <router-link v-else :to="{ name: 'customerRegistration' }">
                        CUSTOMER
                    </router-link>
                  </li>
                  <li>
                    <router-link :to="{ name: 'about' }">ABOUT US</router-link>
                  </li>
                </ul>
                <ul class="col-sm-6 gray-text">
                  <li>
                    <router-link to="/">ADVERTISE WITH US</router-link>
                  </li>
                  <li>
                    <router-link to="/">BLOG</router-link>
                  </li>
                  <li>
                    <router-link to="/">CONTACT US</router-link>
                  </li>
                  <li>
                    <router-link to="/">CAREERS</router-link>
                  </li>
                  <li>
