                  </li>
                  <li>
                    <router-link :to="{name : 'help'}">HELP</router-link>
                  </li>
                </ul>
              </div>
            </div>
            <div class="col-sm-4 animated fadeInRightShort" data-id="3">
              <div class="contact"><div v-if="error">{{error}}</div>
                <p>Join Our Newsletter</p>
                <div class="input-group">
                  <input v-model.trim="newsEmail" @input="$v.newsEmail.$touch()" type="text" class="form-control" placeholder="Enter email address" />
                  
                  <span class="input-group-btn">
                   <button class="btn join-btn" @click.prevent="submitNewsEmail" type="button">
                       <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>join</button>
                  </span>
                  
                </div>
                <div v-if="$v.newsEmail.$dirty">
                    <p class="text-danger" v-if="!$v.newsEmail.required">Please enter email address.</p>
                    <p class="text-danger" v-if="!$v.newsEmail.email">Please enter valid email address.</p>
                  </div>
                <div class="social-media">
