<script>
/*eslint-disable */
const $ = window.jQuery = require('jquery');  

import firebase from "firebase";
import * as fb from "../../firebaseConfig";
import "firebase/messaging";
import {required,email} from "vuelidate/lib/validators";
export default {
  data() {
    return {
      footerLogo:this.$store.state.API_URL_ROOT+"images/footer-logo.png",
      loginUser: null,
      role:null,
      usePublicVapidKey:this.$store.state.fcmPublicVapidKey,
      messaging: firebase.messaging(),
      newsEmail: null,
      error: false,
      spinnerShow: false,
    }
  },
  validations: {
    newsEmail: {
      required,
      email
    },
  },
  created() {
    this.loginUser = firebase.auth().currentUser || false;
    this.role = this.$store.state.userProfile.role;

    if(this.loginUser){ 
        if(this.$store.state.userProfile.role==null)
            this.checkExsitUserRole();
            
        if(!this.$store.state.currentDeviceToken)    
            this.notificationPermission()    
        this.receiveMessage()
    }
  },
  methods: {
      async submitNewsEmail(){ 
          var vueInstance = this;
          vueInstance.error = false;
          vueInstance.$v.$touch()
         
          if (!vueInstance.$v.$invalid) {
            await fb.newsletterCollection.where("email", "==", vueInstance.newsEmail).get().then((doc) => {
              if (doc.empty) {
                vueInstance.spinnerShow = true;
                $('.btn').prop("disabled", true);
                let currentTime = firebase.firestore.Timestamp.now().toMillis();
                let dataInsert = fb.newsletterCollection.doc();
                dataInsert.set({
                  email: vueInstance.newsEmail,
                  created_at: currentTime,
                }).then(function () {
                  $('.btn').prop("disabled", false);
                  vueInstance.error = "<p class='alert alert-success'>Thanks for subscribing us.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                  vueInstance.spinnerShow = false;
                });
            } else {
                  $('.btn').prop("disabled", false);
                  vueInstance.spinnerShow = false;
                  vueInstance.error = "<p class='alert alert-danger'>You're already subscribed.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                } 
              })
          }
        },
        logout() {
            this.$store.dispatch("logout");
        },
        async checkExsitUserRole(){
            await this.$store.dispatch("fetchUserProfile")   
            this.role = await this.$store.state.userProfile.role;
        },
        
        receiveMessage() {
            var vueInstance = this;
            try {
                vueInstance.messaging.onMessage((payload) => {
                    console.log(payload.notification);
                    var  title =payload.notification.title;
                    var options ={
                        body: payload.notification.body,
                        icon: payload.notification.icon,
                        image: payload.notification.image,
                        data:{
                            time: new Date(Date.now()).toString(),
                            click_action: payload.notification.click_action
                        }
                    };
                    var myNotification = new Notification(title, options)
                });
            } catch (e) {
                console.log(e);
            }
        },
        async notificationPermission () { 
           var vueInstance = this;
            vueInstance.messaging.usePublicVapidKey(this.usePublicVapidKey)
            vueInstance.messaging.requestPermission().then(() => {
                vueInstance.messaging.getToken().then((token) => { 
                  if (token !== this.$store.state.currentDeviceToken) {  console.log("currentDeviceToken" + token);
                    fb.usersCollection.doc(fb.auth.currentUser.uid).update({
                        device_id: token,
                      });
                   Vue.set(this.$store.state, 'currentDeviceToken', token);
                  }
                }).catch((err) => console.log('--- token error:', err))
          }).catch(function (err) {
            console.log('Unable to get permission to notify.', err)
          })
        },
     },
}
</script>
