<template>
  <div class="row contact-form">
    <div class="col-sm-2"></div>
    <div class="col-sm-8"><flash-message></flash-message></div>
    <div class="col-sm-2"></div>
    <form @submit.prevent="UpdateDocumentForm">
    <div class="col-sm-5">
      <div class="contact-title border-bottom">
        <h3>Documents</h3>
      </div>
      <div class="media-heading top-spce">IDENTIFICATION INFORMATION</div>

      <div class="form-group">
        <select class="form-control" v-model.trim="DocumnetFormData.identification">
              <option value="" selected>Select Type of Identification*</option>
              <option value="Driver License">Drivers License</option>
              <option value="State ID">State ID</option>
            </select>
            <div v-if="$v.DocumnetFormData.identification.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.identification.required">Please select type of identification.</p>
            </div>
      </div>
      <div class="form-group">
        <select class="form-control" v-model.trim="DocumnetFormData.issuingState">
              <option value="">Issuing State *</option>
              <option v-for="(state, index) in getStateData" :key="index" :value="state">{{ state }}</option>
            </select>
             <div v-if="$v.DocumnetFormData.issuingState.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.issuingState.required">Please select issuing state.</p>
            </div>
      </div>

      <div class="form-group top-spce">
        <input v-model.trim="DocumnetFormData.driverLicenseNo" @input="$v.DocumnetFormData.driverLicenseNo.$touch()"
              class="form-control"
              placeholder="Drivers License Number *"
              type="text"
            />
             <div v-if="$v.DocumnetFormData.driverLicenseNo.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.driverLicenseNo.required">Please enter your driver license number.</p>
            </div>
      </div>

      <div class="input-group">
        <input v-model.trim="DocumnetFormData.expirationDate" @input="$v.DocumnetFormData.expirationDate.$touch()"
              class="form-control calender-date"
              aria-describedby="basic-addon2"
              type="text"
              placeholder="Expiration Date (MM/DD/YYYY) *"
              id="expirationDate"
              name="expirationDate"
            />
         
        <span class="input-group-addon cuorser" id="basic-addon2"
          ><i class="fa fa-calendar" aria-hidden="true"></i
        ></span>
      </div>
      <div v-if="$v.DocumnetFormData.expirationDate.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.expirationDate.required">Please select expiration date.</p>
            </div>

      <div class="right-title margin-top spaceing">
        <h2>MEDICAL INFORMATION</h2>
      </div>

      <div class="form-group">
        <input v-model.trim="DocumnetFormData.patientId" @input="$v.DocumnetFormData.patientId.$touch()"
              class="form-control"
              placeholder="Patient ID Number *"
              type="text"
            />
             <div v-if="$v.DocumnetFormData.patientId.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.patientId.required">Please enter patient id.</p>
            </div>
      </div>
      <div class="input-group">
        <input v-model.trim="DocumnetFormData.recommendationDate" @input="$v.DocumnetFormData.recommendationDate.$touch()"
              class="form-control calender-date"
              aria-describedby="basic-addon2"
              type="text"
              placeholder="Doctor's Recommendation Expiration Date"
              id="recommendationDate"
              name="recommendationDate"
            />
            <span class="input-group-addon cuorser" id="basic-addon2"
              ><i class="fa fa-calendar" aria-hidden="true"></i
            ></span>
             
          </div>
          <div v-if="$v.DocumnetFormData.recommendationDate.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.recommendationDate.required">Please select recommendation date*.</p>
            </div>
    </div>

    <div class="col-sm-2"></div>

    <div class="col-sm-5">
      <div class="contact-title border-bottom">
        <h3>Upload Photos</h3>
      </div>
      <div class="driver-details">
        <div class="upload-text">
          <p>Photo of Drivers License.</p>
        </div>
        <p class="doct-content">
          Please upload a photo of your <span>DOCTORS RECOMMENDATION. </span>.
          Please make sure we can read all the letters and numbers on the
          license. *
        </p>
        <div class="row top-spce">
          <div class="row-height">
            <div class="col-sm-6 col-sm-height">
              <div class="driver-photo">
                <img v-if="recommendationFile" :src="recommendationFile" class="img-responsive" />
                <p id="fileNamerecommendation"></p>
              </div>
            </div>
            <div class="col-sm-6 col-sm-height col-middle">
              <div class="remove-btn">
                <a href="#" v-if="recommendationFile" @click.prevent="removeRecommendationImage">Remove</a>
                <input
                  type="file"
                  @change="onFileRecommendationChange"
                  accept="image/jpeg"
                  id="uploadRecommendationImage"
                  style="display:none"
                />
                <a href="#" v-if="!recommendationFile" @click.prevent="addRecommendationImage">Add</a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="driver-details">
        <div class="upload-text">
          <p>Government Photo ID</p>
        </div>
        <p class="doct-content">
          Please upload a photo of proof of your
          <span>GOVERNMENT PHOTO IDENTIFICATION </span>. Please make sure we can
          read all of the numbers/letters in the image. *
        </p>
        <div class="row top-spce">
          <div class="row-height">
            <div class="col-sm-6 col-sm-height">
              <div class="driver-photo">
                <img
                  v-if="govermentIdFile" :src="govermentIdFile"
                  class="img-responsive"
                />
                <p id="fileNameGoverment"></p>
              </div>
            </div>
            <div class="col-sm-6 col-sm-height col-middle">
              <div class="remove-btn">
                <a href="#" v-if="govermentIdFile" @click.prevent="removeGovermentIdImage">Remove</a>
                <input
                  type="file"
                  @change="onFileGovermentChange"
                  accept="image/jpeg"
                  id="uploadGovermentImage"
                  style="display:none"
                />
                <a href="#" v-if="!govermentIdFile" @click.prevent="addGovermentIdImage">Add</a>
              </div>
            </div>
          </div>
        </div>
      </div>

   <!--   <div class="driver-details">
        <div class="upload-text">
          <p>PHOTO OF ADDRESS</p>
        </div>
        <p class="doct-content">
          Delivering to a different address than the one shown on your ID?
          Please upload a photo of a
          <span> document with your name and correct delivery address </span>.on
          it (ex. utility bill, lease, hotel receipt). Please make sure we can
          read all of the numbers/letters in the image. *
        </p>

        <div class="row top-spce">
          <div class="row-height">
            <div class="col-sm-6 col-sm-height">
              <div class="driver-photo">
                <img v-if="deliveringFile" :src="deliveringFile" class="img-responsive" />
                <p id="fileNameDelivering"></p>
              </div>
            </div>
            <div class="col-sm-6 col-sm-height col-middle">
              <div class="remove-btn">
                <a href="#" v-if="deliveringFile" @click.prevent="removeDeliverImage">Remove</a>
                <input
                  type="file"
                  @change="onFileDeliverChange"
                  accept="image/jpeg"
                  id="uploadDeliverImage"
                  style="display:none"
                />
                <a href="#" v-if="!deliveringFile" @click.prevent="addDeliverImage">Add</a>
              </div>
            </div>
          </div>
        </div>
      </div>-->
      <button class="btn send-btn" :disabled="$v.$invalid">
        <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>SAVE</button>
    </div>
    </form>
  </div>
</template>

<script>
import {
  required,
  numeric,
  alpha,
  sameAs,
  requiredIf,
} from "vuelidate/lib/validators";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
export default {
  data() {
    return {
      getStateData: [],
      image:null,
      DocumnetFormData:{
        identification:"",
        issuingState:"",
        driverLicenseNo:null,
        expirationDate:null,
        patientId:null,
        recommendationDate:null,
      },
      spinnerShow: false,
      error: null,

      recommendationFile:null,
      govermentIdFile:null,
   //   deliveringFile:null,

      uploadImage:{
        deliverImage: null,
        govermentIdImage: null,
     //   recommendationImage: null,
      },
    };
  },
  validations: {
    DocumnetFormData: {
      identification: {
        required,
      },
      issuingState: {
        required,
      },
      driverLicenseNo: {
        required,
      },
      expirationDate: {
        required,
      },
      patientId: {
        required,
      },
      recommendationDate: {
        required,
      },
    },
    recommendationFile: {
      required,
    },
    govermentIdFile: {
      required,
    },
    /* deliveringFile: {
      required,
    },
    */
  },
  
  mounted() {
      $(".flash__wrapper").empty();
    this.fbState();
    this.customerDocuments();
    var vm = this;
    $("input[name=expirationDate]").datepicker({ 
      changeMonth: true,
      changeYear: true,
      minDate: 0,
      onSelect: (dateForm) => {
        vm.DocumnetFormData.expirationDate = dateForm;
      },
    });
    $("input[name=recommendationDate]").datepicker({ 
      changeMonth: true,
      changeYear: true,
      minDate: 0,
      onSelect: (dateForm) => {
        vm.DocumnetFormData.recommendationDate = dateForm;
      },
    });
  },
  methods: {

    removeRecommendationImage(){
      this.recommendationFile= null; 
      this.uploadImage.recommendationImage = null;
      $('#fileNamerecommendation').html('');
    },

    onFileRecommendationChange(e) {
      const file = e.target.files[0];
      this.uploadImage.recommendationImage = file;
      $('#fileNamerecommendation').html(file.name);
      this.recommendationFile = URL.createObjectURL(file);
    },
    
    addRecommendationImage() {
      $("#uploadRecommendationImage").trigger("click");
    },

    removeGovermentIdImage(){
      this.govermentIdFile= null; 
      this.uploadImage.govermentIdImage = null;
      $('#fileNameGoverment').html('');
    },

    onFileGovermentChange(e) { 
      const file = e.target.files[0];
      this.uploadImage.govermentIdImage = file;
      $('#fileNameGoverment').html(file.name);
      this.govermentIdFile = URL.createObjectURL(file);
    },
    
    addGovermentIdImage() {
      $("#uploadGovermentImage").trigger("click");
    },

  /*  removeDeliverImage(){
      this.deliveringFile= null; 
      this.uploadImage.deliverImage = null;
      $('#fileNameDelivering').html('');
    },

    onFileDeliverChange(e) {
      const file = e.target.files[0];
      this.uploadImage.deliverImage = file;
      $('#fileNameDelivering').html(file.name);
      this.deliveringFile = URL.createObjectURL(file);
    },
    
    addDeliverImage() {
      $("#uploadDeliverImage").trigger("click");
    },
*/
    async uploadImageAsPromise(imageDataUrl) {
      let userId = fb.auth.currentUser.uid;
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      const type = imageDataUrl.type;
      return new Promise(function (resolve, reject) {
      var storageRef = firebase
          .storage()
          .ref(`customerDoc/${userId}/${currentTime}.`+type)
          .put(imageDataUrl, {
              contentType: type,
          });
        storageRef.then((data) => {
          data.ref.getDownloadURL().then((url) => {
            resolve(url);
          });
        });
      });
    },

    async UpdateDocumentForm() {

     
    // console.log(recommendationImage);
    // return false;

      $(".flash__wrapper").empty();
      this.error = null;
      this.spinnerShow = true;
      if (!this.$v.$invalid) {
        $('.btn').prop("disabled", true);
        var documentImagesData= false;
        var recommendationImage = this.uploadImage.recommendationImage;
        var govermentIdImage = this.uploadImage.govermentIdImage;
       // var deliveringImage = this.uploadImage.deliverImage;
        var vm= this; 
        let dataUpdate = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_account');
       // if(recommendationImage ||govermentIdImage ||deliveringImage){
        if(recommendationImage ||govermentIdImage){
          let document_images={};
         
          if(recommendationImage){
            await this.uploadImageAsPromise(recommendationImage).then((url) => {
             document_images['doctor_recommendation_letter']=url;
             dataUpdate.set({document_images:document_images},{merge:true})
            });
          }
          if(govermentIdImage){
            await this.uploadImageAsPromise(govermentIdImage).then((url) => {
              document_images['government_photo_id']=url;
              dataUpdate.set({document_images:document_images},{merge:true})
            });
          }
    /*      if(deliveringImage){
            await this.uploadImageAsPromise(deliveringImage).then((url) => {
              document_images['address_proof']=url;
              dataUpdate.set({document_images:document_images},{merge:true})
            });
          }
          */
        }
        
        
        
        dataUpdate.update({
          "document_info.document_type": this.DocumnetFormData.identification,
          "document_info.issuing_state": this.DocumnetFormData.issuingState,
          
          "document_info.expiration_date": await firebase.firestore.Timestamp.fromDate(new Date(this.DocumnetFormData.expirationDate)).toMillis(),
          "document_info.recommendation_letter_expiration_date": await firebase.firestore.Timestamp.fromDate(new Date(this.DocumnetFormData.recommendationDate)).toMillis(),
          
          "document_info.identity_number":  this.DocumnetFormData.driverLicenseNo,
          "document_info.patient_id": this.DocumnetFormData.patientId,
          
        }) 
        .then((success) => {

         
          this.flashSuccess("You have successfully update your document information.", {
            timeout: 10000,
          });
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        
        })
        .catch((err) => {
          this.flashError("There is some problem. Please try again later."+err, {
            timeout: 10000,
          });
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        });
      }
    },

    
    async customerDocuments() {
      await this.$store.dispatch("getCustomerUser");
      let customerData = await this.$store.state.customerData;
      this.DocumnetFormData.identification = customerData.document_info.document_type;
      this.DocumnetFormData.issuingState = customerData.document_info.issuing_state;
      this.DocumnetFormData.driverLicenseNo =customerData.document_info.identity_number;
      var expirationDate  = firebase.firestore.Timestamp.fromMillis(customerData.document_info.expiration_date).toDate().toUTCString();
      var exDate = new Date (expirationDate);
      this.DocumnetFormData.expirationDate=  exDate.toLocaleString ([], {year: 'numeric', month: '2-digit', day: '2-digit'});
      this.DocumnetFormData.patientId = customerData.document_info.patient_id;
    
      var recommendationDate  = firebase.firestore.Timestamp.fromMillis(customerData.document_info.recommendation_letter_expiration_date).toDate().toUTCString();
      var reDate = new Date (recommendationDate);
      this.DocumnetFormData.recommendationDate=  reDate.toLocaleString ([], {year: 'numeric', month: '2-digit', day: '2-digit'});
    
      this.recommendationFile = customerData.document_images.doctor_recommendation_letter;
      this.govermentIdFile = customerData.document_images.government_photo_id;
    //  this.deliveringFile = customerData.document_images.address_proof;
    },
   
    async fbState() {
      await this.$store.dispatch("getAllState");
      this.getStateData = this.$store.state.GetState[0].state;
    },
  },
};
</script>
