<script>
import registrationLogin from "../../components/forntEnd/dispensary/RegistrationLoginComponent";
import registrationProfile from "../../components/forntEnd/dispensary/RegistrationProfileComponent";
import registrationInfo from "../../components/forntEnd/dispensary/RegistrationInfoComponent";
export default {
    data() {
        return {
            tempRegistrationFormData: null,
            randomString: null,
            email: null,
            spinnerShow: false,
        };
    },
    components: {
        registrationLogin,
        registrationProfile,
        registrationInfo,
    },

    created() {
        this.randomString = this.$route.params.randomString;
         this.CheckDataExist();
         
    },
    methods: {
        async CheckDataExist() {
            var vueInstance = this;
            await vueInstance.$store.dispatch("checkTempUserExit", {
                id: vueInstance.$route.params.randomString,
            });
           let checkTempRegExist = await vueInstance.$store.state.tempRegistrationData;
            if (checkTempRegExist == false) {
                vueInstance.flashError("Please enter your first basic information with below form.", {
                    timeout: 8000,
                });
                vueInstance.$router.push({ path: "/dispensary" })
            }
            vueInstance.tempRegistrationFormData = checkTempRegExist;
            vueInstance.email = checkTempRegExist.email;
            localStorage.clear();
        },
    },
}; 
</script>
