<template>
  <div class="row">
    <div class="col-lg-11 col-lg-offset-1" v-if="dispensaryData.user_id">
      <div class="tab-title">
        <h2>
          <span></span> {{ dispensaryData.busniess_name || ""}}
        </h2>
      </div>
      <div class="row">

        <div class="col-sm-12">
          <div class="account-form">
            <div class="border-bottom"></div>
            <div class="acc-top-space">
              <div class="form-group">
                <div class="col-sm-2">
                  <label>First Name</label>
                </div>
                <div class="col-sm-4">
                  {{ dispensaryData.address.first_name || ""}}
                </div>
                <div class="col-sm-2">
                  <label>Last Name</label>
                </div>
                <div class="col-sm-4">
                  {{ dispensaryData.address.last_name || ""}}
                </div>
              </div>
              
            </div>
          </div>
        </div>

        <div class="col-sm-12">
          <div class="account-form">
            <div class="border-bottom"></div>
            <div class="acc-top-space">
              <div class="form-group">
                <div class="col-sm-2">
                  <label>Business Name</label>
                </div>
                <div class="col-sm-4">
                  {{ dispensaryData.busniess_name || ""}}
                </div>
             
                <div class="col-sm-2">
                  <label>Email Id</label>
                </div>
                <div class="col-sm-4">
                   
                </div>
              </div>
              
            </div>
          </div>
        </div>

        

        <div class="col-sm-12">
          <div class="account-form">
            <div class="border-bottom"></div>
            <div class="acc-top-space">
              <div class="form-group">
                <div class="col-sm-2">
                  <label>Street1</label>
                </div>
                <div class="col-sm-4">
                  {{ dispensaryData.address.street1 || ""}}
                </div>
                <div class="col-sm-2">
                  <label>Street2</label>
                </div>
                <div class="col-sm-4">
                   {{ dispensaryData.address.street2 || "-"}}
                </div>
              </div>
              
            </div>
          </div>
        </div>

        <div class="col-sm-12">
          <div class="account-form">
            <div class="border-bottom"></div>
            <div class="acc-top-space">
              <div class="form-group">
                <div class="col-sm-2">
                  <label>City</label>
                </div>
                <div class="col-sm-2">
                  {{ dispensaryData.address.city || ""}}
                </div>
                <div class="col-sm-2">
                  <label>State</label>
                </div>
                <div class="col-sm-2">
                   {{ dispensaryData.address.state || ""}}
                </div>
                <div class="col-sm-2">
                  <label>Zip Code</label>
                </div>
                <div class="col-sm-2">
                   {{ dispensaryData.address.zip_code || ""}}
                </div>
              </div>
              
            </div>
          </div>
        </div>

         <div class="col-sm-12">
          <div class="account-form">
            <div class="border-bottom"></div>
            <div class="acc-top-space">
              <div class="form-group">
                <div class="col-sm-2">
                  <label>Phone Number</label>
                </div>
                <div class="col-sm-4">
                   {{ dispensaryData.address.phone_number | phoneNoFormate}}
                </div>
                <div class="col-sm-2">
                  <label>License Number</label>
                </div>
                <div class="col-sm-4">
                  {{ dispensaryData.license_number || ""}}
                </div>
                
              </div>
              
            </div>
          </div>
        </div>

         <div class="col-sm-12">
          <div class="account-form">
            <div class="border-bottom"></div>
            <div class="acc-top-space">
              <div class="form-group">
                <div class="col-sm-2">
                  <label>Opening Hours</label>
                </div>
                <div class="col-sm-4">
                <ul class="hours-listing">
                  <li class="col-sm-3">Mon:</li>
                  <li class="col-sm-9" v-if="dispensaryData.opening_hours.some((openingDay,i) => openingDay.day === 'Monday')">
                    <span v-for="(openingtime,index) in dispensaryData.opening_hours" :key="index">
                      <span v-if="(openingtime.day === 'Monday')">
                        {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                      </span>
                    </span>
                  </li>
                  <li v-else class="col-sm-9 text-danger">CLOSED</li>

                  <li class="col-sm-3">Tue:</li>
                  <li class="col-sm-9" v-if="dispensaryData.opening_hours.some((openingDay,i) => openingDay.day === 'Tuesday')">
                    <span v-for="(openingtime,index) in dispensaryData.opening_hours" :key="index">
                      <span v-if="(openingtime.day === 'Tuesday')">
                        {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                      </span>
                    </span>
                  </li>
                  <li v-else class="col-sm-9 text-danger">CLOSED</li>

                  <li class="col-sm-3">Wed:</li>
                  <li class="col-sm-9" v-if="dispensaryData.opening_hours.some((openingDay,i) => openingDay.day === 'Wednesday')">
                    <span v-for="(openingtime,index) in dispensaryData.opening_hours" :key="index">
                      <span v-if="(openingtime.day === 'Wednesday')">
                        {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                      </span>
                    </span>
                  </li>
                  <li v-else class="col-sm-9 text-danger">CLOSED</li>

                  <li class="col-sm-3">Thur:</li>
                  <li class="col-sm-9" v-if="dispensaryData.opening_hours.some((openingDay,i) => openingDay.day === 'Thursday')">
                    <span v-for="(openingtime,index) in dispensaryData.opening_hours" :key="index">
                      <span v-if="(openingtime.day === 'Thursday')">
                        {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                      </span>
                    </span>
                  </li>
                  <li v-else class="col-sm-9 text-danger">CLOSED</li>

                  <li class="col-sm-3">Fri:</li>
                  <li class="col-sm-9" v-if="dispensaryData.opening_hours.some((openingDay,i) => openingDay.day === 'Friday')">
                    <span v-for="(openingtime,index) in dispensaryData.opening_hours" :key="index">
                      <span v-if="(openingtime.day === 'Friday')">
                        {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                      </span>
                    </span>
                  </li>
                  <li v-else class="col-sm-9 text-danger">CLOSED</li>

                  <li class="col-sm-3">Sat:</li>
                  <li class="col-sm-9" v-if="dispensaryData.opening_hours.some((openingDay,i) => openingDay.day === 'Saturday')">
                    <span v-for="(openingtime,index) in dispensaryData.opening_hours" :key="index">
                      <span v-if="(openingtime.day === 'Saturday')">
                        {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                      </span>
                    </span>
                  </li>
                  <li  v-else class="col-sm-9 col-sm-9 text-danger">CLOSED</li>

                  <li class="col-sm-3">Sun:</li>
                  <li class="col-sm-9" v-if="dispensaryData.opening_hours.some((openingDay,i) => openingDay.day === 'Sunday')">
                    <span v-for="(openingtime,index) in dispensaryData.opening_hours" :key="index">
                      <span v-if="(openingtime.day === 'Sunday')">
                        {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                      </span>
                    </span>
                  </li>
                  <li v-else class="col-sm-9 text-danger">CLOSED</li>
                </ul>
                </div>
                <div class="col-sm-3">
                  <label>Subscribe to receive email</label>
                </div>
                <div class="col-sm-3">
                   {{ dispensaryData.subscribe? "YES": "NO"}}
                </div>
              </div>
              
            </div>
          </div>
        </div>

         <div class="col-sm-12">
          <div class="account-form">
            <div class="border-bottom"></div>
            <div class="acc-top-space">
              <div class="form-group">
                <div class="col-sm-2">
                  <label>Name Of Contact</label>
                </div>
                <div class="col-sm-4">
                  {{ dispensaryData.address.name_of_contact || ""}}
                </div>
                <div class="col-sm-2">
                  <label>Method Of Contact</label>
                </div>
                <div class="col-sm-3">
                   {{ dispensaryData.address.method_of_contact || ""}}
                </div>
              </div>
              
            </div>
          </div>
        </div>

         <div class="col-sm-12">
          <div class="account-form">
            <div class="border-bottom"></div>
            <div class="acc-top-space">
              <div class="form-group">
                <div class="col-sm-2">
                  <label>About Us</label>
                </div>
                <div class="col-sm-4">
                  {{ dispensaryData.about_us || ""}}
                </div>
                <div class="col-sm-2">
                  <label>Profile Image</label>
                </div>
                <div class="col-sm-4">
                   <img v-if="dispensaryData.profile_image" :src="dispensaryData.profile_image" class="img-responsive" />
                </div>
              </div>
              
            </div>
          </div>
        </div>










      </div>
    </div>
  </div>
</template>

<script>
import * as fb from "../../../firebaseConfig";

export default {
  props:['dispensaryId'],  
  data() {
    return {
      dispensaryData:{},
    };
  },
  filters: {
      hoursConverter(time) {  
        time = time.substring(0, time.length-3);  
          return time;
      }
  },
 
  created() {
    this.dispensaryUser();
  },
  methods: {

    async dispensaryUser() {
      var vueInstance = this;
   
      let getData = fb.dispensaryCollection.where("user_id", "==", vueInstance.dispensaryId).get();
        getData.then(dataDoc => {
          if(!dataDoc.empty) {
            vueInstance.dispensaryData = dataDoc.docs[0].data();
          }
        });
    },
    
  },
};
</script>

<style scoped>
.hours-listing {
	padding-left: 0;
  padding-top: 0;
}
</style>