<template>
<div>
    <div class="tab-title">
        <h3 class="text-center"><span>Step 5:</span> Phone Information</h3>
    </div>
    <div class="row">
        <div class="col-lg-8 col-lg-offset-2">
            <form @submit.prevent="SubmitPhoneDataForm">
                <div class="right-title margin-top">
                    <h2>Smartphone Verification</h2>
                    <p>A smartphone is required to become a driver. Do you have a smartphone?</p>
                    <div class="chck-box">
                        <label class="radio-inline">
                            <input v-model.trim="InfoPhoneData.phoneSmart" @change="$v.InfoPhoneData.phoneSmart.$touch()"  id="inlineRadioOptions" value="Yes" type="radio"> Yes
                        </label>
                        <label class="radio-inline">
                            <input v-model.trim="InfoPhoneData.phoneSmart" @change="$v.InfoPhoneData.phoneSmart.$touch()"  id="inlineRadioOptions" value="No" type="radio"> No
                        </label>
                        <div v-if="$v.InfoPhoneData.phoneSmart.$dirty">
                            <p class="text-danger" v-if="!$v.InfoPhoneData.phoneSmart.required">Please select this field.</p>
                        </div>
                    </div>
                </div>
                <div class="contact-form">
                    <div class="form-group">
                        <input v-model.trim="InfoPhoneData.phoneCompany" @input="$v.InfoPhoneData.phoneCompany.$touch()"  class="form-control" placeholder="Make *" type="text">
                        <div v-if="$v.InfoPhoneData.phoneCompany.$dirty">
                            <p class="text-danger" v-if="!$v.InfoPhoneData.phoneCompany.required">Please enter valid phone manufacturer company.</p>
                        </div>
                    </div>

                    <div class="form-group">
                        <input v-model.trim="InfoPhoneData.phoneModal" @input="$v.InfoPhoneData.phoneModal.$touch()"  class="form-control" placeholder="Model *" type="text">
                        <div v-if="$v.InfoPhoneData.phoneModal.$dirty">
                            <p class="text-danger" v-if="!$v.InfoPhoneData.phoneModal.required">Please enter valid phone modal.</p>
                        </div>
                    </div>

                </div>
                <div class="text-center">
                    <button class="btn send-btn save-btn"  :disabled="$v.$invalid">
                    <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>Save & Continue <i class="fa fa-long-arrow-right" aria-hidden="true"></i></button>
                </div>
            </form>
        </div>
    </div>
</div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
export default {
  data() {
    return {
       InfoPhoneData: {
        phoneCompany: null,
        phoneModal: null,
        phoneSmart: "Yes",
      },
      spinnerShow: false,
      
    };
  },
  validations: {
    InfoPhoneData: {
      phoneCompany: {
        required,
      },
      phoneModal: {
        required,
      },
      phoneSmart: {
        required,
      },
    },
  },

  methods: {
    
    async SubmitPhoneDataForm() {
      this.spinnerShow = true;

      if (!this.$v.$invalid) {
        $('.btn').prop("disabled", true);
         
        localStorage.setItem('InfoPhoneData', JSON.stringify(this.InfoPhoneData))
        $(".nav-tabs li a[href='#settings']").tab("show");
        this.spinnerShow = false;
      } else {
         this.spinnerShow = false;
        $('.btn').prop("disabled", false);
      }
    },
  },
};
</script>
