<template>
<div>
  <div :class="{ loader:dataLoader }"></div>
  <div class="row">
    <div class="col-lg-12">
      <div class="row contact-form">
        <div v-if="error" v-html="error">{{error}}</div>
       
        <div class="col-sm-3">
          <div class="start-date">
            <label>User Status</label>
            <select class="form-control" v-model.trim="search.UserStatus">
              <option value=1>Activated</option>
              <option value=0>Inactivated</option>
            </select>
          </div>
        </div>
        <div class="col-sm-3">
          <div class="start-date">
            <button
              class="btn search-btn order-search-btn"
              @click.prevent="searchData"
            >SEARCH ORDERS</button>
          </div>
        </div>
        <div class="col-sm-2">
          <div class="reset-btn">
            <a href="#" @click.prevent="resetSearchForm">
              <i class="fa fa-refresh" aria-hidden="true"></i>
              reset
            </a>
          </div>
        </div>
        <div class="col-sm-2 col-lg-offset-2" v-if="exportDataDiv">
          <download-excel class="export-btn cursor-pointer"
            :data="exportdispensaryData"
            :fields="dataForExcel"
            worksheet="My Worksheet"
            name="dispensary data.xls"
            :before-generate = "startDownload"
            :before-finish   = "finishDownload">
            <i class="fa fa-file-excel-o" aria-hidden="true"></i>
              Export Data
          </download-excel>
        </div>
      </div>
    </div>
  </div> 
  <div class="row table-space-top">
      <flash-message></flash-message>
    <div class="col-sm-12">
      <div class="details-table">
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr class="table-title">
                <th>USER ID</th>
                <th>FULL NAME</th>
                <th>EMAIL</th>
                <th>CITY</th>
                <th>STATE</th>
                <th>CREATED DATE</th>
                <th>STATUS</th>
                <th>DETAILS</th>
              </tr>
            </thead> 
            <tbody>
              <tr v-for="(dispensary, index) in displayedDispensaryList" :key="index">
                <td>{{ dispensary.intoUser.user_id}}</td>
                <td>{{ dispensary.intoDispensary.address.first_name}} {{dispensary.intoDispensary.address.last_name}}</td>
                <td>{{ dispensary.intoUser.email}}</td>
                <td>{{ dispensary.intoDispensary.address.city}}</td>
                <td>{{ dispensary.intoDispensary.address.state}}</td>
                <td>{{ dispensary.intoUser.created_at | timeFormate}}</td>
                <td>
                  <label class="switch">
                    <input type="checkbox" :checked="dispensary.intoUser.activated_at" @change="changeUserStatus(dispensary.intoUser.user_id,$event)">
                    <span class="slider round"></span>
                  </label>
                </td>
                <td>
                  <router-link class="table-view-btn" :to="{ name: 'dispensaryDetails', params: { dispensaryId: dispensary.intoUser.user_id} }">VIEW DETAIL</router-link>
                </td>
              </tr>
              <tr v-show="emptyDataVal && getDispensaryData.length < 1 ">
                <td colspan="7"><h4>You don't have any record.</h4></td>
              </tr> 
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div class="col-sm-4"></div>
  </div>
  <div class="page top-spce">
    <nav aria-label="pagenavigation" class="text-right">
      <ul class="pagination">
        <li>
          <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li>
          <a
            href="#"
            v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
            :key="index"
            @click.prevent="page = pageNumber"
          >{{pageNumber}}</a>
        </li>

        <li>
          <a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</div>
</template>

<script>
import downloadExcel from "vue-json-excel";
import * as fb from "../../../firebaseConfig";
export default {
  data() {
    return {
      dataForExcel: {
        "Registration Date": "registration_date",
        "User Id": "user_id",
        "Busniess Name": "busniess_name",
        "Business License Number" : "license_number",
        "Full Name": "full_name",
        "Phone Number": "phone_number",
        "Email Id": "email_id",
        Street1: "Street1",
        Street2: "Street2",
        City: "city",
        State: "State",
        "Zip Code": "zip_code",
        "Subscribe to receive email" : "subscribe",
        "Method Of Contact" : "method_of_contact",
        "Name Of Contact" : "name_of_contact",
        Status : "status"
      },
      exportdispensaryData: [],
      json_meta: [
        [
          {
            key: "charset",
            value: "utf-8",
          },
        ],
      ],
      exportDataDiv: false,
      search: {
        UserStatus: 1 ,
      },
      dataLoader:true,
      dispensaryRole: "Dispensary",
      page: 1,
      perPage: 10,
      pages: [],
      getDispensaryData: [],
      error: null,
      spinnerShow: false,
      emptyDataVal: false
    };
  },
  components:{
    downloadExcel
  },
  computed: {
    displayedDispensaryList() {
      return this.paginate(this.getDispensaryData);
    },
  },

  watch: {
    getDispensaryData() {
      this.page = 1;
      this.perPage= 10;
      this.pages= [];
      this.setPages();
    },
  },
  created() {
    this.dispensaryData();
  },
  methods: {
    setPages() {
      let numberOfPages = Math.ceil(this.getDispensaryData.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },
     async dispensaryData(UserStatus = "") {
      var vueInstance = this;
      vueInstance.getDispensaryData = []
      vueInstance.exportdispensaryData = []
      let query = await fb.usersCollection.where("role", "==", vueInstance.dispensaryRole);
        if(UserStatus != ""){
          let status = UserStatus==1 ? true : false
          query = await query.where("activated_at", "==", status);
        }
        query.orderBy("created_at", "desc").get().then(querySnapshot =>{
        querySnapshot.forEach((userDoc) => { 
          let allCustomer={};
          vueInstance.dispensaryDataAsPromise(userDoc.data().user_id).then(dataDoc => {
            allCustomer ={
              intoUser: userDoc.data(),
              intoDispensary: dataDoc
            }
           vueInstance.getDispensaryData.push(allCustomer)

            let nameOfContact = allCustomer.intoDispensary.address.name_of_contact
            if(allCustomer.intoDispensary.address.method_of_contact == "Phone Number")
              nameOfContact = vueInstance.$options.filters.phoneNoFormate(allCustomer.intoDispensary.address.name_of_contact)

            let exportData={
              registration_date: vueInstance.$options.filters.longDate(allCustomer.intoUser.created_at),
              user_id: allCustomer.intoUser.user_id,
              busniess_name: allCustomer.intoDispensary.busniess_name,
              license_number: allCustomer.intoDispensary.license_number,
              full_name: allCustomer.intoDispensary.address.first_name +' '+ allCustomer.intoDispensary.address.last_name,
              phone_number: vueInstance.$options.filters.phoneNoFormate(allCustomer.intoDispensary.address.phone_number),
              email_id: allCustomer.intoUser.email,
              Street1: allCustomer.intoDispensary.address.street1,
              Street2: allCustomer.intoDispensary.address.street2 || "-",
              city: allCustomer.intoDispensary.address.city,
              State: allCustomer.intoDispensary.address.state,
              zip_code: allCustomer.intoDispensary.address.zip_code,
              subscribe: allCustomer.intoDispensary.subscribe? "YES": "NO",
              method_of_contact: allCustomer.intoDispensary.address.method_of_contact,
              name_of_contact: nameOfContact,
              status: allCustomer.intoUser.activated_at? "Actived": "Inactived",
            }
            vueInstance.exportdispensaryData.push(exportData)
            vueInstance.exportDataDiv = true
          });
          vueInstance.dataLoader = false
           
        });
        setTimeout(function(){
          if(vueInstance.getDispensaryData.length <= 0){ 
            vueInstance.emptyDataVal=  true;
            vueInstance.dataLoader = false
            vueInstance.exportDataDiv = false
            return false;
          }
        }, 2000);
      });

     },
    async changeUserStatus(userId, event){
      var vueInstance = this;
      let value = event.target.checked;                 
      $(".flash__wrapper").empty();
      var updateRef = fb.usersCollection.doc(userId);
        return updateRef.update({
            activated_at: value
        }).then((suucess) => {
          window.scrollTo(0,0);
          if(value == true)
            vueInstance.flashSuccess("Successfully activated this dispensary.", { timeout: 3000 });
          else  
            vueInstance.flashSuccess("Successfully inactivated this dispensary.", { timeout: 3000 });

          setTimeout(function(){ 
            vueInstance.$router.go();
          }, 4000);
        });
    },
    async dispensaryDataAsPromise(userId) {
      return new Promise(function (resolve, reject) {
        let getData = fb.dispensaryCollection.where("user_id", "==", userId).get();
        getData.then(dataDoc => {
          if(!dataDoc.empty) {
            resolve(dataDoc.docs[0].data());
          }
        });
      });
    }, 

    async searchData() {
      var vueInstance = this;
      vueInstance.dataLoader = true
      this.dispensaryData(vueInstance.search.UserStatus);
    },

    async resetSearchForm() {
      this.search.UserStatus = 1;
      this.dataLoader = true
      this.dispensaryData();
    },

    startDownload(){
      this.dataLoader = true
    },
    finishDownload(){
      this.dataLoader = false
    }

  },
  
};
</script>

<style scoped>
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #8dc73f;
}

input:focus + .slider {
  box-shadow: 0 0 1px #8dc73f;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.export-btn{
	background: #78ca49;
	padding: 10px 8px;
	color: #fff;
	text-decoration: none;
	margin-top: 27px;
	display: block;
	text-align: center;
}
</style>
