<template>
    <div>
        <header-component />
        <section id="banner-section" class="" data-sequence="100">
            <div class="container" data-id="1">
                <div class="row" data-sequence="100">
                    <div class="col-lg-10 col-lg-offset-1" data-id="1">
                        <div class="text-center">
                            <div class="banner">
                                <!--    <img :src="baseUrl + '/images/CANNABIS-icon.png'" />-->
                                <h2 class="banner-title">LOGIN OR CREATE AN ACCOUNT</h2>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <section id="contact-section" class="register-margin-top">
            <div class="container">
                <div class="row">
                    <div class="col-lg-10 col-lg-offset-1">
                        <div class="row" data-sequence="100">
                            <div class="col-sm-5" data-id="1">
                                <flash-message></flash-message>
                                <div class="login-left">
                                    <div class="contact-title">
                                        <h3>Login</h3>
                                    </div>

                                    <loginForm />
                                </div>
                            </div>
                            <div class="col-sm-1"></div>
                            <div class="col-sm-6" data-id="2">
                                <div class="login-left">
                                    <div class="contact-title">
                                        <h3>Register</h3>
                                        <p>
                                            Registering for an account with Grab-A-Gram is easy. Simply select the
                                            <br class="hidden-xs" />type of account you want to register for and click the
                                            button below to
                                            <br class="hidden-xs" />begin the application process.
                                        </p>
                                    </div>
                                    <div class="contact-form">
                                        <div class="form-inline">
                                            <label class="label-title">I would like to register as a:</label>
                                            <select v-model="getRedirect" class="form-control">
                                                <option>Customer</option>
                                                <option>Driver</option>
                                                <option>Dispensary</option>
                                            </select>
                                        </div>
                                        <button @click.prevent="registrationRedirect"
                                            class="btn send-btn register-margin-top">REGISTER</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

        <footer-component />
    </div>
</template>

<script>
/*eslint-disable */
const $ = window.jQuery = require('jquery');
require('vue-flash-message/dist/vue-flash-message.min.css');
import loginForm from "../../components/forntEnd/LoginComponent";
import * as fb from '../../firebaseConfig'
export default {
    name:'Login',
    data() {
        return {
            getRedirect: "Customer",
            baseUrl: $('head base').attr('href')
        };
    },
    components: {
        loginForm,
    },
    methods: {
        registrationRedirect() {
            const redirect = this.getRedirect;

            switch (redirect) {
                case "Customer":
                    this.$router.push({ name: 'customerRegistration' })
                    break;
                case "Driver":
                    this.$router.push({ name: 'driver', query: { from: "login" } })
                    break;
                case "Dispensary":
                    this.$router.push({ name: 'dispensary', query: { from: "login" } })
                    break;
            }
        },
    }
};
</script>
