<template>
  <div>
    <div class="verify-title nop-space">My Products</div>
    <div class="row">
      <div class="col-sm-6">
        <div class="tab-search contact-form">
          <div class="row">
            <div class="col-sm-8">
              <div class="form-group">
                <input
                  v-model.trim="search.productName"
                  class="form-control"
                  type="text"
                  placeholder="Search By Product Name"
                  list="browsers"
                  id="browser"
                />
              </div>
              <datalist id="browsers">
                <option
                  v-for="productName in productNameData"
                  :key="productName.id"
                >{{productName.name}}</option>
              </datalist>
            </div>
            <div class="col-sm-4">
              <div class="form-group">
                <button class="btn black-btn" @click.prevent="searchByProductName">
                  <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>Search
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-sm-6">
        <div class="send-btn post-btn">
          <a href="#" @click.prevent="addNewProduct">ADD NEW PRODUCT</a>
        </div>
        <div></div>
      </div>
    </div>

    <div class="form-filter">
      <flash-message></flash-message>
      <div class="row contact-form">
        <div class="col-sm-4">
          <div class="row">
            <div class="col-sm-9">
              <div class="start-date">
                <label>Bulk Actions</label>
                <select class="form-control" v-model.trim="search.bulkActionData">
                  <option>Delete</option>
                </select>
              </div>
            </div>
            <div class="col-sm-3">
              <button class="apply-btn" @click.prevent="bulkAction">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>APPLY
              </button>
            </div>
          </div>
        </div>
        <div class="col-sm-4">
          <div class="row">
            <div class="col-sm-9">
              <div class="start-date">
                <label>Filter by Date</label>
                <select class="form-control" v-model.trim="search.dateByfilter">
                  <option value="7">7 Dates</option>
                  <option value="30">30 Dates</option>
                  <option value="90">90 Dates</option>
                </select>
              </div>
            </div>
            <div class="col-sm-3">
              <button class="apply-btn" @click.prevent="filterByDate">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>FILTER
              </button>
            </div>
          </div>
        </div>
        <div class="col-sm-4">
          <div class="row">
            <div class="col-sm-9">
              <div class="start-date">
                <label>Filter by SKU</label>
                <select class="form-control" v-model.trim="search.filterTypeSkuProduct">
                 <option
                  v-for="productName in productNameData"
                  :key="productName.id"
                >{{productName.sku}}</option>
                </select>
              </div>
            </div>
            <div class="col-sm-3">
              <button class="apply-btn" @click.prevent="filterBySku">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>FILTER
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
      <div class="page top-spce">
        <nav aria-label="pagenavigation" class="text-right">
          <ul class="pagination">
            <li>
              <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li>
              <a
                href="#"
                v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
                :key="index"
                @click.prevent="page = pageNumber"
              >{{pageNumber}}</a>
            </li>

            <li>
              <a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
      <div class="details-table top-spce">
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr class="table-title">
                <th>SELECT</th>
                <th>PRODUCT NAME</th>
                <th>PRODUCT SKU</th>
                <th>STOCK QUANTITY</th>
                <th>REGULAR PRICE</th>
                <th>SALE PRICE</th>
                <th>CREATED DATE</th>
                <th>PUBLISH</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(product, index) in displayedProduct" :key="index" :id="product.id">
                <td>
                  <input type="checkbox" v-model.trim="actionCheckbox" :value="product.id" />
                </td>
                <td>{{ product.name}}</td>
                <td>{{ product.sku}}</td>
                <td>{{ product.stock_quantity}}</td>
                <td>{{ product.regular_price | currency}}</td>
                <td>{{ product.sale_price | currency}}</td>
                <td>{{ product.created_at | longDate}}</td>
                <td v-if="product.isPublic ===false">

                  <label class="switch">
                    <input type="checkbox" @click="changePublicStatus(product.id)">
                    <span class="slider round"></span>
                  </label>
                </td>
                <td v-else>YES</td>
              </tr>
              <tr v-show="emptyDataVal">
                  <td colspan="8"><h4>You don't have any product.</h4></td>
                </tr> 
            </tbody>
          </table>
        </div>
      </div>
      <div class="page top-spce">
        <nav aria-label="pagenavigation" class="text-right">
          <ul class="pagination">
            <li>
              <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li>
              <a
                href="#"
                v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
                :key="index"
                @click.prevent="page = pageNumber"
              >{{pageNumber}}</a>
            </li>

            <li>
              <a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
    

  </div>
</template>

<script>
import evenBus from "../../../event-bus";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
export default {
  data() {
    return {
      page: 1,
      perPage: 10,
      pages: [],
      getProductData: [],
      productNameData: [],
      search: {
        dateByfilter: null,
        filterTypeSkuProduct: null,
        bulkActionData: null,
      },
      actionCheckbox: [],
      spinnerShow: false,
      emptyDataVal: false
    };
  },

  created() {
    this.productDataList();
    this.productDataName();
  },
  computed: {
    displayedProduct() {
      return this.paginate(this.getProductData);
    },

  },
  watch: {
    getProductData() {
      this.page = 1;
      this.perPage= 10;
      this.pages= [];
      this.setPages();
    },
  },
  methods: {
    setPages() {
      let numberOfPages = Math.ceil(this.getProductData.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },

    async productDataList() {
      var vueInstance = this;
      let productData = await fb.productCollection
        .where("dispensary_id", "==", fb.auth.currentUser.uid)
        .where("isDeleted", "==", false)
        .orderBy("created_at", "desc");

      productData.onSnapshot(function (querySnapshot) {
        var allProductData = [];
        vueInstance.emptyDataVal=  false;
        querySnapshot.forEach((doc) => {
          allProductData.push(doc.data());
        });
        if(allProductData.length <= 0){ 
          vueInstance.emptyDataVal=  true;
          return false;
        }
        vueInstance.getProductData = allProductData;
      });
    },

    async productDataName() {
      await this.$store.dispatch("allProcutName");
      this.productNameData = await this.$store.state.productNameData;
    },

    async bulkAction() {
      $('.flash__wrapper').empty();
      if (this.search.bulkActionData == null) {
        this.flashError("Please select any action.", { timeout: 7000 });
        this.spinnerShow = false;
        return false;
      }
      if (this.actionCheckbox.length <= 0) {
        this.flashError("Please check atleast one record.", { timeout: 7000 });
        this.spinnerShow = false;
        return false;
      }
      await this.$store
        .dispatch("poductBulkAction", {
          action: this.search.bulkActionData,
          actionValue: this.actionCheckbox,
        })
        .then((suucess) => {
          this.actionCheckbox=[];
          this.flashSuccess("Successfully deteled.", { timeout: 7000 });
          this.spinnerShow = false;
        });
    },

    async filterByDate() {
      $('.flash__wrapper').empty();
      var vueInstance = this;
      if (this.search.dateByfilter == null) {
        this.flashError("Please select date by filter.", { timeout: 7000 });
        this.spinnerShow = false;
        return false;
      }
      await this.$store.dispatch("productFilterData", {
        startDate: this.search.dateByfilter,
        sku: null,
        productByName: null,
      });
       vueInstance.getProductData = await this.$store.state.productNameData;
    },

    async searchByProductName() {
      $('.flash__wrapper').empty();
       var vueInstance = this;
      if (this.search.productName == null) {
        this.flashError("Please enter product name.", { timeout: 7000 });
        this.spinnerShow = false;
        return false;
      } 
      await this.$store.dispatch("productFilterData", {
        startDate: null,
        sku: null,
        productByName: this.search.productName,
      });
      vueInstance.getProductData = await this.$store.state.productNameData;
    },

    async filterBySku() {
      $('.flash__wrapper').empty();
       var vueInstance = this;
      if (this.search.filterTypeSkuProduct == null) {
        this.flashError("Please select filter by SKU.", { timeout: 7000 });
        this.spinnerShow = false;
        return false;
      }
      await this.$store.dispatch("productFilterData", {
        startDate: null,
        productByName: null,
        sku: this.search.filterTypeSkuProduct,
      });
      vueInstance.getProductData = await this.$store.state.productNameData;
    },

    addNewProduct() {
      evenBus.$emit("addProduct");
    },
    async changePublicStatus(id){
      await this.$store
        .dispatch("poductBulkAction", {
          action: 'needToPublish',
          actionValue: id,
        })
        .then((suucess) => {
          this.flashSuccess("Successfully publish this product.", { timeout: 7000 });
          this.spinnerShow = false;
        });
    },
 
  },
};
</script>

<style scoped>
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #8dc73f;
}

input:focus + .slider {
  box-shadow: 0 0 1px #8dc73f;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
</style>