<template>
  <div>
    <header-component />

    <section id="banner-section">
      <section id="customer-form">
        <div class="container">
          <div class="row">
            <div class="col-lg-11 col-lg-offset-1 animatedParent" data-sequence='100'>
              <div class="form-menu ">
                <div class="back-menu"><i class="fa fa-long-arrow-left" aria-hidden="true"></i><router-link
                    to="/customer/dashboard">Back to dashboard</router-link> </div>
              </div>
              <div class="tab-section animated bounceIn go" data-id="1">
                <!-- Nav tabs -->
                <selectTab :tabPage="this.tab" />

                <!-- Tab panes -->
                <div class="tab-content page-tab bottom-space">
                  <div role="tabpanel" class="tab-pane active" id="deals-ads">
                    <component v-bind:is="currentComponent" :id="this.dataId"></component>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </section>

    <div class="modal fade bs-example-modal-lg" tabindex="-1" role="dialog" aria-labelledby="myLargeModalLabel"
      id="rating-review">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-content">
            <form @submit.prevent="SubmitProductReview">
              <div class="share-box">
                <div class="title">
                  <h2>Product Review <span class="product-name">{{ reviewProduct.productName }}</span></h2>
                </div><br>
                <div class="row">
                  <div class="select-rating col-sm-6" @mouseleave="showCurrentRating(0)" style="display:inline-block;">
                    <star-rating v-model="reviewProduct.ratingAdd" @input="$v.reviewProduct.ratingAdd.$touch()"
                      :animate="true" :show-rating="false" @rating-selected="setCurrentSelectedRating"
                      @current-rating="showCurrentRating" :star-size="20" active-color="#99dc3f" :border-width="3" />
                    <div style="margin-right:10px;margin-bottom:20px;font-weight:bold;">{{ reviewProduct.currentRating }}
                    </div>
                  </div>
                  <div class="col-sm-4">
                    <select @change="checkProductReview()" class="form-control"
                      v-model.trim="reviewProduct.selectedForProductReview">
                      <option value=null>Select Product for review *</option>
                      <option v-for="(iteam, index) in reviewProduct.productDetailsData.items" :key="index"
                        :value="iteam">
                        {{ iteam.name }}
                      </option>
                    </select>
                  </div>
                </div>
                <div v-if="reviewProduct.alreadyAddederror" v-html="reviewProduct.alreadyAddederror">
                  {{reviewProduct.alreadyAddederror}}</div>
                <div v-if="!reviewProduct.hideTextField">
                  <div class="contact-form">
                    <textarea v-model.trim="reviewProduct.productComment"
                      @input="$v.reviewProduct.productComment.$touch()" class="form-control text-msg" rows="6"
                      placeholder="What do you think about this product?*"></textarea>
                  </div>

                  <div class="share-btn-pop-up">
                    <div class="row">
                      <div class="col-sm-4"></div>
                      <div class="col-sm-8">
                        <div class="row">
                          <flash-message></flash-message>
                          <div class="col-sm-6">
                            <div class="text-center">
                              <a href="#" data-dismiss="modal" aria-label="Close" class="cancel-review">CANCEL REVIEW</a>
                            </div>
                          </div>

                          <div class="col-sm-6">
                            <button class="btn send-btn" :disabled="$v.$invalid">
                              <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>POST REVIEW
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </div>
            </form>
          </div>
        </div>
      </div>
    </div>

    <dispensary-review-component />
    <footer-component />
  </div>
</template>

<script>
/*eslint-disable */
const $ = window.jQuery = require('jquery');

import evenBus from '../../../event-bus'
import selectTab from '../../../components/backEnd/customer/SettingTabComponent'
import myOrders from '../../../components/backEnd/customer/MyOrdersComponent'
import myOrderDetail from "../../../components/backEnd/customer/MyOrderDetailComponent";

import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import { required, numeric } from "vuelidate/lib/validators";
import starRatingCount from '../../../components/backEnd/RatingComponent';
import StarRating from 'vue-star-rating'
import DispensaryReviewComponent from '../../../components/backEnd/customer/DispensaryReviewComponent.vue';

export default {
  data() {
    return {
      currentComponent: myOrders,
      dataId: null,
      tab: null,
      reviewProduct: {
        ratingAdd: null,
        productComment: null,
        productName: null,
        currentRating: "No Rating",
        currentSelectedRating: "No Current Rating",
        productDetailsData: {},
        selectedForProductReview: null,
        alreadyAddederror: null,
        hideTextField: false
      },
      spinnerShow: null,

    }
  },
  validations: {
    reviewProduct: {
      ratingAdd: {
        required,
        numeric
      },
      productComment: {
        required
      },
      selectedForProductReview: {
        required
      },
    },
  },
  components: {
    myOrders,
    selectTab,
    myOrderDetail,
    starRatingCount,
    StarRating,
    DispensaryReviewComponent,
  },
  beforeRouteEnter(to, from, next) {
    if (Object.keys(to.params).length !== 0 && to.params.orderId != undefined) {
      next(vm => {
        vm.dataId = to.params.orderId
        vm.currentComponent = myOrderDetail;
      })
    }
    next()
  },
  created() {
    this.tab = this.$route.name;
    evenBus.$on("myOrderDetails", (id) => {
      this.dataId = id;
      this.currentComponent = myOrderDetail;
    });
    evenBus.$on("backToList", () => {
      this.currentComponent = myOrders;
    });
    evenBus.$on("productReviewModal", (allProductDetailsData) => {
      this.reviewProduct.ratingAdd = null;
      this.reviewProduct.productComment = null;
      this.reviewProduct.selectedForProductReview = null;
      this.reviewProduct.productDetailsData = allProductDetailsData;
      $('#rating-review').modal('show')
    });

  },

  methods: {
    async checkProductReview() {
      var vueInstance = this;
      vueInstance.reviewProduct.productName = vueInstance.reviewProduct.selectedForProductReview.name;
      vueInstance.reviewProduct.alreadyAddederror = null;
      vueInstance.reviewProduct.hideTextField = false
      console.log(vueInstance.reviewProduct.selectedForProductReview.product_id);
      await fb.productCollection.doc(vueInstance.reviewProduct.selectedForProductReview.product_id).collection('reviews').where("product_id", "==", vueInstance.reviewProduct.selectedForProductReview.product_id).where("order_id", "==", vueInstance.reviewProduct.productDetailsData.id).get().then(function (doc) {
        if (!doc.empty) {
          vueInstance.reviewProduct.ratingAdd = null;
          vueInstance.reviewProduct.productComment = null;
          vueInstance.reviewProduct.alreadyAddederror = `<p class='alert alert-danger'>You have already added a review for ${vueInstance.reviewProduct.selectedForProductReview.name} product.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>`;
          vueInstance.reviewProduct.hideTextField = true
        }
      })

    },
    showCurrentRating(rating) {
      this.reviewProduct.currentRating = (rating === 0) ? this.reviewProduct.currentSelectedRating : "Click to select " + rating + " stars"
    },
    setCurrentSelectedRating(rating) {
      this.reviewProduct.currentSelectedRating = "You have Selected: " + rating + " stars";
    },

    async SubmitProductReview() {
      $('.btn').prop("disabled", true);
      this.spinnerShow = true;
      var vueInstance = this;
      if (!this.$v.$invalid) {
        let currentTime = firebase.firestore.Timestamp.now().toMillis();//fetchUserProfile
        await vueInstance.$store.dispatch("getCustomerUser").then(response => {
          let userProfile = vueInstance.$store.state.customerData;
          fb.productCollection.doc(vueInstance.reviewProduct.selectedForProductReview.product_id).collection('reviews').doc().set({
            created_at: currentTime,
            comment: vueInstance.reviewProduct.productComment,
            rating: vueInstance.reviewProduct.ratingAdd,
            product_id: vueInstance.reviewProduct.selectedForProductReview.product_id,
            order_id: vueInstance.reviewProduct.productDetailsData.id,
            updated_at: currentTime,
            user_id: userProfile.user.user_id,
            user_name: userProfile.account.first_name + ' ' + userProfile.account.last_name
          });
          vueInstance.flashSuccess("You have given a successful product review.", {
            timeout: 4000,
          });
          setTimeout(function () {
            $('#rating-review').modal('hide')
          }, 4500);

          vueInstance.spinnerShow = false;
          $('.btn').prop("disabled", false);
        }, error => {
          $('.btn').prop("disabled", false);
          vueInstance.spinnerShow = false;
          console.error("Got nothing from server. Prompt user to check internet connection and try again")
        })
      }
    },

  },
};
</script>
<style scoped>
.product-name {
  color: #8ec741;
}
</style>