<template>
<div>
    <div class="tab-title">
        <h3 class="text-center"><span>Step 6:</span> References</h3>
    </div>
    <div class="row">
        <div class="col-lg-8 col-lg-offset-2">

            <div class="sub-heading-tab">
                <p>Are you a good driver? Are you a trustworthy individual? How are your
                    customer service skills? Let us know by having your friends and family
                    endorse your driving skills and character.  Well ask your references by
                    phone or email.</p>
            </div>

            <div class="contact-form">
                <form @submit.prevent="SubmitDriverInfoFrom">
                    <div class="right-title margin-top">
                        <h2>Friend or Family Member 1</h2>
                    </div>
                    <div class="form-group">
                        <input v-model.trim="firstName1" @input="$v.firstName1.$touch()" class="form-control" placeholder="First Name" type="text">
                        <div v-if="$v.firstName1.$dirty">
                        <p class="text-danger" v-if="!$v.firstName1.required">Please enter valid first name.</p>
                        </div>
                    </div>
                    <div class="form-group">
                        <input v-model.trim="lastName1" @input="$v.lastName1.$touch()" class="form-control" placeholder="Last Name" type="text">
                        <div v-if="$v.lastName1.$dirty">
                        <p class="text-danger" v-if="!$v.lastName1.required">Please enter valid last name.</p>
                        </div>
                    </div>
                    <div class="form-group">
                        <input v-model.trim="email1" @input="$v.email1.$touch()" class="form-control" placeholder="Email Address " type="text">
                        <div v-if="$v.email1.$dirty">
                        <p class="text-danger" v-if="!$v.email1.required">Please enter email address.</p>
                        <p class="text-danger" v-if="!$v.email1.email">Please enter valid email address.</p>  
                        </div>
                    </div>
                    <div class="form-group">
                        <input @keyup="acceptNumber1" v-model.trim="phoneNo1" @input="$v.phoneNo1.$touch()" class="form-control" placeholder="Phone Number " type="text">
                        <div v-if="$v.phoneNo1.$dirty">
                            <p class="text-danger" v-if="!$v.phoneNo1.required">Please enter valid phone no.</p>
                        </div>
                    </div>


                    <div class="right-title  spaceing">
                        <h2>Friend or Family Member 2</h2>
                    </div>
                    <div class="form-group">
                        <input v-model.trim="firstName2" @input="$v.firstName2.$touch()" class="form-control" placeholder="First Name" type="text">
                        <div v-if="$v.firstName2.$dirty">
                        <p class="text-danger" v-if="!$v.firstName2.required">Please enter valid first name.</p>
                        </div>
                    </div>
                    <div class="form-group">
                        <input v-model.trim="lastName2" @input="$v.lastName2.$touch()" class="form-control" placeholder="Last Name" type="text">
                        <div v-if="$v.lastName2.$dirty">
                        <p class="text-danger" v-if="!$v.lastName2.required">Please enter valid last name.</p>
                        </div>
                    </div>
                    <div class="form-group">
                        <input v-model.trim="email2" @input="$v.email2.$touch()" class="form-control" placeholder="Email Address " type="text">
                        <div v-if="$v.email2.$dirty">
                        <p class="text-danger" v-if="!$v.email2.required">Please enter email address.</p>
                        <p class="text-danger" v-if="!$v.email2.email">Please enter valid email address.</p>  
                        </div>
                    </div>
                    <div class="form-group">
                        <input @keyup="acceptNumber2" v-model.trim="phoneNo2" @input="$v.phoneNo2.$touch()" class="form-control" placeholder="Phone Number " type="text">
                        <div v-if="$v.phoneNo2.$dirty">
                            <p class="text-danger" v-if="!$v.phoneNo2.required">Please enter valid phone no.</p>
                        </div>
                    </div>


                    <div class="right-title  spaceing">
                        <h2>Friend or Family Member 3</h2>
                    </div>
                    <div class="form-group">
                        <input v-model.trim="firstName3" @input="$v.firstName3.$touch()" class="form-control" placeholder="First Name" type="text">
                        <div v-if="$v.firstName3.$dirty">
                        <p class="text-danger" v-if="!$v.firstName3.required">Please enter valid first name.</p>
                        </div>
                    </div>
                    <div class="form-group">
                        <input v-model.trim="lastName3" @input="$v.lastName3.$touch()" class="form-control" placeholder="Last Name" type="text">
                        <div v-if="$v.lastName3.$dirty">
                        <p class="text-danger" v-if="!$v.lastName3.required">Please enter valid last name.</p>
                        </div>
                    </div>
                    <div class="form-group">
                        <input v-model.trim="email3" @input="$v.email3.$touch()" class="form-control" placeholder="Email Address " type="text">
                        <div v-if="$v.email3.$dirty">
                        <p class="text-danger" v-if="!$v.email3.required">Please enter email address.</p>
                        <p class="text-danger" v-if="!$v.email3.email">Please enter valid email address.</p>  
                        </div>
                    </div>
                    <div class="form-group">
                        <input @keyup="acceptNumber3" v-model.trim="phoneNo3" @input="$v.phoneNo3.$touch()" class="form-control" placeholder="Phone Number " type="text">
                        <div v-if="$v.phoneNo3.$dirty">
                            <p class="text-danger" v-if="!$v.phoneNo3.required">Please enter valid phone no.</p>
                        </div>
                    </div>

                    <div class="text-center">
                        <button class="btn send-btn save-btn" :disabled="$v.$invalid">
                        <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i> Save &amp; Continue <i class="fa fa-long-arrow-right" aria-hidden="true"></i></button>
                    </div>
                </form>
            </div>

        </div>
    </div>
</div>
</template>

<script>
import {
    required,
    numeric,
    email
} from "vuelidate/lib/validators";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import axios from "axios";
import {
    mapState
} from "vuex";
export default {
    props:['tempRegistration'],
    data() {
        return {
            firstName1: null,
            lastName1: null,
            email1: null,
            phoneNo1: "",

            firstName2: null,
            lastName2: null,
            email2: null,
            phoneNo2: "",

            firstName3: null,
            lastName3: null,
            email3: null,
            phoneNo3: "",
        
            spinnerShow: false,
            error: null,
            getStateData: [],
            deviceType: "Web",
            role: "Driver",
            type: "OFFICE",
        };
    },

    validations: {
        firstName1: {
            required,
        },
        lastName1: {
            required,
        },
        email1: {
            required,
            email,
        },
        phoneNo1: {
            required,
        },

        firstName2: {
            required,
        },
        lastName2: {
            required,
        },
        email2: {
            required,
            email,
        },
        phoneNo2: {
            required,
        },

        firstName3: {
            required,
        },
        lastName3: {
            required,
        },
        email3: {
            required,
            email,
        },
        phoneNo3: {
            required,
        },   
        
    },
 
    methods: {
        acceptNumber1() { 
            var x = this.phoneNo1.replace(/\D/g, '').match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
            this.phoneNo1 = !x[2] ? x[1] : '(' + x[1] + ') ' + x[2] + (x[3] ? '-' + x[3] : '');
        },

      acceptNumber2() { 
            var x = this.phoneNo2.replace(/\D/g, '').match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
            this.phoneNo2 = !x[2] ? x[1] : '(' + x[1] + ') ' + x[2] + (x[3] ? '-' + x[3] : '');
        },

      acceptNumber3() { 
            var x = this.phoneNo3.replace(/\D/g, '').match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
            this.phoneNo3 = !x[2] ? x[1] : '(' + x[1] + ') ' + x[2] + (x[3] ? '-' + x[3] : '');
        },
        
        //Handle waiting to upload each file using promise
        async uploadImageAsPromise(imageDataUrl,userId) {
            let currentTime = firebase.firestore.Timestamp.now().toMillis();
            const type = imageDataUrl.split(';')[0].split('/')[1];
            return new Promise(function (resolve, reject) {
            var storageRef = firebase
                .storage()
                .ref(`driverDoc/${userId}/${currentTime}.`+type)
                .putString(imageDataUrl, "data_url", {
                    contentType: type,
                });
                storageRef.then((data) => {
                data.ref.getDownloadURL().then((url) => {
                    resolve(url);
                });
                });
            });
        },
        
        async SubmitDriverInfoFrom() {
            
           var vueInstance = this;
            $('.btn').prop("disabled", true);
            vueInstance.error = null;
            vueInstance.spinnerShow = true;
            let currentTime = firebase.firestore.Timestamp.now().toMillis();
          
        //    let fullName = vueInstance.tempRegistration.first_name +" " +vueInstance.tempRegistration.last_name;
            let email = vueInstance.tempRegistration.email;
            let password = JSON.parse(localStorage.getItem("DriverRegistratonLoginForm")).password;
        
            var phoneNumber1 = vueInstance.phoneNo1.replace(/[^\d]/g, "");
            var referencesPhoneNo1 = phoneNumber1.substr(0, 3) + phoneNumber1.substr(3, 3) + phoneNumber1.substr(6);
                    
            var phoneNumber2 = vueInstance.phoneNo2.replace(/[^\d]/g, "");
            var referencesPhoneNo2 = phoneNumber2.substr(0, 3) + phoneNumber2.substr(3, 3) + phoneNumber2.substr(6);
                    
            var phoneNumber3 = vueInstance.phoneNo3.replace(/[^\d]/g, "");
            var referencesPhoneNo3 = phoneNumber3.substr(0, 3) + phoneNumber3.substr(3, 3) + phoneNumber3.substr(6);
              
             let referencesArray=[{
                    first_name:vueInstance.firstName1,
                    last_name:vueInstance.lastName1,
                    email:vueInstance.email1,
                    phone_number:referencesPhoneNo1,
                },{
                    first_name:vueInstance.firstName2,
                    last_name:vueInstance.lastName2,
                    email:vueInstance.email2,
                    phone_number:referencesPhoneNo2,
                },{
                    first_name:vueInstance.firstName3,
                    last_name:vueInstance.lastName3,
                    email:vueInstance.email3,
                    phone_number:referencesPhoneNo3,
                }
            ]
            if (!vueInstance.$v.$invalid) {
                await vueInstance.$store.dispatch("checkUserExit", {
                    email: email,
                });
                let checkExist = vueInstance.$store.state.existUser;

                if (checkExist == false) {
                     
                    await vueInstance.$store.dispatch("signUp", {
                        email: email,
                        password: password,
                    });
                    let signUpData = await vueInstance.$store.state.getSignUpData;
                    let userId = signUpData.user.uid;
                    /* Logout */
                    await fb.auth.signOut()
                    let driverLicenseImage = vueInstance.$store.state.driverLicenseImage;
                    let insuranceImage = vueInstance.$store.state.insuranceImage;
                    let registrationImage = vueInstance.$store.state.registrationImage;
                    let caregiversImage = vueInstance.$store.state.caregiversImage;
                    let profileImageUrl = vueInstance.$store.state.driverProfileImage;

                    let document_images={};
                    if(driverLicenseImage){
                        await vueInstance.uploadImageAsPromise(driverLicenseImage,userId).then((url) => {
                            document_images['driving_license']=url;
                        });
                    }

                    if(insuranceImage){
                        await vueInstance.uploadImageAsPromise(insuranceImage,userId).then((url) => {
                            document_images['car_insurance_policy']=url;
                        });
                    }

                    if(registrationImage){
                        await vueInstance.uploadImageAsPromise(registrationImage,userId).then((url) => {
                            document_images['car_registration']=url;
                        });
                    }
                    if(caregiversImage){
                        await vueInstance.uploadImageAsPromise(caregiversImage,userId).then((url) => {
                            document_images['caregiver_certificate']=url;
                        });
                    }

                    let dob = JSON.parse(localStorage.getItem("InfoFormData")).dob;
                    var dobDate = await firebase.firestore.Timestamp.fromDate(new Date(dob)).toMillis();

                    let expirationDate = JSON.parse(localStorage.getItem("DocumnetFormData")).expirationDate;
                    var expireDate = await firebase.firestore.Timestamp.fromDate(new Date(expirationDate)).toMillis();
          
                    var phoneNumber = vueInstance.tempRegistration.phone_no.replace(/[^\d]/g, "");
                    var driverPhoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);
                    
                    const imageType = profileImageUrl.split(';')[0].split('/')[1];
                    var uploadImage = await firebase.storage().ref(`driverProfile/${userId}/${currentTime}.`+imageType)
                        .putString(profileImageUrl, "data_url", {
                        contentType: imageType,
                    }).then((data) => {
                        data.ref.getDownloadURL().then((url) => {

                           // vueInstance.$store.dispatch("DriverDataInsert", {
                            let dataInsert = fb.driverCollection.doc(userId).collection('response');
                                dataInsert.doc('data_account').set({ 
                                profile_image : url,
                                about_us : vueInstance.tempRegistration.about_you,
                                address:{
                                    phone_number : driverPhoneNo,
                                    street_1 : JSON.parse(localStorage.getItem("InfoFormData")).StreetAddress1,
                                    street_2 : JSON.parse(localStorage.getItem("InfoFormData")).StreetAddress2,
                                    city : JSON.parse(localStorage.getItem("InfoFormData")).city,
                                    state : JSON.parse(localStorage.getItem("InfoFormData")).state,
                                    zip_code : JSON.parse(localStorage.getItem("InfoFormData")).ZipCode,
                                },
                                account:{
                                    first_name : vueInstance.tempRegistration.first_name,
                                    last_name : vueInstance.tempRegistration.last_name,
                                    date_of_birth : dobDate,
                                    message_notification : JSON.parse(localStorage.getItem("DriverRegistratonLoginForm")).textMessage == true ? true : false,
                                    email_communication : vueInstance.tempRegistration.subscribe == true ? true : false,
                                    is_caregiver : JSON.parse(localStorage.getItem("InfoFormData")).isCaregiver == true ? true : false,
                                },
                                document_images : document_images,
                                documents:{
                                    document_type : "DRIVING_LICENSE",
                                    expiration_date : expireDate,
                                    identity_number : JSON.parse(localStorage.getItem("DocumnetFormData")).driverLicenseNo,
                                    issuing_state : JSON.parse(localStorage.getItem("DocumnetFormData")).issuingState,
                                    licence_issuing_state : JSON.parse(localStorage.getItem("DocumnetFormData")).lincensePlateState,
                                    licence_plate_number : JSON.parse(localStorage.getItem("DocumnetFormData")).licensePlateNumber,
                                    manufacture_company : JSON.parse(localStorage.getItem("DocumnetFormData")).vehicleCompany,
                                    manufacture_model : JSON.parse(localStorage.getItem("DocumnetFormData")).vehicleModal,
                                    manufacture_year : JSON.parse(localStorage.getItem("DocumnetFormData")).selectYearCar,
                                    regular_maintenance : JSON.parse(localStorage.getItem("DocumnetFormData")).regularMaintenance,
                                }, 
                                phones:{
                                    phone_manufacture_company : JSON.parse(localStorage.getItem("InfoPhoneData")).phoneCompany,
                                    phone_manufacture_model : JSON.parse(localStorage.getItem("InfoPhoneData")).phoneModal,
                                    smart_phone : JSON.parse(localStorage.getItem("InfoPhoneData")).phoneSmart,
                                },
                                user_id:userId,
                                created_at: currentTime,
                                updated_at: currentTime,
                            }).then(() => { 
                                dataInsert.doc('references').set({ references:  referencesArray})
                                
                                vueInstance.$store.dispatch("UserDataInsert", {
                                    activated_at: true,
                                    device_id: "",
                                    device_mac_id: "",
                                    device_type: vueInstance.deviceType,
                                    email: email,
                                //    name: fullName,
                                    password: password,
                                    role: vueInstance.role,
                                    user_id: userId,
                                    created_at: currentTime,
                                })
                                fb.tempRegistrationCollection.doc(vueInstance.tempRegistration.id).delete()
                                localStorage.clear();
                                vueInstance.flashSuccess('You are successfully registered. Please log in here.', {
                                    timeout: 15000,
                                });
                                $('.btn').prop("disabled", false);
                                
                                return vueInstance.$router.push({name:'driverRegistrationThanks'})
                            })
                            .catch((err) => {
                                vueInstance.error =
                                    "<p class='alert alert-danger'>There is some went wrong. Please try again later .<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                                vueInstance.spinnerShow = false;
                                    $('.btn').prop("disabled", false);
                            });
                        });
                    });
                    
                } else {
                    vueInstance.error =
                        "<p class='alert alert-danger'>This email id already exit.Please try other email id.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                    vueInstance.spinnerShow = false;
                     $('.btn').prop("disabled", false);
                }
            } else {
                vueInstance.spinnerShow = false;
                 $('.btn').prop("disabled", false);
            }
        },
    },
};
</script>
