<template>
  <div>
  
    <div class="tab-title">
      <div class="text-center">
        <h3><span>Step 2:</span> Personal Information</h3>
        <div class="sub-heading-tab">
          Please make sure that the personal information you provide matches<br />
          your government ID.
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
      <form @submit.prevent="SubmitPersonalForm">
        <div class="contact-form">
          <div class="form-group">
            <input v-model.trim="InfoFormData.FirstName" @input="$v.InfoFormData.FirstName.$touch()" class="form-control" placeholder="First Name" type="text" />
            <div v-if="$v.InfoFormData.FirstName.$dirty">
                <p class="text-danger" v-if="!$v.InfoFormData.FirstName.required">Please enter your first name.</p>
            </div>
          </div>
          <div class="form-group">
            <input v-model.trim="InfoFormData.LastName" @input="$v.InfoFormData.LastName.$touch()" class="form-control" placeholder="Last Name	" type="text" />
            <div v-if="$v.InfoFormData.LastName.$dirty">
                <p class="text-danger" v-if="!$v.InfoFormData.LastName.required">Please enter your last name.</p>
            </div>
          </div>
          <div class="form-group">
            <input v-model.trim="InfoFormData.StreetAddress1" @input="$v.InfoFormData.StreetAddress1.$touch()"
              class="form-control"
              placeholder="Street Address"
              type="text"
            />
            <div v-if="$v.InfoFormData.StreetAddress1.$dirty">
              <p class="text-danger" v-if="!$v.InfoFormData.StreetAddress1.required">Please enter your street address.</p>
            </div>
          </div>
          <div class="form-group">
            <input v-model.trim="InfoFormData.StreetAddress2"
              class="form-control"
              placeholder="Street Address 2"
              type="text"
            />
          </div>
          <div class="form-group">
            <input v-model.trim="InfoFormData.city" @input="$v.InfoFormData.city.$touch()" class="form-control" placeholder="City" type="text" />
            <div v-if="$v.InfoFormData.city.$dirty">
              <p class="text-danger" v-if="!$v.InfoFormData.city.required">Please enter your city.</p>
              <p class="text-danger" v-if="!$v.InfoFormData.city.alpha">Please enter your valid city name.</p>
            </div>
          </div>
          <div class="form-group">
            <div class="row">
              <div class="col-sm-8">
                <select class="form-control" v-model.trim="InfoFormData.state">
                    <option value="">Select State *</option>
                  <option v-for="(state, index) in getStateData" :key="index">{{ state }}</option>
                </select>
                <div v-if="$v.InfoFormData.state.$dirty">
              <p class="text-danger" v-if="!$v.InfoFormData.state.required">Please select issuing state.</p>
            </div>
              </div>

              <div class="col-sm-4">
                <input v-model.trim="InfoFormData.ZipCode" @input="$v.InfoFormData.ZipCode.$touch()"
                  class="form-control"
                  placeholder="Zip Code"
                  type="text"
                />
                <div v-if="$v.InfoFormData.ZipCode.$dirty">
                    <p class="text-danger" v-if="!$v.InfoFormData.ZipCode.required">Please enter zip code.</p>
                    <p class="text-danger" v-if="!$v.InfoFormData.ZipCode.numeric">Please enter valid zip code.</p>
                </div>
              </div>
            </div>
          </div>
          <div class="form-group">
            <input @keyup="acceptNumber" v-model.trim="InfoFormData.phoneNo" @input="$v.InfoFormData.phoneNo.$touch()" 
              class="form-control"
              placeholder="Mobile Number"
              type="text"
            />
            <div v-if="$v.InfoFormData.phoneNo.$dirty">
                <p class="text-danger" v-if="!$v.InfoFormData.phoneNo.required">Please enter your valid phone no..</p>

            </div>
          </div>

          <div class="checkbox">
            <label>
              <input type="checkbox"  v-model.trim="InfoFormData.textMessage"/> Send me text message notifications
            </label>
          </div>
          <div class="input-group">
            <input v-model.trim="InfoFormData.dob" @input="$v.InfoFormData.dob.$touch()"
              class="form-control calender-date"
              aria-describedby="basic-addon2"
              type="text"
              placeholder="Date of Birth"
              id="dob"
              name="dob"
            />
            <div v-if="$v.InfoFormData.dob.$dirty">
                <p class="text-danger" v-if="!$v.InfoFormData.dob.required">Please enter your date of birth.</p>

            </div>
            <span class="input-group-addon cuorser" id="basic-addon2"
              ><i class="fa fa-calendar" aria-hidden="true"></i
            ></span>
          </div>
            <div v-if="personalInfoError">{{personalInfoError}}</div>
         
          <div class="text-center">
            <button class="btn send-btn save-btn"  @click="$v.InfoFormData.$touch">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>
              Save & Continue
              <i class="fa fa-long-arrow-right" aria-hidden="true"></i>
            </button>
          </div>
        </div>
      </form>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
const $ = window.jQuery = require('jquery'); 
import { required, minLength, alpha, numeric, helpers } from "vuelidate/lib/validators";
import axios from "axios";
export default {
  data() {
    return {
      getStateData: [],
       InfoFormData: {
        FirstName: null,
        LastName: null,
        StreetAddress1: null,
        StreetAddress2: null,
        city: null,
        state: "",
        ZipCode: null,
        phoneNo: null,
        textMessage: null,
        dob: null,
      },
      spinnerShow: false,
      personalInfoError: null,
      
    };
  },
  validations: {
    InfoFormData: {
      FirstName: {
        required,
      },
      LastName: {
        required,
      },
      StreetAddress1: {
        required,
      },
      city: {
        required,
        alpha:helpers.regex('alpha', /^[a-zA-Z ]*$/),
      },
      state: {
        required,
      },
      ZipCode: {
        required,
        numeric,
      },
      phoneNo: {
        required,
      },
      dob: {
        required,
      },
    },
  },
 
  mounted() {
    this.fbState();
    var vm = this;
    // $("input[name=dob]").datepicker({ 
    //   defaultDate: new Date(2000,1-1,1),
    //   changeMonth: true,
    //   changeYear: true,
    //   yearRange: '-100:-18',
    //   onSelect: (dateForm) => {
    //     vm.InfoFormData.dob = dateForm;
    //   },
    // });
    },
  methods: {
    acceptNumber() { 
      var x = this.InfoFormData.phoneNo.replace(/\D/g, '').match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
      this.InfoFormData.phoneNo = !x[2] ? x[1] : '(' + x[1] + ') ' + x[2] + (x[3] ? '-' + x[3] : '');
    },
    async fbState() {
        await this.$store.dispatch("getAllState");
        this.getStateData = this.$store.state.GetState[0].state;
    },
    async SubmitPersonalForm() {
      this.personalInfoError = null;

      this.spinnerShow = true;

      if (!this.$v.$invalid) {
        $('.btn').prop("disabled", true);
        var address = this.InfoFormData.StreetAddress1 + this.InfoFormData.StreetAddress2 +
          this.InfoFormData.city + this.InfoFormData.state + this.InfoFormData.ZipCode
           
        let { data } = await axios.get(this.$store.state.googleMapsUrl+`&address=${address}`);
        if(data.status=='OK'){
          var geocodedLocation = data.results[0];
          this.$set(this.InfoFormData, 'latitude', geocodedLocation.geometry.location.lat);
          this.$set(this.InfoFormData, 'longitude', geocodedLocation.geometry.location.lng);
          localStorage.setItem('InfoFormData', JSON.stringify(this.InfoFormData))
          $(".nav-tabs li a[href='#messages']").tab("show");
        }else {
          this.personalInfoError ="<p class='alert alert-danger'>Please enter valid address.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
          $('.btn').prop("disabled", false);
        }
        this.spinnerShow = false;
      } else {
        this.spinnerShow = false;
         $('.btn').prop("disabled", false);
      }
    },
  },
};
</script>
