            </div>
            <div class="row">
              <ul class="hours-listing">
                <li class="col-sm-3">Mon:</li>
                <li class="col-sm-9" v-if="openingHours.some((openingDay,i) => openingDay.day === 'Monday')">
                  <span v-for="(openingtime,index) in openingHours" :key="index">
                    <span v-if="(openingtime.day === 'Monday')">
                      {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                    </span>
                  </span>
                </li>
                <li v-else class="col-sm-9 text-danger">CLOSED</li>

                <li class="col-sm-3">Tue:</li>
