</template>

<script>
export default {
  data() {
    return {
      openingHours:null,
      dispensaryUserPhone:null,
      dispensaryUserAboutUs:null,
      dispensaryUserEmail:null,
      startLocation:{}
    };
  },
  filters: {
    hoursConverter(time) {  
      time = time.substring(0, time.length-3);  
        return time;
    }
  },
  created (){
   this.dispensaryUser();
  },
  methods: {
    async dispensaryUser(){
      await this.$store.dispatch("getDispensaryUser");
      let dispensaryUserData = await this.$store.state.dispensaryUser;
      this.dispensaryUserPhone = dispensaryUserData.allData.address.phone_number;
      this.dispensaryUserAboutUs = dispensaryUserData.allData.about_us;
      this.openingHours = dispensaryUserData.allData.opening_hours;
      this.dispensaryUserEmail = dispensaryUserData.email;

      this.startLocation={
        lat:dispensaryUserData.allData.address.latitude,
        lng:dispensaryUserData.allData.address.longitude
      }
    }
  },
};
</script>
