const $ = window.jQuery = require('jquery');

import './assets/style.css';

import * as VueGoogleMaps from 'vue2-google-maps'

import App from './app/app.vue';
import Vue from 'vue'
import VueFlashMessage from 'vue-flash-message';
import Vuelidate from 'vuelidate'
import { auth } from './firebaseConfig'
import router from './router';
import store from './store'

window.Vue = require('vue');



Vue.use(Vuelidate);


Vue.use(VueFlashMessage);

Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyDO6PDftrAIISl0H5qPPXNluhP7yu1vmwY',
    libraries: 'places', 
  },
})

Vue.component('header-component', require('./components/template/HeaderComponent.vue').default); 
Vue.component('footer-component', require('./components/template/FooterComponent.vue').default); 

Vue.filter('timeFormate',  function(value)  {
  if  (!value)  {
    return  "";
  }
  var date = new Date (value);
  return  date.toLocaleString ([], {year: 'numeric', month: '2-digit', day: '2-digit'});
});
Vue.filter('longDate',  function(value)  {
  if  (!value)  {
    return  "";
  }
  var date = new Date (value);
  return  date.toLocaleString ([], {year: 'numeric', month: 'long', day: 'numeric'});
});
Vue.filter("capitalize",  function(value)  {
  if  (!value)  {
    return  "";
  }
  value  =  value.toString();
  return  value.charAt(0).toUpperCase()  +  value.slice(1);
});
Vue.filter("currency",  function(num)  {
  var num = Number(num);
  if (isNaN(num)) {
      return 0;
  }
  //if (String(num).split(".").length < 2 || String(num).split(".")[1].length<=2 ){
  if (String(num).split(".").length < 2 || String(num).split(".")[1].length ){
      num = num.toFixed(2);
  }
  return num; 
});
Vue.filter('timeAmPm',  function(value)  {
  if  (!value)  {
    return  "";
  }
  var date = new Date (value);
  return  date.toLocaleString ([], { hour: 'numeric' , minute: 'numeric', hour12: true });
});
Vue.filter('phoneNoFormate',  function(number)  {
  if  (!number)  {
    return  "";
  }
  var x = number.replace(/\D/g, "").match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
  return  !x[2]? x[1]: "(" + x[1] + ") " + x[2] + (x[3] ? "-" + x[3] : "");
});
Vue.filter('strLimit',  function(value, size)  {
  if (!value) return '';
  value = value.toString();

  if (value.length <= size) {
    return value;
  }
  return value.substr(0, size) + '...';
});


Vue.config.productionTip = false

new Vue({
  render: h => h(App),
  router: router,
  store: store,
}).$mount('#app')