<template>
  <div class="modal fade" tabindex="-1" role="dialog" id="addressModal">
    <div class="modal-dialog modal-md" role="document">
      <div class="modal-content">
        <div class="share-box">
          <div class="text-center">
            <div class="title">
              <h2 class="text-uppercase">{{ modaltitle }}</h2>
            </div>
            <br />
            <div class="row">
              <div class="col-md-12">
                <div class="contact-form margin-top">
                  <div class="form-group">
                    <input
                      v-model.trim="AddressData.FirstName"
                      @input="$v.AddressData.FirstName.$touch()"
                      class="form-control"
                      placeholder="First Name*"
                      type="text"
                    />
                    <div v-if="$v.AddressData.FirstName.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.AddressData.FirstName.required"
                      >
                        Please enter your first name.
                      </p>
                    </div>
                  </div>
                  <div class="form-group">
                    <input
                      v-model.trim="AddressData.LastName"
                      @input="$v.AddressData.LastName.$touch()"
                      class="form-control"
                      placeholder="Last Name*"
                      type="text"
                    />
                    <div v-if="$v.AddressData.LastName.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.AddressData.LastName.required"
                      >
                        Please enter your last name.
                      </p>
                    </div>
                  </div>
                  <div class="form-group">
                    <input
                      v-model.trim="AddressData.StreetAddress1"
                      @input="$v.AddressData.StreetAddress1.$touch()"
                      class="form-control"
                      placeholder="Street Address*"
                      type="text"
                    />
                    <div v-if="$v.AddressData.StreetAddress1.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.AddressData.StreetAddress1.required"
                      >
                        Please enter your street address.
                      </p>
                    </div>
                  </div>
                  <div class="form-group">
                    <input
                      v-model.trim="AddressData.StreetAddress2"
                      class="form-control"
                      placeholder="Street Address 2"
                      type="text"
                    />
                  </div>
                  <div class="form-group">
                    <input
                      v-model.trim="AddressData.city"
                      @input="$v.AddressData.city.$touch()"
                      class="form-control"
                      placeholder="City*"
                      type="text"
                    />
                    <div v-if="$v.AddressData.city.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.AddressData.city.required"
                      >
                        Please enter your city.
                      </p>
                      <p class="text-danger" v-if="!$v.AddressData.city.alpha">
                        Please enter your valid city name.
                      </p>
                    </div>
                  </div>
                  <div class="form-group">
                    <div class="row">
                      <div class="col-sm-8">
                        <select
                          class="form-control"
                          v-model.trim="AddressData.state"
                        >
                          <option value="">Select State *</option>
                          <option
                            v-for="(state, index) in getStateData"
                            :key="index"
                            :value="state"
                          >
                            {{ state }}
                          </option>
                        </select>
                        <div v-if="$v.AddressData.state.$dirty">
                          <p
                            class="text-danger"
                            v-if="!$v.AddressData.state.required"
                          >
                            Please select issuing state.
                          </p>
                        </div>
                      </div>

                      <div class="col-sm-4">
                        <input
                          v-model.trim="AddressData.ZipCode"
                          @input="$v.AddressData.ZipCode.$touch()"
                          class="form-control"
                          placeholder="Zip*"
                          type="text"
                        />
                        <div v-if="$v.AddressData.ZipCode.$dirty">
                          <p
                            class="text-danger"
                            v-if="!$v.AddressData.ZipCode.required"
                          >
                            Please enter zip code.
                          </p>
                          <p
                            class="text-danger"
                            v-if="!$v.AddressData.ZipCode.numeric"
                          >
                            Please enter valid zip code.
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="form-group">
                    <input
                      @keyup="acceptNumber"
                      v-model.trim="AddressData.phoneNo"
                      @input="$v.AddressData.phoneNo.$touch()"
                      class="form-control"
                      placeholder="Mobile Number*"
                      type="text"
                    />
                    <div v-if="$v.AddressData.phoneNo.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.AddressData.phoneNo.required"
                      >
                        Please enter your valid phone no..
                      </p>
                    </div>
                  </div>

                  <div class="driver-details">
                    <div class="upload-text">
                      <p>PHOTO OF ADDRESS</p>
                    </div>
                    <p class="doct-content">
                      Delivering to a different address than the one shown on
                      your ID? Please upload a photo of a
                      <span>
                        document with your name and correct delivery address </span
                      >.on it (ex. utility bill, lease, hotel receipt). Please
                      make sure we can read all of the numbers/letters in the
                      image. *
                    </p>

                    <div class="row top-spce">
                      <div class="row-height">
                        <div class="col-sm-6 col-sm-height">
                          <div class="driver-photo">
                            <img
                              v-if="deliveringFile"
                              :src="deliveringFile"
                              class="img-responsive"
                            />
                            <p id="fileNameDelivering"></p>
                          </div>
                        </div>
                        <div class="col-sm-6 col-sm-height col-middle">
                          <div class="remove-btn">
                            <a
                              href="#"
                              v-if="deliveringFile"
                              @click.prevent="removeDeliverImage"
                              >Remove</a
                            >
                            <input
                              type="file"
                              @change="onFileDeliverChange"
                              accept="image/jpeg"
                              id="uploadDeliverImage"
                              style="display: none"
                            />
                            <a
                              href="#"
                              v-if="!deliveringFile"
                              @click.prevent="addDeliverImage"
                              >Add</a
                            >
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div v-if="shippingAddresserror" v-html="shippingAddresserror">{{shippingAddresserror}}</div>
                  <div class="text-left">
                    <button
                      v-if="checkOperation == 'Add'"
                      class="btn send-btn padding-low"
                      @click.prevent="addShippingData"
                      :disabled="$v.$invalid"
                    >
                      <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>
                      {{ checkOperation }} INFORMATION
                    </button>
                    <button
                      v-if="checkOperation == 'Update'"
                      class="btn send-btn padding-low"
                      @click.prevent="editShippingData"
                      :disabled="$v.$invalid"
                    >
                      <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>
                      {{ checkOperation }} INFORMATION
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { required, numeric, alpha, helpers } from "vuelidate/lib/validators";
import evenBus from "../../../event-bus";
import * as fb from "../../../firebaseConfig";
import addressType from "../../../address-type";
import firebase from "firebase";
import axios from "axios";
export default {
  data() {
    return {
        secondaryAddressType: addressType.secondaryAddressType,
      modaltitle: "Add Primary Address",
      AddressData: {
        FirstName: null,
        LastName: null,
        StreetAddress1: null,
        StreetAddress2: null,
        city: null,
        state: "",
        ZipCode: null,
        phoneNo: null,
        type: null,
        indexOfAddress: null,
      },
      checkOperation: "Add",
      deliveringFile: null,
      uploadImage: {
        deliverImage: null,
      },
      getStateData: [],
      spinnerShow: false,
      shippingAddresserror: null,
    };
  },
  validations: {
    AddressData: {
      FirstName: {
        required,
      },
      LastName: {
        required,
      },
      StreetAddress1: {
        required,
      },
      city: {
        required,
        alpha: helpers.regex("alpha", /^[a-zA-Z ]*$/),
      },
      state: {
        required,
      },
      ZipCode: {
        required,
        numeric,
      },
      phoneNo: {
        required,
      },
    },
    deliveringFile: {
      required,
    },
  },
    
  mounted() {
    //  $(".flash__wrapper").empty();
    this.fbState();

    evenBus.$on("addAddressModal", (type) => {
      $("#addressModal").modal();
      var self = this; 
      Object.keys(this.AddressData).forEach(function(key,index) {
        self.AddressData[key] = ''; 
        setTimeout(function() {
             $('.text-danger').html('');
        }, 200);
        });
      if (type == this.secondaryAddressType)
        this.modaltitle = "Add Secondary Address";
      this.AddressData.type = type;  
    //  this.AddressData.id = 1;  
    });

    evenBus.$on("editAddressModal", (type, indexData) => {
      var vueInstance = this;
      let dataUpdate = fb.customerCollection
        .doc(fb.auth.currentUser.uid)
        .collection("response");
      dataUpdate.doc("data_address").onSnapshot(function (querySnapshot) {
        var customerDataAddress = querySnapshot.data().addresses;
        customerDataAddress.some(function (elem, i) {
          if (elem["type"] === type) {
            vueInstance.AddressData.indexOfAddress = i;
            vueInstance.AddressData.FirstName = elem["first_name"];
            vueInstance.AddressData.LastName = elem["last_name"];
            vueInstance.AddressData.city = elem["city"];
            vueInstance.AddressData.state = elem["state"];
            vueInstance.AddressData.StreetAddress1 = elem["street_1"];
            vueInstance.AddressData.StreetAddress2 = elem["street_2"];
            vueInstance.AddressData.ZipCode = elem["zip_code"];
            var cleaned = ('' + elem['phone_number']).replace(/\D/g, '')
            var match= cleaned.match(/^(\d{3})(\d{3})(\d{4})$/)
            if (match) {
              vueInstance.AddressData.phoneNo= '(' + match[1] + ') ' + match[2] + '-' + match[3]
            }
           // vueInstance.AddressData.phoneNo = elem["phone_number"];
            vueInstance.deliveringFile = elem["address_proof"];
          }
        });
      });
      this.modaltitle = "Edit Primary Address";
      if (type == this.secondaryAddressType)
        this.modaltitle = "Edit Secondary Address";
      this.checkOperation = "Update";
      $("#addressModal").modal();
      this.AddressData.type = type;
    });
  },
  methods: {
    async editShippingData() {
      $(".flash__wrapper").empty();
      this.shippingAddresserror = null;
      this.spinnerShow = true;
      if (!this.$v.$invalid) {
        $(".btn").prop("disabled", true);
        var deliveringImage = this.uploadImage.deliverImage;
        var document_images = this.deliveringFile;
        if (deliveringImage) {
          await this.uploadImageAsPromise(deliveringImage).then((url) => {
            document_images = url;
          });
        }
        var phoneNumber = this.AddressData.phoneNo.replace(/[^\d]/g, "");
        var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);

        var phoneNumber = this.AddressData.phoneNo.replace(/[^\d]/g, "");
          var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);
        var address = this.AddressData.StreetAddress1 + this.AddressData.StreetAddress2 +
          this.AddressData.city + this.AddressData.state + this.AddressData.ZipCode
           
        let { data } = await axios.get(this.$store.state.googleMapsUrl+`&address=${address}`);
        if(data.status=='OK'){
          var geocodedLocation = data.results[0];
          var latitude = geocodedLocation.geometry.location.lat
          var longitude =geocodedLocation.geometry.location.lng
          
        let dataUpdate = await fb.customerCollection
          .doc(fb.auth.currentUser.uid)
          .collection("response");
        dataUpdate
          .doc("data_address")
          .get()
          .then((doc) => {
            var objects = doc.data().addresses;
            var objectToupdate = {};
            //Update field of the element assigned
            objectToupdate.first_name = this.AddressData.FirstName;
            objectToupdate.last_name = this.AddressData.LastName;
            objectToupdate.city = this.AddressData.city;
            objectToupdate.latitude = latitude
              objectToupdate.longitude = longitude
            objectToupdate.phone_number = phoneNo;
            objectToupdate.state = this.AddressData.state;
            objectToupdate.street_1 = this.AddressData.StreetAddress1;
            objectToupdate.street_2 = this.AddressData.StreetAddress2;
            objectToupdate.type = this.AddressData.type;
            objectToupdate.zip_code = this.AddressData.ZipCode;
            objectToupdate.address_proof = document_images;
            objects[this.AddressData.indexOfAddress] = objectToupdate;
            dataUpdate.doc("data_address").update({
              addresses: objects,
            });
          })
          .then((success) => {
            $(".btn").prop("disabled", false);
            this.flashSuccess(`You have successfully ${this.modaltitle}.`, {
              timeout: 10000,
            });
            this.spinnerShow = false;
            $("#addressModal").modal("hide");
            var self = this; 
            Object.keys(this.AddressData).forEach(function(key,index) {
              self.AddressData[key] = '';
            });
          })
          .catch((err) => {
            console.log(err);
            $(".btn").prop("disabled", false);
            this.flashError("There is some problem. Please try again later.", {
              timeout: 10000,
            });
            this.spinnerShow = false;
            $("#addressModal").modal("hide");
          });
        }else {
          this.shippingAddresserror ="<p class='alert alert-danger'>Please enter valid address.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        }
      }
    },

    acceptNumber() {
      var x = this.AddressData.phoneNo
        .replace(/\D/g, "")
        .match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
      this.AddressData.phoneNo = !x[2]
        ? x[1]
        : "(" + x[1] + ") " + x[2] + (x[3] ? "-" + x[3] : "");
    },
    removeDeliverImage() {
      this.deliveringFile = null;
      this.uploadImage.deliverImage = null;
      $("#fileNameDelivering").html("");
    },

    onFileDeliverChange(e) {
      const file = e.target.files[0];
      this.uploadImage.deliverImage = file;
      $("#fileNameDelivering").html(file.name);
      this.deliveringFile = URL.createObjectURL(file);
    },

    addDeliverImage() {
      $("#uploadDeliverImage").trigger("click");
    },

    async fbState() {
      await this.$store.dispatch("getAllState");
      this.getStateData = this.$store.state.GetState[0].state;
    },

    async uploadImageAsPromise(imageDataUrl) {
      let userId = fb.auth.currentUser.uid;
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      const type = imageDataUrl.type;
      return new Promise(function (resolve, reject) {
        var storageRef = firebase
          .storage()
          .ref(`customerDoc/${userId}/${currentTime}.` + type)
          .put(imageDataUrl, {
            contentType: type,
          });
        storageRef.then((data) => {
          data.ref.getDownloadURL().then((url) => {
            resolve(url);
          });
        });
      });
    },

    async addShippingData() {
      $(".flash__wrapper").empty();
      this.shippingAddresserror = null;
      this.spinnerShow = true;
      if (!this.$v.$invalid) {
        $(".btn").prop("disabled", true);
        var deliveringImage = this.uploadImage.deliverImage;
        var document_images = "";
        if (deliveringImage) {
          await this.uploadImageAsPromise(deliveringImage).then((url) => {
            document_images = url;
          });
        }
        var phoneNumber = this.AddressData.phoneNo.replace(/[^\d]/g, "");
         var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);
        var address = this.AddressData.StreetAddress1 + this.AddressData.StreetAddress2 +
          this.AddressData.city + this.AddressData.state + this.AddressData.ZipCode
           
        let { data } = await axios.get(this.$store.state.googleMapsUrl+`&address=${address}`);
        if(data.status=='OK'){
          var geocodedLocation = data.results[0];
          var latitude = geocodedLocation.geometry.location.lat
          var longitude =geocodedLocation.geometry.location.lng
        await this.$store
          .dispatch("ShippingDataInsert", {
            first_name: this.AddressData.FirstName,
            last_name: this.AddressData.LastName,
            city: this.AddressData.city,
            latitude: latitude,
            longitude: longitude,
            phone_number: phoneNo,
            state: this.AddressData.state,
            street_1: this.AddressData.StreetAddress1,
            street_2: this.AddressData.StreetAddress2,
            zip_code: this.AddressData.ZipCode,
            type: this.AddressData.type,
            address_proof: document_images,
          })
          .then((success) => {
            console.log(success);
            $(".btn").prop("disabled", false);
            this.flashSuccess("You have successfully add shipping address.", {
              timeout: 10000,
            });
            this.spinnerShow = false;
            $("#addressModal").modal("hide");
            var self = this; 
            Object.keys(this.AddressData).forEach(function(key,index) {
              self.AddressData[key] = '';
            });
          })
          .catch((err) => {
            console.log(err);
            $(".btn").prop("disabled", false);
            this.flashError("There is some problem. Please try again later.", {
              timeout: 10000,
            });
            this.spinnerShow = false;
            $("#addressModal").modal("hide");
          });
      }else{
        this.shippingAddresserror ="<p class='alert alert-danger'>Please add mandatory field .<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
        this.spinnerShow = false;
        $(".btn").prop("disabled", false);
      }
    }else {
          this.shippingAddresserror ="<p class='alert alert-danger'>Please enter valid address.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        }
    },
  },
};
</script>
