<script>
/*eslint-disable */
const $ = window.jQuery = require('jquery');

import RegistrationLoginComponent from '../../components/forntEnd/driver/RegistrationLoginComponent.vue';
import RegistrationProfileComponent from '../../components/forntEnd/driver/RegistrationProfileComponent.vue';
import registrationLogin from "../../components/forntEnd/driver/RegistrationLoginComponent";
import registrationProfile from "../../components/forntEnd/driver/RegistrationProfileComponent";
import RegistrationPhoneNoComponent from "../../components/forntEnd/driver/RegistrationPhoneNoComponent";
import RegistrationPersonalInfoComponent from '../../components/forntEnd/driver/RegistrationPersonalInfoComponent.vue';
import RegistrationDocumentsComponent from '../../components/forntEnd/driver/RegistrationDocumentsComponent.vue';
import RegistrationReferencesComponent from '../../components/forntEnd/driver/RegistrationReferencesComponent.vue';

import * as fb from "../../firebaseConfig";
import firebase from "firebase";
export default {
    data() {
        return {
            tempRegistrationFormData: {},
            randomString: null,
            email: null,
            spinnerShow: false,
        };
    },
    components: {
        RegistrationLoginComponent,
        RegistrationProfileComponent,
        RegistrationPhoneNoComponent,
        RegistrationPersonalInfoComponent,
        RegistrationDocumentsComponent,
        RegistrationReferencesComponent,

    },
    created() {
        this.CheckDataExist();
    },
    methods: {
        async CheckDataExist() {
            var vueInstance = this;
            await vueInstance.$store.dispatch("checkTempUserExit", {
                id: vueInstance.$route.params.randomString,
            });
            let checkTempRegExist = await vueInstance.$store.state.tempRegistrationData;
            if (checkTempRegExist == false) {
                vueInstance.flashError("Please enter your first basic information with below form.", {
                    timeout: 8000,
                });
                vueInstance.$router.push({ path: "/driver#driver-bacic-information" })
            }
            vueInstance.tempRegistrationFormData = checkTempRegExist;
            vueInstance.email = checkTempRegExist.email;
            localStorage.clear();
        },
    },
};
</script>
