<template>
<div>
    <div class="tab-title">
        <div class="text-center">
            <h3>
                <span>Step 3:</span> Dispensary Information
            </h3>
        </div>
        <div class="row">
            <div class="col-lg-10 col-lg-offset-1">
                <div class="sub-heading-tab">
                    Thank you for your interest in partnering with Grab-A-Gram, a medical
                    marijuana delivery service! Grab-A-Gram offers dispensaries the unique
                    opportunity to increase and expand your business, reaching customers by delivery, without additional overhead costs to you. By partnering with Grab-A-Gram, youll gain an increased web and social media presence, your business will join a growing network of patients and caregivers looking to have your products delivered to them, and youll add another revenue stream. Please take a moment to fill in the information below to learn more about this exciting opportunity adn take your first steps toward growing your business with no cost to you!
                </div>
            </div>
        </div>
    </div>
    <div class="row">
        <div class="col-lg-10 col-lg-offset-1">
            <form @submit.prevent="SubmitDispensaryInfoFrom">
                <div class="contact-form">
                    <div class="form-group">
                        <input class="form-control" placeholder="Street Address*" type="text" v-model.trim="DispensaryInfoData.StreetAddress1" @input="$v.DispensaryInfoData.StreetAddress1.$touch()" />
                        <div v-if="$v.DispensaryInfoData.StreetAddress1.$dirty">
                            <p class="text-danger" v-if="!$v.DispensaryInfoData.StreetAddress1.required">Please enter your street address.</p>
                        </div>
                    </div>
                    <div class="form-group">
                        <input class="form-control" placeholder="Street Address 2" type="text" v-model.trim="DispensaryInfoData.StreetAddress2" />
                    </div>
                    <div class="form-group">
                        <input class="form-control" placeholder="City*" type="text" v-model.trim="DispensaryInfoData.city" @input="$v.DispensaryInfoData.city.$touch()" />
                        <div v-if="$v.DispensaryInfoData.city.$dirty">
                            <p class="text-danger" v-if="!$v.DispensaryInfoData.city.required">Please enter your city.</p>
                            <p class="text-danger" v-if="!$v.DispensaryInfoData.city.alpha">Please enter your valid city name.</p>
                        </div>
                    </div>
                    <div class="form-group">
                        <div class="row">
                            <div class="col-sm-8">
                                <select class="form-control" v-model.trim="DispensaryInfoData.state">
                                    <option value="">Select State</option>
                                    <option v-for="(state, index) in getStateData" :value="state" :key="index">{{ state }}</option>
                              
                                </select>
                            </div>

                            <div class="col-sm-4">
                                <input class="form-control" placeholder="Zip Code*" type="text" v-model.trim="DispensaryInfoData.ZipCode" @input="$v.DispensaryInfoData.ZipCode.$touch()" />
                                <div v-if="$v.DispensaryInfoData.ZipCode.$dirty">
                                    <p class="text-danger" v-if="!$v.DispensaryInfoData.ZipCode.required">Please enter zip code.</p>
                                    <p class="text-danger" v-if="!$v.DispensaryInfoData.ZipCode.numeric">Please enter valid zip code.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <input class="form-control" placeholder="Business License Number *" type="text" v-model.trim="DispensaryInfoData.licenseNumber" @input="$v.DispensaryInfoData.licenseNumber.$touch()" />
                        <div v-if="$v.DispensaryInfoData.licenseNumber.$dirty">
                            <p class="text-danger" v-if="!$v.DispensaryInfoData.licenseNumber.required">Please enter your business license number.</p>
                        </div>
                    </div>
                    <div class="form-group">
                        <div class="row">
                            <div class="col-sm-12">
                                <select class="form-control" v-model.trim="DispensaryInfoData.methodOfContact">
                                    <option value="">Select Preferred Method of Contact *</option>
                                    <option value="Email Id">Email Id</option>
                                    <option value="Phone Number">Phone Number</option>
                                </select>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <input class="form-control" placeholder="Name of Contact *" type="text" v-model.trim="DispensaryInfoData.nameOfContact" @input="$v.DispensaryInfoData.nameOfContact.$touch()" />
                        <div v-if="$v.DispensaryInfoData.nameOfContact.$dirty">
                            <p class="text-danger" v-if="!$v.DispensaryInfoData.nameOfContact.required">Please enter name of contact.</p>
                        </div>
                    </div>
                    <div class="form-group">
                        <div class="row">
                            <div class="col-sm-12"><label>Select all applicable Business Hours</label></div>
                        </div>
                    </div>
                    <div class="form-group">
                        <div class="row">
                            <div class="col-sm-12" v-for="(timeOfContact, index) in DispensaryInfoData.timeOfContacts" :key="index">

                                <div class="col-sm-4 checkbox">
                                    <label>
                                        <input type="checkbox" :true-value="timeOfContact.dayRead" v-model="timeOfContact.day" />
                                        {{timeOfContact.dayRead}}
                                    </label>
                                </div>
                                <div class="col-sm-4">
                                    <select class="form-control" v-model="timeOfContact.in_time">
                                        <option value="">Select open hour</option>
                                        <option value="07:00:00">07:00</option>
                                        <option value="08:00:00">08:00</option>
                                        <option value="09:00:00">09:00</option>
                                        <option value="10:00:00">10:00</option>
                                        <option value="11:00:00">11:00</option>
                                        <option value="12:00:00">12:00</option>
                                        <option value="13:00:00">13:00</option>
                                        <option value="14:00:00">14:00</option>
                                        <option value="15:00:00">15:00</option>
                                        <option value="16:00:00">16:00</option>
                                    </select>
                                </div>
                                <div class="col-sm-4">
                                    <select class="form-control" v-model="timeOfContact.out_time">
                                        <option value="">Select close hour</option>
                                        <option value="17:00:00">17:00</option>
                                        <option value="18:00:00">18:00</option>
                                        <option value="19:00:00">19:00</option>
                                        <option value="20:00:00">20:00</option>
                                        <option value="21:00:00">21:00</option>
                                        <option value="22:00:00">22:00</option>
                                        <option value="23:00:00">23:00</option>
                                        <option value="24:00:00">24:00</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="term-contetnt">
                        <p>By submitting this information, I agree that Grab-A-Gram, its affiliates and/or representatives may contact me . Contact information will primarily be used for partnership purposes, but you also agree that by subimitting information, that Grab-A-Gram may contact you in future about marketing and research. You may opt out of participation in marketing and research at anytime. Further, by submitting this information, you attest under penalty of law that the information is accurate and correct. By submitting this information and proceeding with the partnership inquiry, you attest under penalty of law that you are a licensed dispensary and that the person submitting the inquiry is an authorized representative of said dispensary.</p>
                    </div>

                    <div class="checkbox">
                        <label>
                            <input type="checkbox" checked @change="$v.DispensaryInfoData.termAndCondition.$touch()" v-model.trim="DispensaryInfoData.termAndCondition" />I have read and agree to the
                            <a href="#">Terms and Conditions</a>
                            <div v-if="$v.DispensaryInfoData.termAndCondition.$dirty">
                                <p class="text-danger" v-if="!$v.DispensaryInfoData.termAndCondition.sameAs">Please agree our term and condition.</p>
                            </div>
                        </label>
                    </div>
                    <div v-if="error" v-html="error">{{error}}</div>
                    <div class="text-center">
                        <button class="btn send-btn save-btn" :disabled="$v.$invalid">
                            <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>SUBMIT
                        </button>
                    </div>
                </div>
            </form>
        </div>
    </div>
</div>
</template>

<script>
import {
    required,
    numeric,
    alpha,
    sameAs,
    helpers,
    minLength
} from "vuelidate/lib/validators";
import firebase from "firebase";
import axios from "axios";
import * as fb from "../../../firebaseConfig";
export default {
    props:['tempRegistration'],
    data() {
        return {
            DispensaryInfoData: {
                StreetAddress1: null,
                StreetAddress2: null,
                city: null,
                state: '',
                ZipCode: null,
                licenseNumber: null,
                nameOfContact: null,
                methodOfContact: "",
                termAndCondition: null,
                timeOfContacts: [{
                        dayRead: "Monday",
                        day: null,
                        in_time: "",
                        out_time: "",
                    },
                    {
                        dayRead: "Tuesday",
                        day: null,
                        in_time: "",
                        out_time: "",
                    },
                    {
                        dayRead: "Wednesday",
                        day: null,
                        in_time: "",
                        out_time: "",
                    },
                    {
                        dayRead: "Thursday",
                        day: null,
                        in_time: "",
                        out_time: "",
                    },
                    {
                        dayRead: "Friday",
                        day: null,
                        in_time: "",
                        out_time: "",
                    },
                    {
                        dayRead: "Saturday",
                        day: null,
                        in_time: "",
                        out_time: "",
                    },
                    {
                        dayRead: "Sunday",
                        day: null,
                        in_time: "",
                        out_time: "",
                    },
                ],
            },
            spinnerShow: false,
            error: null,
            getStateData: [],
            deviceType: "Web",
            role: "Dispensary",
           // rating: 0,
            type: "OFFICE",
        };
    },
    validations: {
        DispensaryInfoData: {
            StreetAddress1: {
                required,
            },
            // StreetAddress2: {
            //     required,
            // },
            city: {
                required,
                 alpha:helpers.regex('alpha', /^[a-zA-Z ]*$/),
            },
            state: {
                required,
            },
            ZipCode: {
                required,
                numeric,
            },
            licenseNumber: {
                required,
            },
            nameOfContact: {
                required,
            },
            methodOfContact: {
                required,
            },
           /* timeOfContact: {
                day: {
                    required,
                    minLength: minLength(1), 
                },
                in_time: {
                    required,
                    minLength: minLength(1),
                },
                out_time: {
                    required,
                    minLength: minLength(1),
                },
            },*/
            termAndCondition: {
                sameAs: sameAs(() => true),
            },
        },
    },
    mounted() {
        this.fbState();
    },
    methods: {
        async fbState() {
            await this.$store.dispatch("getAllState");
            this.getStateData = this.$store.state.GetState[0].state;
        },
        async SubmitDispensaryInfoFrom() {
            var vueInstance = this;
        //    let fullName = vueInstance.tempRegistration.first_name +" " +vueInstance.tempRegistration.last_name;
            let email =  vueInstance.tempRegistration.email;
            let password = JSON.parse(localStorage.getItem("DisRegistratonLoginForm"))
                .password;

            vueInstance.error = null;
            vueInstance.spinnerShow = true;
            if (!vueInstance.$v.$invalid) {
                $('.btn').prop("disabled", true);
                let currentTime = firebase.firestore.Timestamp.now().toMillis();
                var contactTime = [];
                await $.each(vueInstance.DispensaryInfoData.timeOfContacts, function (key,value) {
                    if (value.day && value.in_time && value.out_time) { 
                        var jsonObj = {};
                        jsonObj.day = value.day;
                        jsonObj.in_time = value.in_time;
                        jsonObj.out_time = value.out_time;
                        jsonObj.created_at = currentTime;
                        jsonObj.updated_at = currentTime;
                        contactTime.push(jsonObj);
                    }
                });
                await vueInstance.$store.dispatch("checkUserExit", {
                    email: email,
                });
                let checkExist = vueInstance.$store.state.existUser;

                if (checkExist == false) {
                    // Get latitude longitude from address using google map api
                    var address = vueInstance.DispensaryInfoData.StreetAddress1 + vueInstance.DispensaryInfoData.StreetAddress2 +
                    vueInstance.DispensaryInfoData.city + vueInstance.DispensaryInfoData.state + vueInstance.DispensaryInfoData.ZipCode
           
                    let { data } = await axios.get(vueInstance.$store.state.googleMapsUrl+`&address=${address}`);
                    if(data.status=='OK'){
                        var geocodedLocation = data.results[0];
                        await vueInstance.$store.dispatch("signUp", {
                            email: email,
                            password: password,
                        });
                        let signUpData = await vueInstance.$store.state.getSignUpData;
                        let userId = await signUpData.user.uid;
                        /* Logout */
                        await fb.auth.signOut()
                        let imageDataUrl = await vueInstance.$store.state.dispensaryProfileImage;
                        let imageType = imageDataUrl.split(';')[0].split('/')[1];
                        var uploadImage = firebase.storage().ref(`dispensaryProfile/${userId}/${currentTime}.`+imageType)
                            .putString(imageDataUrl, "data_url", {
                            contentType: imageType,
                        });
                        var phoneNumber = vueInstance.tempRegistration.phone_no.replace(/[^\d]/g, "");
                        var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);
                      
                        uploadImage.then((data) => {
                            data.ref.getDownloadURL().then((url) => {
                                
                                vueInstance.$store.dispatch("DispensaryDataInsert", {
                                    about_us:  vueInstance.tempRegistration.about_you,
                                    profile_image: url,
                                    busniess_name:  vueInstance.tempRegistration.business_name,
                                        license_number:vueInstance.DispensaryInfoData.licenseNumber,  
                                //   rating: vueInstance.rating,
                                    user_id: userId,
                                    subscribe: vueInstance.tempRegistration.subscribe == true ? true : false,
                                    location:  new firebase.firestore.GeoPoint(geocodedLocation.geometry.location.lat, geocodedLocation.geometry.location.lng),      
                                    address: {
                                        first_name: vueInstance.tempRegistration.first_name, 
                                        last_name:vueInstance.tempRegistration.last_name,

                                        city: vueInstance.DispensaryInfoData.city,
                                        latitude: geocodedLocation.geometry.location.lat,
                                        longitude: geocodedLocation.geometry.location.lng,
                                        phone_number: phoneNo,
                                        state: vueInstance.DispensaryInfoData.state,
                                        street1: vueInstance.DispensaryInfoData.StreetAddress1,
                                        street2: vueInstance.DispensaryInfoData.StreetAddress2,
                                        type: vueInstance.type,
                                        zip_code: vueInstance.DispensaryInfoData.ZipCode,
                                        name_of_contact: vueInstance.DispensaryInfoData.nameOfContact,
                                        method_of_contact: vueInstance.DispensaryInfoData.methodOfContact,
                                    },
                                    opening_hours: contactTime,
                                }).then((success) => {
                                    vueInstance.$store.dispatch("UserDataInsert", {
                                        activated_at: true,
                                        device_id: "",
                                        device_mac_id: "",
                                        device_type: vueInstance.deviceType,
                                        email: email,
                                    //    name: fullName,
                                        password: password,
                                        role: vueInstance.role,
                                        user_id: userId,
                                        created_at: currentTime,
                                    });
                                    fb.tempRegistrationCollection.doc(vueInstance.tempRegistration.id).delete()
                                    localStorage.removeItem('DisRegistratonLoginForm');
                                    localStorage.clear();
                                    localStorage.removeItem('userImage');
                                    vueInstance.flashSuccess('You are successfully registered. Please log in here.', {
                                        timeout: 15000,
                                    });
                                    return vueInstance.$router.push({name:'login'})
                                }).catch((err) => {
                                    vueInstance.error =
                                            "<p class='alert alert-danger'>There is some went wrong. Please try again later .<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                                        vueInstance.spinnerShow = false;
                                        $('.btn').prop("disabled", false);
                                        return false;
                                });
                            });
                        });
                        }else{ 
                            vueInstance.error =
                                "<p class='alert alert-danger'>Plese enter valid Address .<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                            vueInstance.spinnerShow = false;
                            $('.btn').prop("disabled", false);
                            return false;
                        }
                    } else {
                        vueInstance.error =
                            "<p class='alert alert-danger'>This email id already exit.Please try other email id.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                        vueInstance.spinnerShow = false;
                        $('.btn').prop("disabled", false);
                        return false;
                    }
                } else {
                vueInstance.spinnerShow = false;
                $('.btn').prop("disabled", false);
                return false;
            }
        },
    },
};
</script>
