<template>
  <div>
    <header-component />
    <section id="banner-section">
      <div class="container">
        <div class="col-lg-8 col-lg-offset-2">
          <div class="text-center animatedParent" data-sequence="100">
            <div class="main-page-title animated bounceIn go" data-id="1">
              <h1>Driver Registration</h1>
              <div class="verify-title">Verify Email Address</div>
              <div class="sent-email">
                We sent an email to
                <span>{{ email }}</span>
              </div>

              <div class="sub-heading">
                Please check your inbox to verify your email address and continue
                <br class="hidden-xs" />the registration process
                <p class="mail-link">Didnt Recieve an Email?</p>
                <div class="send-btn resned-email-btn cursor-pointer" @click.prevent="SubmitResendEmail">
                  <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>Resend Email now
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <footer-component />
  </div>
</template>

<script>
/*eslint-disable */
const $ = window.jQuery = require('jquery');

import axios from "axios";
export default {
  data() {
    return {
      email: null,
      spinnerShow: false,
      url: this.$store.state.API_URL_ROOT,
    };
  },
  mounted() {
    this.CheckDataExist();
  },
  methods: {
    CheckDataExist() {
      let dataEmail = localStorage.getItem("DriverForm")
        ? JSON.parse(localStorage.getItem("DriverForm")).email
        : "";
      if (!dataEmail) return this.$router.push("/driver");
      return this.email = JSON.parse(localStorage.getItem("DriverForm")).email;
    },

    SubmitResendEmail() {
      this.spinnerShow = true;
      let randomString = localStorage.getItem("DriverForm")
        ? JSON.parse(localStorage.getItem("DriverForm")).linkString
        : "";
      if (!randomString) return this.$router.push("/driver");

      axios
        .get(`${this.url}sendDriverVerifyEmail/${this.email}/${randomString}`)
        .then((response) => {
          /* if(response.data.status == 0)
                return  this.mailError = response.data.msg;
              */
        });
      this.spinnerShow = false;
    },
  },
};
</script>