<template>
  <div>
    <header-component />

    <orders-details-component v-if="orderData" :orderData=orderData />

    <footer-component />
  </div>
</template>
 
<script>

/*eslint-disable */
const $ = window.jQuery = require('jquery');

import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import OrdersDetailsComponent from '../../../components/backEnd/driver/OrdersDetailsComponent.vue';

export default {
  props: ['orderId'],
  data() {
    return {
      orderData: null,

    }
  },

  components: {
    OrdersDetailsComponent,
  },
  created() {
    this.getOrderDetail(this.orderId)
  },

  methods: {

    async getOrderDetail(id) {
      let dataValues = null;
      var vueInstance = this;

      await fb.orderCollection.doc(id).get().then(function (doc) {
        if (doc.exists) {
          vueInstance.orderData = doc.data(0);
        } else {
          console.log("No such document!");
        }
      })
        .catch(function (error) {
          console.log("Error getting document:", error);
        });

    },
  },
};
</script>
