<template>
<div>

  <div class="tab-about">
    <h3>Coupon Deals</h3>
  </div>
  <div class="box-gray" v-for="(couponData, index) in DispensaryReviewList" :key="index">
    <div class="row">
      <div class="col-sm-3">
        <div class="img-pro">
          <img :src="couponData.image" class="img-responsive">
        </div>
        <div class="pro-img">
          <div class="display-table">
            <div class="display-table-cell">
              <div class="discount-title">
                <h3>{{couponData.percentage}} <span v-if="couponData.discount_type==discountType">%</span></h3>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="col-sm-9">
        <div class="deals-discount-contet">
          <h4>Coupon code:- {{couponData.coupon_code_name}} </h4>
          <div class="tag">Expires {{couponData.expiration_date | timeFormate}}</div>
          <p>
           {{couponData.coupon_description}} 
          </p>
        </div>
      </div>
    </div>
  </div>
<div class="client-reviews" v-if="emptyDataVal == 0 && DispensaryReviewList.length <=0">
      Dispensary coupon is not available now.
    </div>
  <div class="pagination-review">
      <div class="row">
        <div class="col-sm-6">
         <!-- <div class="top-spce hidden-xs">Page 1 of 5</div> -->
          <div class="top-spce hidden-xs"></div>
        </div>
     
        <div class="col-sm-6 text-right">
          <nav aria-label="Page navigation">
            <ul class="pagination">
              <li>
                <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
                  <span aria-hidden="true"></span>
                </a>
              </li>
              <li><a
										href="#"
										v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
										:key="index"
										@click.prevent="page = pageNumber"
									>{{pageNumber}}</a></li>
              <li>
                <a href="#" @click.prevent="page++" v-if="page < pages.length"  aria-label="Next">
                  <span aria-hidden="true"></span>
                </a>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </div>
</div>
</template>

<script>
import * as fb from "../../../../firebaseConfig";
import firebase from "firebase";
export default {
  props: ["dispnesaryData"],
  data() {
    return {
      page: 1,
      perPage: 10,
      pages: [],
      dispensaryCouponData: [],
      emptyDataVal: false,
      discountType:'percentage',
    };
  },
  created() {
    this.dispensaryCouponList();
  },
  computed: {
    DispensaryReviewList() {
      return this.paginate(this.dispensaryCouponData);
    },
  },
  watch: {
    dispensaryCouponData() {
      this.page = 1;
      this.perPage = 10;
      this.pages = [];
      this.setPages();
    },
  },
  methods: {
    setPages() {
      let numberOfPages = Math.ceil(
        this.dispensaryCouponData.length / this.perPage
      );
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },
    async dispensaryCouponList() {
      var vueInstance = this;
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      let couponCodeQuery = await fb.couponCollection.
        where("dispensary_user_id","==",this.dispnesaryData.user_id).where("expiration_date",">=",currentTime).where("isPublic","==",true).orderBy("expiration_date");

      couponCodeQuery.get().then(function (querySnapshot) {
        var allCouponData = [];

        querySnapshot.forEach((doc) => {
          allCouponData.push(doc.data());
        });
        vueInstance.dispensaryCouponData = allCouponData;
        setTimeout(function(){
          if(allCouponData.length <= 0)
            vueInstance.emptyDataVal = 0;
        }, 3000);
      });
    },
  },
};
</script>
