<template>
  <div>
    <div id="invoice">
      <img :src="logo" width="140px" height="50px" />
      <h1 style="text-align:center;">INVOICE</h1>

      <h2>ORDER #{{ this.allData.order_no}}</h2>

      <table id="shipping-table" class="table-hide">
        <tbody
          v-for="(address, index ) in this.allData.addresses"
          :key="index"
         
        ><template v-if="address.type!=billingAddressType">
          <tr>
            <td v-if="address.type==billingAddressType">{{ address.type.toUpperCase() }} </td>
            <td v-else>SHIPPING ADDRESS</td>
          </tr>
          <tr>
            <td>{{ allData.customer_name}}</td>
          </tr>
          <tr>
            <td>{{ address.street_1}}</td>
          </tr>
          <tr v-if="address.street_2">
            <td>{{ address.street_2}}</td>
          </tr>
          <tr>
            <td>{{ address.city}}, {{ address.state}} {{ address.zip_code}}</td>
          </tr>
            </template>
        </tbody>
      </table>
      <div class="order-table">
        <div class="signature-section">ORDER DETAILS</div>

        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th style="width: 50%">PRODUCT</th>
                <th style="width: 50%" class="text-right">TOTAL</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(iteam, index) in this.allData.items" :key="index">
                <td>{{ iteam.name}}</td>
                <td>${{ iteam.total_count | currency}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="left-space">
        <div class="order-table">
          <div class="table-responsive">
            <table class="table">
              <thead>
                <tr>
                  <th style="width: 50%">SUBTOTAL</th>
                  <th style="width: 50%" class="text-right">${{ this.allData.sub_total | currency}}</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>DELIVERY FEE</td>
                  <td class="text-right">${{ this.allData.delivery_fee | currency}}</td>
                </tr>

                <tr>
                  <td>SALES TAX</td>
                  <td class="text-right">${{ this.allData.sales_tax | currency}}</td>
                </tr>

                <tr>
                  <td class="total-text">TOTAL</td>
                  <td class="total-text text-right">${{ this.allData.grand_total | currency}}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div id="invoiceReport">
      <img :src="logo" width="140px" height="50px" />
      <h1 style="text-align:center;">INVOICE</h1>

      <h2>ORDER #{{ this.allData.order_no}}</h2>

      <table id="shipping-table" class="table-hide">
        <tbody
          v-for="(address, index ) in this.allData.addresses"
          :key="index"
          
        ><template  v-if="address.type!=billingAddressType">
          <tr>
            <td v-if="address.type==billingAddressType">{{ address.type.toUpperCase() }} </td>
            <td v-else>SHIPPING ADDRESS</td>
          </tr>
          <tr>
            <td>{{ allData.customer_name}}</td>
          </tr>
          <tr>
            <td>{{ address.street_1}}</td>
          </tr>
          <tr v-if="address.street_2">
            <td>{{ address.street_2}}</td>
          </tr>
          <tr>
            <td>{{ address.city}}, {{ address.state}} {{ address.zip_code}}</td>
          </tr>
            </template>
        </tbody>
      </table>
      <div class="order-table">
        <div class="signature-section">ORDER DETAILS</div>

        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th style="width: 50%">PRODUCT</th>
                <th style="width: 50%" class="text-right">TOTAL</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(iteam, index) in this.allData.items" :key="index">
                <td>{{ iteam.name}}</td>
                <td>${{ iteam.total_count | currency}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="left-space">
        <div class="order-table">
          <div class="table-responsive">
            <table class="table">
              <thead>
                <tr>
                  <th style="width: 50%">SUBTOTAL</th>
                  <th style="width: 50%" class="text-right">${{ this.allData.sub_total | currency}}</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>DELIVERY FEE</td>
                  <td class="text-right">${{ this.allData.delivery_fee | currency}}</td>
                </tr>

                <tr>
                  <td>SALES TAX</td>
                  <td class="text-right">${{ this.allData.sales_tax | currency}}</td>
                </tr>

                <tr>
                  <td class="total-text">TOTAL</td>
                  <td class="total-text text-right">${{ this.allData.grand_total | currency}}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div class="delivery-info">
        <div class="signature-section">DELIVERY INFORMATION</div>
        <div class="row">
          <div class="col-sm-4">
            <div class="type-sub">Driver Name:</div>
          </div>
          <div class="col-sm-8">
            <div class="time-title">{{this.driverName | capitalize }}</div>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-4">
            <div class="type-sub">Employeed ID#:</div>
          </div>
          <div class="col-sm-8">
            <div class="time-title">{{ this.allData.assign_driver_id}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import evenBus from "../../../event-bus";
import addressType from "../../../address-type";
export default {
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
        billingAddressType: addressType.billingAddressType,
      error: null,
      spinnerShow: false,
      dataId: {
        id: this.id,
      },
      allData: {},
      driverName: null,
      logo: this.$store.state.API_URL_ROOT + "images/logo.png",
    };
  },
  created() {
    this.getOrderDetail(this.dataId.id);
    evenBus.$on("getInvoice", () => {
      this.printInvoice();
    });
    evenBus.$on("downloadReport", () => {
      this.downloadReport();
    });
  },
  mounted() {
    const plugin = document.createElement("script");
    plugin.setAttribute(
      "src",
      "//cdnjs.cloudflare.com/ajax/libs/jspdf/1.0.272/jspdf.debug.js"
    );
    plugin.async = true;
    document.head.appendChild(plugin);
  },
  methods: {
    async getOrderDetail(id) {
      let dataValues = null;
      //let getDriverName = null;
      await fb.orderCollection
        .doc(id)
        .get()
        .then(function (doc) {
          if (doc.exists) {
            dataValues = doc.data(0);
          } else {
            console.log("No such document!");
          }
        })
        .catch(function (error) {
          console.log("Error getting document:", error);
        });
      this.allData = dataValues;
      if (dataValues.assign_driver_id != "") {
        let getDriverName = null;
        await fb.driverCollection
          .doc(dataValues.assign_driver_id).collection('response').doc('data_account')
          .get()
          .then(doc => { 
            getDriverName = doc.data(0).account.first_name + ' ' + doc.data(0).account.last_name;
          });
        this.driverName = getDriverName;
      }
    },
    /*async printInvoice() {
      var pdf = new jsPDF("p", "pt", "a4");
      var elements = $(".invoiceClass");
      var i = 0;
      var recursiveAddHtml = function () {
        if (i < elements.length) {
          var x = 0,
            y = i * 20;
          pdf.addHTML(elements.get(i), x, y, function () {
            i++;
            recursiveAddHtml();
          });
        } else {
          pdf.save();
        }
      };

      recursiveAddHtml();
    },*/
    /*    async printInvoice() {
       var printDoc = new jsPDF();
            printDoc.fromHTML($('#invoicePrint').get(0), 10, 10, {
                'width': 180
            });alert(12);
            printDoc.autoPrint();
            printDoc.save("dataurlnewwindow");
     },
     */
    async printInvoice() {
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      var pdf = new jsPDF("p", "pt", "letter");

      //  pdf.addImage(img, "png", 10, 15, 0, 0);
      // source can be HTML-formatted string, or a reference
      // to an actual DOM element from which the text will be scraped.
      var source = $("#invoice")[0];

      // we support special element handlers. Register them with jQuery-style
      // ID selector for either ID or node name. ("#iAmID", "div", "span" etc.)
      // There is no support for any other type of selectors
      // (class, of compound) at this time.
      var specialElementHandlers = {
        // element with id of "bypass" - jQuery style selector
        "#bypassme": function (element, renderer) {
          // true = "handled elsewhere, bypass text extraction"
          return true;
        },
      };
      var margins = {
        top: 10,
        bottom: 60,
        left: 40,
        width: 522,
      };
      // all coords and widths are in jsPDF instance's declared units
      // 'inches' in this case
      pdf.fromHTML(
        source, // HTML string or DOM elem ref.
        margins.left, // x coord
        margins.top,
        {
          // y coord
          width: margins.width, // max width of content on PDF
          elementHandlers: specialElementHandlers,
        },

        function (dispose) {
          // dispose: object with X, Y of the last line add to the PDF
          //          this allow the insertion of new lines after html

          pdf.save(currentTime + ".pdf");
        },
        margins
      );
    },
    async downloadReport() {
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      var pdf = new jsPDF("p", "pt", "letter");

      //  pdf.addImage(img, "png", 10, 15, 0, 0);
      // source can be HTML-formatted string, or a reference
      // to an actual DOM element from which the text will be scraped.
      var source = $("#invoiceReport")[0];

      // we support special element handlers. Register them with jQuery-style
      // ID selector for either ID or node name. ("#iAmID", "div", "span" etc.)
      // There is no support for any other type of selectors
      // (class, of compound) at this time.
      var specialElementHandlers = {
        // element with id of "bypass" - jQuery style selector
        "#bypassme": function (element, renderer) {
          // true = "handled elsewhere, bypass text extraction"
          return true;
        },
      };
      var margins = {
        top: 10,
        bottom: 60,
        left: 40,
        width: 522,
      };
      // all coords and widths are in jsPDF instance's declared units
      // 'inches' in this case
      pdf.fromHTML(
        source, // HTML string or DOM elem ref.
        margins.left, // x coord
        margins.top,
        {
          // y coord
          width: margins.width, // max width of content on PDF
          elementHandlers: specialElementHandlers,
        },

        function (dispose) {
          // dispose: object with X, Y of the last line add to the PDF
          //          this allow the insertion of new lines after html

          pdf.save(currentTime + ".pdf");
        },
        margins
      );
    },
  },
};
</script>

<style scoped>
#invoice,
#invoiceReport {
  visibility: hidden;
  height: 0;
  overflow: hidden;
}
</style>