<template>
<div>
<flash-message></flash-message>
<div :class="{ loader:dataLoader }"></div>
  <div class="row">
    <div class="col-sm-7"></div>
    <div class="col-sm-3 order-number">Earn Total Amount :</div>
    <div class="col-sm-2 status-order">${{ totalEarningAmount | currency}} </div>
  </div>
  <div class="row top-spce">

    <div class="col-lg-12">
      <div class="row">
         <div class="col-sm-12">
          <div class="details-table">
            <div class="table-responsive">
              <table class="table">
                <thead>
                  <tr class="table-title">
                    <th>ORDER #</th>
                    <th>DATE</th>
                    <th>DELIVARY DATE</th>
                    <th>EARNING STATUS</th>
                    <th>EARNING AMOUNT</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(order, index) in earningOrderLists" :key="index">
                    <td>{{ order.order_no}}</td>
                    <td>{{ order.created_at | timeFormate}}</td>
                    <td>{{ order.updated_at | timeFormate}}  at {{ order.updated_at | timeAmPm}}</td>
                    <td>UNPAID</td>
                    <td>${{ order.delivery_fee.toFixed(2) | orderEarningPrice | currency}}</td>
                  </tr>
                  <tr v-show="emptyDataVal">
                    <td colspan="5"><h4>You don't have any record.</h4></td>
                  </tr> 
                </tbody>
              </table>

            </div>
          </div>
        </div>

      </div>
    </div>
    <div class="col-sm-4"></div>
  </div>
  <div class="page top-spce">
    <nav aria-label="pagenavigation" class="text-right">
      <ul class="pagination">
        <li>
          <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li>
          <a
            href="#"
            v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
            :key="index"
            @click.prevent="page = pageNumber"
          >{{pageNumber}}</a>
        </li>

        <li>
          <a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import orderStatus from "../../../order-status";
export default {
  data() {
    return {
      dataLoader:true,
      orderStatusComplate: orderStatus.complate,
      page: 1,
      perPage: 10,
      pages: [],
      getOderData: [],
      error: null,
      spinnerShow: false,
      emptyDataVal: false
    };
  },
  filters: {
    orderEarningPrice (amountEarning) { 
      return (amountEarning * 70 / 100).toFixed(2);
    }
  },
  computed: {
    earningOrderLists() {
      return this.paginate(this.getOderData);
    },
    totalEarningAmount() {
      var sum = 0;
      this.getOderData.forEach(e => { 
        sum += e.delivery_fee * 70/100;
      });
      return sum.toFixed(2)
    }
  },

  watch: {
    getOderData() {
      this.page = 1;
      this.perPage= 10;
      this.pages= [];
      this.setPages();
    },
  },
  created() {
    this.earningOrderData();
  },
  methods: {
    setPages() {
      let numberOfPages = Math.ceil(this.getOderData.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) { 
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },
     async earningOrderData() {
       var vueInstance = this;
      let earningOrderQuery = await fb.orderCollection
        .where("assign_driver_id", "==", fb.auth.currentUser.uid)
        .where("status", "==", vueInstance.orderStatusComplate)
        .orderBy("updated_at", "desc");
     
    //  let earningOrderQuery = await fb.orderCollection.where("status", "==", vueInstance.orderStatusComplate).orderBy('updated_at', 'desc');

      earningOrderQuery.onSnapshot(function (querySnapshot) {
        var allOrderData = [];
        var allEaringAmount = 0;
        vueInstance.emptyDataVal=  false;
        querySnapshot.forEach((doc) => {
          allOrderData.push(doc.data());
        });
        if(allOrderData.length <= 0){ 
          vueInstance.emptyDataVal=  true;
          vueInstance.dataLoader=false
          return false;
        }
        vueInstance.getOderData = allOrderData;
        vueInstance.dataLoader=false
      });
     },
   
  },
  
};
</script>
