<template>
  <div>
    <header-component />

    <section id="banner-section" v-if="orderDetailsData">
      <div class="container">
        <div class="col-lg-8 col-lg-offset-2 min-height">
          <div class=" animatedParent" data-sequence='100'>
            <div class="main-page-title animated bounceIn go" data-id="1">
              <h1>Thank you for your order at Grab-A-Gram</h1>

              <div class="order-number margin-top">Order Number: <span>{{orderDetailsData.order_no }}</span></div>
              <div class="order-number ">Confirmation Code: <span>{{orderDetailsData.confirmation_code }}</span></div>
              <div class="order-number">Delivery Scheduled For: <span>{{ orderDetailsData.created_at | longDate}} at {{ orderDetailsData.created_at | timeAmPm}} </span></div>

              <div class="email-confirmation">
                <p><b>* Please provide your confirmation code to your driver at the time of delivery</b></p>
              </div>

              <div class="email-confirmation">
                <p>You will receive an email confirmation shortly at <a v-bind:href="'mailto:' + orderDetailsData.customer_email">{{ orderDetailsData.customer_email }}</a></p>
              </div>

              <div class="sub-heading text-left  link-green">
                You may log into your account at any time to view your order status and track your order. You may also check that status of
                your order and track your drivers whereabouts by using our <a href="#"> iOS app</a> or our <a href="#">Android app</a>. Please call 1-888-888-8888 if you
                have any questions or concerns about your order.
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <footer-component />
  </div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
export default {
 data() {
    return {
      orderDetailsData:null,
    }
 },
  created() {
    this.deleteOderList();
  },

  methods: {
    
    async deleteOderList(){
      var vueInstance = this;
      let getAddedOrderData = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
      await fb.orderCollection.doc(this.$route.params.orderId).get().then((orderDetails)=> {

        if (orderDetails.exists) {
          vueInstance.orderDetailsData = orderDetails.data() 
          getAddedOrderData.doc('data_orderList').get().then((getProduct)=> {
            
            if (getProduct.exists) {
              var OrderListData = getProduct.data()
              if(OrderListData.used_coupon_id){

                let updateCouponLimit = getAddedOrderData.doc('data_coupon').collection('usedCoupon').doc(OrderListData.used_coupon_id)
                  updateCouponLimit.set({
                    used_limit: firebase.firestore.FieldValue.increment(1)
                }, {merge: true});
              }

              getAddedOrderData.doc('data_orderList').collection('orderList').get().then(querySnapshot => {
                querySnapshot.forEach((doc) => {
                   doc.ref.delete();
                   getAddedOrderData.doc('data_orderList').delete();
                });
              })

            }else{
              
               vueInstance.$router.push({ name: 'cartReviewProductView' }).catch((err)=>{
                 console.log(err)
               })
            }
          });
        }else{
          vueInstance.$router.push({ name: 'cartReviewProductView' }).catch((err)=>{
            console.log(err)
          })
        }
        })
      },
    },
};
</script>
