<template>
  <div>
    <div class="tab-title">
      <h3 class="text-center"><span>Step 1:</span> Account Info</h3>
    </div>
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
        <form @submit.prevent="SubmitLoginForm">
          <div class="contact-form">
            <!--<div class="form-group">-->
            <!--  <input class="form-control" v-model.trim="AccountFormData.userName" @input="$v.AccountFormData.userName.$touch()" placeholder="Username *" type="text" />-->
            <!--  <div v-if="$v.AccountFormData.userName.$dirty">-->
            <!--      <p class="text-danger" v-if="!$v.AccountFormData.userName.required">Please enter your user name.</p>-->
            <!--  </div>-->
            <!--</div>-->

            <div class="form-group top-spce">
              <input v-model.trim="AccountFormData.email" @input="$v.AccountFormData.email.$touch()" class="form-control"
                placeholder="Email Address *" type="email" />
              <div v-if="$v.AccountFormData.email.$dirty">
                <p class="text-danger" v-if="!$v.AccountFormData.email.required">Please enter your email address.</p>
                <p class="text-danger" v-if="!$v.AccountFormData.email.email">Please enter valid email address.</p>
              </div>
            </div>
            <div class="input-group mb-4">
              <input v-model.trim="AccountFormData.password" @input="$v.AccountFormData.password.$touch()"
                class="form-control" placeholder="Password *" :type="ShowPassword" />
              <span class="input-group-btn show-pass">
                <button @click="showHidePass" class="btn btn-default mt-0" type="button">{{ btnText }}</button>
              </span>
            </div>
            <div v-if="$v.AccountFormData.password.$dirty">
              <p class="text-danger" v-if="!$v.AccountFormData.password.required">Please enter your password.</p>
              <p class="text-danger" v-if="!$v.AccountFormData.password.minLength">Please enter password minimum 6 lenght.
              </p>
            </div>
            <div class="checkbox">
              <label>
                <input type="checkbox" v-model.trim="AccountFormData.subscribe" /> I opt into receiving email
                communications from Grab-A-Gram
              </label>
            </div>
            <div class="text-center">
              <div v-if="error">{{error}}</div>
              <button class="btn send-btn save-btn" :disabled="$v.$invalid">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>
                SAVE & CONTINUE
                <i class="fa fa-long-arrow-right" aria-hidden="true"></i>
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
const $ = window.jQuery = require('jquery');
import { required, minLength, email } from "vuelidate/lib/validators";
export default {
  data() {
    return {
      AccountFormData: {
        email: null,
        //  userName: null,
        password: null,
        subscribe: null
      },
      spinnerShow: false,
      error: null,
      ShowPassword: "password",
      btnText: "Show Password",
    };
  },
  validations: {
    AccountFormData: {
      password: {
        required,
        minLength: minLength(6),
      },
      email: {
        required,
        email
      },
      //   userName: {
      //     required,
      //   },
    },
  },

  methods: {
    showHidePass() {
      if (this.ShowPassword === "password") {
        this.ShowPassword = "text";
        this.btnText = "Hide Password!";
      } else {
        this.ShowPassword = "password";
        this.btnText = "SHOW Password!";
      }
    },
    async SubmitLoginForm() {
      this.error = null;
      this.spinnerShow = true;

      if (!this.$v.$invalid) {
        await this.$store.dispatch("checkUserExit", {
          email: this.AccountFormData.email,
        });
        let checkExist = this.$store.state.existUser;
        if (checkExist == false) {
          localStorage.setItem('AccountFormData', JSON.stringify(this.AccountFormData))
          // $(".nav-tabs li a[href='#profile']").tab("show");
          this.spinnerShow = false;

        } else {
          this.error = "<p class='alert alert-danger'>This email id already exit.Please try other email id.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
          this.spinnerShow = false;
        }
        //});
      } else {
        this.spinnerShow = false;
      }
    },
  },
};
</script>
