<template>
<div>
    <section id="banner-section">
        <div class="inner-banner">
            <div class="container">
                <div class="row">
                    <div class="col-lg-10 col-lg-offset-1">
                        <div class="banner-height">
                            <div class="display-table">
                                <div class="display-table-cell">
                                    <div class="row">
                                        <div class="col-lg-10 col-lg-offset-1">
                                            <div class="store-address">
                                                <div class="row">
                                                    <div class="row-height">
                                                        <div class="col-sm-4 col-sm-height">
                                                            <div class="store-img">
                                                                <img :src="dispensaryProfileImg" class="img-responsive" />
                                                            </div>
                                                        </div>
                                                        <div class="col-sm-8 col-sm-height col-middle">
                                                            <div class="high-street-section">
                                                                <div class="title">
                                                                    <h2>{{dispensaryBusinessName}}</h2>
                                                                </div>
                                                                <div class="sub-heading">
                                                                {{dispensaryStreetAddress}} {{dispensaryStreetAddress2}},  
                                                                {{dispensaryCity}},  {{dispensaryState}} {{dispensaryZipCode}}
                                      
                                                                </div>
                                                                <div class="star">
                                                                    <starRatingCount :max="5" :current="totalRating" />
                                                                    <div class="reviews">({{totalReview}} Review)</div>
                                                                </div>
                                                                <p>
                                                                    Edibles, Concentrates, Apparel,
                                                                    <br class="hidden-xs" />Sativa, Indica
                                                                </p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</div>
</template>

<script>
import starRatingCount from '../../../components/backEnd/RatingComponent';
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
export default {
    data() {
        return {
            totalReview: null,
            totalRating: null,
            dispensaryBusinessName: null,
            dispensaryStreetAddress: null,
            dispensaryStreetAddress2: null,
            dispensaryCity: null,
            dispensaryState: null,
            dispensaryZipCode: null,
            dispensaryProfileImg: null,
        }
    },
    components: {
        starRatingCount,
    },
    created() {
       // alert(1);
        this.getDispnesaryData();
    },
    
    methods: {
        async getDispnesaryData(){  
            await this.$store.dispatch("getDispensaryUser");
            let dispensaryUserData = await this.$store.state.dispensaryUser;
            this.dispensaryBusinessName = dispensaryUserData.allData.busniess_name;
            this.dispensaryProfileImg = dispensaryUserData.allData.profile_image;
            this.dispensaryStreetAddress = dispensaryUserData.allData.address.street1;
            this.dispensaryStreetAddress2 =dispensaryUserData.allData.address.street2;
            this.dispensaryState = dispensaryUserData.allData.address.state;
            this.dispensaryCity = dispensaryUserData.allData.address.city;
            this.dispensaryZipCode = dispensaryUserData.allData.address.zip_code;
            
            this.dispensaryRating(dispensaryUserData.allData.id);
            
        },

        async dispensaryRating(id){
            var vueInstance = this; 
            var totalReviewCount =0
            var ratingCount =0
            await  fb.dispensaryCollection.doc(id).collection('reviews').get()
            .then(function(querySnapshot) { 
            totalReviewCount = querySnapshot.size;
            if(totalReviewCount >0){
                
                querySnapshot.forEach(function(docreview) {
                    ratingCount += parseInt(docreview.data().rating);
                });
                vueInstance.totalReview= totalReviewCount;
                vueInstance.totalRating= ratingCount/totalReviewCount;
                            
            }else{
                vueInstance.totalReview= totalReviewCount;
                vueInstance.totalRating= ratingCount;
            }

            }).catch(function(error) {
            console.log("Error getting documents: ", error);
            });
      },
    },
}
</script>
