<template>
  <div>
    <header-component />

<section class="top-gray-border gray-section mt-0">
		<div class="container">
			<div class="row">
				<div class="col-lg-10 col-lg-offset-1">
					<div class="row">
						<div class="col-sm-6">
							<div class="main-page-title">
								<h1>Your Orders</h1>
								<span>Manage you orders & re-order your favorites</span>
							</div>
						</div>
						<div class="col-sm-6">
							<div class="view-btn">
								<div class="send-btn">
									<router-link :to="{name: 'myOrdersSetting'}">View all orders</router-link>
								</div>
							</div>
						</div>
					</div>

					<div class="row">
						<div class="col-sm-4 center-text" v-for="(order, index) in getOderData" :key="index">
							<div class="white-box">
								<div class="right-title">
									<h2>{{ order.dispensary_data.busniess_name}}</h2>
								</div>
								<div class="order-title">
									<span class="sub-title"> Order Total: ${{ order.grand_total | currency}}</span>
									<div class="link-btn"><router-link :to="{name: 'myOrdersSetting',params: { orderId: order.id }}">View order details </router-link></div>
								</div>
								<div class="order-date">
									<span>Order Placed On: </span>
									<div class="date-date">{{ order.created_at | longDate}}</div>
								</div>
								<div class="order-date">
									<span>Delivary Placed On: </span>
									<div class="date-date">{{ order.updated_at | longDate}}</div>
								</div>
						<!--		<div class="reorder-btn">
									<a href="#">Reorder</a>
								</div>
                        -->
							</div>
						</div>
						<div class="col-sm-12 center-text" v-if="notAvailable == 0 && getOderData.length <=0">
							You have no order yet..
						</div>
					</div>
				</div>
			</div>
		</div>

</section>

<section id="papular-section">
	<div class="container">
		<div class="row">
			<div class="col-lg-10 col-lg-offset-1">
				<div class="title">
					<h2>Popular Near You</h2>
				</div> 
				<div class="row">
					<div class="row-height">
						<div class="col-sm-2 col-sm-height cursor-pointer" v-for="(dispensaryListData,index) in nearDispensaryListData" :key="index"  @click="dispensaryProfile(dispensaryListData.id)">
							<div class="hight-street">
								<div class="hight-street-img"> <img :src="dispensaryListData.profile_image" class="img-thumbnail nearImg"></div>
								<!-- <img :src="dispensaryListData.profile_image" width="230px" height="215px" class="img-thumbnail"> -->
								<span class="street-title">{{ dispensaryListData.busniess_name}}</span>
							</div>
							<div class="prod-star">
							<star-rating :max="5" :current="reviewCount[index][dispensaryListData.id].rating"></star-rating>
							<!--<i v-for="n in 5" :key="n" class="fa fa-star" :class="{'total-item-no': n <= reviewCount[dispensaryListData.id].rating,'gray': n > reviewCount[dispensaryListData.id].rating}"></i>-->
								<div class="reviews">({{reviewCount[index][dispensaryListData.id].count}} Reviews)</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</section>

<dowmloadApp />

    <footer-component />
  </div>
</template>

<script>
import orderStatus from "../../../order-status";
import dowmloadApp from "../../../components/backEnd/customer/DownloadAppComponent";
import * as fb from "../../../firebaseConfig";
import addressType from "../../../address-type";
import starRating from '../../../components/backEnd/RatingComponent';
import firebase from "firebase";

export default {
  data() {
    return {
        personalAddressType: addressType.personalAddressType,
        primaryAddressType: addressType.primaryAddressType,
        secondaryAddressType: addressType.secondaryAddressType,
      getOderData: [],
      notAvailable: false,
	  status:orderStatus.complate,
	  despensaryDistance:200,
	  nearDispensaryListData:[],
	  reviewCount:{},
    };
  },
  components: {
    dowmloadApp,
	starRating
  },
  
  created(){
	this.orderData();
	this.nearDispensary();
  },
	
  methods:{
	 
     async orderData() {
        var vueInstance = this;
      	let orderData = await fb.orderCollection.where("user_id", "==", fb.auth.currentUser.uid)
        .where("status","==",this.status).orderBy("created_at", "desc").limit(3);
        
     // let orderData = await fb.orderCollection.orderBy('created_at', 'desc').limit(3);
           

      orderData.onSnapshot(function (querySnapshot) {
        var allOrderData = [];

        querySnapshot.forEach((doc) => {
          allOrderData.push(doc.data());
        });
        setTimeout(function(){
          if(allOrderData.length <= 0)
            vueInstance.notAvailable = 0;
        }, 3000);
        vueInstance.getOderData = allOrderData;
      });
     },

	async nearDispensaryList(latitude, longitude){

        var distance = this.despensaryDistance;
        // ~1 mile of lat and lon in degrees
        let lat = 0.0144927536231884
        let lon = 0.0181818181818182

        let lowerLat = latitude - (lat * distance)
        let lowerLon = longitude - (lon * distance)

        let greaterLat = latitude + (lat * distance)
        let greaterLon = longitude + (lon * distance)

        let lesserGeopoint = new firebase.firestore.GeoPoint(lowerLat, lowerLon)
        let greaterGeopoint = new firebase.firestore.GeoPoint(greaterLat, greaterLon)

        var vueInstance = this; 
        let query = fb.dispensaryCollection.where("location", '>' , lesserGeopoint).where("location", '<' , greaterGeopoint).limit(5);
        
        query.onSnapshot(function (querySnapshot) {
        var dispensaryData = [];
        var reviewCountData = [];

        querySnapshot.forEach((doc) => { 
			const data =doc.data();
			
			var id = data.id;
			var reviewData = {};
			var ratingCount = 0;
			try {
		
				fb.dispensaryCollection.doc(id).collection('reviews').get()
				.then(function(querySnapshot) { 
					var totalCount = querySnapshot.size;
					if(totalCount >0){
						
					querySnapshot.forEach(function(docreview) {
						
						ratingCount += parseInt(docreview.data().rating);
						
					});
					reviewData[id]={
						count:totalCount,
						rating:ratingCount/totalCount
					};
					reviewCountData.push(reviewData);
					ratingCount =0;
					}else{
						reviewData[id]={
							count:0,
							rating:ratingCount
						};
						reviewCountData.push(reviewData);
					}
				})
				.catch(function(error) {
					console.log("Error getting documents: ", error);
				});
			} catch (error) {}
			
			dispensaryData.push(data);
        });
		vueInstance.reviewCount  = reviewCountData;
        setTimeout(function(){
			vueInstance.nearDispensaryListData  = dispensaryData;
			}, 2000);
      	
        
        setTimeout(function(){
          if(dispensaryData.length <= 0)
            vueInstance.notAvailable = 0;
        }, 3000);
      });
    },

	async nearDispensary(){
		var vueInstance = this; 
		let getData = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
      
		getData.doc('data_address').onSnapshot(function (querySnapshot) {
			var customerDataAddress =querySnapshot.data();
			var customerAddress = customerDataAddress.addresses;
			customerAddress.some(function (elem, i) { 
				if (customerAddress.some(address => address.type === vueInstance.primaryAddressType) && customerDataAddress.isPersonAddress == false) {
					
					if(customerAddress[i].type == vueInstance.primaryAddressType){
						vueInstance.nearDispensaryList(customerAddress[i].latitude,customerAddress[i].longitude);
					}

				}else if (customerAddress.some(address => address.type === vueInstance.personalAddressType) && customerDataAddress.isPersonAddress == true) {
					
					if(customerAddress[i].type == vueInstance.personalAddressType){
						vueInstance.nearDispensaryList(customerAddress[i].latitude,customerAddress[i].longitude);
					}
				
				}else if (customerAddress.some(address => address.type === vueInstance.secondaryAddressType)) {
					vueInstance.nearDispensaryList(customerAddress[i].latitude,customerAddress[i].longitude);
				}else if (customerAddress.some(address => address.type === vueInstance.personalAddressType) && customerDataAddress.isPersonAddress == false) {
					
					if(customerAddress[i].type == vueInstance.personalAddressType){
						vueInstance.nearDispensaryList(customerAddress[i].latitude,customerAddress[i].longitude);
					}
				
				}
				 
			  });
        })
	},

	dispensaryProfile(id){
		this.$router.push({ name: "dispensaryPrfoileView",  params: { dispesaryId: id }})
	}

  },
};
</script>
<style scoped>
#papular-section{ padding-top: 50px;}
.nearImg{
	min-height: 215px;
	max-height: 215px;
}
</style>
