<template>
  <div>
    <div class="verify-title nop-space">My Coupons</div>
    <div class="row">
      <div class="col-sm-6">
        <div class="tab-search contact-form">
          <div class="row">
            <div class="col-sm-8">
              <div class="form-group">
                <input
                  v-model.trim="search.couponName"
                  class="form-control"
                  type="text"
                  placeholder="Search Coupons"
                  list="browsers"
                  id="browser"
                />
              </div>
              <datalist id="browsers">
                <option
                  v-for="couponName in couponNameData"
                  :key="couponName.id"
                >{{couponName.coupon_code_name}}</option>
              </datalist>
            </div>
            <div class="col-sm-4">
              <div class="form-group">
                <button class="btn black-btn" @click.prevent="searchByCoupon">
                  <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>Search
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-sm-6">
        <div class="send-btn post-btn">
          <a href="#" @click.prevent="addNewCoupon">ADD NEW COUPON</a>
        </div>
        <div></div>
      </div>
    </div>

    <div class="form-filter">
      <flash-message></flash-message>
      <div class="row contact-form">
        <div class="col-sm-4">
          <div class="row">
            <div class="col-sm-9">
              <div class="start-date">
                <label>Bulk Actions</label>
                <select class="form-control" v-model.trim="search.bulkActionData">
                  <option>Delete</option>
                </select>
              </div>
            </div>
            <div class="col-sm-3">
              <button class="apply-btn" @click.prevent="bulkAction">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>APPLY
              </button>
            </div>
          </div>
        </div>
        <div class="col-sm-4">
          <div class="row">
            <div class="col-sm-9">
              <div class="start-date">
                <label>Filter by Date</label>
                <select class="form-control" v-model.trim="search.dateByfilter">
                  <option value="7">7 Dates</option>
                  <option value="30">30 Dates</option>
                  <option value="90">90 Dates</option>
                </select>
              </div>
            </div>
            <div class="col-sm-3">
              <button class="apply-btn" @click.prevent="filterByDate">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>FILTER
              </button>
            </div>
          </div>
        </div>
        <div class="col-sm-4">
          <div class="row">
            <div class="col-sm-9">
              <div class="start-date">
                <label>Filter by Type of Coupon</label>
                <select class="form-control" v-model.trim="search.filterTypeCoupon">
                  <option value="percentage">Discount Percentage</option>
                </select>
              </div>
            </div>
            <div class="col-sm-3">
              <button class="apply-btn" @click.prevent="filterBytype">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>FILTER
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
      <div class="page top-spce">
        <nav aria-label="pagenavigation" class="text-right">
          <ul class="pagination">
            <li>
              <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li>
              <a
                href="#"
                v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
                :key="index"
                @click.prevent="page = pageNumber"
              >{{pageNumber}}</a>
            </li>

            <li>
              <a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
      <div class="details-table top-spce">
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr class="table-title">
                <th>SELECT</th>
                <th>COUPON CODE</th>
                <th>COUPON TYPE</th>
                <th>PERCENTAGE (%)</th>
                <th>DESCRIPTION</th>
           <!--     <th>PRODUCT SKU</th>-->
                <th>USE LIMIT</th>
                <th>EXPERATION DATE</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(couopn, index) in displayedCoupon" :key="index" :id="couopn.id">
                <td>
                  <input type="checkbox" v-model.trim="actionCheckbox" :value="couopn.id" />
                </td>
                <td>
                  <a href="javascript:void(0)" @click.prevent="cououpReview(couopn.id)" class="table-view-btn">{{ couopn.coupon_code_name}}</a>
                </td>
                <td>{{ couopn.discount_type}}</td>
                <td>{{ couopn.percentage}}%</td>
                <td>{{ couopn.coupon_description}}</td>
           <!--     <td>{{ couopn.product_sku }}</td>-->
                <td>{{ couopn.usage_limit_per_user}}</td>
                <td>{{ couopn.expiration_date | longDate}}</td>
              </tr>
              <tr v-if="notAvailable == 0 && displayedCoupon.length <=0">
                <td colspan="7"><b>You don't have created any coupon code yet.</b></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="page top-spce">
        <nav aria-label="pagenavigation" class="text-right">
          <ul class="pagination">
            <li>
              <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li>
              <a
                href="#"
                v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
                :key="index"
                @click.prevent="page = pageNumber"
              >{{pageNumber}}</a>
            </li>

            <li>
              <a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
    

  </div>
</template>

<script>
import evenBus from "../../../event-bus";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
export default {
  data() {
    return {
      page: 1,
      perPage: 10,
      pages: [],
      getCouponData: [],
      couponNameData: [],
      search: {
        dateByfilter: null,
        filterTypeCoupon: null,
        bulkActionData: null,
      },
      actionCheckbox: [],
      spinnerShow: false,
      notAvailable:null,
    };
  },

  created() {
    this.couponData();
    this.couponDataName();
  },
  computed: {
    displayedCoupon() {
      return this.paginate(this.getCouponData);
    },

  },
  watch: {
    getCouponData() {
      this.page = 1;
      this.perPage= 10;
      this.pages= [];
      this.setPages();
    },
  },
  methods: {
    setPages() {
      let numberOfPages = Math.ceil(this.getCouponData.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },

    async couponData() {
      var vueInstance = this;
      let couponData = await fb.couponCollection
        .where("dispensary_user_id", "==", fb.auth.currentUser.uid)
        .orderBy("created_at", "desc");
      //let allCouponData = couponData.onSnapshot.map(snapshot => snapshot.data())

      couponData.onSnapshot(function (querySnapshot) {
        var allCouponData = [];

        querySnapshot.forEach((doc) => {
          allCouponData.push(doc.data());
        });
        vueInstance.getCouponData = allCouponData;
        setTimeout(function(){
          if(allCouponData.length <= 0)
            vueInstance.notAvailable = 0;
        }, 3000);
      });
    },

    async couponDataName() {
      await this.$store.dispatch("allCouponName");
      this.couponNameData = await this.$store.state.couponNameData;
    },

    async bulkAction() {
      if (this.search.bulkActionData == null) {
        this.flashError("Please select any action.", { timeout: 7000 });
        this.spinnerShow = false;
        return false;
      }
      if (this.actionCheckbox.length <= 0) {
        this.flashError("Please check atleast one record.", { timeout: 7000 });
        this.spinnerShow = false;
        return false;
      }
      await this.$store
        .dispatch("couponBulkAction", {
          action: this.search.bulkActionData,
          actionValue: this.actionCheckbox,
        })
        .then((suucess) => {
          this.actionCheckbox=[];
          this.flashSuccess("Successfully deteled.", { timeout: 7000 });
          this.spinnerShow = false;
        });
    },
    async filterByDate() {
      var vueInstance = this;
      if (this.search.dateByfilter == null) {
        this.flashError("Please select date by filter.", { timeout: 7000 });
        this.spinnerShow = false;
        return false;
      }
      await this.$store.dispatch("couponFilterData", {
        startDate: this.search.dateByfilter,
        discountType: null,
        couponByName: null,
      });
       vueInstance.getCouponData = await this.$store.state.couponList;
    },

    async searchByCoupon() {
       var vueInstance = this;
      if (this.search.couponName == null) {
        this.flashError("Please enter coupon name.", { timeout: 7000 });
        this.spinnerShow = false;
        return false;
      } 
      await this.$store.dispatch("couponFilterData", {
        startDate: null,
        discountType: null,
        couponByName: this.search.couponName,
      });
      vueInstance.getCouponData = await this.$store.state.couponList;
    },

    async filterBytype() {
       var vueInstance = this;
      if (this.search.filterTypeCoupon == null) {
        this.flashError("Please select type of coupon.", { timeout: 7000 });
        this.spinnerShow = false;
        return false;
      }
      await this.$store.dispatch("couponFilterData", {
        startDate: null,
        couponByName: null,
        discountType: this.search.filterTypeCoupon,
      });
      vueInstance.getCouponData = await this.$store.state.couponList;
    },

    addNewCoupon() {
      evenBus.$emit("addCoupon");
    },
    cououpReview(id) {
      //alert(id);
      evenBus.$emit("couponReview",id);
    },
  },
};
</script>
