<template>
  <div>
    <header-component />
    <section id="banner-section" class="" data-sequence="100">
      <div class="container" data-id="1">
        <div class="row" data-sequence="100">
          <div class="col-lg-10 col-lg-offset-1" data-id="1">
            <div class="text-center">
              <div class="banner">
                <img :src="baseUrl + '/images/CANNABIS-icon.png'" />
                <h2 class="banner-title">RECOVER YOUR PASSWORD</h2>
                <h4>ENTER YOUR EMAIL ADDRESS BELOW AND WE WILL SEND YOU A PASSWORD RESET LINK</h4>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section id="contact-section" class="register-margin-top">
      <div class="container">
        <div class="row">
          <div class="col-lg-12 col-lg-offset-1">
            <div class="row animatedParent" data-sequence="100">
              <div class="col-sm-2"></div>
              <div class="col-sm-6" data-id="1">
                <flash-message></flash-message>
                <div class="login-left">
                  <h3>Forgot Password</h3>

                  <div class="contact-form">
                    <form @submit.prevent="SubmitForgotFrom">
                      <input class="form-control" placeholder="Email Address" type="text" v-model.trim="email"
                        @input="$v.email.$touch()" />
                      <div v-if="$v.email.$dirty">
                        <p class="text-danger" v-if="!$v.email.required">Please enter your email address.</p>
                        <p class="text-danger" v-if="!$v.email.email">Please enter valid email address.</p>
                      </div>
                      <button class="btn send-btn" :disabled="$v.$invalid">
                        <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>LOGIN
                      </button>
                    </form>
                  </div>

                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <footer-component />
  </div>
</template>

<script>
/*eslint-disable */
const $ = window.jQuery = require('jquery');
import { required, email } from "vuelidate/lib/validators";
import * as fb from "../../firebaseConfig";
import firebase from "firebase";
import axios from "axios";
export default {
  data() {
    return {
      baseUrl: $('head base').attr('href'),
      email: "",
      spinnerShow: false,
      error: null,
    };
  },
  validations: {
    email: {
      required,
      email,
    },
  },
  methods: {

    async SubmitForgotFrom() {
      var vueInstance = this;
      vueInstance.error = null;
      vueInstance.spinnerShow = true;
      $('.btn').prop("disabled", true);
      if (!vueInstance.$v.$invalid) {
        await fb.usersCollection.where("email", "==", vueInstance.email).get().then((doc) => {
          if (!doc.empty) {
            let existUser = doc.docs[0].data();
            if (existUser.activated_at == false) {
              $('.btn').prop("disabled", false);
              vueInstance.spinnerShow = false;
              vueInstance.flashError('Your login email address deactivated now. Please contact our support team.', {
                timeout: 8000,
              });
              return false;
            }
            fb.auth.sendPasswordResetEmail(vueInstance.email).then(function () {
              vueInstance.flashSuccess("We have sent a link for recover of your password.Please click it.", { timeout: 8000 });
            }).catch(function (error) {
              vueInstance.flashError("We have sent a link for recover of your password.Please click it.", { timeout: 8000 });
            });
            $('.btn').prop("disabled", false);
            vueInstance.spinnerShow = false;
            return false;
          } else {
            $('.btn').prop("disabled", false);
            vueInstance.spinnerShow = false;
            vueInstance.flashError('The email address you entered is not registered..', {
              timeout: 15000,
            });
          }
        })
      } else {
        $('.btn').prop("disabled", false);
        vueInstance.spinnerShow = false;
      }
    },
  },

};
</script>
