<template>
<div>
    <header-component />
    <sectionBanner />

    <section id="customer-form">
        <div class="container">
            <div class="row">
                <div class="col-lg-10 col-lg-offset-1 animatedParent" data-sequence="100">
                    <div class="tab-section animated bounceIn go" data-id="1">
                        <!-- Nav tabs -->
                        <selectTab :tabPage="this.tab"/>

                        <!-- Tab panes -->
                        <div class="tab-content page-tab bottom-space">
                            <div role="tabpanel" class="tab-pane active" id="deals-ads">
                            
                            <component v-bind:is="currentComponent" ></component>
                           
                               
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
        
    </section>

<!-- Modal -->
<div class="modal fade" id="addProductGallery" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
	<div class="modal-dialog" role="document">
		<div class="modal-content">
            <div class="modal-header border-0">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
            </div>
            <div class="modal-body">
				<h3 class="text-center text-dark">Add Images to Product Gallery</h3>
                <div class="gray-form-box border-0 text-center uploadfileModal"
                    @dragenter="OnDragEnter"
                    @dragleave="OnDragLeave"
                    @dragover.prevent
                    @drop="onDrop"
                    :class="{ dragging: isDragging }">
    
                <div class="font-weight-bold ">Drop Files to Upload</div>
                <div class="or-text"><span>OR</span></div>
					<div class="custom-file">
						<input type="file" class="custom-file-input
						" @change="onInputChange" multiple>
						<a href="#" class="btn gray-btn d-inline-block mt-0"><i class="fa fa-upload" aria-hidden="true"></i> SELECT FILES</a>
					</div>
					
					<div class="images-preview" v-show="images.length">
                    <div class="img-wrapper" v-for="(image, index) in images" :key="index">
                        <img accept="image/*" width="120px" height="120px" :src="image" :alt="`Image Uplaoder ${index}`">
                        <div class="details">
                            <span class="name" v-text="files[index].name"></span>
                        <!--   <span class="size" v-text="getFileSize(files[index].size)"></span>-->
                        </div>
                    </div>
                </div>
                
                <div class="custom-file-label"></div>
                </div>

                
                <div class="text-center" v-show="images.length">
					<a href="javascript:void(0)" @click="upload" class="btn send-btn save-btn mb-5">ADD TO GALLERY</a>
				</div>
      
    
            </div>
        </div>
    </div>
</div>



    <footer-component />
  
</div>
</template>

<script>
/*eslint-disable */
const $ = window.jQuery = require('jquery');

import evenBus from '../../../event-bus'
import sectionBanner from '../../../components/backEnd/dispensary/SectionBannerComponent'
import selectTab from '../../../components/backEnd/dispensary/TabComponent'
import productList from '../../../components/backEnd/dispensary/ProductListComponent'
import productAdd from '../../../components/backEnd/dispensary/ProductAddComponent'

export default {
    data() {
        return {
            isDragging: false,
            dragCount: 0,
            files: [],
            images: [],
        
            currentComponent:productList,
            dataId: null,
            tab:null
        }
    },
    components: {
        sectionBanner,
        productList,
        productAdd,
        selectTab
    },
    created (){
        localStorage.removeItem("DisRegistratonLoginForm");
        localStorage.removeItem("DispensaryForm");
        localStorage.removeItem("userImage");
        this.tab = this.$route.name;
        evenBus.$on('addProduct', () => {
            this.currentComponent=productAdd;
        })
        evenBus.$on('backToList', () => {
        this.currentComponent=productList;
        })

    },
    methods: {
      
        OnDragEnter(e) { 
            e.preventDefault();
            
            this.dragCount++;
            this.isDragging = true;
            return false;
        },
        OnDragLeave(e) { 
            e.preventDefault();
            this.dragCount--;
            if (this.dragCount <= 0)
                this.isDragging = false;
        },
        onInputChange(e) { 
            const files = e.target.files;
            Array.from(files).forEach(file => this.addImage(file));
        },
        onDrop(e) { 
            e.preventDefault();
            e.stopPropagation();
            this.isDragging = false;
            const files = e.dataTransfer.files;
            Array.from(files).forEach(file => this.addImage(file));
        },
        addImage(file) { 
            if (!file.type.match('image.*')) {
                this.$toastr.e(`${file.name} is not an image`);
                return;
            }
            this.files.push(file);
            const img = new Image(),
                reader = new FileReader();
            reader.onload = (e) => this.images.push(e.target.result);
            reader.readAsDataURL(file);
        },
           

    upload() { 
        evenBus.$emit('previewGalleryImages', this.files)
        this.isDragging = false;
        this.dragCount= 0;
        this.files= [];
        this.images= [];
        $('#addProductGallery').modal('hide');
    },

  },
};
</script>
<style scoped>
.images-preview, .img-wrapper{
  padding: 8px;
}
</style>
