<template>
  <div class="row contact-form">
    <div class="col-sm-2"></div>
    <div class="col-sm-8">
      <flash-message></flash-message>
      <div :class="{ loader:dataLoader }"></div>
    </div>
    <div class="col-sm-2"></div>
    <form @submit.prevent="UpdateDocumentForm">
      <div class="col-sm-5">
        <div class="contact-title border-bottom">
          <h3>Drivers License & Vehicle Information</h3>
        </div>
        <div class="media-heading top-spce">DRIVERS LICENSE INFORMATION</div>
        <div class="form-group top-spce">
            <input v-model.trim="DocumnetFormData.driverLicenseNo" @input="$v.DocumnetFormData.driverLicenseNo.$touch()" type="text" placeholder="Drivers License Number *" class="form-control">
            <div v-if="$v.DocumnetFormData.driverLicenseNo.$dirty">
                <p class="text-danger" v-if="!$v.DocumnetFormData.driverLicenseNo.required">Please enter your driver license.</p>
            </div>
        </div>
        <div class="form-group ">
          <select class="form-control" v-model.trim="DocumnetFormData.issuingState">
            <option value="">Issuing State *</option>
            <option v-for="(state, index) in getStateData" :key="index" :value="state">{{ state }}</option>
          </select>
            <div v-if="$v.DocumnetFormData.issuingState.$dirty">
            <p class="text-danger" v-if="!$v.DocumnetFormData.issuingState.required">Please select issuing state.</p>
          </div>
        </div>

        <div class="input-group">
          <input v-model.trim="DocumnetFormData.dob" @input="$v.DocumnetFormData.dob.$touch()"
              class="form-control calender-date"
              aria-describedby="basic-addon2"
              type="text"
              placeholder="Date of Birth *"
              id="dob"
              name="dob"
            />
          <span class="input-group-addon cuorser" id="basic-addon2"><i class="fa fa-calendar" aria-hidden="true"></i></span>
        </div>


        <div class="right-title margin-top spaceing">
          <h2>Vehicle Information </h2>
          </div>

          <div class="form-group" v-if="DocumnetFormData.manufactureYear">
            <select class="form-control" v-model.trim="DocumnetFormData.manufactureYear">
              <option>Select Year of Car *</option>
               <option v-for="(yearCar, index) in manufactureYearList" :key="index" :value="yearCar">{{ yearCar }}</option>
            </select>
              <div v-if="$v.DocumnetFormData.manufactureYear.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.manufactureYear.required">Please select manufacture state.</p>
            </div>
          </div>
          <div class="form-group">
            <input v-model.trim="DocumnetFormData.manufactureCompany" @input="$v.DocumnetFormData.manufactureCompany.$touch()"  class="form-control" placeholder="Make *" type="text">
            <div v-if="$v.DocumnetFormData.manufactureCompany.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.manufactureCompany.required">Please enter vehicle company.</p>
            </div>
          </div>
          <div class="form-group">
            <input v-model.trim="DocumnetFormData.manufactureModel" @input="$v.DocumnetFormData.manufactureModel.$touch()"  class="form-control" placeholder="Model *" type="text">
            <div v-if="$v.DocumnetFormData.manufactureModel.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.manufactureModel.required">Please enter vehicle modal number.</p>
            </div>
          </div>
          <div class="form-group">
            <select class="form-control" v-model.trim="DocumnetFormData.lincensePlateState">
              <option>License Plate State *</option>
              <option v-for="(state, index) in getStateData" :key="index" :value="state">{{ state }}</option>
            </select>
          </div>
          <div class="form-group">
            <input v-model.trim="DocumnetFormData.licensePlateNumber" @input="$v.DocumnetFormData.licensePlateNumber.$touch()" class="form-control" placeholder="License Plate Number *" type="text">
          </div>
      </div>

      <div class="col-sm-2"></div>

      <div class="col-sm-5">
        <div class="contact-title border-bottom">
          <h3>Upload Photos</h3>
        </div>
        <div class="driver-details">
          <div class="upload-text">
            <p>Photo of Drivers License. </p>
          </div>
          <p class="doct-content">
            Please upload a photo of your <span>Drivers License </span>. Please  make sure we can read all the letters and numbers on the license. *
          </p>
          <div class="row top-spce">
          <div class="row-height">
            <div class="col-sm-6 col-sm-height">
              <div class="driver-photo">
                <img v-if="driverLicenseImage" :src="driverLicenseImage" class="img-responsive" />
                <p id="fileNameDriverLicense"></p>
              </div>

            </div>
            <div class="col-sm-6 col-sm-height col-middle">
              <div class="remove-btn">
                <a href="#" v-if="driverLicenseImage" @click.prevent="removeDriverLicenseImage">Remove</a>
                <input
                  type="file"
                  @change="onFileRecommendationChange"
                  accept="image/jpeg"
                  id="uploadDriverLicenseImage"
                  style="display:none"
                />
                <a href="#" v-if="!driverLicenseImage" @click.prevent="addDriverLicenseImage">Add</a>
              </div>
            </div>
          </div>
        </div>
        </div>
        <div class="driver-details">
          <div class="upload-text">
            <p>Photo of PROOF OF INSURANCE </p>
          </div>
          <p class="doct-content">
            Please upload a photo of proof of your <span>current car insurance policy </span>. Please make sure we can read all of the numbers/letters in the image. *
          </p>
          <div class="row top-spce">
            <div class="row-height">
              <div class="col-sm-6 col-sm-height">
                <div class="driver-photo">
                  <img v-if="insuranceImage" :src="insuranceImage" class="img-responsive"/>
                <p id="fileInsuranceImage"></p>
                </div>

              </div>
              <div class="col-sm-6 col-sm-height col-middle">
                <div class="remove-btn">
                  <a href="#" v-if="insuranceImage" @click.prevent="removeInsuranceImage">Remove</a>
                <input
                  type="file"
                  @change="onInsuranceChange"
                  accept="image/jpeg"
                  id="uploadGovermentImage"
                  style="display:none"
                />
                <a href="#" v-if="!insuranceImage" @click.prevent="addInsuranceImage">Add</a>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="driver-details">
          <div class="upload-text">
            <p>PHOTO OF VEHICLE REGISTRATION</p>
          </div>
          <p class="doct-content">
            Please upload a photo of proof of your  <span>current car registration.</span>.Please make sure we can read all of the numbers/letters in the image. *
          </p>

          <div class="row top-spce">
            <div class="row-height">
              <div class="col-sm-6 col-sm-height">
                <div class="driver-photo">
                  <img v-if="vehicleRegistrationImage" :src="vehicleRegistrationImage" class="img-responsive"/>
                  <p id="fileVehicleRegistrationImage"></p>
                </div>

              </div>
              <div class="col-sm-6 col-sm-height col-middle">
                <div class="remove-btn">
                  <a href="#" v-if="vehicleRegistrationImage" @click.prevent="removeVehicleRegistrationImage">Remove</a>
                  <input
                    type="file"
                    @change="onVehicleRegistrationChange"
                    accept="image/jpeg"
                    id="uploadVehicleRegistrationImage"
                    style="display:none"
                  />
                  <a href="#" v-if="!vehicleRegistrationImage" @click.prevent="addVehicleRegistrationImage">Add</a>
                </div>
              </div>
            </div>
          </div>
        </div>
        <button class="btn send-btn" :disabled="$v.$invalid">
          <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>
            SAVE
        </button>


      </div>
    </form>  
  </div>
</template>

<script>
import {
  required,
  numeric,
  alpha,
  sameAs,
  requiredIf,
} from "vuelidate/lib/validators";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
export default {
  data() {
    return {
      dataLoader:true,
      getStateData: [],
      image:null,
      DocumnetFormData:{
        driverLicenseNo:null,
        issuingState:null,
        dob:null,
        manufactureYear:null,
        manufactureCompany:null,
        manufactureModel:null,
        lincensePlateState:null,
        licensePlateNumber:null
      },
      spinnerShow: false,
      error: null,

      driverLicenseImage:null,
      insuranceImage:null,
      vehicleRegistrationImage:null,

      uploadImage:{
        driverLicenseImage: null,
        insuranceImage: null,
        vehicleRegistrationImage: null,
      },
      manufactureYearList:{}
    };
  },
  validations: {
    DocumnetFormData: {
      driverLicenseNo: {
        required,
      },
      issuingState: {
        required,
      },
      dob: {
        required,
      },
      manufactureYear: {
        required,
      },
      manufactureCompany: {
        required,
      },
      manufactureModel: {
        required,
      },
      lincensePlateState: {
        required,
      },
      licensePlateNumber: {
        required,
      },

    },
    driverLicenseImage: {
      required,
    },
    insuranceImage: {
      required,
    },
    vehicleRegistrationImage: {
      required,
    },
  },
  mounted() { 
    this.fbState();
    this.driverDocuments();
    this.SelectedYearCar();
    var vm = this;
    $("input[name=dob]").datepicker({ 
      changeMonth: true,
      changeYear: true,
      minDate: 0,
      dateFormat: 'mm/dd/yy',
      onSelect: (dateForm) => {
        vm.DocumnetFormData.dob = dateForm;
      },
    });
  },
  methods: {

    removeDriverLicenseImage(){
      this.driverLicenseImage= null; 
      this.uploadImage.driverLicenseImage = null;
      $('#fileNameDriverLicense').html('');
    },

    onFileRecommendationChange(e) {
      const file = e.target.files[0];
      this.uploadImage.driverLicenseImage = file;
      $('#fileNameDriverLicense').html(file.name);
      this.driverLicenseImage = URL.createObjectURL(file);
    },
    
    addDriverLicenseImage() {
      $("#uploadDriverLicenseImage").trigger("click");
    },

    removeInsuranceImage(){
      this.insuranceImage= null; 
      this.uploadImage.insuranceImage = null;
      $('#fileInsuranceImage').html('');
    },

    onInsuranceChange(e) { 
      const file = e.target.files[0];
      this.uploadImage.insuranceImage = file;
      $('#fileInsuranceImage').html(file.name);
      this.insuranceImage = URL.createObjectURL(file);
    },
    
    addInsuranceImage() {
      $("#uploadGovermentImage").trigger("click");
    },

    removeVehicleRegistrationImage(){
      this.vehicleRegistrationImage= null; 
      this.uploadImage.vehicleRegistrationImage = null;
      $('#fileVehicleRegistrationImage').html('');
    },

    onVehicleRegistrationChange(e) {
      const file = e.target.files[0];
      this.uploadImage.vehicleRegistrationImage = file;
      $('#fileVehicleRegistrationImage').html(file.name);
      this.vehicleRegistrationImage = URL.createObjectURL(file);
    },
    
    addVehicleRegistrationImage() {
      $("#uploadVehicleRegistrationImage").trigger("click");
    },

    async uploadImageAsPromise(imageDataUrl) {
      let userId = fb.auth.currentUser.uid;
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      const type = imageDataUrl.type;
      return new Promise(function (resolve, reject) {
      var storageRef = firebase
          .storage()
          .ref(`driverDoc/${userId}/${currentTime}.`+type)
          .put(imageDataUrl, {
              contentType: type,
          });
        storageRef.then((data) => {
          data.ref.getDownloadURL().then((url) => {
            resolve(url);
          });
        });
      });
    },

    async UpdateDocumentForm() {
      var vueInstance = this;
      $(".flash__wrapper").empty();
      vueInstance.error = null;
      vueInstance.spinnerShow = true;
      if (!vueInstance.$v.$invalid) {
        $('.btn').prop("disabled", true);
        var driverLicenseImage = vueInstance.uploadImage.driverLicenseImage;
        var insuranceImage = vueInstance.uploadImage.insuranceImage;
        var vehicleRegistrationImage = vueInstance.uploadImage.vehicleRegistrationImage;
       
        let dataUpdate = await fb.driverCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_account');
       
        if(driverLicenseImage ||insuranceImage ||vehicleRegistrationImage){
          let document_images={};
        
          if(driverLicenseImage){
            await vueInstance.uploadImageAsPromise(driverLicenseImage).then((url) => {
            document_images['driving_license']=url;
            dataUpdate.set({document_images:document_images},{merge:true})
            });
          }
          if(insuranceImage){
            await vueInstance.uploadImageAsPromise(insuranceImage).then((url) => {
              document_images['car_insurance_policy']=url;
              dataUpdate.set({document_images:document_images},{merge:true})
            });
          }
          if(vehicleRegistrationImage){ 
            await vueInstance.uploadImageAsPromise(vehicleRegistrationImage).then((url) => {
              document_images['car_registration']=url;
              dataUpdate.set({document_images:document_images},{merge:true})
            });
          }
        }

        dataUpdate.update({
          "documents.driver_license_no": vueInstance.DocumnetFormData.driverLicenseNo,
          "documents.issuing_state": vueInstance.DocumnetFormData.issuingState,
          "documents.manufacture_year":  vueInstance.DocumnetFormData.manufactureYear,
          "documents.manufacture_model": vueInstance.DocumnetFormData.manufactureModel,
          "documents.manufacture_company":  vueInstance.DocumnetFormData.manufactureCompany,
          "documents.licence_issuing_state":  vueInstance.DocumnetFormData.lincensePlateState,
          "documents.licence_plate_number":  vueInstance.DocumnetFormData.licensePlateNumber,
          "account.date_of_birth": await firebase.firestore.Timestamp.fromDate(new Date(vueInstance.DocumnetFormData.dob)).toMillis(),
        }) 
        .then((success) => {
       
          vueInstance.flashSuccess("You have successfully update your document information.", {
            timeout: 10000,
          });
          $('.btn').prop("disabled", false);
          vueInstance.spinnerShow = false;
        
        })
        .catch((err) => {
          vueInstance.flashError("There is some problem. Please try again later."+err, {
            timeout: 10000,
          });
          $('.btn').prop("disabled", false);
          vueInstance.spinnerShow = false;
        });
      }
    },

    
    async driverDocuments() {
      await this.$store.dispatch("getDriverUser");
      let driverData = await this.$store.state.driverData;
      this.DocumnetFormData.driverLicenseNo = driverData.documents.driver_license_no;
      this.DocumnetFormData.issuingState = driverData.documents.issuing_state; 
      var dob  = firebase.firestore.Timestamp.fromMillis(driverData.account.date_of_birth).toDate().toUTCString();
      var dateOfBirth = new Date (dob);
      var day = dateOfBirth.getDate();
      var month = dateOfBirth.getMonth() + 1;
      var year = dateOfBirth.getFullYear()
      this.DocumnetFormData.dob=  month + '/'+day + '/' + year;
      
      this.DocumnetFormData.manufactureYear = driverData.documents.manufacture_year;
      this.DocumnetFormData.manufactureModel = driverData.documents.manufacture_model;
      this.DocumnetFormData.manufactureCompany = driverData.documents.manufacture_company;
      this.DocumnetFormData.lincensePlateState = driverData.documents.licence_issuing_state;
      this.DocumnetFormData.licensePlateNumber = driverData.documents.licence_plate_number;
      
      this.driverLicenseImage = driverData.document_images.driving_license;
      this.insuranceImage = driverData.document_images.car_insurance_policy;
      this.vehicleRegistrationImage = driverData.document_images.car_registration;
      this.dataLoader=false
    },
   
    async fbState() {
      await this.$store.dispatch("getAllState");
      this.getStateData = this.$store.state.GetState[0].state;
    },

    async SelectedYearCar(){ 
      const currentYear = (new Date()).getFullYear(); 
      const range = (start, stop, step) => Array.from({ length: (stop - start) / step + 1}, (_, i) => start + (i * step));
      this.manufactureYearList = range(currentYear, currentYear - 15, -1)
    },
  },
};
</script>
