<template>
  <div class="contact-form">
    <form @submit.prevent="SubmitDispensaryFrom">
      <div class="form-group">
        <input
          class="form-control"
          placeholder="Business Name"
          type="text"
          v-model.trim="DispensaryFormData.businessName"
          @input="$v.DispensaryFormData.businessName.$touch()"
        />
        <div v-if="$v.DispensaryFormData.businessName.$dirty">
          <p
            class="text-danger"
            v-if="!$v.DispensaryFormData.businessName.required"
          >Please enter your business name.</p>
        </div>
      </div>
      <div class="form-group">
        <input
          class="form-control"
          placeholder="First Name"
          type="text"
          v-model.trim="DispensaryFormData.firstName"
          @input="$v.DispensaryFormData.firstName.$touch()"
        />
        <div v-if="$v.DispensaryFormData.firstName.$dirty">
          <p
            class="text-danger"
            v-if="!$v.DispensaryFormData.firstName.required"
          >Please enter your first name.</p>
        </div>
      </div>
      <div class="form-group">
        <input
          class="form-control"
          placeholder="Last Name"
          type="text"
          v-model.trim="DispensaryFormData.lastName"
          @input="$v.DispensaryFormData.lastName.$touch()"
        />
        <div v-if="$v.DispensaryFormData.lastName.$dirty">
          <p
            class="text-danger"
            v-if="!$v.DispensaryFormData.lastName.required"
          >Please enter your last name.</p>
        </div>
      </div>
      <div class="form-group">
        <input
          class="form-control"
          placeholder="Phone Number"
          type="text" @keyup="acceptNumber"
          v-model.trim="DispensaryFormData.phoneNo"
          @input="$v.DispensaryFormData.phoneNo.$touch()"
        />
        <div v-if="$v.DispensaryFormData.phoneNo.$dirty">
          <p
            class="text-danger"
            v-if="!$v.DispensaryFormData.phoneNo.required"
          >Please enter your valid phone no..</p>
          
        </div>
      </div>
      <div class="form-group">
        <input
          class="form-control"
          placeholder="Email Address"
          type="text"
          v-model.trim="DispensaryFormData.email"
          @input="$v.DispensaryFormData.email.$touch()"
        />
        <div v-if="$v.DispensaryFormData.email.$dirty">
          <p
            class="text-danger"
            v-if="!$v.DispensaryFormData.email.required"
          >Please enter your email address.</p>
          <p
            class="text-danger"
            v-if="!$v.DispensaryFormData.email.email"
          >Please enter valid email address.</p>
        </div>
      </div>
      <div class="checkbox">
        <label>
          <input type="checkbox" v-model.trim="DispensaryFormData.subscribe" />
          Subscribe to receive email communications from Grab-A-Gram
        </label>
      </div>
      <div class="form-group">
        <textarea  :maxlength="maxWord"
          class="form-control text-msg"
          rows="6"
          placeholder="Tell us a little about you and your company..."
          v-model.trim="DispensaryFormData.aboutYou"
          @input="$v.DispensaryFormData.aboutYou.$touch()"
        ></textarea>
        <div>About you must be no longer than {{(maxWord - DispensaryFormData.aboutYou.length)}} word</div>
            
        <div v-if="$v.DispensaryFormData.aboutYou.$dirty">
          <p
            class="text-danger"
            v-if="!$v.DispensaryFormData.aboutYou.required"
          >Please enter tell us about you.</p>
        </div>
      </div>
      <div v-if="error">{{error}}</div>
      <div class="text-center">
        <button class="btn send-btn help-btn" :disabled="$v.$invalid">
          <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>APPLY TO BECOME A PARTNER
        </button>
      </div>
    </form>
  </div>
</template>

<script>
/* eslint-disable */
const $ = window.jQuery = require('jquery');
import { required, numeric, email } from "vuelidate/lib/validators";
import axios from "axios";

import * as fb from "../../firebaseConfig";
import firebase from "firebase";
export default {
  data() {
    return {
      DispensaryFormData: {
        businessName: null,
        firstName: null,
        lastName: null,
        phoneNo: "",
        email: null,
        subscribe: null,
        aboutYou: '',
       // linkString: null
      },
      maxWord: 250,
      spinnerShow: false,
      error: null,
      url:this.$store.state.API_URL_ROOT,
    };
  },
  validations: {
    DispensaryFormData: {
      businessName: { required },
      firstName: { required },
      lastName: { required },
      phoneNo: { required },
      email: { required, email },
      aboutYou: { required },
    },
  },
   
  methods: {
      acceptNumber() {
            var x = this.DispensaryFormData.phoneNo.replace(/\D/g, '').match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
            this.DispensaryFormData.phoneNo = !x[2] ? x[1] : '(' + x[1] + ') ' + x[2] + (x[3] ? '-' + x[3] : '');
        },
    async SubmitDispensaryFrom() {
        var vueInstance = this;
      vueInstance.error= null;
      vueInstance.spinnerShow = true;
        $('.btn').prop("disabled", true);
      if (!vueInstance.$v.$invalid) {
        await vueInstance.$store.dispatch("checkUserExit", {
          email: vueInstance.DispensaryFormData.email,
        });
        let checkExist = vueInstance.$store.state.existUser;
        if (checkExist == false) {
        //    vueInstance.generateRandomString().then((randomString) => {
            //let randomString = btoa(vueInstance.DispensaryFormData.email);
            let currentTime = firebase.firestore.Timestamp.now().toMillis();
            let dataInsert = await fb.tempRegistrationCollection.doc();
              dataInsert.set({
                role: "Dispensary",
                business_name: vueInstance.DispensaryFormData.businessName,
                first_name: vueInstance.DispensaryFormData.firstName,
                last_name: vueInstance.DispensaryFormData.lastName,
                phone_no: vueInstance.DispensaryFormData.phoneNo,
                email: vueInstance.DispensaryFormData.email,
                about_you: vueInstance.DispensaryFormData.aboutYou,
                subscribe: vueInstance.DispensaryFormData.subscribe,
                created_at: currentTime,
                updated_at: currentTime,
              }).then(function () {
                dataInsert.update({
                    id: dataInsert.id,
                });
                 let randomString =dataInsert.id  
                axios.get(`${vueInstance.url}sendDispensaryVerifyEmail/${vueInstance.DispensaryFormData.email}/${randomString}`)
                  .then((response) => {  
                    if(response.data.status == 0){ 
                      vueInstance.spinnerShow = false;
                      $('.btn').prop("disabled", false); 
                      return  vueInstance.error = response.data.msg; 
                    } 
                   vueInstance.$set(vueInstance.DispensaryFormData, 'linkString', randomString);
                    localStorage.setItem('DispensaryForm',JSON.stringify(vueInstance.DispensaryFormData))
                    vueInstance.$router.push({ name : "verification"});
                
                  });
              });
        //  });
        }else{
          vueInstance.error= "<p class='alert alert-danger'>This email id already exit.Please try other email id.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
          vueInstance.spinnerShow = false;
          $('.btn').prop("disabled", false);
          
        }
      } else {
        vueInstance.spinnerShow = false;
        $('.btn').prop("disabled", false);
      }
      
    },
   /* async generateRandomString() {
            var length = 30;
            var randomString = "";
            var possible =
                "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
            for (var i = 0; i < length; i++)
                randomString += possible.charAt(
                    Math.floor(Math.random() * possible.length)
                );
            return randomString;
        },
*/
  },
};
</script>
