<template>
  <div>
      <div :class="{ loader:dataLoader }"></div>
    <section id="banner-section">
      <div class="container">
        <div class="row">
          <div class="col-lg-10 col-lg-offset-1"><flash-message></flash-message>
            <div class="dispensaries-result-section">
              <div class="row">
                <div class="col-sm-4">
                  <div class="title">
                    <h2>Dispensaries Near You</h2>
                    <div class="sub-heading">{{customerAddressData}}</div>
                  </div>

                  <div class="location=map-scroll">

                    <div class="search-location" v-for="(dispensaryListData,index) in nearDispensaryData" :key="index">
                      <div class="address-box">
                        <div class="right-title">
                          	<h2>{{dispensaryListData.count+1}}. 
						  		<span class="cursor-pointer" @click="dispensaryProfile(dispensaryListData.id)">
							  		{{dispensaryListData.busniess_name}}
						  		</span>
							</h2>
							
						  <div class="row top-spce">
                            <div class="col-sm-4">
                              <div class="star cursor-pointer">
                                <star-rating :max="5" :current="reviewCount[dispensaryListData.count][dispensaryListData.id].rating"></star-rating>
                                <p class="reviews">({{reviewCount[dispensaryListData.count][dispensaryListData.id].count}} Reviews)</p>
                              </div>
                            </div>
                            <div class="col-sm-8">
                              <div class="media add-title">
                                <div class="media-left">
                                  <a href="#">
                                    <i
                                      class="fa fa-map-marker"
                                      aria-hidden="true"
                                    ></i>
                                  </a>
                                </div>
                                <div class="media-body">
                                  <h4 class="media-heading cursor-pointer" @click="getPositionInfo(dispensaryListData, dispensaryListData.count)">
                                    {{dispensaryListData.address.street1}} {{dispensaryListData.address.street2}},<br />
                                    {{dispensaryListData.address.city}}, {{dispensaryListData.address.state}} {{dispensaryListData.address.zip_code}}
                                  </h4>
                                </div>
                              </div>
                            </div>
                          </div>
                          <p>
                             {{dispensaryListData.about_us | strLimit(75)}}
                          </p>
                        </div>
                      </div>
                    </div>

                    <div class="pagination-bottom">
                      <div class="row">
                        <!--<div class="col-sm-4">
                          <div class="page-of">Page 1 of 5</div>
                        </div>-->

                        <div class="col-sm-10">
                          <nav aria-label="Page navigation" class="text-right">
                            <ul class="pagination">
                              	<li>
									<a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
										<span aria-hidden="true">&laquo;</span>
									</a>
								</li>
                              	<li>
									<a
										href="#"
										v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
										:key="index"
										@click.prevent="page = pageNumber"
									>{{pageNumber}}</a>
								</li>
                              	<li>
									<a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
										<span aria-hidden="true">&raquo;</span>
									</a>
								</li>
                            </ul>
                          </nav>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="col-sm-8">
                  <div class="result-map" v-if="nearDispensaryListData.length>0">
					<GmapMap
						:center="startLocation"
						:zoom="9"
						map-type-id="terrain"
						style="width: 630px; height: 700px"
						>
						<gmap-info-window :options="infoPosition"  :position="infoPosition" 
							:opened="infoOpened" @closeclick="infoOpened=false">
							{{infoContent}}
						</gmap-info-window>
					<!--	<GmapMarker 
							:key="index"
							v-for="(m, index) in markers"
							:position="m.position"
							:clickable="true"
							:draggable="true"
							:label="{color: '#fff', fontSize: '14px', fontWeight: '600', text: '1'}"
							@mouseover="polygonHover"
							:options="polygonOptions"
						/>
							<gmap-info-window :position="{lat:10, lng:10}">
            					Hello world!
          					</gmap-info-window>
		  			-->
						<!--	@mouseover="getPositionInfo(item, key)"-->
				<!--		   <gmap-marker v-for="(item, key) in coordinates" :key="key" 
						   		:position="getPosition(item)" :clickable="true" :draggable="true" 
							   	@click="getPositionInfo(item, key)" />-->
								<gmap-marker v-for="item in nearDispensaryData" :key="item.count" 
						   		:position="getPosition(item)" :clickable="true" :draggable="true" 
							   	@click="getPositionInfo(item, item.count)" />
							</GmapMap>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import starRating from "../../../components/backEnd/RatingComponent";
import * as fb from "../../../firebaseConfig";
import addressType from "../../../address-type";
import firebase from "firebase";
//import {gmapApi} from 'vue2-google-maps'
export default {
  data() {
    return {
        dataLoader:true,
        personalAddressType: addressType.personalAddressType,
        primaryAddressType: addressType.primaryAddressType,
        secondaryAddressType: addressType.secondaryAddressType,
		page: 1,
      	perPage: 3,
      	pages: [],
		despensaryDistance:200,
	  	nearDispensaryListData:[],
	  	reviewCount:{},
		customerAddressData:null,
		startLocation: {}, 
	/*	startLocation1:{
			lat:39.9727076,
			lng:-82.9689318
		},
*/
		infoPosition: null,
		infoContent: null,
		infoOpened: false,
		infoCurrentKey: null,
		infoOptions: {
			pixelOffset: {
				width: 0,
				height: -35
      	}	
		
		}, 
    };
  },
  mounted(){
	  $(".flash__wrapper").empty();
  },
  components: {
    starRating,
  },
  computed: {
    nearDispensaryData() {
      return this.paginate(this.nearDispensaryListData);
    },

  },
  watch: {
    nearDispensaryListData() {
      this.page = 1;
      this.perPage= 3;
      this.pages= [];
      this.setPages();
    },
  },
  created() {
    this.nearDispensary();
  },
  methods: {

	getPosition: function(marker) {
      return {
        lat: parseFloat(marker.address.latitude),
        lng: parseFloat(marker.address.longitude)
      }
    },

	getPositionInfo: function(marker, key) {
		
		this.infoPosition = this.getPosition(marker);
		this.infoContent = marker.address.street1 + ' , ' + marker.address.city;
	
		if( marker.address.street2 !='' && marker.address.street2 != null)
		    this.infoContent = marker.address.street1 + ' '+ marker.address.street2 +', ' + marker.address.city;
		
		
		
		if (this.infoCurrentKey == key) {
			this.infoOpened = !this.infoOpened;
		} else {
			this.infoOpened = true;
			this.infoCurrentKey = key;
		}
	},

	setPages() {
      let numberOfPages = Math.ceil(this.nearDispensaryListData.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },

	async nearDispensary(){
		var vueInstance = this; 
		let getData = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
      
	    await getData.doc('data_address').onSnapshot(function (querySnapshot) {
			var customerDataAddress =querySnapshot.data();
			var customerAddress = customerDataAddress.addresses;
			customerAddress.some(function (elem, i) { 
				let latLong={
					lat:customerAddress[i].latitude,
					lng:customerAddress[i].longitude
				}
				if (customerAddress.some(address => address.type === vueInstance.primaryAddressType) && customerDataAddress.isPersonAddress == false) {
					
					if(customerAddress[i].type == vueInstance.primaryAddressType){ 
						vueInstance.startLocation=latLong;
						vueInstance.customerAddressData= customerAddress[i].city +', '+customerAddress[i].state;
						vueInstance.nearDispensaryList(customerAddress[i].latitude,customerAddress[i].longitude);
					}

				}else if (customerAddress.some(address => address.type === vueInstance.personalAddressType) && customerDataAddress.isPersonAddress == true) {
					
					if(customerAddress[i].type == vueInstance.personalAddressType){ 
						vueInstance.startLocation=latLong;
						vueInstance.customerAddressData= customerAddress[i].city +', '+customerAddress[i].state;
						vueInstance.nearDispensaryList(customerAddress[i].latitude,customerAddress[i].longitude);
					}
				
				}else if (customerAddress.some(address => address.type === vueInstance.secondaryAddressType)) { 
					vueInstance.startLocation=latLong;
					vueInstance.customerAddressData= customerAddress[i].city +', '+customerAddress[i].state;
					vueInstance.nearDispensaryList(customerAddress[i].latitude,customerAddress[i].longitude);
				}else if (customerAddress.some(address => address.type === vueInstance.personalAddressType) && customerDataAddress.isPersonAddress == false) {
					
					if(customerAddress[i].type == vueInstance.personalAddressType){ 
						vueInstance.startLocation=latLong;
						vueInstance.customerAddressData= customerAddress[i].city +', '+customerAddress[i].state;
						vueInstance.nearDispensaryList(customerAddress[i].latitude,customerAddress[i].longitude);
					}
				
				}
				 
			  });
        })
	},

	async nearDispensaryList(latitude, longitude){

        var distance = this.despensaryDistance;
        // ~1 mile of lat and lon in degrees
        let lat = 0.0144927536231884
        let lon = 0.0181818181818182

        let lowerLat = latitude - (lat * distance)
        let lowerLon = longitude - (lon * distance)

        let greaterLat = latitude + (lat * distance)
        let greaterLon = longitude + (lon * distance)

        let lesserGeopoint = new firebase.firestore.GeoPoint(lowerLat, lowerLon)
        let greaterGeopoint = new firebase.firestore.GeoPoint(greaterLat, greaterLon)

        var vueInstance = this; 
        let query = await fb.dispensaryCollection.where("location", '>' , lesserGeopoint).where("location", '<' , greaterGeopoint);
        
        query.onSnapshot(function (querySnapshot) {
        var dispensaryData = [];
		var reviewCountData = [];
		var indexCount=0;

        querySnapshot.forEach((doc) => { 
			const data =doc.data();
			
			var id = data.id;
			var reviewData = {};
			var ratingCount = 0;
			try {
			fb.dispensaryCollection.doc(id).collection('reviews').get()
			.then(function(querySnapshot) { 
				var totalCount = querySnapshot.size;
				if(totalCount >0){
				
				querySnapshot.forEach(function(docreview) {
					 
					ratingCount += parseInt(docreview.data().rating);
					
				});
					reviewData[id]={
						count:totalCount,
						rating:ratingCount/totalCount
					};
				//	console.log(reviewData);
					reviewCountData.push(reviewData);
					ratingCount =0;
				}else{
					reviewData[id]={
						count:0,
						rating:ratingCount
					};
					reviewCountData.push(reviewData);
				}
				
			})
			.catch(function(error) {
				console.log("Error getting documents: ", error);
			});
			} catch (error) {}
			data['count']=indexCount;
			dispensaryData.push(data);
			indexCount++;
			//vueInstance.reviews(doc.data().id)
        });
       vueInstance.reviewCount  = reviewCountData;
        setTimeout(function(){
			vueInstance.nearDispensaryListData  = dispensaryData;
			vueInstance.dataLoader = false
			}, 1500);
        setTimeout(function(){
          if(dispensaryData.length <= 0)
            vueInstance.notAvailable = 0;
            vueInstance.dataLoader = false
        }, 3000);
      });
    },
	dispensaryProfile(id){
		this.$router.push(`${id}/dispesaryProfile`)
	}
  },
};
</script>

