<template>
  <div>
    <div class="rightbox">
      <p class="para14">{{dispnesaryData.about_us}}  </p>
    </div>
    <div class="rightbox">
        <h4 class="text-uppercase font-weight-bold">Contact Info</h4>
        <p class="para14">
          Phone: <a v-bind:href="'tel:' + dispnesaryData.address.phone_number">
                    {{ dispnesaryData.address.phone_number | phoneNoFormate }}
                  </a><br>
          Email: <a v-bind:href="'mailto:' + dispesaryUserdata.email">
                    {{ dispesaryUserdata.email }}
                  </a>
        </p>
    </div>
    <div class="rightbox">
        <h4 class="text-uppercase font-weight-bold">Hours</h4>
        <div class="para14 fw-medium">
          <p><span class="days">Mon:</span>  
            <span v-if="openingHours.some((openingDay,i) => openingDay.day === 'Monday')">
              <span v-for="(openingtime,index) in openingHours" :key="index">
                <span v-if="(openingtime.day === 'Monday')">
                  {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                </span>
              </span>
            </span>
           <span v-else class="text-danger">CLOSED</span>
          </p>
          <p><span class="days">Tue:</span>  
            <span v-if="openingHours.some((openingDay,i) => openingDay.day === 'Tuesday')">
              <span v-for="(openingtime,index) in openingHours" :key="index">
                <span v-if="(openingtime.day === 'Tuesday')">
                  {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                </span>
              </span>
            </span>
           <span v-else class="text-danger">CLOSED</span>
          </p>
          <p><span class="days">Wed:</span>  
            <span v-if="openingHours.some((openingDay,i) => openingDay.day === 'Wednesday')">
              <span v-for="(openingtime,index) in openingHours" :key="index">
                <span v-if="(openingtime.day === 'Wednesday')">
                  {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                </span>
              </span>
            </span>
           <span v-else class="text-danger">CLOSED</span>
          </p>
          <p><span class="days">Thur:</span>   
            <span v-if="openingHours.some((openingDay,i) => openingDay.day === 'Thursday')">
              <span v-for="(openingtime,index) in openingHours" :key="index">
                <span v-if="(openingtime.day === 'Thursday')">
                  {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                </span>
              </span>
            </span>
           <span v-else class="text-danger">CLOSED</span>
          </p>
          <p><span class="days">Fri:</span>    
            <span v-if="openingHours.some((openingDay,i) => openingDay.day === 'Friday')">
              <span v-for="(openingtime,index) in openingHours" :key="index">
                <span v-if="(openingtime.day === 'Friday')">
                  {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                </span>
              </span>
            </span>
           <span v-else class="text-danger">CLOSED</span>
          </p>
          <p><span class="days">Sat:</span>  
          <span v-if="openingHours.some((openingDay,i) => openingDay.day === 'Saturday')">
              <span v-for="(openingtime,index) in openingHours" :key="index">
                <span v-if="(openingtime.day === 'Saturday')">
                  {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                </span>
              </span>
            </span>
           <span v-else class="text-danger">CLOSED</span>
          </p>
          <p><span class="days">Sun:</span>   
          <span v-if="openingHours.some((openingDay,i) => openingDay.day === 'Sunday')">
              <span v-for="(openingtime,index) in openingHours" :key="index">
                <span v-if="(openingtime.day === 'Sunday')">
                  {{openingtime.in_time | hoursConverter}} - {{openingtime.out_time | hoursConverter}}
                </span>
              </span>
            </span>
           <span v-else class="text-danger">CLOSED</span>
          </p>
        </div>
    </div>
    <div class="map-form mb-5">
      <GmapMap
				:center="startLocation"
				:zoom="17"
				map-type-id="terrain"
				style="border:0; width: 260px; height: 300px"
			>
        <gmap-marker :position="startLocation" :clickable="true" :draggable="true"/>
      </GmapMap>
    </div>
  </div>
</template>

<script>
import * as fb from "../../../../firebaseConfig";
export default {
  props: ["dispnesaryData"],
   data() {
        return {
          dispesaryAddress:this.dispnesaryData.address.street1+this.dispnesaryData.address.street2+this.dispnesaryData.address.city+this.dispnesaryData.address.state+this.dispnesaryData.address.zip_code,
          openingHours:this.dispnesaryData.opening_hours,
          startLocation:{
            lat:this.dispnesaryData.address.latitude,
			      lng:this.dispnesaryData.address.longitude
          },
          dispesaryUserdata: {}
        };
    },
  filters: {
      hoursConverter(time) {  
        time = time.substring(0, time.length-3);  
          return time;
      }
  },
  created() {
    this.userData()
  },
  methods: {
    async userData(){
       const userProfile = await fb.usersCollection.doc(fb.auth.currentUser.uid).get()
       this.dispesaryUserdata = userProfile.data()
    },
  },
};
</script>
