<template>
<div>
    <div class="tab-title">
        <h3 class="text-center">
            <span>Step 1:</span> Account Info
        </h3>
    </div>
    <div class="row">
        <div class="col-lg-8 col-lg-offset-2">
            <form @submit.prevent="SubmitLoginForm">
                <div class="contact-form">
                    <div class="form-group">
                        <input class="form-control" placeholder="Email *" type="text" :disabled="isEmailDisable" v-model.trim="DriverRegistratonLoginForm.email" />
                    </div>
                    <div class="form-group">
                        <input class="form-control" placeholder="Password *" type="password" v-model.trim="DriverRegistratonLoginForm.password" @input="$v.DriverRegistratonLoginForm.password.$touch()" />
                        <div v-if="$v.DriverRegistratonLoginForm.password.$dirty">
                            <p class="text-danger" v-if="!$v.DriverRegistratonLoginForm.password.required">Please enter your password.</p>
                            <p class="text-danger" v-if="!$v.DriverRegistratonLoginForm.password.minLength">Please enter password minimum 6 lenght.</p>
                        </div>
                    </div>
                    <div class="checkbox">
                       <!-- <label>
                            <input v-model.trim="DriverRegistratonLoginForm.emailCommunication" type="checkbox"> I opt into receiving email communications from
                            Grab-A-Gram
                        </label>-->
                        <label>
                            <input v-model.trim="DriverRegistratonLoginForm.textMessage" type="checkbox"> Send me text message notifications
                            Grab-A-Gram
                        </label>
                    </div>
                    <div class="text-center">
                        <button class="btn send-btn save-btn" :disabled="$v.$invalid">
                            <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>
                            SAVE & CONTINUE
                            <i class="fa fa-long-arrow-right" aria-hidden="true"></i>
                        </button>

                    </div>
                </div>
            </form>
        </div>
    </div>

</div>
</template>

<script>
import {
    required,
    minLength
} from "vuelidate/lib/validators";
export default {
    props:['tempRegistration'],
    data() {
        return {
            DriverRegistratonLoginForm: {
                email: this.tempRegistration.email,
                password: null,
                textMessage: null,
                //emailCommunication: null,
            },
            spinnerShow: false,
            error: null,

        };
    },
    validations: {
        DriverRegistratonLoginForm: {
            password: {
                required,
                minLength: minLength(6),
            },
        },
    },
    computed: {

        isEmailDisable: function () {
            return !this.terms;
        },

    },
    methods: {

        async SubmitLoginForm() {
            $('.btn').prop("disabled", true);
            var vueInstance = this;
            vueInstance.error = null;
            vueInstance.spinnerShow = true;

            if (!vueInstance.$v.$invalid) {
                // Store all form data into local storage
                localStorage.setItem(
                    "DriverRegistratonLoginForm",
                    JSON.stringify(vueInstance.DriverRegistratonLoginForm)
                );
                $('.btn').prop("disabled", false);
                $(".nav-tabs li a[href='#profile-photo']").tab("show")
            } else {
                vueInstance.spinnerShow = false;
                $('.btn').prop("disabled", false);
            }
        },
    },
};
</script>
