<template>
  <div class="review-section">
    <div class="tab-about">
      <h3>Customer Reviews</h3>
    </div>

    <div class="client-reviews" v-for="(customerReview, index) in DispensaryReviewList" :key="index">
      <div class="row">
        <div class="col-sm-10">
          <h5>{{customerReview.user_name}}</h5>
          <div class="star-icon">
            <star-rating :max="5" :current="customerReview.rating"></star-rating>
          </div>
        </div>
        <div class="col-sm-2">
          <div class="post-time">{{ customerReview.created_at | timeFormateAgo}}</div>
        </div>
      </div>
      <p>
        {{customerReview.comment}}
      </p>
    </div>
    <div class="client-reviews" v-if="emptyDataVal == 0 && DispensaryReviewList.length <=0">
      Dispensary review is not available now.
    </div>
    

    <div class="pagination-review">
      <div class="row">
        <div class="col-sm-6">
         <!-- <div class="top-spce hidden-xs">Page 1 of 5</div> -->
          <div class="top-spce hidden-xs"></div>
        </div>
     
        <div class="col-sm-6 text-right">
          <nav aria-label="Page navigation">
            <ul class="pagination">
              <li>
                <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
                  <span aria-hidden="true"></span>
                </a>
              </li>
              <li><a
										href="#"
										v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
										:key="index"
										@click.prevent="page = pageNumber"
									>{{pageNumber}}</a></li>
              <li>
                <a href="#" @click.prevent="page++" v-if="page < pages.length"  aria-label="Next">
                  <span aria-hidden="true"></span>
                </a>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as fb from "../../../../firebaseConfig";
import starRating from "../../../../components/backEnd/RatingComponent";
export default {
  props: ["dispnesaryData"],
  data() {
    return {
      page: 1,
      	perPage: 10,
      	pages: [],
        dispensaryReviewData:[],
        emptyDataVal: null,
    };
  },
  components: {
    starRating,
  },
  filters: {
    timeFormateAgo(date) {  
      const intervals = [
        { label: 'year', seconds: 31536000 },
        { label: 'month', seconds: 2592000 },
        { label: 'day', seconds: 86400 },
        { label: 'hour', seconds: 3600 },
        { label: 'minute', seconds: 60 },
        { label: 'second', seconds: 0 }
      ];

     // var date = '2015-09-17';
    //  alert(new Date(date))
     // const seconds = Math.floor((new Date() - new Date(date)) / 1000);
      const seconds = Math.floor((new Date().getTime() - date) / 1000);
      const interval = intervals.find(i => i.seconds < seconds);
      const count = Math.floor(seconds / interval.seconds);
      if(count == "Infinity") return `Few seconds ago`;
      return `${count} ${interval.label}${count !== 1 ? 's' : ''} ago`;
    }
  },
  created() {
    this.customreReviewList();
  },
  computed: {
    DispensaryReviewList() {
      return this.paginate(this.dispensaryReviewData);
    },

  },
  watch: {
    dispensaryReviewData() {
      this.page = 1;
      this.perPage= 10;
      this.pages= [];
      this.setPages();
    },
  },
  methods: {
    setPages() {
      let numberOfPages = Math.ceil(this.dispensaryReviewData.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },
     async customreReviewList() {
        var vueInstance = this;
        let customreReview = await fb.dispensaryCollection.doc(this.dispnesaryData.id).collection('reviews').orderBy('created_at', 'desc');
           

        customreReview.onSnapshot(function (querySnapshot) {
        var allReviewData = [];

        querySnapshot.forEach((doc) => {
          allReviewData.push(doc.data());
        });
        
        vueInstance.dispensaryReviewData = allReviewData;
        // setTimeout(()=>{
        //   if(allReviewData.length <= 0)
        //     vueInstance.emptyDataVal = 0;
        // }, 6000);
      });
     },
  },
};
</script>
