<template>
  <div class="row">
    <div class="col-lg-12 col-lg-offset-1">
      <div class="form-menu">
        <div class="back-menu">
          <i class="fa fa-long-arrow-left" aria-hidden="true"></i>
          <a href="#" @click.prevent="couponListBack">Back to Deals & ADS</a>
        </div>
      </div>
      <div class="title contact-form">
        <h2>Coupon Name:- {{ this.allData.coupon_code_name}}</h2>
      </div>

      <div class="row top-spce">
        <div class="col-sm-3">
          <div class="start-date">
            <p>
              Start Date:
              <span>{{ this.allData.created_at | timeFormate}} </span>
            </p>
          </div>
        </div>
        <div class="col-sm-3">
          <div class="start-date">
            <p>
              End Date:
              <span>{{ this.allData.expiration_date | timeFormate}}</span>
            </p>
          </div>
        </div>
        <div class="col-sm-6"></div>
      </div>

      <div class="row" v-if="this.allData.coupon_description !== null">
        <div class="col-sm-12">
          <div class="gray-form-box">
            <div class="right-title">
              <h2 class="font-weight-bold">Coupon Description</h2>
            </div>
            <div class="deals-ads-datails">{{ this.allData.coupon_description}}</div>
          </div>
          <!--</div>-->
        </div>
      </div>

      <div class="row">
        <div class="col-sm-6">
          <div class="gray-form-box">
            <div class="right-title">
              <h2 class="font-weight-bold">Coupon Details</h2>
            </div>
            <table class="table table-borderless table-hover">
              <tbody>
                <tr>
                  <th scope="row">Discount Type</th>
                  <td>{{ this.allData.discount_type}}</td>
                </tr>
                <tr>
                  <th scope="row">Percentage</th>
                  <td>{{ this.allData.percentage}}%</td>
                </tr>
                <tr>
            <!--      <th scope="row">Minimum Spend</th>
                  <td>{{ this.allData.minimum_spend}}</td>
                </tr>
                <tr>
                  <th scope="row">Maximum Spend</th>
                  <td>{{ this.allData.maximum_spend}}</td>
                </tr>
                <tr>
                  <th scope="row">Individual Use only</th>
                  <td>{{ (this.allData.individual_use_only== true)?'YES':'NO'}}</td>
                </tr>
                <tr>
                  <th scope="row">Exclude Sale Items Only</th>
                  <td>{{ (this.allData.exclude_sale_iteam== true)?'YES':'NO'}}</td>
                </tr>
                <tr>
                  <th scope="row">Usage Limit Per COUPON</th>
                  <td>{{ this.allData.usage_limit_per_coupon}}</td>
                </tr>  -->
                <tr>
                  <th scope="row">Usage Limit Per USER</th>
                  <td>{{ this.allData.usage_limit_per_user}}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="col-sm-6">
          <div class="gray-form-box">
            <div class="right-title">
              <h2 class="font-weight-bold">Category &amp; Products Details</h2>
            </div>
            <table class="table table-borderless table-hover">
              <thead>
                <tr>
                  <th>Category Name</th>
                  <th>Products Name</th>
                  <th>SKU</th>
                </tr>
              </thead>
              <tbody>
                <tr >
                  <th :rowspan="this.productLength" style="vertical-align : middle;text-align:center;" scope="row">{{ this.allData.category_name}}</th>
                 
                </tr>
                <tr  v-for="(product, index ) in this.allData.product"
                  :key="index">
                  <td scope="row">{{ product.product_name}}</td>
                  <td scope="row">{{ product.product_sku}}</td>
                </tr>

              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import evenBus from "../../../event-bus";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
export default {
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      dataId: {
        id: this.id,
      },
      allData: {},
      productLength: '',
    };
  },
  components: {},
  created() {
    this.getCouopnDetail(this.dataId.id);
  },

  methods: {
    couponListBack() {
      evenBus.$emit("backToList");
    },
    async getCouopnDetail(id) {
      let dataValues = null;
      //let getDriverName = null;
      await fb.couponCollection
        .doc(id)
        .get()
        .then(function (doc) {
          if (doc.exists) {
            dataValues = doc.data(0);
          } else {
            console.log("No such document!");
          }
        })
        .catch(function (error) {
          console.log("Error getting document:", error);
        });
       this.productLength=  dataValues.product.length + 1;
      this.allData = dataValues;
    },
  },
};
</script>
