<template>
<div>
  <div :class="{ loader:dataLoader }"></div>
  <div class="row">
    <div class="col-lg-12">
      <div class="row contact-form">
        <div v-if="error" v-html="error">{{error}}</div>
       
        <div class="col-sm-3">
          <div class="start-date">
            <label>User Status</label>
            <select class="form-control" v-model.trim="search.UserStatus">
              <option value=1>Activated</option>
              <option value=0>Inactivated</option>
            </select>
          </div>
        </div>
        <div class="col-sm-3">
          <div class="start-date">
            <button
              class="btn search-btn order-search-btn"
              @click.prevent="searchData"
            >SEARCH ORDERS</button>
          </div>
        </div>
        <div class="col-sm-2">
          <div class="reset-btn">
            <a href="#" @click.prevent="resetSearchForm">
              <i class="fa fa-refresh" aria-hidden="true"></i>
              reset
            </a>
          </div>
        </div>
        <div class="col-sm-2 col-lg-offset-2" v-if="exportDataDiv">
          <download-excel class="export-btn cursor-pointer"
            :data="exportdriverData"
            :fields="dataForExcel"
            worksheet="My Worksheet"
            name="driver data.xls"
            :before-generate = "startDownload"
            :before-finish   = "finishDownload">
            <i class="fa fa-file-excel-o" aria-hidden="true"></i>
              Export Data
          </download-excel>
        </div>
      </div>
    </div>
  </div> 
  <div class="row table-space-top">
      <flash-message></flash-message>
    <div class="col-sm-12">
      <div class="details-table">
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr class="table-title">
                <th>USER ID</th>
                <th>FULL NAME</th>
                <th>EMAIL</th>
                <th>CITY</th>
                <th>STATE</th>
                <th>CREATED DATE</th>
                <th>STATUS</th>
                <th>ORDER LIST</th>
              </tr>
            </thead> 
            <tbody>
              <tr v-for="(driver, index) in displayedDriverList" :key="index">
                <td>
                  <router-link class="table-view-btn" :to="{ name: 'driverDetails', params: { driverId: driver.intoUser.user_id} }">{{ driver.intoUser.user_id}}</router-link>
                </td>
                <td>{{ driver.intoDriver.account.first_name}} {{driver.intoDriver.account.last_name}}</td>
                <td>{{ driver.intoUser.email}}</td>
                <td>{{ driver.intoDriver.address.city}}</td>
                <td>{{ driver.intoDriver.address.state}}</td>
                <td>{{ driver.intoUser.created_at | timeFormate}}</td>
                <td>
                  <label class="switch">
                    <input type="checkbox" :checked="driver.intoUser.activated_at" @change="changeUserStatus(driver.intoUser.user_id,$event)">
                    <span class="slider round"></span>
                  </label>
                </td>
                <td>
                  <router-link class="table-view-btn" :to="{ name: 'driverOrders', params: { driverId: driver.intoUser.user_id} }">VIEW ORDERS</router-link>
                </td>
              </tr>
              <tr v-show="emptyDataVal && getDriverData.length < 1 ">
                <td colspan="5"><h4>You don't have any record.</h4></td>
              </tr> 
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div class="col-sm-4"></div>
  </div>
  <div class="page top-spce">
    <nav aria-label="pagenavigation" class="text-right">
      <ul class="pagination">
        <li>
          <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li>
          <a
            href="#"
            v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
            :key="index"
            @click.prevent="page = pageNumber"
          >{{pageNumber}}</a>
        </li>

        <li>
          <a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import downloadExcel from "vue-json-excel";
export default {
  data() {
    return {
      dataForExcel: {
        "Registration Date": "registration_date",
        "User Id": "user_id",
        "Full Name": "full_name",
        "Phone Number": "phone_number",
        "Email Id": "email_id",
        Street1: "Street1",
        Street2: "Street2",
        City: "city",
        State: "State",
        "Zip Code": "zip_code",
        "Date Of Birth" : "birthdate",
        "Text Message Notifications" : "message_notification",
        "Subscribe to receive email" : "email_communication",
        "Driver License No" : "identity_number",
        "Driver License Issuing State" : "issuing_state",
        "Driver License Expiration Date" : "expiration_date",
        "Vehicle Licence Issuing State" : "licence_issuing_state",
        "Vehicle Licence Plate Number" : "licence_plate_number",
        "Vehicle Manufacture Year" : "manufacture_year",
        "Vehicle Manufacture Model" : "manufacture_model",
        "Vehicle Manufacture Company" : "manufacture_company",
        "Vehicle Regular Maintenance" : "regular_maintenance",
        "Have a smartphone?" : "smart_phone",
        "Phone Manufacture Model" : "phone_manufacture_model",
        "Phone Manufacture Company" : "phone_manufacture_company",
        "Is Caregiver" : "is_caregiver",
        Status : "status"
      },
      exportdriverData: [],
      json_meta: [
        [
          {
            key: "charset",
            value: "utf-8",
          },
        ],
      ],
      exportDataDiv: false,
      search: {
        UserStatus: 1,
      },
      dataLoader:true,
      driverRole: "Driver",
      page: 1,
      perPage: 10,
      pages: [],
      getDriverData: [],
      error: null,
      spinnerShow: false,
      emptyDataVal: false
    };
  },
  components:{
    downloadExcel
  },
  computed: {
    displayedDriverList() {
      return this.paginate(this.getDriverData);
    },
  },

  watch: {
    getDriverData() {
      this.page = 1;
      this.perPage= 10;
      this.pages= [];
      this.setPages();
    },
  },
  created() {
    this.driverData();
  },
  methods: {
    setPages() {
      let numberOfPages = Math.ceil(this.getDriverData.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },
     async driverData(UserStatus = "") {
        var vueInstance = this;
        vueInstance.getDriverData= []
        vueInstance.exportdriverData = []
        let query = await fb.usersCollection.where("role", "==", vueInstance.driverRole);
        if(UserStatus != ""){
          let status = UserStatus==1 ? true : false
          query = await query.where("activated_at", "==", status);
        }

        query.orderBy("created_at", "desc").get().then(querySnapshot =>{ 
        querySnapshot.forEach((userDoc) => { 
          // loadedPosts[userDoc.data().user_id] = [];
          let allCustomer={};
          vueInstance.driverDataAsPromise(userDoc.data().user_id).then((dataDoc) => {
            allCustomer ={
              intoUser: userDoc.data(),
              intoDriver: dataDoc
            }
           vueInstance.getDriverData.push(allCustomer)

           let exportData={
              registration_date: vueInstance.$options.filters.longDate(allCustomer.intoUser.created_at),
              user_id: allCustomer.intoUser.user_id,
              full_name: allCustomer.intoDriver.account.first_name +' '+ allCustomer.intoDriver.account.last_name,
              phone_number: vueInstance.$options.filters.phoneNoFormate(allCustomer.intoDriver.address.phone_number),
              email_id: allCustomer.intoUser.email,
              Street1: allCustomer.intoDriver.address.street_1,
              Street2: allCustomer.intoDriver.address.street_2 || "-",
              city: allCustomer.intoDriver.address.city,
              State: allCustomer.intoDriver.address.state,
              zip_code: allCustomer.intoDriver.address.zip_code,
              birthdate: vueInstance.$options.filters.longDate(allCustomer.intoDriver.account.date_of_birth),
              message_notification: allCustomer.intoDriver.account.message_notification? "YES": "NO",
              email_communication: allCustomer.intoDriver.account.email_communication? "YES": "NO",
              identity_number: allCustomer.intoDriver.documents.identity_number,
              issuing_state: allCustomer.intoDriver.documents.issuing_state,
              expiration_date: vueInstance.$options.filters.longDate(allCustomer.intoDriver.documents.expiration_date),
              licence_issuing_state: allCustomer.intoDriver.documents.licence_issuing_state,              
              licence_plate_number: allCustomer.intoDriver.documents.licence_plate_number,
              manufacture_year: allCustomer.intoDriver.documents.manufacture_year,
              manufacture_model: allCustomer.intoDriver.documents.manufacture_model,
              manufacture_company: allCustomer.intoDriver.documents.manufacture_company,
              regular_maintenance: allCustomer.intoDriver.documents.regular_maintenance,
              smart_phone: allCustomer.intoDriver.phones.smart_phone,
              phone_manufacture_model: allCustomer.intoDriver.phones.phone_manufacture_model,
              phone_manufacture_company: allCustomer.intoDriver.phones.phone_manufacture_company,
              is_caregiver: allCustomer.intoDriver.account.is_caregiver? "YES": "NO",
              status: allCustomer.intoUser.activated_at? "Actived": "Inactived",
            }
            vueInstance.exportdriverData.push(exportData)
            vueInstance.exportDataDiv = true
          });
          vueInstance.dataLoader = false
          
        });
        setTimeout(function(){
          if(vueInstance.getDriverData.length <= 0){ 
            vueInstance.emptyDataVal=  true;
            vueInstance.dataLoader = false
            vueInstance.exportDataDiv = false
            return false;
          }
        }, 2000);
      });

     },
    async changeUserStatus(userId, event){
      var vueInstance = this;
      let value = event.target.checked;                 
      $(".flash__wrapper").empty();
      var updateRef = fb.usersCollection.doc(userId);
        return updateRef.update({
            activated_at: value
        }).then((suucess) => {
          window.scrollTo(0,0);
          if(value == true)
            vueInstance.flashSuccess("Successfully activated this driver.", { timeout: 3000 });
          else  
            vueInstance.flashSuccess("Successfully inactivated this driver.", { timeout: 3000 });
          
          setTimeout(function(){ 
            vueInstance.$router.go();
          }, 4000);
        });
    },
    async driverDataAsPromise(userId) {
      return new Promise(function (resolve, reject) {
        let getData = fb.driverCollection.doc(userId).collection('response').doc('data_account').get();
        getData.then((dataDoc) => {
          resolve(dataDoc.data());
        });
      });
    }, 

    async searchData() {
      var vueInstance = this;
      vueInstance.dataLoader = true
      this.driverData(vueInstance.search.UserStatus);
    },

    async resetSearchForm() {
      this.search.UserStatus = 1;
      this.dataLoader = true
      this.driverData();
    },

    startDownload(){
      this.dataLoader = true
    },
    finishDownload(){
      this.dataLoader = false
    }
  },
  
};
</script>

<style scoped>
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #8dc73f;
}

input:focus + .slider {
  box-shadow: 0 0 1px #8dc73f;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.export-btn{
	background: #78ca49;
	padding: 10px 8px;
	color: #fff;
	text-decoration: none;
	margin-top: 27px;
	display: block;
	text-align: center;
}
</style>
