<script>
import addressType from '../../../address-type';
import * as fb from "../../../firebaseConfig";

export default {
  props:['customerId'],  
  data() {
    return {
      personalAddressType: addressType.personalAddressType,
      primaryAddressType: addressType.primaryAddressType,
      secondaryAddressType: addressType.secondaryAddressType,
      billingAddressType: addressType.billingAddressType,
      customerData:{},
      customerUserData:{},
      customerAddress: [],
      showDiv: false
    };
  },
  
 
  created() {
    this.customerUser();
  },
  methods: {

    async customerUser() {
      var vueInstance = this;
   
      let getData = await fb.customerCollection.doc(vueInstance.customerId).collection('response');
        getData.doc('data_account').get().then(dataDoc => {
          if(!dataDoc.empty) {
            vueInstance.customerData = dataDoc.data();  
            fb.usersCollection.doc(vueInstance.customerId).get().then(customerUserDoc => {
              if(!dataDoc.empty) {
                vueInstance.customerUserData =  customerUserDoc.data();  
             
                }
             });
            
            getData.doc('data_address').get().then(addressDoc => {
              if(!dataDoc.empty) {
                vueInstance.customerAddress =  addressDoc.data().addresses;  
                vueInstance.showDiv = true
                }
             });
          }
        });
    },
    
  },
};
</script>

<style scoped>
.address-space {
	margin-top: 50px;
}
</style>