<template>
  <div class="row">
    <div class="col-lg-10 col-lg-offset-1">
      <div class="form-menu contact-form">
        <div class="back-menu">
          <i class="fa fa-long-arrow-left" aria-hidden="true"></i>

          <a href="#" @click.prevent="couponListBack">Back to coupons</a>
        </div>
      </div>
      <flash-message></flash-message>
      <div class="title">
        <h2>Add New Coupon</h2>
      </div>
      <div class="row">
        <div class="col-sm-7">
          <div class="gray-form-box">
            <div class="right-title">
              <h2 class="font-weight-bold">Product Name & Description</h2>
            </div>

            <div class="contact-form">
              <div class="form-group">
                <label>Coupon Code Name*</label>
                <input
                  v-model.trim="couponData.couponCodeName"
                  class="form-control"
                  type="text"
                  placeholder="LTDHLKGHO"
                  @input="$v.couponData.couponCodeName.$touch()"
                />
                <div v-if="$v.couponData.couponCodeName.$dirty">
                  <p
                    class="text-danger"
                    v-if="!$v.couponData.couponCodeName.required"
                  >Please enter coupon code name.</p>
                  <p
                    class="text-danger"
                    v-if="!$v.couponData.couponCodeName.isUnique && couponData.isUniqueCouponName"
                  >This couoon name is already entered.</p>
                </div>
              </div>
              <div class="form-group">
                <label>Coupon Description</label>
                <textarea
                  v-model.trim="couponData.couponDescription"
                  class="form-control msg-height"
                  rows="8"
                  placeholder="Description (Optional)"
                ></textarea>
              </div>
            </div>
          </div>
          <div class="gray-form-box">
            <div class="right-title">
              <h2 class="font-weight-bold">Coupon Data</h2>
            </div>
            <br />
            <h3 class="border-bottom text-uppercase sub-title pb-4 mt-0">General Information</h3>

            <div class="contact-form">
              <div class="row">
                <div class="col-sm-4">
                  <div class="sku-title">Discount Type*:</div>
                </div>
                <div class="col-sm-7">
                  <div class="form-group">
                    <select class="form-control" v-model.trim="couponData.discountType">
                      <option value="percentage">Discount Percentage</option>
                    </select>
                    <div v-if="$v.couponData.discountType.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.couponData.discountType.required"
                      >Please select discount type.</p>
                    </div>
                  </div>
                </div>
                <div class="col-sm-1 infoIcon">
                  <i class="fa fa-question-circle" aria-hidden="true"></i>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-4">
                  <div class="sku-title">Percentage*:</div>
                </div>
                <div class="col-sm-7">
                  <div class="form-group">
                    <input  @input="$v.couponData.couponAmount.$touch()" v-model.trim="couponData.couponAmount" class="form-control" type="text" />
                    <div v-if="$v.couponData.couponAmount.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.couponData.couponAmount.required"
                      >Please enter percentage amount.</p>
                      <p
                        class="text-danger"
                        v-if="!$v.couponData.couponAmount.decimal"
                      >Please enter only numeric percentage amount.</p>
                    </div>
                  </div>
                </div>
                <div class="col-sm-1 infoIcon">
                  <i class="fa fa-question-circle" aria-hidden="true"></i>
                </div>
              </div>
              <div class="row border-bottom">
                <div class="col-sm-4">
                  <div class="sku-title">Expiration Date*:</div>
                </div>
                <div class="col-sm-7">
                  <div class="form-group">
                    <input
                      v-model.trim="couponData.expirationDate"
                      class="form-control"
                      id="expirationDate"
                      name="expirationDate"
                      type="text"
                      placeholder="DD/MM/YYYY"
                      @input="$v.couponData.expirationDate.$touch()"
                    />
                    <div v-if="$v.couponData.expirationDate.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.couponData.expirationDate.required"
                      >Please enter expiration date.</p>
                    </div>
                  </div>
                </div>
                <div class="col-sm-1 infoIcon">
                  <i class="fa fa-question-circle" aria-hidden="true"></i>
                </div>
              </div>

              <br />

              <div class="border-bottom pb-4">
                <div class="row">
                  <div class="col-sm-4">
                    <div class="sku-title">Categories*:</div>
                  </div>
                  <div class="col-sm-7">
                    <div class="form-group">
                      <select
                        class="form-control"
                        @change="getProduct"
                        v-model.trim="couponData.categories"
                      >
                        <option
                          v-for="category in allCategories"
                          :key="category.id"
                          :value="{ categoryId:category.id,categoryName:category.category_name}"
                        >{{category.category_name}}</option>
                      </select>
                    </div>
                  </div>
                  <div class="col-sm-1 infoIcon">
                    <i class="fa fa-question-circle" aria-hidden="true"></i>
                  </div>
                </div>
              </div>
              <div class="border-bottom pb-4">
                <div class="row">
                  <div class="col-sm-4">
                    <div class="sku-title">Products*:</div>
                  </div>
                  <div class="col-sm-7">
                    <div class="form-group">
                      <select multiple class="form-control" v-model.trim="couponData.product">
                        <option
                          v-for="productData in allProduct"
                          :key="productData.id"
                          :value="{ product_id:productData.id,product_name:productData.name,product_sku:productData.sku}"
                        >{{productData.name}}</option>
                      </select>
                    </div>
                  </div>
                  <div class="col-sm-1 infoIcon">
                    <i class="fa fa-question-circle" aria-hidden="true"></i>
                  </div>
                </div>
              </div>

              <br />
              <h3 class="border-bottom text-uppercase sub-title pb-4 mb-5">Usage Limits</h3>
              <div class="border-bottom pb-4">
                
                </div>
                <div class="row">
                  <div class="col-sm-4">
                    <div class="sku-title">Usage Limit Per USER*:</div>
                  </div>
                  <div class="col-sm-7">
                    <div class="form-group">
                      <select class="form-control" v-model.trim="couponData.usageLimitPerUser">
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                      </select>
                    </div>
                  </div>
                  <div class="col-sm-1 infoIcon">
                    <i class="fa fa-question-circle" aria-hidden="true"></i>
                  </div>
                </div>
              </div>
            </div>
        </div>
          
        <div class="col-sm-1"></div>
        <div class="col-sm-4">
          <div class="gray-form-box new-from-box">
            <div class="text-center">
              <div class="add-photo">
                <div class="save-btn-new">
                  <button
                  class="btn black-btn"
                 @click.prevent="SubmitSaveDraft"
                  :disabled="$v.$invalid"
                >
                  <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>SAVE DRAFT
                </button>
                </div>
                <div class="border-bottom space-border"></div>
                <button
                  class="send-btn color btn"
                  @click.prevent="SubmitCoupon"
                  :disabled="$v.$invalid"
                >
                  <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>PUBLISH
                </button>
               
              </div>
            </div>
          </div>

          <div class="gray-form-box new-from-box">
            <span class="featured-title">Coupon Image</span>
            <div class="box-pro-img">
              <img
                v-if="couponData.couponImg"
                :src="couponData.couponImg"
                class="img-responsive"
              />
            </div>
            <div class="delete">
              
              <a href="javascript:void(0)" @click="browserImage" class="text-uppercase">
                UPLOAD COUPON IMAGE
                <input
                  type="file"
                  @change="onFileChange"
                  accept="image/jpeg"
                  id="couponImage"
                  style="display:none"
                />
              </a>
              
            </div>
          </div>
        </div>
    </div>

        

        
      </div>
    </div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import evenBus from "../../../event-bus";
import { required, numeric, alpha,decimal, sameAs } from "vuelidate/lib/validators";

export default {
  data() {
    return {
      couponData: {
        couponCodeName: null,
        couponDescription: null,
        discountType: null,
        couponAmount: null,
        expirationDate: null,
        product: [],
        categories: null,
        usageLimitPerUser: null,
        couponImg:null,
        couponImageUpload:null,
        isUniqueCouponName:false
      },
      allCategories: [],
      allProduct: [],
      spinnerShow: false,
      successMessage: null,
    };
  },
  validations: {
    couponData: {
      couponCodeName: {
        required,
        isUnique(value) {
          if (value === '') return true
          var vueInstance = this;
          let currentTime = firebase.firestore.Timestamp.now().toMillis();
          return new Promise((resolve, reject) => {
           
          let getCouponCode = fb.couponCollection.where("coupon_code_name", "==", value).where("expiration_date",">=",currentTime).where("isPublic","==",true);
      //    setTimeout(function () { 
          getCouponCode.get().then(function(doc)  {
             if (!doc.empty) {
                vueInstance.couponData.isUniqueCouponName=true     
               reject(false)
             }else{ 
               resolve(true)
             }
          }).catch(function(error) {
           //reject(false) 
          });
       //   }, 4000);
          })
        }
      },
      discountType: {
        required,
      },
      couponAmount: {
        required,
        decimal
      },
      expirationDate: {
        required,
      },
      usageLimitPerUser: {
        required,
      },
      categories: {
        required,
      },
      product: {
        required,
      },
      couponImageUpload: {
        required,
      },
    },
  },
 
  created() {
    this.filterCategories();
  },
  mounted() {
    var vm = this;

    $("input[name=expirationDate]").datepicker({
      minDate: 0,
      onSelect: function (dateText) {
        vm.couponData.expirationDate = dateText;
      },
    });
  },
  methods: {
    browserImage() {
      $("#couponImage").trigger("click");
    },

    onFileChange(e) {
      const file = e.target.files[0];
      this.couponData.couponImageUpload = file;
      this.couponData.couponImg = URL.createObjectURL(file);
    }, 

    couponListBack() {
      evenBus.$emit("backToList");
    },
    async getProduct() {
      await this.$store.dispatch("getDespensaryProduct", {
        category_id: this.couponData.categories.categoryId,
        category_name: this.couponData.categories.categoryName,
      });
      this.allProduct = this.$store.state.allProduct;
    },

    async filterCategories() {
        $(".flash__wrapper").empty();
      await this.$store.dispatch("fetchCreatedCategories");
      this.allCategories = this.$store.state.allCategories;
    },

    async SubmitCoupon() {
      $('.btn').prop("disabled", true);
      this.spinnerShow = true
      if (!this.$v.$invalid) {
          let expirationDate= new Date(this.couponData.expirationDate)
        let couponImageUploadUrl = this.couponData.couponImageUpload;
        this.uploadImageAsPromise(couponImageUploadUrl).then((url) => {

          this.$store.dispatch("CouponyDataInsert", {
            coupon_code_name: this.couponData.couponCodeName,
            coupon_description: this.couponData.couponDescription,
            discount_type: this.couponData.discountType,
            percentage: parseFloat(this.couponData.couponAmount),
            expiration_date: firebase.firestore.Timestamp.fromDate(new Date(expirationDate.getFullYear(), expirationDate.getMonth(), expirationDate.getDate(),23,59,59,59)).toMillis(),
            image: url, 
            product: this.couponData.product,
            category_name: this.couponData.categories.categoryName,
            category_id: this.couponData.categories.categoryId,
            usage_limit_per_user: parseInt(this.couponData.usageLimitPerUser),
            isPublic: true,
          })
          .then((suucess) => {

            this.flashSuccess('Your coupon code successfully submitted.', {timeout: 4000,});
            this.spinnerShow = false;
            setTimeout(function () { 
              evenBus.$emit("backToList");
            }, 5000);
            $('.btn').prop("disabled", false);
          });
        });
      }
    },

    async uploadImageAsPromise(imageFile) {
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      let userId = fb.auth.currentUser.uid;
      return new Promise(function (resolve, reject) {
        var storageRef = firebase.storage().ref(`couponImages/${userId + currentTime}.jpg`);
        var task = storageRef.put(imageFile).then((data) => {
          data.ref.getDownloadURL().then((url) => {
            resolve(url);
          });
        });
      });
    }, 

    async SubmitSaveDraft() {
        $('.btn').prop("disabled", true);
      this.spinnerShow = true;
      if (!this.$v.$invalid) {
          let expirationDate= new Date(this.couponData.expirationDate)
        let couponImageUploadUrl = this.couponData.couponImageUpload;
        this.uploadImageAsPromise(couponImageUploadUrl).then((url) => {
          this.$store.dispatch("CouponyDataInsert", {
            coupon_code_name: this.couponData.couponCodeName,
            coupon_description: this.couponData.couponDescription,
            discount_type: this.couponData.discountType,
            percentage: parseFloat(this.couponData.couponAmount),
            expiration_date: firebase.firestore.Timestamp.fromDate(new Date(expirationDate.getFullYear(), expirationDate.getMonth(), expirationDate.getDate(),23,59,59,59)).toMillis(),
            image: url, 
            product: this.couponData.product,
            category_name: this.couponData.categories.categoryName,
            category_id: this.couponData.categories.categoryId,
            usage_limit_per_user: parseInt(this.couponData.usageLimitPerUser),
            isPublic: false,
          }).then((suucess) => {
              this.flashSuccess('Your coupon code successfully save as draft.', {timeout: 7000,});
            this.spinnerShow = false;
              setTimeout(function () { 
                evenBus.$emit("backToList");
              }, 8000);
              $('.btn').prop("disabled", false);
            });
        });  
      }
    },
  },
};
</script>

<style scoped>
.save-btn-new {
	background: #404140;
	padding: 4px 0;
	margin-top: 10px;
	display: block;
	color: #fff;
}
</style>
