import router from './router';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        API_URL_ROOT: 'https://work.splitreef.com/client/development/grab-gram/', 
        googleMapsApiKey : "AIzaSyDO6PDftrAIISl0H5qPPXNluhP7yu1vmwY",
        googleMapsUrl :'https://maps.googleapis.com/maps/api/geocode/json?key=AIzaSyDO6PDftrAIISl0H5qPPXNluhP7yu1vmwY',
        googleDistanceUrl :'https://maps.googleapis.com/maps/api/js?key=AIzaSyDO6PDftrAIISl0H5qPPXNluhP7yu1vmwY&libraries=place',
        fcmPublicVapidKey:'BLb9fDayHZ3cFApJ42KJikQHuHZHWhs7T0I5NEJg6xV_k_ME1kT9BzYmmbzXPuirhwlznQthA4l8cjllc3Lms6E',
        existUser: {},
        GetState: [],
        getSignUpData: {},
        getOderData:{},
        userProfile: {},
        allCategories: {},
        allProduct: {},
        couponList: {},
        couponNameData: {},
        dispensaryUser: {},
        relatedproductName: {},
        productNameData:{},
        customerData:{},
        dispensaryProfileImage: null,
        recommendationImage: null,
        govermentIdImage: null,
        deliveringImage: null,
        dispensaryReviewOpenModal:false,
        driverProfileImage: null,
        tempRegistrationData:{},
        driverLicenseImage: null,
        insuranceImage: null,
        registrationImage: null,
        caregiversImage: null,
        driverData:{},
        isCaregiverState:null,
        despensaryWithStateData:{},
        stateAbbr:{"Arizona":"AZ","Alabama":"AL","Alaska":"AK","Arkansas":"AR","California":"CA","Colorado":"CO","Connecticut":"CT","districtofcolumbia":"DC","Delaware":"DE","Florida":"FL","Georgia":"GA","Hawaii":"HI","Idaho":"ID","Illinois":"IL","Indiana":"IN","Iowa":"IA","Kansas":"KS","Kentucky":"KY","Louisiana":"LA","Maine":"ME","Maryland":"MD","Massachusetts":"MA","Michigan":"MI","Minnesota":"MN","Mississippi":"MS","Missouri":"MO","Montana":"MT","Nebraska":"NE","Nevada":"NV","Newhampshire":"NH","Newjersey":"NJ","Newmexico":"NM","Newyork":"NY","Northcarolina":"NC","Northdakota":"ND","Ohio":"OH","Oklahoma":"OK","Oregon":"OR","Pennsylvania":"PA","Rhodeisland":"RI","Southcarolina":"SC","Southdakota":"SD","Tennessee":"TN","Texas":"TX","Utah":"UT","Vermont":"VT","Virginia":"VA","Washington":"WA","Westvirginia":"WV","Wisconsin":"WI","Wyoming":"WY","Alberta":"AB","Britishcolumbia":"BC","Manitoba":"MB","Newbrunswick":"NB","Newfoundland":"NF","Northwestterritory":"NT","Novascotia":"NS","Nunavut":"NU","Ontario":"ON","Princeedwardisland":"PE","Quebec":"QC","Saskatchewan":"SK","Yukon":"YT"}
    },
    getters: {
        existUser: state => state.existUser,
        GetState: state => state.GetState,
    },
    mutations: {
        IS_EXITING(state, val) {
            state.existUser = val
        },
        STATE_DATA(state, stateAllData) {
            state.GetState = stateAllData;
        },
        SIGN_UP_DATA(state, SignUpData) {
            state.getSignUpData = SignUpData;
        },
        SET_USER_PROFILE(state, val) {
            state.userProfile = val
        },
        ORDER_DATA(state, allOrderData) {
            state.getOderData = allOrderData;
        },
        GET_CATEGORIES(state, allCategories) {
            state.allCategories = allCategories;
        },
        PRODCT_DATA(state, allProduct) {
            state.allProduct = allProduct;
        },
        COUPON_DATA(state, couponData) {
            state.couponList = couponData;
        },
        COUPON_NAME(state, couponName) {
            state.couponNameData = couponName;
        },
        DISPENSARY_USER(state, dispensaryUser) {
            state.dispensaryUser = dispensaryUser;
        },
        RELATEDPRODUCT_NAME(state, relatedProduct) {
            state.relatedproductName = relatedProduct;
        },
        PRODUCT_DATA(state, productName) {
            state.productNameData = productName;
        },
        CUSTOMER_DATA(state, customerData) {
            state.customerData = customerData;
        },
        TEMP_REGISTRATION(state, registraionId) {
            state.tempRegistrationData = registraionId;
        },
        DRIVER_DATA(state, driverData) {
            state.driverData = driverData;
        },
        DESPENSARRY_STATE_DATA(state, despensaryWithStateData) {
            state.despensaryWithStateData = despensaryWithStateData;
        },
    },


    actions: {

        async checkUserExit({ commit }, form) {
            let userExiting = false;
            await fb.usersCollection.where("email", "==", form.email).get().then((doc) => {
                if (!doc.empty) {
                    userExiting = true;
                }
            })
            commit('IS_EXITING', userExiting)
        },
        async getAllState({ commit }) {

            let stateData = await fb.stateCollection.get();
            let allStateData = stateData.docs.map(doc => doc.data())
            commit('STATE_DATA', allStateData)
        },

        async signUp({ commit }, form) {
            // sign user in
            await fb.auth.createUserWithEmailAndPassword(form.email, form.password)
                .then(data => {
                 
                    commit('SIGN_UP_DATA', data)
                }).catch(function (error) {
                    var errorCode = error.code;
                    var errorMessage = error.message;
                });
        },

         async UserDataInsert({ commit }, form) {
            // let userdata = false;
            await fb.usersCollection.doc(form.user_id).set(form);
            //commit('IS_EXITING', userExiting)
        },

        async DispensaryDataInsert({ commit }, form) {
          
            let currentTime = firebase.firestore.Timestamp.now().toMillis();
            let dataInsert = await fb.dispensaryCollection.doc();
            dataInsert.set(form).then(function () {
                dataInsert.update({
                    created_at: currentTime,
                    id: dataInsert.id,
                    updated_at: currentTime,
                    "address.created_at": currentTime,
                    "address.updated_at": currentTime,
                });
                return true;
            });
            return false;
           
        },

        async fetchUserProfile({ commit }) {
            // fetch user profile
            const userProfile = await fb.usersCollection.doc(fb.auth.currentUser.uid).get()
            commit('SET_USER_PROFILE', userProfile.data())
        },

        async logout({ commit }) {
            await fb.usersCollection.doc(fb.auth.currentUser.uid).update({
                device_id: "",
              });
            await fb.auth.signOut()
            // clear userProfile and redirect to /login
            commit('SET_USER_PROFILE', {})
            router.push('/login')
        },

        async orderData({ commit }, orderFilter) {
            let orderData = await fb.orderCollection.orderBy('created_at', 'desc').get();
            let allOrderData = orderData.docs.map(doc => doc.data())
            commit('ORDER_DATA', allOrderData)
        },
        async orderFilterData({ commit }, orderFilter) {
            let query = await fb.orderCollection.where("dispensary_data.user_id", "==", fb.auth.currentUser.uid);
            if (orderFilter.startDate !== null) {
                let startDateMilis = new Date(orderFilter.startDate);
                const createdAt = await firebase.firestore.Timestamp.fromDate(startDateMilis).toMillis();
                query = await query.where("created_at", ">=", createdAt)
            }
            if (orderFilter.endDate !== null) {
                let endDateMilis = new Date(orderFilter.endDate);
                const createdEndAt = await firebase.firestore.Timestamp.fromDate(endDateMilis).toMillis();
                query = await query.where("created_at", "<=", createdEndAt)
            }
            if (orderFilter.orderStatus !== null) query = await query.where("status", "==", orderFilter.orderStatus)
            if (orderFilter.orderId !== null) query = await query.where("order_no", "==", orderFilter.orderId)
            if (orderFilter.customerEmail !== null) query = await query.where("customer_email", "==", orderFilter.customerEmail)
            let orderData = await query.get();
            let allOrderData = orderData.docs.map(doc => doc.data())
            commit('ORDER_DATA', allOrderData)
        },
         async fetchCreatedCategories({ commit }) {
            const productCategory = await fb.productCategoriesCollection.get()
            let productCategories = productCategory.docs.map(doc => doc.data())
            // set user profile in state
            await commit('GET_CATEGORIES', productCategories)
        },
        async CouponyDataInsert({ commit }, form) {
            let currentTime = firebase.firestore.Timestamp.now().toMillis();
            let dataInsert = await fb.couponCollection.doc();
            dataInsert.set(form).then(function () {
                dataInsert.update({
                    id: dataInsert.id,
                    dispensary_user_id: fb.auth.currentUser.uid,
                    created_at: currentTime,
                    updated_at: currentTime,
                });
                return true;
            });
        },
        async getDespensaryProduct({ commit }, form) {
            let prodcutData = await fb.productCollection.where("dispensary_id", "==", fb.auth.currentUser.uid).where("categories", "array-contains", {category_id:form.category_id,category_name:form.category_name}).get();
            let allProductData = prodcutData.docs.map(doc => doc.data())
            commit('PRODCT_DATA', allProductData)
        },
        async allCouponName({ commit }) {
            let couponName = await fb.couponCollection.where("dispensary_user_id", "==", fb.auth.currentUser.uid).orderBy('created_at', 'desc').get();
            let couponNameData = couponName.docs.map(doc => doc.data())
            commit('COUPON_NAME', couponNameData)
        },

        async couponFilterData({ commit }, couponFilter) { 
            let query = await fb.couponCollection.where("dispensary_user_id", "==", fb.auth.currentUser.uid)
            if (couponFilter.startDate !== null) {
                let date = new Date();
                const createdAt = date.setDate(date.getDate() - couponFilter.startDate);
                 query = await query.where("created_at", ">=", createdAt)
            }
            if (couponFilter.discountType !== null) query = await query.where("discount_type", "==", couponFilter.discountType)
            if (couponFilter.couponByName !== null) query = await query.where("coupon_code_name", "==", couponFilter.couponByName)

            let couponData = await query.orderBy('created_at', 'desc').get();
            let allCouponData = couponData.docs.map(doc => doc.data())
            commit('COUPON_DATA', allCouponData)
        },

        async couponBulkAction({ dispatch }, couponAction) {  
            if (couponAction.action == 'Delete') {
                await Promise.all(couponAction.actionValue.map(function(value, key) {
                    fb.couponCollection.doc(value).delete ();
                }));
            }
        },
        async getDispensaryUser({ commit }) {
           
            let userData = {};
            await fb.dispensaryCollection.where("user_id", "==", fb.auth.currentUser.uid).get().then(querySnapshot => {
                querySnapshot.forEach(function (doc) {
                    userData['allData'] = doc.data(),
                    userData['email'] = fb.auth.currentUser.email,
                    commit('DISPENSARY_USER', userData)
                });
            }).catch(function (error) {
                console.log("Error getting document:", error);
            });

        },
        async allrealatedProduct({ commit }) { 
            let productName = await fb.productCollection.where("dispensary_id", "==", fb.auth.currentUser.uid).get();
            let productNameData = productName.docs.map(doc => doc.data())
            commit('RELATEDPRODUCT_NAME', productNameData)
        },
        async allProcutName({ commit }) { 
            let product = await fb.productCollection.where("dispensary_id", "==", fb.auth.currentUser.uid).where("isDeleted", "==", false).orderBy('created_at', 'desc').get();
            let productNameData = product.docs.map(doc => doc.data())
            commit('PRODUCT_DATA', productNameData)
        },

        async productFilterData({ commit }, productFilter) {
            let query = await fb.productCollection.where("dispensary_id", "==", fb.auth.currentUser.uid).where("isDeleted", "==", false)
            if (productFilter.startDate !== null) { 
                let date = new Date();
                const createdAt = date.setDate(date.getDate() - productFilter.startDate);
                query = await query.where("created_at", ">=", createdAt)
            }
            if (productFilter.sku !== null) query = await query.where("sku", "==", productFilter.sku)
            if (productFilter.productByName !== null) query = await query.where("name", "==", productFilter.productByName)

            let product = await query.orderBy('created_at', 'desc').get();
            let allProductData = product.docs.map(doc => doc.data())
            commit('PRODUCT_DATA', allProductData) 
        },

        async poductBulkAction({ dispatch }, productAction) {
            if (productAction.action == 'Delete') {
                await Promise.all(productAction.actionValue.map(function (value, key) {
                    var updateRef = fb.productCollection.doc(value);
                    return updateRef.update({
                        isDeleted: true
                    })
                })); 
            }
            if (productAction.action == 'needToPublish') {
                var updateRef = fb.productCollection.doc(productAction.actionValue);
                return updateRef.update({
                    isPublic: true
                })
            }
        },
        
        async ShippingDataInsert({ commit }, form) { 

            let dataInsert = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
            dataInsert.doc('data_address').set({addresses:firebase.firestore.FieldValue.arrayUnion(form)},{merge:true}).then(function () {
                return true;
            });
            return false;
        },
        
        async getCustomerUser({ commit }) {
            const userData = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_account').get()
            commit('CUSTOMER_DATA', userData.data())
        },
        
        async checkTempUserExit({ commit }, form) {
            let userTempExiting = false;
            await fb.tempRegistrationCollection.doc(form.id).get().then((doc) => {
                if (doc.exists) {
                    userTempExiting = doc.data();
                } else {
                    userTempExiting = false;
                }
            })
            commit('TEMP_REGISTRATION', userTempExiting)
        },

        async getDriverUser({ commit }) {
            const userData = await fb.driverCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_account').get()
            commit('DRIVER_DATA', userData.data())
        },
        
         async getDespensaryWithState({ commit },form) {
            let despensaryWithState = await fb.dispensaryCollection.where("address.state", "==", form.stateName).get();
            let despensaryWithStateData = despensaryWithState.docs.map(doc => doc.data())
            commit('DESPENSARRY_STATE_DATA', despensaryWithStateData)
        },
        
    }
})