<template>
  <div>
    <div class="row">
      <div class="col-lg-10 col-lg-offset-1">
        <div class="form-menu contact-form">
          <div class="back-menu">
            <i class="fa fa-long-arrow-left" aria-hidden="true"></i>
            <a href="#" @click.prevent="orderListBack">Back to ORDERS</a>
          </div>
        </div>
        <div class="row" v-if="allData.order_no">
          <div class="col-sm-8">
            <div class="row">
              <div class="col-sm-8">
                <div class="title">
                  <h2>ORDER #{{ this.allData.order_no}}</h2>
                </div>
              </div>
            </div>
            <div class="order-number margin-top">
              Order placed on:
              <span>{{ this.allData.created_at | longDate}}</span>
            </div>
            <div class="order-number">
              Order status:
              <span>{{ this.allData.status.replace(/_/g, " ")}}</span>
            </div>
            <div class="order-table">
              <div class="signature-section">ORDER DETAILS</div>

              <div class="table-responsive">
                <table class="table">
                  <thead>
                    <tr>
                      <th style="width: 50%">PRODUCT</th>
                      <th style="width: 50%" class="text-right">TOTAL</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(iteam, index) in this.allData.items" :key="index">
                      <td>{{ iteam.name}}</td>
                      <td class="text-right">${{ iteam.total_count | currency}}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <div class="left-space">
              <div class="order-table">
                <div class="table-responsive">
                  <table class="table">
                    <thead>
                      <tr>
                        <th style="width: 50%">SUBTOTAL</th>
                        <th
                          style="width: 50%"
                          class="text-right"
                        >${{ this.allData.sub_total | currency}}</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>DELIVERY FEE</td>
                        <td class="text-right">${{ this.allData.delivery_fee | currency}}</td>
                      </tr>

                      <tr>
                        <td>SALES TAX</td>
                        <td class="text-right">${{ this.allData.sales_tax | currency}}</td>
                      </tr>

                      <tr>
                        <td class="total-text">TOTAL</td>
                        <td class="total-text text-right">${{ this.allData.grand_total | currency}}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
     
            <div class="acc-top-space border-bottom"></div>
            <div class="delivery-info">
              <div class="signature-section">DELIVERY INFORMATION</div>
              <div class="row">
                <div class="col-sm-4">
                  <div class="type-sub">Driver Name:</div>
                </div>
                <div class="col-sm-8">
                  <div class="time-title">{{this.driverName | capitalize }}</div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-4">
                  <div class="type-sub">Employeed ID#:</div>
                </div>
                <div class="col-sm-8">
                  <div class="time-title">{{ this.allData.assign_driver_id}}</div>
                </div>
              </div>
            </div>
            <div class="acc-top-space border-bottom"></div>
            <div class="delivery-info">
              <div class="signature-section">CUSTOMER DETAILS</div>
              <div class="row">
                <div class="col-sm-4">
                  <div class="type-sub">Name:</div>
                </div>
                <div class="col-sm-8">
                  <div class="time-title">{{ this.allData.customer_name}}</div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-4">
                  <div class="type-sub">Email:</div>
                </div>
                <div class="col-sm-8">
                  <div class="time-title">
                    <a
                      v-bind:href="'mailto:' + this.allData.customer_email"
                    >{{ this.allData.customer_email }}</a>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-4">
                  <div class="type-sub">Phone:</div>
                </div>
                <div class="col-sm-8">
                  <div class="time-title">{{ customerPhoneNo | phoneNoFormate }}</div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-4">
                  <div class="type-sub">Patient ID #:</div>
                </div>
                <div class="col-sm-8">
                  <div class="time-title" v-if="showPatientId">{{ this.allData.patient_id }}</div>
                  <div class="time-title" v-else>XXXXXXX</div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-4">
                  <div class="type-sub-btn">
                    <a
                      href="#"
                      v-if="showPatientId"
                      @click.prevent="showPatientIdNo(false)"
                    >HIDE PATIENT ID</a>
                    <a href="#" v-else @click.prevent="showPatientIdNo(true)">VIEW PATIENT ID</a>
                  </div>
                </div>
                <div class="col-sm-8">
                  <div class="type-sub-btn type-sub-btn-new">
                    <a :href="recommendationLetter">VIEW DOCTORS RECOMMENDATION</a>
                  </div>
                </div>
              </div>
            </div>
            <div class="acc-top-space border-bottom"></div>
            <div class="delivery-info">
              <div class="row">
                <div
                  class="col-sm-6"
                  v-for="(address, index ) in this.allData.addresses"
                  :key="index"
                >
                  <div class="proof-copy-add">
                    <div v-if="address.type==billingAddressType" class="signature-section">{{ address.type.toUpperCase() }}</div>
                    <div v-else class="signature-section">SHIPPING ADDRESS</div>
                    
                    <p>
                      {{ allData.customer_name}}
                      <br />
                      {{ address.street_1}}
                      <br v-if="address.street_2"/>
                      {{ address.street_2}}
                      <br />
                      {{ address.city}}, {{ address.state}} {{ address.zip_code}}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-sm-1"></div>
          <div class="col-sm-3">
            <div class="order-box-section">
              <h3>ORDER DOCUMENTS</h3>

              <div class="type-sub-btn">
                <a href="#" @click.prevent="invoice">VIEW INVOICE</a>
              </div>

              <div class="report-btn">
                <a href="#" @click.prevent="downloadReport">DOWNLOAD ORDER REPORT</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <orderInovice :id="this.dataId.id"/>
  </div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import evenBus from "../../../event-bus";
import orderInovice from "./OrderInvoiceComponent";
import addressType from "../../../address-type";
export default {
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
        billingAddressType: addressType.billingAddressType,
      error: null,
      spinnerShow: false,
      dataId: {
        id: this.id,
      },
      allData: {},
      driverName: null,
      showPatientId: false,
      recommendationLetter: null,
      customerPhoneNo: null
    };
  },
  mounted() {
    const plugin = document.createElement("script");
    plugin.setAttribute(
      "src",
      "//cdnjs.cloudflare.com/ajax/libs/jspdf/1.0.272/jspdf.debug.js"
    );
    plugin.async = true;
    document.head.appendChild(plugin);
  },
  components: {
    orderInovice,
  },
  created() {
    this.getOrderDetail(this.dataId.id);
  },
  methods: {
    async getOrderDetail(id) {
      let dataValues = null;
      //let getDriverName = null;
      await fb.orderCollection
        .doc(id)
        .get()
        .then(function (doc) {
          if (doc.exists) {
            dataValues = doc.data(0);
          } else {
            console.log("No such document!");
          }
        })
        .catch(function (error) {
          console.log("Error getting document:", error);
        });
      this.allData = dataValues;
      
      var customerAddress = dataValues.addresses;
      var customerAddressIndex = customerAddress.findIndex(item => item.type != this.billingAddressType);
        this.customerPhoneNo = customerAddress[customerAddressIndex].phone_number
        
      if (dataValues.assign_driver_id != "") {
        let getDriverName = null;
        await fb.driverCollection
          .doc(dataValues.assign_driver_id).collection('response').doc('data_account')
          .get()
          .then(doc => {
            getDriverName = doc.data(0).account.first_name + ' ' + doc.data(0).account.last_name;
          });
        this.driverName = getDriverName;
      }
      await fb.customerCollection.doc(this.allData.user_id).collection('response').doc('data_account').get().then(doc => { 
            this.recommendationLetter = doc.data(0).document_images.doctor_recommendation_letter;
        });
        
        
    },
    
    showPatientIdNo(condition) {
      this.showPatientId = condition;
    },

    invoice() {
      evenBus.$emit("getInvoice");
    },
    downloadReport() {
      evenBus.$emit("downloadReport");
    },
    orderListBack() {
      evenBus.$emit("backToList");
    },
  },
};
</script>
