<template>
  <div class="row">
    <div class="col-lg-10 col-lg-offset-1">
      <div class="tab-title">
        <h2><span></span> Account Settings</h2>
      </div>
      <div class="row">
        <div class="col-sm-6">
          <form @submit.prevent="UpdateAdminData">
            <div class="account-form">
              <div class="acc-top-space">
                <div class="form-group">
                  <label>First Name</label>
                  <input
                    class="form-control"
                    v-model.trim="adminFirstName"
                    @input="$v.adminFirstName.$touch()"
                    type="text"
                  />
                  <div v-if="$v.adminFirstName.$dirty">
                    <p class="text-danger" v-if="!$v.adminFirstName.required">
                      Please enter your first name.
                    </p>
                  </div>
                </div>
                <div class="form-group">
                  <label>Last Name</label>
                  <input
                    class="form-control"
                    v-model.trim="adminLastName"
                    @input="$v.adminLastName.$touch()"
                    type="text"
                  />
                  <div v-if="$v.adminLastName.$dirty">
                    <p class="text-danger" v-if="!$v.adminLastName.required">
                      Please enter your last name.
                    </p>
                  </div>
                </div>
              </div>
              <div class="border-bottom"></div>

              <div class="acc-top-space">
                <div class="form-group">
                  <label>Mobile Phone Number</label>
                  <input
                    class="form-control"
                    type="text"
                    placeholder="XXX-XXX-XXXX"
                    @keyup="acceptNumber"
                    v-model.trim="adminMobileNumber"
                    @input="$v.adminMobileNumber.$touch()"
                  />
                  <div v-if="$v.adminMobileNumber.$dirty">
                    <p class="text-danger" v-if="!$v.adminMobileNumber.required">
                      Please enter your valid phone no..
                    </p>
                  </div>
                </div>

                <div class="form-group">
                  <label>Email Address</label>
                  <input
                    class="form-control"
                    readonly
                    type="text"
                    :value="adminEmailId"
                  />
                </div>
              </div>
              <div class="border-bottom"></div>

              <div class="acc-top-space">
                <div class="input-group">
                  <label>Current Password</label>
                  <input
                    :type="currentPassword"
                    v-model.trim="adminPassword"
                    class="form-control"
                    placeholder="Enter your Password"
                  />
                  <span class="input-group-btn show-pass">
                    <button
                      @click="showCurrentPass"
                      class="btn btn-default"
                      type="button"
                    >
                      {{ btnText }}
                    </button>
                  </span>
                </div>
                <div class="input-group top-spce">
                  <label>New Password</label>
                  <input
                    :type="newPassword"
                    v-model.trim="adminNewPassword"
                    class="form-control"
                    placeholder="Enter new password"
                  />
                  <span class="input-group-btn show-pass">
                    <button @click="showNewPass" class="btn btn-default" type="button">
                      {{ btnTextNew }}
                    </button>
                  </span>
                </div>
                <div v-if="error" v-html="error">{{ error }}</div>
              </div>
              <div class="border-bottom"></div>
              <flash-message></flash-message>
              <div class="acc-top-space">
                <div class="text-left">
                  <button class="btn send-btn save-btn" :disabled="$v.$invalid">
                    <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>SAVE
                    INFORMATION
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
        <div class="col-sm-6"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { required, numeric, alpha, sameAs } from "vuelidate/lib/validators";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
export default {
  data() {
    return {
      spinnerShow: false,
      adminFirstName: null,
      adminLastName: null,
      adminMobileNumber: null,
      adminEmailId: null,
      adminPassword: null,
      adminNewPassword: null,
      error: null,
      currentPassword: "password",
      newPassword: "password",
      btnText: "Show Password",
      btnTextNew: "Show Password",
    };
  },

  validations: {
    adminFirstName: {
      required,
    },
    adminLastName: {
      required,
    },
    adminMobileNumber: {
      required,
    },
  },
  created() {
    this.adminUser();
  },
  methods: {
    async UpdateAllData() {
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      var phoneNumber = this.adminMobileNumber.replace(/[^\d]/g, "");
      var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);

      let dataUpdate = await fb.adminCollection.doc(fb.auth.currentUser.uid);
        dataUpdate
          .update({
            updated_at: currentTime,
            first_name: this.adminFirstName,
            last_name: this.adminLastName,
            phone_number: phoneNo,
          })
          .then((success) => {
            this.flashSuccess("You have successfully update your profile.", {
              timeout: 10000,
            });
            this.spinnerShow = false;
            $(".btn").prop("disabled", false);
            this.adminPassword= null;
            this.adminNewPassword= null;
          })
          .catch((err) => {
            console.log(err);
            this.flashError("There is some problem. Please try again later1.", {
              timeout: 10000,
            });
            this.spinnerShow = false;
            $(".btn").prop("disabled", false);
          });
    
    },

    async UpdateAdminData() {
      $(".flash__wrapper").empty();
      this.error = null;
      this.spinnerShow = true;
      $(".btn").prop("disabled", true);
      if (!this.$v.$invalid) {
        if (this.adminPassword !== "" && this.adminPassword !== null) {
          if (this.adminNewPassword !== "" && this.adminNewPassword !== null) {
            if (this.adminNewPassword.length > 5) {
              var vueInstance = this;
              var user = firebase.auth().currentUser;
              let newPassword = vueInstance.adminNewPassword;
              let currentPassword = vueInstance.adminPassword;
              var credential = await firebase.auth.EmailAuthProvider.credential(
                user.email,
                currentPassword
              );
              await user
                .reauthenticateAndRetrieveDataWithCredential(credential)
                .then(function () {
                  user
                    .updatePassword(newPassword)
                    .then(function () {
                      // Update successful.
                      fb.usersCollection.doc(fb.auth.currentUser.uid).update({
                        password: newPassword,
                      });
                      vueInstance.UpdateAllData();
                    })
                    .catch(function (error) {
                      vueInstance.flashError(
                        "There is some problem. Please try again later.",
                        {
                          timeout: 10000,
                        }
                      );
                      vueInstance.spinnerShow = false;
                      $(".btn").prop("disabled", false);
                      return false;
                    });
                })
                .catch(function (error) {
                  vueInstance.flashError(
                    "Your current password does not match with our record.",
                    {
                      timeout: 15000,
                    }
                  );
                  vueInstance.spinnerShow = false;
                  $(".btn").prop("disabled", false);
                  return false;
                });
            } else {
              this.error =
                "<p class='alert alert-danger'>Please enter new password minimum 6 lenght.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
              this.spinnerShow = false;
              $(".btn").prop("disabled", false);
              return false;
            }
          } else {
            this.error =
              "<p class='alert alert-danger'>Please enter your new password.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
            this.spinnerShow = false;
            $(".btn").prop("disabled", false);
            return false;
          }
        } else {
          this.UpdateAllData();
          $(".btn").prop("disabled", false);
        }
      }
    },
    showCurrentPass() {
      if (this.currentPassword === "password") {
        this.currentPassword = "text";
        this.btnText = "Hide Password!";
      } else {
        this.currentPassword = "password";
        this.btnText = "SHOW Password!";
      }
    },
    showNewPass() {
      if (this.newPassword === "password") {
        this.newPassword = "text";
        this.btnTextNew = "Hide Password!";
      } else {
        this.newPassword = "password";
        this.btnTextNew = "SHOW Password!";
      }
    },
    acceptNumber() {
      var x = this.adminMobileNumber
        .replace(/\D/g, "")
        .match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
      this.adminMobileNumber = !x[2]
        ? x[1]
        : "(" + x[1] + ") " + x[2] + (x[3] ? "-" + x[3] : "");
    },
    async adminUser() {
      var vueInstance = this;

      await fb.adminCollection
        .doc(fb.auth.currentUser.uid)
        .get()
        .then((querySnapshot) => {
          var adminUserData = querySnapshot.data();
          vueInstance.adminFirstName = adminUserData.first_name;
          vueInstance.adminLastName = adminUserData.last_name;
          var cleaned = ("" + adminUserData.phone_number).replace(/\D/g, "");
          var match = cleaned.match(/^(\d{3})(\d{3})(\d{4})$/);
          if (match) {
            vueInstance.adminMobileNumber =
              "(" + match[1] + ") " + match[2] + "-" + match[3];
          }
          vueInstance.adminEmailId = fb.auth.currentUser.email;
        });
    },
  },
};
</script>
