<template>
  <div>
    <div class="row">
      <div class="col-lg-10 col-lg-offset-1">
        <div class="row contact-form">
          <div v-if="error" v-html="error">{{error}}</div>
          <div class="col-sm-3">
            <div class="start-date">
              <label>Start Date</label>
              <div class="input-group">
                <input
                  class="form-control calender-date"
                  v-model.trim="search.from"
                  id="from"
                  name="from"
                  aria-describedby="basic-addon2"
                  placeholder="Please enter start date"
                  type="text"
                  @input="search.from"
                />
                <span class="input-group-addon cuorser" id="basic-addon2">
                  <i class="fa fa-calendar" aria-hidden="true"></i>
                </span>
              </div>
            </div>
          </div>
          <div class="col-sm-3">
            <div class="start-date">
              <label>End Date</label>
              <div class="input-group">
                <input
                  class="form-control calender-date"
                  v-model.trim="search.to"
                  id="to"
                  name="to"
                  aria-describedby="basic-addon2"
                  placeholder="Please enter end date"
                  type="text"
                />
                <span class="input-group-addon cuorser" id="basic-addon2">
                  <i class="fa fa-calendar" aria-hidden="true"></i>
                </span>
              </div>
            </div>
          </div>
          <div class="col-sm-3">
            <div class="start-date">
              <label>Order Status</label>
              <select class="form-control" v-model.trim="search.OrderStatus">
                <option value="ACCEPT">In-progress</option>
                <option value="COMPLATE">Delivered</option>
                <option value="CANCELLED">Cancelled</option>
              </select>
            </div>
          </div>
          <div class="col-sm-3"></div>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-lg-10 col-lg-offset-1">
        <div class="row contact-form">
          <div class="col-sm-3">
            <div class="start-date">
              <label>Order ID</label>
              <div class="form-group">
                <input
                  class="form-control"
                  v-model.trim="search.orderId"
                  placeholder="XXXXXXXXX"
                  type="text"
                />
              </div>
            </div>
          </div>
          <div class="col-sm-3">
            <div class="start-date">
              <label>Customer Email Address</label>
              <div class="form-group">
                <input
                  class="form-control"
                  v-model.trim="search.emailId"
                  placeholder="XXXXXXXXX"
                  type="text"
                />
              </div>
            </div>
          </div>
          <div class="col-sm-3">
            <div class="start-date">
              <button
                class="btn search-btn order-search-btn"
                @click.prevent="searchData"
              >SEARCH ORDERS</button>
            </div>
          </div>
          <div class="col-sm-2">
            <div class="reset-btn">
              <a href="#" @click.prevent="resetSearchForm">
                <i class="fa fa-refresh" aria-hidden="true"></i>
                reset
              </a>
            </div>
          </div>
          <div class="col-sm-1"></div>
        </div>
      </div>
    </div>
    <div class="page top-spce">
        <nav aria-label="pagenavigation" class="text-right">
          <ul class="pagination">
            <li>
              <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li>
              <a
                href="#"
                v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
                :key="index"
                @click.prevent="page = pageNumber"
              >{{pageNumber}}</a>
            </li>

            <li>
              <a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
    <div class="row table-space-top">
      <div class="col-lg-11 col-lg-offset-1">
        <div class="row">
          <div class="col-sm-11">
            <div class="details-table">
              <div class="table-responsive">
                <table class="table">
                  <thead>
                    <tr class="table-title">
                      <th>ORDER #</th>
                      <th>DATE</th>
                      <th>STATUS</th>
                      <th>TOTAL</th>
                      <th>DETAILS</th>
                      <th>NOTIFIABLE</th>
                      <th>ASSIGN TO DRIVER</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(order, index) in displayedOrderList" :key="index">
                      <td>{{ order.order_no}}</td>
                      <td>{{ order.created_at | timeFormate}}</td>
                      <td>{{ order.status}}</td>
                      <td>${{ order.grand_total | currency}}</td>
                      <td>
                        <a href="#" class="table-view-btn" @click.prevent="orderDetails(order.id)">VIEW ORDER</a>
                      </td>
                      <td v-if="order.isSendNotification == false">NO</td>
                      <td v-if="order.isSendNotification == true">YES</td>
                      <td>
                          <a href="#" v-if="order.status == scheduleOrderStatus" class="table-view-btn" @click.prevent="showDriverList(order)">Near Driver</a>
                        <span v-else>This order already <b>{{ order.status}}</b> status.</span>
                      </td>
                    </tr>
                    <tr v-show="emptyDataVal">
                      <td colspan="8"><h4>You don't have any order.</h4></td>
                    </tr> 
                  </tbody>
                    
                  
                </table>
              </div>
            </div>
          </div>
          <div class="col-sm-4"></div>
        </div>
      </div>
    </div>
    <div class="page top-spce">
        <nav aria-label="pagenavigation" class="text-right">
          <ul class="pagination">
            <li>
              <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li>
              <a
                href="#"
                v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
                :key="index"
                @click.prevent="page = pageNumber"
              >{{pageNumber}}</a>
            </li>

            <li>
              <a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
  </div>
</template>

<script>
import evenBus from '../../../event-bus'
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import orderStatus from "../../../order-status";
export default {
  data() {
    return {
      page: 1,
      perPage: 10,
      pages: [],
      getOderData: [],
      search: {
        from: null,
        to: null,
        OrderStatus: null,
        orderId: null,
        emailId: null,
      },
      scheduleOrderStatus:orderStatus.schedule,
      error: null,
      spinnerShow: false,
      emptyDataVal: false
    };
  },
  mounted() {
    var vm = this;
    $("input[name=from]").datepicker({ 
       minDate: new Date('2020/9/01'),
      onSelect: (dateForm) => {
        vm.search.from = dateForm;
      },
    });
    $("input[name=to]").datepicker({
       minDate: new Date('2020/9/01'),
      onSelect: (dateTo) => {
        vm.search.to = dateTo;
      },
    });
  },
  computed: {
    displayedOrderList() {
      return this.paginate(this.getOderData);
    },

  },
  watch: {
    getOderData() {
      this.page = 1;
      this.perPage= 10;
      this.pages= [];
      this.setPages();
    },
  },
  created() {
    this.orderData();
  },
  methods: {
    setPages() {
      let numberOfPages = Math.ceil(this.getOderData.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },
     async orderData() {
       var vueInstance = this;
      let orderData = await fb.orderCollection
        .where("dispensary_data.user_id", "==", fb.auth.currentUser.uid).where("payment_success", "==", true)
        .orderBy("created_at", "desc");
       
     //   let orderData = await fb.orderCollection.orderBy("created_at", "desc");
      
      orderData.onSnapshot(function (querySnapshot) {
        var allOrderData = [];
        vueInstance.emptyDataVal=  false;

        querySnapshot.forEach((doc) => {
          allOrderData.push(doc.data());
        });
        if(allOrderData.length <= 0){ 
          vueInstance.emptyDataVal=  true;
          return false;
        }
        vueInstance.getOderData = allOrderData;
      });
     },

    resetSearchForm() {
      this.search.from = null;
      this.search.to = null;
      this.search.OrderStatus = null;
      this.search.orderId = null;
      this.search.emailId = null;
    },
    async searchData() {
      var vueInstance = this;

      this.error = null;
      if (
        this.search.OrderStatus == null &&
        this.search.from == null &&
        this.search.to == null &&
        this.search.orderId == null &&
        this.search.emailId == null
      ) {
        this.error =
          "<p class='alert alert-danger'>Please enter any field.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
        this.spinnerShow = false;
        return false;
      }
     
      await this.$store.dispatch("orderFilterData", {
        startDate: this.search.from,
        endDate: this.search.to,
        orderStatus: this.search.OrderStatus,
        orderId: this.search.orderId,
        customerEmail: this.search.emailId,
      });
      vueInstance.getOderData = await this.$store.state.getOderData;
    },

    orderDetails(id){
      evenBus.$emit('orderDetails', id);
    },
    showDriverList(orderDetail){
      evenBus.$emit('driverLists',orderDetail);
    }
  },
};
</script>
