<script>
import addressType from '../../../address-type';
import * as fb from "../../../firebaseConfig";

export default {
  props:['driverId'],  
  data() {
    return {
      personalAddressType: addressType.personalAddressType,
      primaryAddressType: addressType.primaryAddressType,
      secondaryAddressType: addressType.secondaryAddressType,
      billingAddressType: addressType.billingAddressType,
      driverData:{},
      driverUserData:{},
      referencesData: [],
      showDiv: false
    };
  },
  
 
  created() {
    this.driverUser();
  },
  methods: {

    async driverUser() { 
      var vueInstance = this;
   
      let getData = await fb.driverCollection.doc(vueInstance.driverId).collection('response');
        getData.doc('data_account').get().then(dataDoc => {
          if(!dataDoc.empty) {
            vueInstance.driverData = dataDoc.data();  
            fb.usersCollection.doc(vueInstance.driverId).get().then(driverUserDoc => {
              if(!dataDoc.empty) {
                vueInstance.driverUserData =  driverUserDoc.data();  
                //vueInstance.showDiv = true
              }
             });
            
            getData.doc('references').get().then(referencesDoc => {
              if(!dataDoc.empty) {
                vueInstance.referencesData =  referencesDoc.data().references;  
                vueInstance.showDiv = true
                }
             });
          }
        });
    },
    
  },
};
</script>

<style scoped>
.address-space {
	margin-top: 50px;
}
</style>