<template>
  <div class="col-sm-5">
      <div class="contact-title border-bottom">
        <h3>Personal Address</h3>
      </div>

      <div class="contact-form margin-top">
        <form @submit.prevent="UpdatePersonalData">
          <div class="form-group">
            <input v-model.trim="InfoFormData.FirstName" @input="$v.InfoFormData.FirstName.$touch()" class="form-control" placeholder="First Name" type="text" />
            <div v-if="$v.InfoFormData.FirstName.$dirty">
                <p class="text-danger" v-if="!$v.InfoFormData.FirstName.required">Please enter your first name.</p>
            </div>
          </div>
          <div class="form-group">
            <input  v-model.trim="InfoFormData.LastName" @input="$v.InfoFormData.LastName.$touch()"  class="form-control" placeholder="Last Name	" type="text" />
            <div v-if="$v.InfoFormData.LastName.$dirty">
                <p class="text-danger" v-if="!$v.InfoFormData.LastName.required">Please enter your last name.</p>
            </div>
          </div>
          <div class="form-group">
            <input  v-model.trim="InfoFormData.StreetAddress1" @input="$v.InfoFormData.StreetAddress1.$touch()"
              class="form-control"
              placeholder="Street Address"
              type="text"
            />
            <div v-if="$v.InfoFormData.StreetAddress1.$dirty">
              <p class="text-danger" v-if="!$v.InfoFormData.StreetAddress1.required">Please enter your street address.</p>
            </div>
          </div>
          <div class="form-group">
            <input  v-model.trim="InfoFormData.StreetAddress2"
              class="form-control"
              placeholder="Street Address 2"
              type="text"
            />
          </div>
          <div class="form-group">
            <input  v-model.trim="InfoFormData.city" @input="$v.InfoFormData.city.$touch()" class="form-control" placeholder="City" type="text" />
            <div v-if="$v.InfoFormData.city.$dirty">
              <p class="text-danger" v-if="!$v.InfoFormData.city.required">Please enter your city.</p>
              <p class="text-danger" v-if="!$v.InfoFormData.city.alpha">Please enter your valid city name.</p>
            </div>
          </div>
          <div class="form-group">
            <div class="row">
              <div class="col-sm-8">
                <select class="form-control"  v-model.trim="InfoFormData.state">
                  <option value="">Select State *</option>
                  <option v-for="(state, index) in getStateData" :key="index" :value="state">{{ state }}</option>
                </select>
                <div v-if="$v.InfoFormData.state.$dirty">
              <p class="text-danger" v-if="!$v.InfoFormData.state.required">Please select issuing state.</p>
            </div>
              </div>

              <div class="col-sm-4">
                <input  v-model.trim="InfoFormData.ZipCode" @input="$v.InfoFormData.ZipCode.$touch()"
                  class="form-control"
                  placeholder="Zip Code"
                  type="text"
                />
                <div v-if="$v.InfoFormData.ZipCode.$dirty">
                    <p class="text-danger" v-if="!$v.InfoFormData.ZipCode.required">Please enter zip code.</p>
                    <p class="text-danger" v-if="!$v.InfoFormData.ZipCode.numeric">Please enter valid zip code.</p>
                </div>
              </div>
            </div>
          </div>
          <div class="form-group">
            <input  @keyup="acceptNumber" v-model.trim="InfoFormData.phoneNo" @input="$v.InfoFormData.phoneNo.$touch()" 
              class="form-control"
              placeholder="Mobile Number"
              type="text"
            />
            <div v-if="$v.InfoFormData.phoneNo.$dirty">
                <p class="text-danger" v-if="!$v.InfoFormData.phoneNo.required">Please enter your valid phone no..</p>

            </div>
          </div>
          <div class="checkbox">
            <label>
              <input type="checkbox"  v-model.trim="InfoFormData.textMessage"/> Send me text message notifications
            </label>
          </div>
          <div class="input-group">
            <input  v-model.trim="InfoFormData.dob" @input="$v.InfoFormData.dob.$touch()  | timeFormate"
              class="form-control calender-date"
              aria-describedby="basic-addon2"
              placeholder="Date of Birth"
              type="text"
              id="dob"
              name="dob"
            />
            <span class="input-group-addon cuorser" id="basic-addon2"
              ><i class="fa fa-calendar" aria-hidden="true"></i
            ></span>
          </div>
          <div v-if="$v.InfoFormData.dob.$dirty">
            <p class="text-danger" v-if="!$v.InfoFormData.dob.required">Please enter your date of birth.</p>
          </div>

          <div class="driver-details">
        <div class="upload-text">
          <p>PHOTO OF ADDRESS</p>
        </div>
        <p class="doct-content">
          Delivering to a different address than the one shown on your ID?
          Please upload a photo of a
          <span> document with your name and correct delivery address </span>.on
          it (ex. utility bill, lease, hotel receipt). Please make sure we can
          read all of the numbers/letters in the image. *
        </p>

        <div class="row top-spce">
          <div class="row-height">
            <div class="col-sm-6 col-sm-height">
              <div class="driver-photo">
                <img v-if="deliveringFile" :src="deliveringFile" class="img-responsive" />
                <p id="fileNameDelivering"></p>
              </div>
            </div>
            <div class="col-sm-6 col-sm-height col-middle">
              <div class="remove-btn">
                <a href="#" v-if="deliveringFile" @click.prevent="removeDeliverImage">Remove</a>
                <input
                  type="file"
                  @change="onFileDeliverChange"
                  accept="image/jpeg"
                  id="uploadDeliverImage"
                  style="display:none"
                />
                <a href="#" v-if="!deliveringFile" @click.prevent="addDeliverImage">Add</a>
              </div>
            </div>
          </div>
        </div>
      </div>
          
          <div class="text-left buttons-section">
            <button class="btn send-btn save-btn"  :disabled="$v.$invalid">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>Save</button>
          </div>
        </form>
      </div>
    </div>
</template>

<script>
import {
  required,
  numeric,
  alpha,
  sameAs,
  email,
  helpers,
} from "vuelidate/lib/validators";
import * as fb from "../../../firebaseConfig";
import addressType from "../../../address-type";
import firebase from "firebase";
import axios from "axios";
export default {
  data() {
    return {
      
      InfoFormData: {
        FirstName: null,
        LastName: null,
        StreetAddress1: null,
        StreetAddress2: null,
        city: null,
        state: "",
        ZipCode: null,
        phoneNo: null,
        textMessage: null,
        dob: null,
        type: addressType.personalAddressType,
    //    id:1
      },
      deliveringFile:null,
      uploadImage:{
        deliverImage: null,
      },
      addressIndex:null,
      getStateData: [],
      spinnerShow: false,
      error: null,
    };
  },
   validations: {
    deliveringFile: {
      required,
    },
    InfoFormData: {
      FirstName: {
        required,
      },
      LastName: {
        required,
      },
      StreetAddress1: {
        required,
      },
      city: {
        required,
        alpha:helpers.regex('alpha', /^[a-zA-Z ]*$/),
      },
      state: {
        required,
      },
      ZipCode: {
        required,
        numeric,
      },
      phoneNo: {
        required,
      },
      dob: {
        required,
      },
    },
    deliveringFile: {
      required,
    },
  },
 
  mounted() {
      $(".flash__wrapper").empty();
    this.customerUser();
    this.fbState();
    
    var vm = this;
    $("input[name=dob]").datepicker({ 
      changeMonth: true,
      changeYear: true,
      yearRange: '-100:-18',
      onSelect: (dateForm) => {
        vm.InfoFormData.dob = dateForm;
      },
    });
    this.getIndexData();
    },
    
  methods: {

     removeDeliverImage(){
      this.deliveringFile= null; 
      this.uploadImage.deliverImage = null;
      $('#fileNameDelivering').html('');
    },

    onFileDeliverChange(e) {
      const file = e.target.files[0];
      this.uploadImage.deliverImage = file;
      $('#fileNameDelivering').html(file.name);
      this.deliveringFile = URL.createObjectURL(file);
    },
    
    addDeliverImage() {
      $("#uploadDeliverImage").trigger("click");
    },
  
  async uploadImageAsPromise(imageDataUrl) {
      let userId = fb.auth.currentUser.uid;
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      const type = imageDataUrl.type;
      return new Promise(function (resolve, reject) {
      var storageRef = firebase
          .storage()
          .ref(`customerDoc/${userId}/${currentTime}.`+type)
          .put(imageDataUrl, {
              contentType: type,
          });
        storageRef.then((data) => {
          data.ref.getDownloadURL().then((url) => {
            resolve(url);
          });
        });
      });
    },
