            objects[this.addressIndex] = objectToupdate 
            dataUpdate.doc('data_address').update({
                addresses: objects
              })
          })
          this.flashSuccess("You have successfully update your account information.", {
            timeout: 10000,
          });
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        })
        .catch((err) => {
          this.flashError("There is some problem. Please try again later.", {
            timeout: 10000,
          });
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        });
       
      }
    },
 */
 async UpdatePersonalData() {
      $(".flash__wrapper").empty();
      this.error = null;
      this.spinnerShow = true;
      if (!this.$v.$invalid) {

        var address = this.InfoFormData.StreetAddress1 + this.InfoFormData.StreetAddress2 +
          this.InfoFormData.city + this.InfoFormData.state + this.InfoFormData.ZipCode
           
        let { data } = await axios.get(this.$store.state.googleMapsUrl+`&address=${address}`);
        if(data.status=='OK'){ 

          var geocodedLocation = data.results[0];
          var latitude = geocodedLocation.geometry.location.lat
          var longitude =geocodedLocation.geometry.location.lng

          let dataUpdate = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
          $('.btn').prop("disabled", true);
          var deliveringImage = this.uploadImage.deliverImage;
          if(deliveringImage){
            let document_images={};
              await this.uploadImageAsPromise(deliveringImage).then((url) => {
                document_images['address_proof']=url;
                dataUpdate.doc('data_account').set({document_images:document_images},{merge:true})
              });
          }
          var phoneNumber = this.InfoFormData.phoneNo.replace(/[^\d]/g, "");
          var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);
  
          
          dataUpdate.doc('data_account')
          .update({
            "account.first_name": this.InfoFormData.FirstName,
            "account.last_name": this.InfoFormData.LastName,
            "account.date_of_birth": await firebase.firestore.Timestamp.fromDate(new Date(this.InfoFormData.dob)).toMillis(),
            "account.message_notification": this.InfoFormData.textMessage == true ? true : false,
            "addresses.latitude": latitude,
            "addresses.longitude": longitude,
            "addresses.city": this.InfoFormData.city,
            "addresses.phone_number": phoneNo,
            "addresses.state": this.InfoFormData.state,
            "addresses.street_1": this.InfoFormData.StreetAddress1,
            "addresses.street_2": this.InfoFormData.StreetAddress2,
            "addresses.zip_code": this.InfoFormData.ZipCode,
          }) 
          .then((success) => {
            dataUpdate.doc('data_address').get().then((doc) => {
              var objects = doc.data().addresses 
              var objectToupdate = {}

              // Update field of the element assigned 
              objectToupdate.city = this.InfoFormData.city
            //  objectToupdate.id = this.InfoFormData.id
              objectToupdate.latitude = latitude
              objectToupdate.longitude = longitude
              objectToupdate.phone_number = phoneNo
              objectToupdate.state = this.InfoFormData.state
              objectToupdate.street_1 = this.InfoFormData.StreetAddress1
              objectToupdate.street_2 = this.InfoFormData.StreetAddress2
              objectToupdate.type = this.InfoFormData.type
              objectToupdate.zip_code = this.InfoFormData.ZipCode
              objects[this.addressIndex] = objectToupdate 
              dataUpdate.doc('data_address').update({
                  addresses: objects
                })
            })
            this.flashSuccess("You have successfully update your account information.", {
              timeout: 10000,
            });
            $('.btn').prop("disabled", false);
            this.spinnerShow = false;
          })
          .catch((err) => {
            this.flashError("There is some problem. Please try again later.", {
              timeout: 10000,
            });
            $('.btn').prop("disabled", false);
            this.spinnerShow = false;
          });
        
        }else {
          this.flashError("Please enter valid address.", {
              timeout: 10000,
            });
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        }
      }
    },
    acceptNumber() {
      var x = this.InfoFormData.phoneNo
        .replace(/\D/g, "")
        .match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
      this.InfoFormData.phoneNo = !x[2]
        ? x[1]
        : "(" + x[1] + ") " + x[2] + (x[3] ? "-" + x[3] : "");
    },

    async customerUser() {
       var vueInstance = this;
    
      let dataUpdate = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
        dataUpdate.doc('data_account').onSnapshot(function (querySnapshot) {
          var customerData =querySnapshot.data();
          vueInstance.InfoFormData.FirstName = customerData.account.first_name;
          vueInstance.InfoFormData.LastName = customerData.account.last_name;
          vueInstance.InfoFormData.StreetAddress1 = customerData.addresses.street_1;
          vueInstance.InfoFormData.StreetAddress2 = customerData.addresses.street_2;
          vueInstance.InfoFormData.city = customerData.addresses.city;
          vueInstance.InfoFormData.state = customerData.addresses.state;
          vueInstance.InfoFormData.ZipCode = customerData.addresses.zip_code;
          var cleaned = ('' + customerData.addresses.phone_number).replace(/\D/g, '')
          var match= cleaned.match(/^(\d{3})(\d{3})(\d{4})$/)
          if (match) {
            vueInstance.InfoFormData.phoneNo= '(' + match[1] + ') ' + match[2] + '-' + match[3]
          }
         // vueInstance.InfoFormData.phoneNo = customerData.addresses.phone_number;
          vueInstance.InfoFormData.type = customerData.addresses.type;
          var dob  = firebase.firestore.Timestamp.fromMillis(customerData.account.date_of_birth).toDate().toUTCString();
          var date = new Date (dob);
          vueInstance.InfoFormData.dob=  date.toLocaleString ([], {year: 'numeric', month: '2-digit', day: '2-digit'});
          vueInstance.InfoFormData.textMessage =customerData.account.message_notification == true ? true : false;
          vueInstance.deliveringFile = customerData.document_images.address_proof;
      });
     },

    async getIndexData() {
        var vueInstance = this;
        let matchIndexData = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
          matchIndexData.doc('data_address').onSnapshot(function (querySnapshot) {
          var customerDataAddress =querySnapshot.data().addresses;
          var result = -1;
          customerDataAddress.some(function (elem, i) {
              if (customerDataAddress.some(address => address.type === vueInstance.InfoFormData.type)) {
              if(customerDataAddress[i].type == vueInstance.InfoFormData.type){ 
                vueInstance.addressIndex = i;
                return true;
              }
            }
           // if (elem['first_name'] === vueInstance.InfoFormData.FirstName && elem['last_name'] === vueInstance.InfoFormData.LastName &&
            // if(elem['city'] === vueInstance.InfoFormData.city && elem['state'] === vueInstance.InfoFormData.state &&
            //   elem['street_1'] === vueInstance.InfoFormData.StreetAddress1 && elem['street_2'] === vueInstance.InfoFormData.StreetAddress2 &&
            //   elem['zip_code'] === vueInstance.InfoFormData.ZipCode && elem['phone_number'] === vueInstance.InfoFormData.phoneNo &&
            //   elem['type'] === vueInstance.InfoFormData.type) {
            //     vueInstance.addressIndex = i;
            //     return true;
            // } 
          });
      });
     },

    async fbState() {
      await this.$store.dispatch("getAllState");
      this.getStateData = this.$store.state.GetState[0].state;
    },
  },
};
</script>
