<template>
<div>
    <header-component />

    <section id="banner-section">
      <section id="customer-form">
        <div class="container">
          <div class="row">
            <div class="col-lg-11 col-lg-offset-1 animatedParent" data-sequence='100'>
              <div class="form-menu ">
                <div class="back-menu"><i class="fa fa-long-arrow-left" aria-hidden="true"></i><router-link :to="{ name: 'customerList' }">Back to dashboard</router-link></div>
              </div>
              <div class="tab-section animated bounceIn go"  data-id="1">
                <!-- Nav tabs -->
                <selectTab :tabPage="this.tab"/>

                <!-- Tab panes -->
                <div class="tab-content page-tab bottom-space">
                  <div role="tabpanel" class="tab-pane active" id="deals-ads">
                    <component v-bind:is="currentComponent" :driverId="driverId" :id="this.dataId"></component>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </section>

<footer-component />
</div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import evenBus from '../../../event-bus'
import selectTab from '../../../components/backEnd/admin/TabComponent'
import driverOrders from '../../../components/backEnd/admin/DriverOrdersComponent'
import myOrderDetail from "../../../components/backEnd/admin/MyOrderDetailComponent";

export default {
  props:['driverId'],  
    data() {
      return {
        currentComponent: driverOrders,
        dataId: null,
        tab:null,
      }
    },
    
    components: {
        driverOrders,
        selectTab,
        myOrderDetail,
    },
    created (){ 
        this.tab = this.$route.name;
        evenBus.$on("myOrderDetails", (id) => { 
            this.dataId = id; 
            this.currentComponent = myOrderDetail;
        });
        evenBus.$on("backToList", () => {
        this.currentComponent = driverOrders;
        });
    },

};
</script>
