<template>
  <div class="modal fade" tabindex="-1" role="dialog" id="billingAddressModal">
    <div class="modal-dialog modal-md" role="document">
      <div class="modal-content">
        <div class="share-box">
          <div class="text-center">
            <div class="title">
              <h2 class="text-uppercase">{{ billingModalTitle }}</h2>
            </div>
            <br />
            <div class="row">
              <div class="col-md-12">
                <div class="contact-form margin-top">
                  <div class="form-group">
                    <input
                      v-model.trim="billingAddressData.FirstName"
                      @input="$v.billingAddressData.FirstName.$touch()"
                      class="form-control"
                      placeholder="First Name*"
                      type="text"
                    />
                    <div v-if="$v.billingAddressData.FirstName.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.billingAddressData.FirstName.required"
                      >
                        Please enter your first name.
                      </p>
                    </div>
                  </div>
                  <div class="form-group">
                    <input
                      v-model.trim="billingAddressData.LastName"
                      @input="$v.billingAddressData.LastName.$touch()"
                      class="form-control"
                      placeholder="Last Name*"
                      type="text"
                    />
                    <div v-if="$v.billingAddressData.LastName.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.billingAddressData.LastName.required"
                      >
                        Please enter your last name.
                      </p>
                    </div>
                  </div>
                  <div class="form-group">
                    <input
                      v-model.trim="billingAddressData.StreetAddress1"
                      @input="$v.billingAddressData.StreetAddress1.$touch()"
                      class="form-control"
                      placeholder="Street Address*"
                      type="text"
                    />
                    <div v-if="$v.billingAddressData.StreetAddress1.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.billingAddressData.StreetAddress1.required"
                      >
                        Please enter your street address.
                      </p>
                    </div>
                  </div>
                  <div class="form-group">
                    <input
                      v-model.trim="billingAddressData.StreetAddress2"
                      class="form-control"
                      placeholder="Street Address 2"
                      type="text"
                    />
                  </div>
                  <div class="form-group">
                    <input
                      v-model.trim="billingAddressData.city"
                      @input="$v.billingAddressData.city.$touch()"
                      class="form-control"
                      placeholder="City*"
                      type="text"
                    />
                    <div v-if="$v.billingAddressData.city.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.billingAddressData.city.required"
                      >
                        Please enter your city.
                      </p>
                      <p class="text-danger" v-if="!$v.billingAddressData.city.alpha">
                        Please enter your valid city name.
                      </p>
                    </div>
                  </div>
                  <div class="form-group">
                    <div class="row">
                      <div class="col-sm-8">
                        <select
                          class="form-control"
                          v-model.trim="billingAddressData.state"
                        >
                          <option value="">Select State *</option>
                          <option
                            v-for="(state, index) in getStateData"
                            :key="index"
                            :value="state"
                          >
                            {{ state }}
                          </option>
                        </select>
                        <div v-if="$v.billingAddressData.state.$dirty">
                          <p
                            class="text-danger"
                            v-if="!$v.billingAddressData.state.required"
                          >
                            Please select issuing state.
                          </p>
                        </div>
                      </div>

                      <div class="col-sm-4">
                        <input
                          v-model.trim="billingAddressData.ZipCode"
                          @input="$v.billingAddressData.ZipCode.$touch()"
                          class="form-control"
                          placeholder="Zip*"
                          type="text"
                        />
                        <div v-if="$v.billingAddressData.ZipCode.$dirty">
                          <p
                            class="text-danger"
                            v-if="!$v.billingAddressData.ZipCode.required"
                          >
                            Please enter zip code.
                          </p>
                          <p
                            class="text-danger"
                            v-if="!$v.billingAddressData.ZipCode.numeric"
                          >
                            Please enter valid zip code.
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="form-group">
                    <input
                      @keyup="acceptNumber"
                      v-model.trim="billingAddressData.phoneNo"
                      @input="$v.billingAddressData.phoneNo.$touch()"
                      class="form-control"
                      placeholder="Mobile Number*"
                      type="text"
                    />
                    <div v-if="$v.billingAddressData.phoneNo.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.billingAddressData.phoneNo.required"
                      >
                        Please enter your valid phone no..
                      </p>
                    </div>
                  </div>

                  <div v-if="billingAddresserror" v-html="billingAddresserror">{{billingAddresserror}}</div>
                  <div class="text-left">
                    <button
                      v-if="checkOperation == 'Add'"
                      class="btn send-btn padding-low"
                      @click.prevent="addBillingData"
                      :disabled="$v.$invalid"
                    >
                      <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>
                      {{ checkOperation }} INFORMATION
                    </button>
                    <button
                      v-if="checkOperation == 'Update'"
                      class="btn send-btn padding-low"
                      @click.prevent="editBillingData"
                      :disabled="$v.$invalid"
                    >
                      <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>
                      {{ checkOperation }} INFORMATION
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { required, numeric, alpha, helpers } from "vuelidate/lib/validators";
import evenBus from "../../../event-bus";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import axios from "axios";
export default {
  data() {
    return {
      tab: null,
      billingModalTitle: "Edit Billing Address",
      billingAddressData: {
        FirstName: null,
        LastName: null,
        StreetAddress1: null,
        StreetAddress2: null,
        city: null,
        state: "",
        ZipCode: null,
        phoneNo: null,
        type: null,
        indexOfAddress: null,
      },
      address_proof:'',
      checkOperation: "Add",
      getStateData: [],
      spinnerShow: false,
      billingAddresserror: null,
    };
  },
  validations: {
    billingAddressData: {
      FirstName: {
        required,
      },
      LastName: {
        required,
      },
      StreetAddress1: {
        required,
      },
      city: {
        required,
        alpha: helpers.regex("alpha", /^[a-zA-Z ]*$/),
      },
      state: {
        required,
      },
      ZipCode: {
        required,
        numeric,
      },
      phoneNo: {
        required,
      },
    },
  },

  mounted() {
    //  $(".flash__wrapper").empty();
    this.fbState();

    evenBus.$on("addBillingAddressModal", (type) => { 
      $("#billingAddressModal").modal();
      var self = this; 
      Object.keys(this.billingAddressData).forEach(function(key,index) {
        self.billingAddressData[key] = ''; 
        setTimeout(function() {
             $('.text-danger').html('');
        }, 200);
      });
      this.billingModalTitle = "Add Billing Address";
     // this.billingAddressData.id = 2;
      this.billingAddressData.type = type;
    });

    evenBus.$on("editBillingAddressModal", (type, indexData) => {
      var vueInstance = this;
      Object.keys(this.billingAddressData).forEach(function(key,index) {
        vueInstance.billingAddressData[key] = ''; 
        setTimeout(function() {
             $('.text-danger').html('');
        }, 200);
      });
      let dataUpdate = fb.customerCollection
        .doc(fb.auth.currentUser.uid)
        .collection("response");
        dataUpdate.doc("data_address").onSnapshot(function (querySnapshot) {
        var customerDataAddress = querySnapshot.data().addresses;
        customerDataAddress.some(function (elem, i) {
          if (elem["type"] === type) {
            vueInstance.billingAddressData.indexOfAddress = i;
            vueInstance.billingAddressData.FirstName = elem["first_name"];
            vueInstance.billingAddressData.LastName = elem["last_name"];
            vueInstance.billingAddressData.city = elem["city"];
            vueInstance.billingAddressData.state = elem["state"];
            vueInstance.billingAddressData.StreetAddress1 = elem["street_1"];
            vueInstance.billingAddressData.StreetAddress2 = elem["street_2"];
            vueInstance.billingAddressData.ZipCode = elem["zip_code"];
            var cleaned = ('' + elem['phone_number']).replace(/\D/g, '')
            var match= cleaned.match(/^(\d{3})(\d{3})(\d{4})$/)
            if (match) {
              vueInstance.billingAddressData.phoneNo= '(' + match[1] + ') ' + match[2] + '-' + match[3]
            }
            //vueInstance.billingAddressData.phoneNo = elem["phone_number"];
           
          }
        });
      });
      this.checkOperation = "Update";
      $("#billingAddressModal").modal();
      this.billingAddressData.type = type;
    });
  },
  methods: {
  
    async editBillingData() {
      $(".flash__wrapper").empty();
      this.billingAddresserror = null;
      this.spinnerShow = true;
      if (!this.$v.$invalid) {
        $(".btn").prop("disabled", true);
        var phoneNumber = this.billingAddressData.phoneNo.replace(/[^\d]/g, "");
          var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);
        
        var address = this.billingAddressData.StreetAddress1 + this.billingAddressData.StreetAddress2 +
          this.billingAddressData.city + this.billingAddressData.state + this.billingAddressData.ZipCode
           
        let { data } = await axios.get(this.$store.state.googleMapsUrl+`&address=${address}`);
        if(data.status=='OK'){
          var geocodedLocation = data.results[0];
          var latitude = geocodedLocation.geometry.location.lat
          var longitude =geocodedLocation.geometry.location.lng
          
        let dataUpdate = await fb.customerCollection
          .doc(fb.auth.currentUser.uid)
          .collection("response");
        dataUpdate
          .doc("data_address")
          .get()
          .then((doc) => {
            var objects = doc.data().addresses;
            var objectToupdate = {};
            //Update field of the element assigned
            objectToupdate.first_name = this.billingAddressData.FirstName;
            objectToupdate.last_name = this.billingAddressData.LastName;
            objectToupdate.city = this.billingAddressData.city;
            objectToupdate.latitude = latitude;
            objectToupdate.longitude = longitude;
            objectToupdate.phone_number = phoneNo;
            objectToupdate.state = this.billingAddressData.state;
            objectToupdate.street_1 = this.billingAddressData.StreetAddress1;
            objectToupdate.street_2 = this.billingAddressData.StreetAddress2;
            objectToupdate.type = this.billingAddressData.type;
            objectToupdate.zip_code = this.billingAddressData.ZipCode;
            objectToupdate.address_proof = this.address_proof;
            objects[this.billingAddressData.indexOfAddress] = objectToupdate;
            dataUpdate.doc("data_address").update({
              addresses: objects,
            });
          })
          .then((success) => {
            $(".btn").prop("disabled", false);
            this.flashSuccess(`You have successfully ${this.billingModalTitle}.`, {
              timeout: 10000,
            });
            this.spinnerShow = false;
            $("#billingAddressModal").modal("hide");
           
          })
          .catch((err) => {
            console.log(err);
            $(".btn").prop("disabled", false);
            this.flashError("There is some problem. Please try again later.", {
              timeout: 10000,
            });
            this.spinnerShow = false;
            $("#billingAddressModal").modal("hide");
          });
      }
      }else {
          this.billingAddresserror ="<p class='alert alert-danger'>Please enter valid address.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        }
    },

    acceptNumber() {
      var x = this.billingAddressData.phoneNo
        .replace(/\D/g, "")
        .match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
      this.billingAddressData.phoneNo = !x[2]
        ? x[1]
        : "(" + x[1] + ") " + x[2] + (x[3] ? "-" + x[3] : "");
    },
    async fbState() {
      await this.$store.dispatch("getAllState");
      this.getStateData = this.$store.state.GetState[0].state;
    },

    async addBillingData() { 
      $(".flash__wrapper").empty();
      this.billingAddresserror = null;
      this.spinnerShow = true;
      if (!this.$v.$invalid) {
        $(".btn").prop("disabled", true);
        var phoneNumber = this.billingAddressData.phoneNo.replace(/[^\d]/g, "");
          var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);
        
        var address = this.billingAddressData.StreetAddress1 + this.billingAddressData.StreetAddress2 +
          this.billingAddressData.city + this.billingAddressData.state + this.billingAddressData.ZipCode
           
        let { data } = await axios.get(this.$store.state.googleMapsUrl+`&address=${address}`);
        if(data.status=='OK'){
          var geocodedLocation = data.results[0];
          var latitude = geocodedLocation.geometry.location.lat
          var longitude =geocodedLocation.geometry.location.lng
          
        await this.$store
          .dispatch("ShippingDataInsert", {
            first_name: this.billingAddressData.FirstName,
            last_name: this.billingAddressData.LastName,
            city: this.billingAddressData.city,
        //    id: this.billingAddressData.id,
            latitude: latitude,
            longitude: longitude,
            phone_number: phoneNo,
            state: this.billingAddressData.state,
            street_1: this.billingAddressData.StreetAddress1,
            street_2: this.billingAddressData.StreetAddress2,
            zip_code: this.billingAddressData.ZipCode,
            type: this.billingAddressData.type,
            address_proof: this.address_proof,
          })
          .then((success) => {
            console.log(success);
            $(".btn").prop("disabled", false);
            this.flashSuccess(`You have successfully ${this.billingModalTitle}.`, {
              timeout: 10000,
            });
            this.spinnerShow = false;
            $("#billingAddressModal").modal("hide");
             
          })
          .catch((err) => {
            $(".btn").prop("disabled", false);
            this.flashError("There is some problem. Please try again later.", {
              timeout: 10000,
            });
            this.spinnerShow = false;
            $("#billingAddressModal").modal("hide");
          });
      }else{
        this.billingAddresserror ="<p class='alert alert-danger'>Please add mandatory field .<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
        this.spinnerShow = false;
        $(".btn").prop("disabled", false);
      }
    }else {
          this.billingAddresserror ="<p class='alert alert-danger'>Please enter valid address.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        }
    },
  },
};
</script>
