<template>
  <div class="row contact-form">
  <div class="col-sm-2"></div>
  <div class="col-sm-8"><flash-message></flash-message></div>
  <div class="col-sm-2"></div>
    <div class="col-sm-5">
      <div class="contact-title border-bottom">
        <div class="row">
          <div class="col-sm-10">
            <h3>Primary Address</h3>
          </div>

          <div class="col-sm-2">
            <div class="text-right">
              <div class="edit-green">
              <a href="#" v-if="primaryShippingAddress.isPrimaryAddressAdd==1" @click.prevent="editShippingAddress(primaryShippingAddress.type,primaryShippingAddress.indexOfAddress)">Edit</a>
              <a href="#" v-if="primaryShippingAddress.isPrimaryAddressAdd==0" @click.prevent="addShippingAddress(primaryShippingAddress.type)">Add</a>
       
              </div>
            </div>
          </div>
        </div>
      </div>
       <div class="checkbox">
          <label>
            <input type="checkbox" v-model.trim="sameAsPersonalInfo"  @click="checkedSameAs">Same As Personal Info Address
          </label>
        </div>
      <div class="row margin-top bottom-space" v-if="primaryShippingAddress.FirstName">
        <div class="col-sm-7">
          <div class="proof-copy-add">
            <h4>ADDRESS</h4>
            <p>
              {{primaryShippingAddress.StreetAddress1}} {{primaryShippingAddress.StreetAddress2}}<br />
              {{primaryShippingAddress.city}}, {{primaryShippingAddress.state}} {{primaryShippingAddress.ZipCode}}<br />
              {{primaryShippingAddress.phoneNo}}
            </p>
          </div>
        </div>

        <div class="col-sm-5">
          <div class="proof-copy-add">
            <h4>PROOF OF ADDRESS </h4>

            <img :src="primaryShippingAddress.addressProof" class="img-responsive" />
          </div>
        </div>
      </div>

      <div class="contact-title border-bottom add-top-space">
        <div class="row">
          <div class="col-sm-10">
            <h3>Billing Address</h3>
          </div>

          <div class="col-sm-2">
            <div class="text-right">
              <div class="edit-green">
                <a href="#" v-if="billingData.isBillingAddressAdd>0" @click.prevent="editBillingAddress(billingData.type,billingData.indexOfAddress)">Edit</a>
                <a href="#" v-if="billingData.isBillingAddressAdd<1" @click.prevent="addBillingAddress(billingData.type)">Add</a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div  v-if="billingData.isBillingAddressAdd<1" class="checkbox">
        <label>
          <input type="checkbox" @click="checkedSameAsBilling">Same As Personal Info Address
        </label>
      </div>
      <div class="row margin-top">
        <div class="col-sm-7">
          <div class="proof-copy-add">
            <p>
              {{billingData.StreetAddress1}} {{billingData.StreetAddress2}}<br />
              {{billingData.city}}, {{billingData.state}} {{billingData.ZipCode}}<br />
              {{billingData.phoneNo}}
            
            </p>
            <div class="verify-btn"></div>
          </div>
        </div>

        <div class="col-sm-5"></div>
      </div>
    </div>
    <div class="col-sm-2"></div>
    <div class="col-sm-5">
      <div class="contact-title border-bottom">
        <div class="row">
          <div class="col-sm-10">
            <h3>Secondary Address</h3>
          </div>

          <div class="col-sm-2">
            <div class="text-right">
              <div class="edit-none">
                <a href="#" v-if="secondryShippingmData.isSecondryAddressAdd>0" @click.prevent="editShippingAddress(secondryShippingmData.type,secondryShippingmData.indexOfAddress)">Edit</a>
                <a href="#" v-if="secondryShippingmData.isSecondryAddressAdd<1" @click.prevent="addShippingAddress(secondryShippingmData.type)">Add</a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row margin-top bottom-space" v-if="secondryShippingmData.FirstName">
        <div class="col-sm-7">
          <div class="proof-copy-add">
            <h4>ADDRESS</h4>
            <p>
              {{secondryShippingmData.StreetAddress1}} {{secondryShippingmData.StreetAddress2}}<br />
              {{secondryShippingmData.city}}, {{secondryShippingmData.state}} {{secondryShippingmData.ZipCode}}<br />
              {{secondryShippingmData.phoneNo}}
            </p>
          </div>
        </div>
        <div class="col-sm-5">
          <div class="proof-copy-add">
            <h4>
              PROOF OF ADDRESS
            </h4>
            <img :src="secondryShippingmData.addressProof" class="img-responsive" />
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import {
  required,
  numeric,
  alpha,
  sameAs,
  requiredIf,
} from "vuelidate/lib/validators";
import evenBus from "../../../event-bus";
import * as fb from "../../../firebaseConfig";
import addressType from "../../../address-type";
import firebase from "firebase";

export default {
  data() {
    return {
      primaryShippingAddress:{
        FirstName: null,
        LastName: null,
        StreetAddress1: null,
        StreetAddress2: null,
        city: null,
        state: "",
        ZipCode: null,
        phoneNo: null,
        addressProof:null,
        indexOfAddress: null,
        type:addressType.primaryAddressType,
        isPrimaryAddressAdd:null,
      },
      secondryShippingmData: {
        FirstName: null,
        LastName: null,
        StreetAddress1: null,
        StreetAddress2: null,
        city: null,
        state: "",
        ZipCode: null,
        phoneNo: null,
        addressProof:null,
        indexOfAddress: null,
        type:addressType.secondaryAddressType,
        isSecondryAddressAdd:0,
      },
      billingData: {
        FirstName: null,
        LastName: null,
        StreetAddress1: null,
        StreetAddress2: null,
        city: null,
        state: "",
        ZipCode: null,
        phoneNo: null,
        addressProof:null,
        indexOfAddress: null,
        type:addressType.billingAddressType,
        isBillingAddressAdd:0,
      },
      type: addressType.personalAddressType,
      sameAsPersonalInfo:false,
      spinnerShow: false,
      getStateData: [],
      error: null,
    };
  },
  mounted(){
     // $(".flash__wrapper").empty();
    this.fbState();
    this.shippingmAddress();
  },
 
  methods: {

    async addShippingAddress(shippingType){ 
      evenBus.$emit("addAddressModal", shippingType);
    },

    async editShippingAddress(shippingType,updateIndex){ 
      evenBus.$emit("editAddressModal", shippingType,updateIndex);
    },

    async addBillingAddress(billingType){ 
      evenBus.$emit("addBillingAddressModal", billingType);
    },

    async editBillingAddress(billingType,updateIndex){ 
      evenBus.$emit("editBillingAddressModal", billingType,updateIndex);
    },

    async checkedSameAs(event){ 
      let dataInsert = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
        
      if (event.target.checked){
        var deleteIndex = this.primaryShippingAddress.indexOfAddress;
        var personalAddress = confirm('Do you want to same As personal info address1?'); 
        if (personalAddress == true) {
          dataInsert.doc('data_address').get().then((doc) => {
            if(deleteIndex != null && doc.data().addresses[deleteIndex].type==this.primaryShippingAddress.type){
              dataInsert.doc('data_address').update({
                addresses:firebase.firestore.FieldValue.arrayRemove(doc.data().addresses[deleteIndex])
              })
            }
          })
            
            dataInsert.doc('data_address').set({isPersonAddress:true},{merge:true})
        }else{ 
          this.sameAsPersonalInfo=true;
        }
      }
      else {
        var newAddress = confirm('Do you want to add new primary address?'); 
        if (newAddress == true) {
          dataInsert.doc('data_address').set({isPersonAddress:false},{merge:true})
        }else{ 
          this.sameAsPersonalInfo=true;
        }
      }

    },
    
    async checkedSameAsBilling(event){ 
      let dataInsert = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
        
      if (event.target.checked){ 
        var deleteIndex = this.billingData.indexOfAddress;
        var personalAddress = confirm('Do you want to same As personal info address?'); 
        if (personalAddress == true) {
          dataInsert.doc('data_address').get().then((doc) => {
            if(deleteIndex != null && doc.data().addresses[deleteIndex].type==this.billingData.type){
              dataInsert.doc('data_address').update({
                addresses:firebase.firestore.FieldValue.arrayRemove(doc.data().addresses[deleteIndex])
              })
            }
             dataInsert.doc('data_account').get().then((personaDoc) => {
                console.log(personaDoc.data())
                let personalAddress = personaDoc.data();
                this.$store.dispatch("ShippingDataInsert", {
                  first_name: personalAddress.account.first_name,
                  last_name: personalAddress.account.last_name,
                  city: personalAddress.addresses.city,
              //    id: this.billingAddressData.id,
                  latitude: personalAddress.addresses.latitude,
                  longitude: personalAddress.addresses.longitude,
                  phone_number: personalAddress.addresses.phone_number,
                  state: personalAddress.addresses.state,
                  street_1: personalAddress.addresses.street_1,
                  street_2: personalAddress.addresses.street_2,
                  zip_code: personalAddress.addresses.zip_code,
                  type: this.billingData.type,
                  address_proof: "",
                })
                .then((success) => {
                  $(".btn").prop("disabled", false);
                  this.flashSuccess(`You have successfully copy your personal address.`, {
                    timeout: 10000,
                  });
                  
                })
                .catch((err) => {
                  this.flashError("There is some problem. Please try again later.", {
                    timeout: 10000,
                  });
                });

              })
          })
        }
      }

    },
    
    async shippingmAddress() {
      var vueInstance = this;
      var count = 0;
      let dataUpdate = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
          dataUpdate.doc('data_address').onSnapshot(function (querySnapshot) {
          var customerDataAddress =querySnapshot.data();
          
          customerDataAddress.addresses.some(function (elem, i) { 
            //Check primary shipping address add or not
            if(elem['type'] === vueInstance.primaryShippingAddress.type && customerDataAddress.isPersonAddress==false) { 
              vueInstance.primaryShippingAddress.isPrimaryAddressAdd = 1;

              vueInstance.primaryShippingAddress.indexOfAddress =i;
              vueInstance.primaryShippingAddress.FirstName =elem['first_name']; 
              vueInstance.primaryShippingAddress.LastName =elem['last_name']; 
              vueInstance.primaryShippingAddress.city =elem['city']; 
              vueInstance.primaryShippingAddress.state =elem['state']; 
              vueInstance.primaryShippingAddress.StreetAddress1 =elem['street_1']; 
              vueInstance.primaryShippingAddress.StreetAddress2 =elem['street_2']; 
              vueInstance.primaryShippingAddress.ZipCode =elem['zip_code']; 
              var cleaned = ('' + elem['phone_number']).replace(/\D/g, '')
              var match= cleaned.match(/^(\d{3})(\d{3})(\d{4})$/)
              if (match) {
                vueInstance.primaryShippingAddress.phoneNo= '(' + match[1] + ') ' + match[2] + '-' + match[3]
              }
              //vueInstance.primaryShippingAddress.phoneNo =elem['phone_number']; 
              vueInstance.primaryShippingAddress.addressProof =elem['address_proof'];
            }else if(elem['type'] === vueInstance.type && customerDataAddress.isPersonAddress==true) {
              vueInstance.sameAsPersonalInfo=true
              vueInstance.primaryShippingAddress.isPrimaryAddressAdd = null;

              vueInstance.primaryShippingAddress.indexOfAddress =i;
              vueInstance.primaryShippingAddress.city =elem['city']; 
              vueInstance.primaryShippingAddress.state =elem['state']; 
              vueInstance.primaryShippingAddress.StreetAddress1 =elem['street_1']; 
              vueInstance.primaryShippingAddress.StreetAddress2 =elem['street_2']; 
              vueInstance.primaryShippingAddress.ZipCode =elem['zip_code']; 
              
              var cleaned = ('' + elem['phone_number']).replace(/\D/g, '')
              var match= cleaned.match(/^(\d{3})(\d{3})(\d{4})$/)
              if (match) {
                vueInstance.primaryShippingAddress.phoneNo= '(' + match[1] + ') ' + match[2] + '-' + match[3]
              }
              //vueInstance.primaryShippingAddress.phoneNo =elem['phone_number']; 

              dataUpdate.doc('data_account').onSnapshot(function (querySnapshot) {
                var customerData =querySnapshot.data();
                vueInstance.primaryShippingAddress.FirstName  = customerData.account.first_name;
                vueInstance.primaryShippingAddress.LastName = customerData.account.last_name;
                vueInstance.primaryShippingAddress.addressProof = customerData.document_images.address_proof;
              });
            }else if(customerDataAddress.isPersonAddress==false) {
              vueInstance.primaryShippingAddress.FirstName  = null; 
              vueInstance.primaryShippingAddress.isPrimaryAddressAdd = 0;
            }

            //Check secondry shipping address add or not
            if(elem['type'] === vueInstance.secondryShippingmData.type) { 
               vueInstance.secondryShippingmData.isSecondryAddressAdd = 1;

              vueInstance.secondryShippingmData.indexOfAddress =i;
              vueInstance.secondryShippingmData.FirstName =elem['first_name']; 
              vueInstance.secondryShippingmData.LastName =elem['last_name']; 
              vueInstance.secondryShippingmData.city =elem['city']; 
              vueInstance.secondryShippingmData.state =elem['state']; 
              vueInstance.secondryShippingmData.StreetAddress1 =elem['street_1']; 
              vueInstance.secondryShippingmData.StreetAddress2 =elem['street_2']; 
              vueInstance.secondryShippingmData.ZipCode =elem['zip_code']; 
              var cleaned = ('' + elem['phone_number']).replace(/\D/g, '')
              var match= cleaned.match(/^(\d{3})(\d{3})(\d{4})$/)
              if (match) {
                vueInstance.secondryShippingmData.phoneNo= '(' + match[1] + ') ' + match[2] + '-' + match[3]
              }
              //vueInstance.secondryShippingmData.phoneNo =elem['phone_number']; 
              vueInstance.secondryShippingmData.addressProof =elem['address_proof'];
            } 

            //Check billing address add or not
            if(elem['type'] === vueInstance.billingData.type) { 
               vueInstance.billingData.isBillingAddressAdd = 1;

              vueInstance.billingData.indexOfAddress =i;
              vueInstance.billingData.FirstName =elem['first_name']; 
              vueInstance.billingData.LastName =elem['last_name']; 
              vueInstance.billingData.city =elem['city']; 
              vueInstance.billingData.state =elem['state']; 
              vueInstance.billingData.StreetAddress1 =elem['street_1']; 
              vueInstance.billingData.StreetAddress2 =elem['street_2']; 
              vueInstance.billingData.ZipCode =elem['zip_code']; 
              var cleaned = ('' + elem['phone_number']).replace(/\D/g, '')
              var match= cleaned.match(/^(\d{3})(\d{3})(\d{4})$/)
              if (match) {
                vueInstance.billingData.phoneNo= '(' + match[1] + ') ' + match[2] + '-' + match[3]
              }
              //vueInstance.billingData.phoneNo =elem['phone_number']; 
              vueInstance.billingData.addressProof =elem['address_proof'];
            }

          });
      });
     },

    async UpdateAllData() {
      $(".flash__wrapper").empty();
      this.error = null;
      this.spinnerShow = true;
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      let dataUpdate = await fb.dispensaryCollection.doc(this.firebaseId);
      dataUpdate
        .update({
          updated_at: currentTime,
          busniess_name: this.dispensaryBusinessName,
          license_number: this.dispensaryLicenseNumber,
          subscribe: this.dispensarySubscribe == true ? true : false,
          text_notifications:
            this.dispensaryTextNotifications == true ? true : false,
          "address.first_name": this.dispensaryFirstName,
          "address.last_name": this.dispensaryLastName,
          "address.phone_number": this.dispensaryMobileNumber,
          "address.state": this.dispensaryState,
          "address.street1": this.dispensaryStreetAddress,
          "address.street2": this.dispensaryStreetAddress2,
          "address.zip_code": this.dispensaryZipCode,
        })
        .then((success) => {
        /*  fb.usersCollection.doc(fb.auth.currentUser.uid).update({
            name: this.dispensaryFirstName + " " + this.dispensaryLastName,
          });
        */  
          this.flashSuccess("You have successfully update your profile.", {
            timeout: 10000,
          });
          this.spinnerShow = false;
        })
        .catch((err) => {
          this.flashError("There is some problem. Please try again later1.", {
            timeout: 10000,
          });
          this.spinnerShow = false;
        });
    },
   
    async fbState() {
      await this.$store.dispatch("getAllState");
      this.getStateData = this.$store.state.GetState[0].state;
    },
  },
};
</script>
