router.beforeEach(async (to, from, next) => {
  document.title = to.meta.title;
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);
  const requiresUnauth = to.matched.some(record => record.meta.requiresUnauth);
 
  if (requiresAuth &&  await firebase.auth().currentUser){
    const requiresAuthCustomer = to.matched.some(record => record.meta.requiresAuthCustomer);
    const requiresAuthDispensary = to.matched.some(record => record.meta.requiresAuthDispensary);
    const requiresAuthDriver = to.matched.some(record => record.meta.requiresAuthDriver);
    const requiresAuthAdmin = to.matched.some(record => record.meta.requiresAuthAdmin);
  
    if(!store.state.userProfile.role){
      await store.dispatch("fetchUserProfile")     
    }
    let role = store.state.userProfile.role;
    if(requiresAuthCustomer && role=="Customer"){
      next();
    }else if(requiresAuthDispensary && role=="Dispensary"){
      next();
    }else if(requiresAuthDriver && role=="Driver"){
      next();
    }else if(requiresAuthAdmin && role=="Admin"){
      next();
    }else{
      next({ path: '/unauthenticatedError'});
    }
  }else if (requiresAuth &&  !await firebase.auth().currentUser){
    next({ path: '/login' });
  }else if (requiresUnauth &&  await firebase.auth().currentUser){ 
    next({ path: '/unauthenticatedError' });
  }else{
    next();
  } 
});

router.beforeResolve((to, from, next) => {
  // If this isn't an initial page load.
  if (to.name) {
    // NProgress.start()
  }
  next()
})

