<template>
<div>
  
  <div :class="{ loader:dataLoader }"></div>
  <div class="row">
    <div class="col-lg-12">
      <div class="row contact-form">
        <div v-if="error" v-html="error">{{error}}</div> 
       
        <div class="col-sm-3">
          <div class="start-date">
            <label>Order Status</label>
            <select class="form-control" v-model.trim="search.OrderStatus">
              <option value="SCHEDULED">Scheduled</option>
              <option value="ACCEPT">In-progress</option>
              <option value="COMPLATE">Delivered</option>
              <option value="CANCELLED">Cancelled</option>
            </select>
          </div>
        </div>
        <div class="col-sm-3">
          <div class="start-date">
            <button
              class="btn search-btn order-search-btn"
              @click.prevent="searchData"
            >SEARCH ORDERS</button>
          </div>
        </div>
        <div class="col-sm-2">
          <div class="reset-btn">
            <a href="#" @click.prevent="resetSearchForm">
              <i class="fa fa-refresh" aria-hidden="true"></i>
              reset
            </a>
          </div>
        </div>
        <div class="col-sm-2 col-lg-offset-2" v-if="exportDataDiv">
          <download-excel class="cursor-pointer export-btn"
            :data="exportOrderData"
            :fields="dataForExcel"
            worksheet="My Worksheet"
            name="all orders data.xls"
            :before-generate = "startDownload"
            :before-finish   = "finishDownload">
            <i class="fa fa-file-excel-o" aria-hidden="true"></i>
              Export Data
          </download-excel>
        </div>
      </div>
    </div>
  </div>
  <div class="row table-space-top">
      <flash-message></flash-message>
    <div class="col-sm-12">
      <div class="details-table">
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr class="table-title">
                <th>ORDER #</th>
                <th>DATE</th>
                <th>DELIVERY TIME</th>
                <th>STATUS</th>
                <th>TOTAL</th>
                <th>PAYMENT STATUS</th>
                <th>DETAILS</th>
              </tr>
            </thead> 
            <tbody>
              <tr v-for="(order, index) in displayedOrderList" :key="index">
                <td>{{ order.order_no}}</td>
                <td>{{ order.created_at | timeFormate}}</td>
                <td v-if="order.status == orderStatusComplate">{{ order.updated_at | timeFormate}} at {{ order.updated_at | timeAmPm}}</td>
                <td v-else>---</td>
                <td>{{ order.status.replace(/_/g, " ")}}</td>
                <td>${{ order.grand_total | currency}}</td>
                <td> {{order.payment_success? "Successfully": "UnSuccessfully",}}</td>
                <td>
                  <!-- <a href="#" class="table-view-btn" @click.prevent="orderDetails(order.id)">VIEW ORDER</a> -->
                  <router-link class="table-view-btn" :to="{ name: 'orderDetail', params: { id: order.id } }">VIEW ORDER</router-link>
                </td>
              </tr>
              <tr v-show="emptyDataVal && getOderData.length < 1">
                <td colspan="5"><h4>You don't have any record.</h4></td>
              </tr> 
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div class="col-sm-4"></div>
  </div>
  <div class="page top-spce">
    <nav aria-label="pagenavigation" class="text-right">
      <ul class="pagination">
        <li>
          <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li>
          <a href="#" v-for="(pageNumber, index) in pages.slice(page-1, page+5)" :key="index" @click.prevent="page = pageNumber">
            {{pageNumber}}
          </a>
        </li>

        <li>
          <a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import orderStatus from "../../../order-status";
import downloadExcel from "vue-json-excel";
import addressType from "../../../address-type";
export default {
  data() {
    return {
      billingAddressType: addressType.billingAddressType,
      dataForExcel: {
        "Order #": "order_no",
        "Order Date": "created_at",
        "Delivery Date": "updated_at",
        "Order Status": "status",
        "Patient Id": "patient_id",
        "Customer Id": "user_id",
        "Customer Name": "full_name",
        "Customer Email": "customer_email",
        "Customer Shipping Address": "shipping_address",
        "Customer Billing Address": "billing_address",
        "Dispensary Id": "dispensary_id",
        "Dispensary Busniess Name": "busniess_name",
        "Dispensary Address": "dispensary_address",
        "Payment Account Type": "payment_method_type",
        "Payment Success": "payment_success",
        "Payment Transation Id": "Payment_transationId",
        "Sales Tax": "sales_tax",
        "Delivery Fee" : "delivery_fee",
        "Discount Amount" : "discount_amount",
        "Grand Total" : "grand_total",
      },
      json_meta: [
        [
          {
            key: "charset",
            value: "utf-8",
          },
        ],
      ],
      exportDataDiv: false,
      search: {
        OrderStatus: "SCHEDULED",
      },
      exportOrderData: [],
      dataLoader:true,
      orderStatusComplate: orderStatus.complate,
      page: 1,
      perPage: 25,
      pages: [],
      getOderData: [],
      error: null,
      spinnerShow: false,
      emptyDataVal: false
    };
  },

  components:{
    downloadExcel
  },
  computed: {
    displayedOrderList() {
      return this.paginate(this.getOderData);
    },
  },

  watch: {
    getOderData() {
      this.page = 1;
      this.perPage= 25;
      this.pages= [];
      this.setPages();
    },
  },
  created() {
    this.orderData();
  },
  methods: {
    setPages() {
      let numberOfPages = Math.ceil(this.getOderData.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },
     async orderData(OrderStatus = "") {
      var vueInstance = this;
      vueInstance.emptyDataVal=  false;
      vueInstance.exportOrderData = []
      vueInstance.getOderData = []
      let orderData = await fb.orderCollection;
      if(OrderStatus != ""){
        orderData = await orderData.where("status", "==", OrderStatus);
      }
      orderData.orderBy('created_at', 'desc').onSnapshot(function (querySnapshot) {
        var allOrderData = [];

        querySnapshot.forEach((doc) => {
          let data = doc.data();
          allOrderData.push(data);

          let deliveryTime = '-'
          if(data.status == vueInstance.orderStatusComplate)
            deliveryTime =  vueInstance.$options.filters.timeFormate(data.updated_at) + ' '+ vueInstance.$options.filters.timeAmPm(data.updated_at)

          var customerAddressData = data.addresses;
          var customerIndexBilling = customerAddressData.findIndex(item => item.type == vueInstance.billingAddressType);
          let billingData = customerAddressData[customerIndexBilling];
       //   vueInstance.customerPhoneNo = customerAddressData[customerIndexBilling].phone_number 
          
           let billingAddressData = '-'
           if(typeof billingData != 'undefined'){
              billingAddressData =  billingData.street_1 
              if(billingData.street_2  != null){
                billingAddressData += ' ' + billingData.street_2 
              }
              billingAddressData += ', '  + billingData.city +' '+ billingData.state + ' '+ billingData.zip_code  
           }
         
          var customerIndexShipping = customerAddressData.findIndex(item => item.type != vueInstance.billingAddressType);
          let shippingData = customerAddressData[customerIndexShipping];
       //   vueInstance.customerPhoneNo = customerAddressData[customerIndexBilling].phone_number 
           let shippingAddressData = '-'
           if(typeof shippingData != 'undefined'){
              shippingAddressData =  shippingData.street_1 
              if(shippingData.street_2  != null){
                shippingAddressData += ' ' + shippingData.street_2 
              }
              shippingAddressData += ', '  + shippingData.city +' '+ shippingData.state + ' '+ shippingData.zip_code  
           }



          var dispensaryAddressData = data.dispensary_data.address;
          let dispensaryAddress = dispensaryAddressData.street1 
          if(dispensaryAddressData.street2 != null){
            dispensaryAddress += ' ' + dispensaryAddressData.street2
          }
          dispensaryAddress += ',' + dispensaryAddressData.city +' '+ dispensaryAddressData.state + ' '+ dispensaryAddressData.zip_code 

          let exportData={
              order_no: data.order_no,
              created_at: vueInstance.$options.filters.longDate(data.created_at),
              updated_at: deliveryTime,
              status: data.status.replace(/_/g, " "),
              user_id: data.user_id,
              patient_id: data.patient_id,
              full_name: data.customer_name,
              customer_email: data.customer_email,
              shipping_address: shippingAddressData,
              billing_address: billingAddressData,
              dispensary_id: data.dispensary_data.user_id,
              busniess_name: data.busniess_name,
              dispensary_address: dispensaryAddress,
              payment_method_type: data.payment_method.accountType || '-',
              payment_success: data.payment_success? "Successfully": "UnSuccessfully",
              Payment_transationId: data.payment_method.transationId,
              sales_tax: vueInstance.$options.filters.currency(data.sales_tax),
              delivery_fee: vueInstance.$options.filters.currency(data.delivery_fee *.70),
              discount_amount: vueInstance.$options.filters.currency(data.discount_amount),
              grand_total: vueInstance.$options.filters.currency(data.grand_total),
              
            }
            vueInstance.exportOrderData.push(exportData)
            vueInstance.exportDataDiv = true
        });
        if(allOrderData.length <= 0){ 
          vueInstance.emptyDataVal=  true;
          vueInstance.dataLoader = false
          return false;
        }
        vueInstance.getOderData = allOrderData;
        vueInstance.dataLoader = false
      });
     },

    async searchData() {
      var vueInstance = this;
      vueInstance.dataLoader = true
      vueInstance.orderData(vueInstance.search.OrderStatus);
    },

    async resetSearchForm() {
      this.search.OrderStatus = "SCHEDULED";
      this.dataLoader = true
      this.orderData();
    },

    startDownload(){
      this.dataLoader = true
    },
    finishDownload(){
      this.dataLoader = false
    }
  },
  
};
</script>

<style scoped>
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #8dc73f;
}

input:focus + .slider {
  box-shadow: 0 0 1px #8dc73f;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.export-btn{
	background: #78ca49;
	padding: 10px 8px;
	color: #fff;
	text-decoration: none;
	margin-top: 27px;
	display: block;
	text-align: center;
}
</style>
