<template>
  <div>
    <header-component />
    <div :class="{ loader: dataLoader }"></div>
    <section id="banner-section">
      <section id="customer-form">
        <div class="container">
          <div class="row">
            <div class="col-lg-10 col-lg-offset-1 animatedParent" data-sequence="100">
              <div class="form-menu">
                <div class="back-menu">
                  <i class="fa fa-long-arrow-left" aria-hidden="true"></i>
                  <router-link :to="{ name: 'adminDashboard' }"
                    >Back to dashboard</router-link
                  >
                </div>
              </div>
              <div class="tab-section animated bounceIn go" data-id="1">
                <!-- Nav tabs -->
                <selectTab :tabPage="this.tab" />

                <!-- Tab panes -->
                <div class="tab-content page-tab bottom-space">
                  <div role="tabpanel" class="tab-pane active" id="deals-ads">
                    <div class="row">
                      <flash-message></flash-message>
                      <div class="col-sm-12">
                        <div class="details-table">
                          <div class="table-responsive">
                            <table class="table">
                              <thead>
                                <tr class="table-title">
                                  <th>EMAIL</th>
                                  <th>CREATED DATE</th>
                                </tr>
                              </thead>
                              <tbody>
                                <tr
                                  v-for="(newsLetterList, index) in newsLetterLists"
                                  :key="index"
                                >
                                  <td>{{ newsLetterList.email }}</td>
                                  <td>
                                    {{ newsLetterList.created_at | timeFormate }}
                                  </td>
                                </tr>
                                <tr v-show="emptyDataVal && getNewsData.length < 1">
                                  <td colspan="2"><h4>You don't have any record.</h4></td>
                                </tr>
                              </tbody>
                            </table>
                          </div>
                        </div>
                      </div>
                      <div class="col-sm-4"></div>
                    </div>
                    <div class="page top-spce">
                      <nav aria-label="pagenavigation" class="text-right">
                        <ul class="pagination">
                          <li>
                            <a
                              href="#"
                              v-if="page != 1"
                              @click.prevent="page--"
                              aria-label="Previous"
                            >
                              <span aria-hidden="true">&laquo;</span>
                            </a>
                          </li>
                          <li>
                            <a
                              href="#"
                              v-for="(pageNumber, index) in pages.slice(
                                page - 1,
                                page + 5
                              )"
                              :key="index"
                              @click.prevent="page = pageNumber"
                              >{{ pageNumber }}</a
                            >
                          </li>

                          <li>
                            <a
                              href="#"
                              @click.prevent="page++"
                              v-if="page < pages.length"
                              aria-label="Next"
                            >
                              <span aria-hidden="true">&raquo;</span>
                            </a>
                          </li>
                        </ul>
                      </nav>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </section>

    <footer-component />
  </div>
</template>

<script>
import selectTab from "../../../components/backEnd/admin/TabComponent";
import * as fb from "../../../firebaseConfig";
export default {
  data() {
    return {
       dataLoader:true,
        page: 1,
        perPage: 10,
        pages: [],
        getNewsData: [],
        error: null,
        spinnerShow: false,
        emptyDataVal: false,
        tab: null,
    };
  },
  components: {
    selectTab,
  },
  computed: {
    newsLetterLists() {
      return this.paginate(this.getNewsData);
    },
  },

  watch: {
    getNewsData() {
      this.page = 1;
      this.perPage= 10;
      this.pages= [];
      this.setPages();
    },
  },
  created() {
    this.customerData();
  },
  methods: {
    setPages() {
      let numberOfPages = Math.ceil(this.getNewsData.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },
     async customerData() {
      var vueInstance = this;

      await fb.newsletterCollection.orderBy("created_at", "desc").get().then(querySnapshot =>{ 
        if (!querySnapshot.empty) {
         
            querySnapshot.forEach((doc) => { 
                vueInstance.getNewsData.push(doc.data());
                vueInstance.dataLoader = false
            });
        }else{
            vueInstance.emptyDataVal=  true;
            vueInstance.dataLoader = false
            return false;
        }
      });

     },
  

  },
};
</script>
