<template>
  <div class="row contact-form">
  <div class="col-sm-2"></div>
  <div class="col-sm-8"><flash-message></flash-message></div>
  <div class="col-sm-2"></div>
    <div class="col-sm-5">
      <div class="contact-title border-bottom">
        <h3>Account Information</h3>
      </div>
      <form @submit.prevent="UpdateCustomerData">
       <!-- <div class="form-group margin-top">
          <input
            type="text"
            placeholder="Username"
            class="form-control"
            v-model.trim="AccountFormData.userName"
            @input="$v.AccountFormData.userName.$touch()"
          />
          <div v-if="$v.AccountFormData.userName.$dirty">
            <p class="text-danger" v-if="!$v.AccountFormData.userName.required">
              Please enter your user name.
            </p>
          </div>
        </div>-->
        <div class="form-group top-spce">
          <input readonly
            v-model.trim="AccountFormData.email"
            @input="$v.AccountFormData.email.$touch()"
            class="form-control"
            placeholder="Email Address *"
            type="email"
          />
          <div v-if="$v.AccountFormData.email.$dirty">
            <p class="text-danger" v-if="!$v.AccountFormData.email.required">
              Please enter your email address.
            </p>
            <p class="text-danger" v-if="!$v.AccountFormData.email.email">
              Please enter valid email address.
            </p>
          </div>
        </div>
        <div class="acc-top-space">
        <div class="right-title margin-top spaceing">
          <h2>PASSWORD</h2>
        </div>
        <div class="input-group">
          <input v-model.trim="customerPassword"
            class="form-control"
            placeholder="Current Password"
            :type="currentPassword"
          />
          <span class="input-group-btn show-pass">
            <button
              @click="showCurrentPass"
              class="btn btn-default"
              type="button"
            >{{ btnText }}</button>
          </span>
        </div>
        <div class="input-group top-spce">
          <input v-model.trim="customerNewPassword"
            class="form-control"
            placeholder="New Password"
            :type="newPassword"
          />
          <span class="input-group-btn show-pass">
            <button 
              @click="showNewPass"
              class="btn btn-default"
              type="button"
            >{{ btnTextNew }}</button>
          </span>
        </div>
        </div>
        <div class="checkbox">
          <label>
            <input type="checkbox" v-model.trim="AccountFormData.subscribe" />Subscribe to receive email communications
            from Grab-A-Gram
          </label>
        </div>
        
        <div class="buttons-section">
          <div v-if="error" v-html="error">{{error}}</div>
          <button class="btn send-btn save-btn" :disabled="$v.$invalid">
              <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>Save</button>
        </div>
      </form>

      <div class="buttons-section">
        <button class="btn white-btn-new close-acc" @click.prevent="clostAccount">
          <i class="fa fa-ban" aria-hidden="true"></i> CLOSE MY ACCOUNT
        </button>
      </div>
    </div>

    <div class="col-sm-2"></div>
    <personalSetting />
  </div>
</template>

<script>
import {
  required,
  numeric,
  alpha,
  sameAs,
  email,
  requiredIf,
} from "vuelidate/lib/validators";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import personalSetting from '../../../components/backEnd/customer/PersonalInfoComponent'
import axios from "axios";
export default {
  data() {
    return {
        url: this.$store.state.API_URL_ROOT,
      AccountFormData: {
        email: null,
    //    userName: null,
        password: null,
        subscribe: null,
      },
      customerPassword:null,
      customerNewPassword:null,
      spinnerShow: false,
      error: null,
      currentPassword: "password",
      newPassword: "password",
      btnText: "Show Password",
      btnTextNew: "Show Password",
    };
  },
  components: {
      personalSetting,
    },
   validations: {
    AccountFormData: {
      email: {
        required,
        email
      },
    //   userName: {
    //     required,
    //   },
    },
   
  },
  created() {
    this.customerUser();
  },
  methods: { 
    async UpdateAccountData(){
      let dataUpdate = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
        dataUpdate.doc('data_account')
        .update({
          "user.email_communication": this.AccountFormData.subscribe == true ? true : false,
        //  "user.name": this.AccountFormData.userName,
        }) 
        .then((success) => {
          this.flashSuccess("You have successfully update your account information.", {
            timeout: 10000,
          });
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        })
        .catch((err) => {
          this.flashError("There is some problem. Please try again later.", {
            timeout: 10000,
          });
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        });
    }, 

    async UpdateCustomerData() {
      $(".flash__wrapper").empty();
      this.error = null;
      this.spinnerShow = true;
      if (!this.$v.$invalid) {
        $('.btn').prop("disabled", true);
        if (
          this.customerPassword !== "" &&
          this.customerPassword !== null
        ) {
          if (
            this.customerNewPassword !== "" &&
            this.customerNewPassword !== null
          ) {
            if (this.customerNewPassword.length > 5) {
              var vueInstance = this;
              var user = firebase.auth().currentUser;
              let newPassword = vueInstance.customerNewPassword;
              let currentPassword = vueInstance.customerPassword;
              var credential = await firebase.auth.EmailAuthProvider.credential(
                user.email,
                currentPassword
              );
              await user
                .reauthenticateAndRetrieveDataWithCredential(credential)
                .then(function () {
                  user
                    .updatePassword(newPassword)
                    .then(function () {
                      // Update successful.
                    /*  fb.usersCollection.doc(fb.auth.currentUser.uid).update({
                        password: newPassword,
                      });
                    */  
                      vueInstance.UpdateAccountData();
                    })
                    .catch(function (error) {
                      vueInstance.flashError(
                        "There is some problem. Please try again later.",
                        {
                          timeout: 10000,
                        }
                      );
                      $('.btn').prop("disabled", false);
                      vueInstance.spinnerShow = false;
                      return false;
                    });
                })
                .catch(function (error) {
                  vueInstance.flashError(
                    "Your current password does not match with our record.",
                    {
                      timeout: 15000,
                    }
                  );
                  $('.btn').prop("disabled", false);
                  vueInstance.spinnerShow = false;
                  return false;
                });
            } else {
              this.error =
                "<p class='alert alert-danger'>Please enter new password minimum 6 lenght.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
              this.spinnerShow = false;
              $('.btn').prop("disabled", false);
              return false;
            }
          } else {
            this.error =
              "<p class='alert alert-danger'>Please enter your new password.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
            this.spinnerShow = false;
            $('.btn').prop("disabled", false);
            return false;
          }
        } else {
          this.UpdateAccountData();
        }

       
      }
    },

    showCurrentPass() {
      if (this.currentPassword === "password") {
        this.currentPassword = "text";
        this.btnText = "Hide Password!";
      } else {
        this.currentPassword = "password";
        this.btnText = "SHOW Password!";
      }
    },

    showNewPass() {
      if (this.newPassword === "password") {
        this.newPassword = "text";
        this.btnTextNew = "Hide Password!";
      } else {
        this.newPassword = "password";
        this.btnTextNew = "SHOW Password!";
      }
    },
    
    async customerUser() {
       var vueInstance = this;
    
      let dataUpdate = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response');
        dataUpdate.doc('data_account').onSnapshot(function (querySnapshot) {
          var customerData =querySnapshot.data();
          vueInstance.AccountFormData.email = customerData.user.email;
        //  vueInstance.AccountFormData.userName = customerData.user.name;
          vueInstance.AccountFormData.subscribe =customerData.user.email_communication == true ? true : false;
      });
     },

     clostAccount(){
       var close = confirm('Are you sure Want to close your account ?'); 
       if (close == true) {
          axios.get(`${this.url}sendCloaseAccountEmail/${this.AccountFormData.email}`)
            .then((response) => {
                if(response.data.status==0){
                    this.flashSuccess("We have sent an email about close account.", {
                    timeout: 10000,
                  });
                  return ture;
                }
          });
       }
     },
  },

};
</script>

<style scoped>
  
  .input-group-btn.show-pass button {
	margin-top: 0px;
	border-left: 0;
	display: block;
	padding: 9px 15px;
	color: #8dc73f;
	text-transform: uppercase;
	border-radius: 0;
}
</style>
