<template>
	<div>
		<header-component />
		<section id="banner-section">
			<div class="container">
				<div class="row">
					<div class="col-lg-8 col-lg-offset-2 min-height">
						<div class="text-center right-title bounceIn go input-search" data-id="1">
							<h2>Search the Help Section</h2>
							<div class="help-search">
								<input @input="filteredList" v-model="search" autocomplete=off type="text"
									class="form-control" placeholder="Enter Keywords" />
							</div>
						</div>
						<div class="panel-group" :class="{ loader: DataLoader }" id="accordion" role="tablist"
							aria-multiselectable="true">
							<div v-for="(help, index) in helpList" :key="index" class="panel panel-default">
								<div @click="toggleIcon($event)" class="panel-heading" role="tab"
									:id="'headingOne' + index">
									<h4 class="panel-title">
										<a role="button" data-toggle="collapse" data-parent="#accordion"
											:href="'#collapseOne' + index" aria-expanded="true"
											:aria-controls="'collapseOne' + index">
											<i class="more-less glyphicon glyphicon-plus"></i>
											{{ help.title }}
										</a>
									</h4>
								</div>
								<div :id="'collapseOne' + index" class="panel-collapse collapse" role="tabpanel"
									:aria-labelledby="'headingOne' + index">
									<div class="panel-body">{{ help.description }}</div>
								</div>
							</div>
							<div class="page top-spce">
								<nav aria-label="pagenavigation" class="text-right">
									<ul class="pagination">
										<li>
											<a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
												<span aria-hidden="true">&laquo;</span>
											</a>
										</li>
										<li>
											<a href="#" v-for="(pageNumber, index) in pages.slice(page - 1, page + 5)"
												:key="index" @click.prevent="page = pageNumber">{{ pageNumber }}</a>
										</li>

										<li>
											<a href="#" @click.prevent="page++" v-if="page < pages.length"
												aria-label="Next">
												<span aria-hidden="true">&raquo;</span>
											</a>
										</li>
									</ul>
								</nav>
							</div>

						</div>
					</div>
				</div>
				<!-- panel-group -->
			</div>
		</section>

		<footer-component />
	</div>
</template>

<script>
/*eslint-disable */
const $ = window.jQuery = require('jquery');
import * as fb from "../../firebaseConfig";
export default {
	name:'Help',
	data() {
		return {
			search: '',
			helpData: [],
			page: 1,
			perPage: 10,
			pages: [],
			DataLoader: true
		}
	},
	computed: {
		helpList() {
			return this.paginate(this.helpData);
		},
	},
	watch: {
		helpData() {
			this.page = 1;
			this.perPage = 10;
			this.pages = [];
			this.setPages();
		},
	},
	created() {
		this.getHelpData();
	},
	methods: {
		filteredList() {
			if (this.search) {
				this.helpData = this.helpData.filter(helps => {
					return helps.title.toLowerCase().includes(this.search.toLowerCase())
				})
			} else {
				this.helpData = []
				this.getHelpData();
			}
			return false
		},
		setPages() {
			let numberOfPages = Math.ceil(this.helpData.length / this.perPage);
			for (let index = 1; index <= numberOfPages; index++) {
				this.pages.push(index);
			}
		},
		paginate(posts) {
			let page = this.page;
			let perPage = this.perPage;
			let from = page * perPage - perPage;
			let to = page * perPage;
			return posts.slice(from, to);
		},
		toggleIcon(e) {
			$(e.target).find(".more-less").toggleClass('glyphicon-plus glyphicon-minus');
		},

		async getHelpData() {
			var vueInstance = this;
			vueInstance.DataLoader = true
			await fb.helpCollection.get().then(function (querySnapshot) {
				var totalCount = querySnapshot.size;
				if (totalCount > 0) {
					var allHelpData = [];
					querySnapshot.forEach(function (doc) {
						allHelpData.push(doc.data());
					});
					vueInstance.helpData = allHelpData;
					vueInstance.DataLoader = false
				} else {
					vueInstance.DataLoader = false
				}
			})
		},
	},
};
</script>

<style scoped>
.panel-group .panel {
	border-radius: 0;
	box-shadow: none;
	border-color: #eeeeee;
}

.panel-default>.panel-heading {
	padding: 0;
	border-radius: 0;
	color: #212121;
	background-color: #fafafa;
	border-color: #eeeeee;
}

.panel-title {
	font-size: 14px;
}

.panel-title>a {
	display: block;
	padding: 15px;
	text-decoration: none;
}

.more-less {
	float: right;
	color: #212121;
}

.panel-default>.panel-heading+.panel-collapse>.panel-body {
	border-top-color: #eeeeee;
}

.input-search {
	padding-bottom: 35px;
}
</style>
