<template>
  <div class="row contact-form">
      
      <div class="col-sm-2"></div>
      <div class="col-sm-8">
        <flash-message></flash-message>
        <div :class="{ loader:dataLoader }"></div>
      </div>
      <div class="col-sm-2"></div>
      <form @submit.prevent="UpdateDriverData">
        <div class="col-sm-5">
          <div class="contact-title border-bottom">
            <h3>Account Information</h3>
          </div>
          <div class="media-heading top-spce">PERSONAL INFORMATION</div>
          <div class="form-group top-spce">
            <input  v-model.trim="AccountFormData.FirstName" @input="$v.AccountFormData.FirstName.$touch()" type="text" placeholder="First Name" class="form-control">
            <div v-if="$v.AccountFormData.FirstName.$dirty">
                  <p class="text-danger" v-if="!$v.AccountFormData.FirstName.required">Please enter your first name.</p>
              </div>
          </div>
          <div class="form-group">
            <input v-model.trim="AccountFormData.LastName" @input="$v.AccountFormData.LastName.$touch()" class="form-control" placeholder="Last Name	" type="text" />
            <div v-if="$v.AccountFormData.LastName.$dirty">
                <p class="text-danger" v-if="!$v.AccountFormData.LastName.required">Please enter your last name.</p>
            </div>
          </div>
          <div class="form-group">
            <input v-model.trim="AccountFormData.StreetAddress1" @input="$v.AccountFormData.StreetAddress1.$touch()"
              class="form-control"
              placeholder="Street Address"
              type="text"
            />
            <div v-if="$v.AccountFormData.StreetAddress1.$dirty">
              <p class="text-danger" v-if="!$v.AccountFormData.StreetAddress1.required">Please enter your street address.</p>
            </div>
          </div>
          <div class="form-group ">
            <input v-model.trim="AccountFormData.StreetAddress2" type="text" placeholder="Street Address 2" class="form-control">
          </div>
          <div class="form-group">
            <input v-model.trim="AccountFormData.city" @input="$v.AccountFormData.city.$touch()" class="form-control" placeholder="City" type="text" />
            <div v-if="$v.AccountFormData.city.$dirty">
              <p class="text-danger" v-if="!$v.AccountFormData.city.required">Please enter your city.</p>
              <p class="text-danger" v-if="!$v.AccountFormData.city.alpha">Please enter your valid city name.</p>
            </div>
          </div>
          <div class="form-group">
            <div class="row">
              <div class="col-sm-8">
                <select class="form-control" v-model.trim="AccountFormData.state">
                    <option value="">Select State *</option>
                    <option v-for="(state, index) in getStateData" :key="index" :value="state">{{ state }}</option>
                  </select>
                  <div v-if="$v.AccountFormData.state.$dirty">
                <p class="text-danger" v-if="!$v.AccountFormData.state.required">Please select issuing state.</p>
              </div>
              </div>

              <div class="col-sm-4">
                <input v-model.trim="AccountFormData.ZipCode" @input="$v.AccountFormData.ZipCode.$touch()"
                    class="form-control"
                    placeholder="Zip Code"
                    type="text"
                  />
                  <div v-if="$v.AccountFormData.ZipCode.$dirty">
                      <p class="text-danger" v-if="!$v.AccountFormData.ZipCode.required">Please enter zip code.</p>
                      <p class="text-danger" v-if="!$v.AccountFormData.ZipCode.numeric">Please enter valid zip code.</p>
                  </div>
              </div>
            </div>

          </div>
          <div class="form-group">
            <input @keyup="acceptNumber" v-model.trim="AccountFormData.phoneNo" @input="$v.AccountFormData.phoneNo.$touch()" 
                class="form-control"
                placeholder="Mobile Number"
                type="text" 
              />
              <div v-if="$v.AccountFormData.phoneNo.$dirty">
                  <p class="text-danger" v-if="!$v.AccountFormData.phoneNo.required">Please enter your valid phone no.</p>

              </div>
          </div>
          <div class="checkbox">
            <label>
              <input type="checkbox" v-model.trim="AccountFormData.textMessage"> Send me text message notifications
            </label>
          </div>
          <div class="input-group">
            <input v-model.trim="AccountFormData.dob" @input="$v.AccountFormData.dob.$touch()"
                class="form-control calender-date"
                aria-describedby="basic-addon2"
                type="text"
                placeholder="Date of Birth"
                id="dob"
                name="dob"
              />
              <div v-if="$v.AccountFormData.dob.$dirty">
                  <p class="text-danger" v-if="!$v.AccountFormData.dob.required">Please enter your date of birth.</p>
              </div>
            <span class="input-group-addon cuorser" id="basic-addon2"><i class="fa fa-calendar" aria-hidden="true"></i></span>
          </div>

          <div class="checkbox">
            <label>
              <input type="checkbox" v-model.trim="AccountFormData.subscribe"> Subscribe to receive email communications from Grab-A-Gram
            </label>
          </div>

          <div class="media-heading margin-top">PASSWORD</div>
          <div class="form-group">
            <input v-model.trim="driverPassword" class="form-control" :type="currentPassword" placeholder="Current Password">
            <span class="input-group-btn show-pass">
              <button
                @click="showCurrentPass"
                class="btn btn-default"
                type="button"
              >{{ btnText }}</button>
            </span>
          </div>
          <div class="form-group">
            <input v-model.trim="driverNewPassword" class="form-control" :type="newPassword" placeholder="New Password">
            <span class="input-group-btn show-pass">
              <button 
                @click="showNewPass"
                class="btn btn-default"
                type="button"
              >{{ btnTextNew }}</button>
            </span>
          </div>
          <div v-if="error" v-html="error">{{error}}</div>
          <button class="btn send-btn" :disabled="$v.$invalid"><i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>
                Save & ContinueSAVE
          </button>
        </div>
        <div class="col-sm-2"></div>
        <div class="col-sm-5">
          <div class="contact-title border-bottom">
            <h3>Profile Photo</h3>
          </div>
          <div class="user-details">
            <div class="text-center">
              <div class="user-admin">
                <img v-if="AccountFormData.profileImage" :src="AccountFormData.profileImage" class="img-circle" />
              </div>

              <div class="add-photo">
                <div class="upload-photo">
                  <a href="#"  @click.prevent="uploadProfileImage"> <i class="fa fa-upload" aria-hidden="true"></i>
                    UPLOAD PHOTO
                  </a>
                  <input type="file" @change="onProfileImageChange" accept="image/jpeg" id="uploadProfileImage" style="display:none"/>
                </div>
                <div class="dark-btn">
                  <a href="#"> <i class="fa fa-camera" aria-hidden="true"></i>Take A Photo</a>
                </div>
              </div>



            </div>
          </div>

        </div>
      </form>
  </div>
</template>

<script>
import {
  required,
  numeric,
  alpha,
  helpers,
} from "vuelidate/lib/validators";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
export default {
  data() {
    return {
      dataLoader:true,
      getStateData: [],
      AccountFormData: {
        FirstName: null,
        LastName: null,
        StreetAddress1: null,
        StreetAddress2: null,
        city: null,
        state: "",
        ZipCode: null,
        phoneNo: "",
        textMessage: false,
        dob: null,
        subscribe: null,
        profileImage: null,
      },
      profileImageUrl:null,
      driverPassword:null,
      driverNewPassword:null,
      spinnerShow: false,
      error: null,
      currentPassword: "password",
      newPassword: "password",
      btnText: "Show Password",
      btnTextNew: "Show Password",
    };
  },
   validations: {
    AccountFormData: {
      FirstName: {
        required,
      },
      LastName: {
        required,
      },
      StreetAddress1: {
        required,
      },
      city: {
        required,
        alpha:helpers.regex('alpha', /^[a-zA-Z ]*$/),
      },
      state: {
        required,
      },
      ZipCode: {
        required,
        numeric,
      },
      phoneNo: {
        required,
      },
      dob: {
        required,
      },
      profileImage: {
        required,
      },
     
    },
   
  },
  mounted() {
    this.fbState();
    var vm = this;
    $("input[name=dob]").datepicker({ 
      changeMonth: true,
      changeYear: true,
      maxDate: 0,
      onSelect: (dateForm) => {
        vm.AccountFormData.dob = dateForm;
      },
    });
    },
  created() {
    this.driverUser();
  },
  methods: { 
    async UpdateAccountData(){
      var vueInstance = this;
      var phoneNumber = vueInstance.AccountFormData.phoneNo.replace(/[^\d]/g, "");
      var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);
      let dataUpdate = await fb.driverCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_account');
        dataUpdate.update({
          "account.first_name" : vueInstance.AccountFormData.FirstName,
          "account.last_name" : vueInstance.AccountFormData.LastName,
          "address.street_1" : vueInstance.AccountFormData.StreetAddress1,
          "address.street_2" : vueInstance.AccountFormData.StreetAddress2,
          "address.city" : vueInstance.AccountFormData.city,
          "address.state" : vueInstance.AccountFormData.state,
          "address.zip_code" : vueInstance.AccountFormData.ZipCode,
          "address.phone_number" : phoneNo,
          "account.date_of_birth" : firebase.firestore.Timestamp.fromDate(new Date(vueInstance.AccountFormData.dob)).toMillis(),
          "address.state" : vueInstance.AccountFormData.state,
          "account.email_communication": vueInstance.AccountFormData.subscribe == true ? true : false,
          "account.message_notification": vueInstance.AccountFormData.textMessage == true ? true : false,
        }) 
        .then((success) => {
          if(vueInstance.profileImageUrl){ 
            vueInstance.uploadImageAsPromise(vueInstance.profileImageUrl).then((url) => {
              dataUpdate.update({
                "profile_image" : url
              })
            });
          }
          this.flashSuccess("You have successfully update your account information.", {
            timeout: 10000,
          });
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        })
        .catch((err) => {
          console.log(err);
          this.flashError("There is some problem. Please try again later.", {
            timeout: 10000,
          });
          $('.btn').prop("disabled", false);
          this.spinnerShow = false;
        });
        var element = document.getElementsByClassName("row contact-form");
        var top = element.offsetTop;
        window.scrollTo(0, top);
    }, 

    async uploadImageAsPromise(imageDataUrl) {
      let userId = fb.auth.currentUser.uid;
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      const type = imageDataUrl.type;
      return new Promise(function (resolve, reject) {
      var storageRef = firebase
          .storage()
          .ref(`driverProfile/${userId}/${currentTime}.`+type)
          .put(imageDataUrl, {
              contentType: type,
          });
        storageRef.then((data) => {
          data.ref.getDownloadURL().then((url) => {
            resolve(url);
          });
        });
      });
    },

    async UpdateDriverData() {
      $(".flash__wrapper").empty();
      this.error = null;
      this.spinnerShow = true;
      if (!this.$v.$invalid) {
        $('.btn').prop("disabled", true); 
        if (this.driverPassword !== "" && this.driverPassword !== null) {
          if (this.driverNewPassword !== "" && this.driverNewPassword !== null) {
            if (this.driverNewPassword.length > 5) {
              var vueInstance = this;
              var user = firebase.auth().currentUser;
              let newPassword = vueInstance.driverNewPassword;
              let currentPassword = vueInstance.driverPassword;
              var credential = await firebase.auth.EmailAuthProvider.credential(
                user.email,
                currentPassword
              );
              await user.reauthenticateAndRetrieveDataWithCredential(credential).then(function () {
                  user.updatePassword(newPassword).then(function () {
                      // Update successful.
                    /*  fb.usersCollection.doc(fb.auth.currentUser.uid).update({
                        password: newPassword,
                      });
                    */  
                      vueInstance.UpdateAccountData();
                    })
                    .catch(function (error) {
                      vueInstance.flashError(
                        "There is some problem. Please try again later.",
                        {
                          timeout: 10000,
                        }
                      );
                      $('.btn').prop("disabled", false);
                      vueInstance.spinnerShow = false;
                      return false;
                    });
                })
                .catch(function (error) {
                  vueInstance.flashError(
                    "Your current password does not match with our record.",
                    {
                      timeout: 15000,
                    }
                  );
                  $('.btn').prop("disabled", false);
                  vueInstance.spinnerShow = false;
                  var element = document.getElementsByClassName("row contact-form");
                  var top = element.offsetTop;
                  window.scrollTo(0, top);
                  return false;
                });
            } else {
              this.error =
                "<p class='alert alert-danger'>Please enter new password minimum 6 lenght.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
              this.spinnerShow = false;
              $('.btn').prop("disabled", false);
              return false;
            }
          } else {
            this.error =
              "<p class='alert alert-danger'>Please enter your new password.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
            this.spinnerShow = false;
            $('.btn').prop("disabled", false);
            return false;
          }
        } else {
          this.UpdateAccountData();
        }

       
      }
    },

    showCurrentPass() {
      if (this.currentPassword === "password") {
        this.currentPassword = "text";
        this.btnText = "Hide Password!";
      } else {
        this.currentPassword = "password";
        this.btnText = "SHOW Password!";
      }
    },

    showNewPass() {
      if (this.newPassword === "password") {
        this.newPassword = "text";
        this.btnTextNew = "Hide Password!";
      } else {
        this.newPassword = "password";
        this.btnTextNew = "SHOW Password!";
      }
    },
    
    async driverUser() {
       var vueInstance = this;
    
      let dataUpdate = await fb.driverCollection.doc(fb.auth.currentUser.uid).collection('response');
        dataUpdate.doc('data_account').onSnapshot(function (querySnapshot) {
          var driverData =querySnapshot.data();
          
          vueInstance.AccountFormData.FirstName = driverData.account.first_name;
          vueInstance.AccountFormData.LastName = driverData.account.last_name;
          vueInstance.AccountFormData.StreetAddress1 = driverData.address.street_1;
          vueInstance.AccountFormData.StreetAddress2 = driverData.address.street_2;
          vueInstance.AccountFormData.city = driverData.address.city;
          vueInstance.AccountFormData.state = driverData.address.state;
          vueInstance.AccountFormData.ZipCode = driverData.address.zip_code;

          var cleaned = ('' + driverData.address.phone_number).replace(/\D/g, '')
          var match= cleaned.match(/^(\d{3})(\d{3})(\d{4})$/)
          if (match) {
            vueInstance.AccountFormData.phoneNo= '(' + match[1] + ') ' + match[2] + '-' + match[3]
          }
          var dob  = firebase.firestore.Timestamp.fromMillis(driverData.account.date_of_birth).toDate().toUTCString();
          var dateOfBirth = new Date (dob);
          var day = dateOfBirth.getDate();
          var month = dateOfBirth.getMonth() + 1;
          var year = dateOfBirth.getFullYear()
          vueInstance.AccountFormData.dob = month + '/'+day + '/' + year;
          vueInstance.AccountFormData.profileImage = driverData.profile_image;
          vueInstance.AccountFormData.subscribe =driverData.account.email_communication == true ? true : false;
          vueInstance.AccountFormData.textMessage =driverData.account.message_notification == true ? true : false;
          vueInstance.dataLoader=false
      });
     },
     async fbState() {
        await this.$store.dispatch("getAllState");
        this.getStateData = this.$store.state.GetState[0].state;
    },
    
    uploadProfileImage() {
      $("#uploadProfileImage").trigger("click");
    },
    onProfileImageChange(e) {
      const file = e.target.files[0];
      this.profileImageUrl = file;
      this.AccountFormData.profileImage = URL.createObjectURL(file);
    },
    acceptNumber() {
      var x = this.AccountFormData.phoneNo.replace(/\D/g, "").match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
      this.AccountFormData.phoneNo = !x[2]? x[1]: "(" + x[1] + ") " + x[2] + (x[3] ? "-" + x[3] : "");
    },
  },
};
</script>

<style scoped>
.img-circle {
  max-width: 206px;
  max-height: 206px;
}
</style>