<template>
  <div>
    <header-component />

    <section id="banner-section" class="section">
         <section id="customer-form">
            <div class="container">
               <div class="row">
                  <div class="col-md-3">
                     <div class="left-sidebar">
                        <h4 class="text-uppercase font-weight-bold">Categories</h4>
                        <ul class="list-unstyled left-menu" v-if="allProductCategories.length > 0">
                           <li><a href="#" :class="{ active : activeClass == 0 }" v-if="allProductCategories.length > 0" @click.prevent="filterByCategory">All Products</a> </li>
                           <li v-for="(category, index) in allProductCategories" :key="index">
                              <a :class="{ active : activeClass == category.id }" href="#" @click.prevent="filterByCategory({categoryId:category.id,categoryName:category.category_name})">{{category.category_name}}</a>
                            </li>
                           
                        </ul>
                        <!-- <h4 class="text-uppercase font-weight-bold">Title</h4>
                        <ul class="list-unstyled left-menu">
                           <li><a href="#" class="active">Lore mipsome</a> </li>
                           <li><a href="#">Lore mipsome</a> </li>
                           <li><a href="#">Lore mipsome</a> </li>
                           <li><a href="#">Lore mipsome</a> </li>
                        </ul> -->
                     </div>
                  </div>
                  <component v-bind:is="currentComponent" v-if="dispnesaryData.user_id" :dispesaryId="dispesaryId" :allCategories="allProductCategories" :dispnesaryData="dispnesaryData" :productId="productId"></component>
                  <!-- <component v-bind:is="currentComponent"  v-if="allProductCategories.length > 0" :dispesaryId="dispesaryId" :allCategories="allProductCategories" :dispnesaryData="dispnesaryData" :productId="productId"></component> -->
                  <!-- <allProductData v-if="dispnesaryData && allProductCategories" :dispnesaryData="dispnesaryData" :dispesaryId="dispesaryId"/> -->

               </div>
            </div>
         </section>
      </section>
    <footer-component />
  </div>
</template>

<script>

import evenBus from "../../../../event-bus";
import * as fb from "../../../../firebaseConfig";
import firebase from "firebase";

import allProductData from "../../../../components/backEnd/customer/dispensaryData/AllProductsComponent";
import productDetails from "../../../../components/backEnd/customer/dispensaryData/ProducDetailsComponent";

export default {
    props:['dispesaryId'],
    data() {
        return {
          currentComponent: allProductData,
          productId: null,
          dispnesaryData: {},
          allProductCategories:[],
          activeClass:0,
        };
    },
    components: {
        allProductData,
    },
    created() {
      evenBus.$on("dispensaryProductDetail", (producAutotId) => { 
          this.productId=producAutotId;
          this.currentComponent = productDetails;
      });
      this.dispenaryExist(this.dispesaryId);
      this.allProductCategory();
      // evenBus.$on("dispensaryCategoryData", (categories) => {
      //   this.allProductCategories= categories;
      // });
    },
   
    methods: {
      filterByCategory(categoryData=''){
        evenBus.$emit("categoryFilter", categoryData);
        this.currentComponent = allProductData;
        this.activeClass = 0;
        if(categoryData.categoryId)
          this.activeClass = categoryData.categoryId;
      },
      async dispenaryExist(dispesary_id){ 
          var vueInstance = this;
          if(dispesary_id){ 
              //  let dispesary_id = 'KIdMgulXz0Ubaj01FFUe'
              let query = fb.dispensaryCollection.doc(dispesary_id)
              query.get().then(function(doc) {
                  if (doc.exists) {
                      vueInstance.dispnesaryData = doc.data();
                  } else {
                    vueInstance.flashError("This disapnesay is not available now. please try again.", {
                      timeout: 10000,
                    });
                    vueInstance.$router.push({ name: 'dispensarySearch' })
                  }
              }).catch(function(error) {
                    vueInstance.flashError("This disapnesay is not available now. please try again.", {
                      timeout: 10000,
                    });
                    vueInstance.$router.push({ name: 'dispensarySearch' })
              });
          }else{
              vueInstance.flashError("This disapnesay is not available now. please try again.", {
                timeout: 10000,
              });
              vueInstance.$router.push({ name: 'dispensarySearch' })
          }
      },

      async allProductCategory(){
      var vueInstance = this;
      let categoryCodeQuery = await fb.productCategoriesCollection.orderBy("category_name");

      categoryCodeQuery.get().then(function (querySnapshot) {
        var allCategory = [];

        querySnapshot.forEach((doc) => {

            var categoryCollection = doc.data();
            fb.productCollection.where("categories", "array-contains", {category_id:categoryCollection.id,category_name:categoryCollection.category_name})
              .where("dispensary_id", "==", vueInstance.dispnesaryData.user_id).where("isPublic", "==", true).where("isDeleted", "==", false).get().then(function(categoryDoc) {
              if (!categoryDoc.empty) { 
                  allCategory.push(categoryCollection);
                 //  vueInstance.$store.state.dispensarySelectedCategories.push(categoryCollection);
              } 
          })
        });
  //      evenBus.$emit("dispensaryCategoryData",allCategory);
        vueInstance.allProductCategories = allCategory;
        
        setTimeout(function(){
          if(allCategory.length <= 0)
            vueInstance.emptyDataVal = 0;
        }, 3000);
      });
    },

    },
};
</script>
