<template>
  <div>
    <div class="tab-title">
      <h3 class="text-center"><span>Step 4:</span> License & Vehicle Information</h3>
    </div>
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
      <form @submit.prevent="SubmitDocumentForm">
        <div class="right-title margin-top">
          <h2>Driver's License Information</h2>
        </div>
        <div class="contact-form">
          <div class="form-group">
            <input v-model.trim="DocumnetFormData.driverLicenseNo" @input="$v.DocumnetFormData.driverLicenseNo.$touch()"
              class="form-control"
              placeholder="Drivers License Number *"
              type="text"
            />
             <div v-if="$v.DocumnetFormData.driverLicenseNo.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.driverLicenseNo.required">Please enter your driver license number.</p>
            </div>
          </div>
          <div class="form-group">
            <select class="form-control" v-model.trim="DocumnetFormData.issuingState">
              <option value="">Issuing State *</option>
              <option v-for="(state, index) in getStateData" :key="index" :value="state">{{ state }}</option>
            </select>
             <div v-if="$v.DocumnetFormData.issuingState.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.issuingState.required">Please select issuing state.</p>
            </div>
          </div>

          <div class="input-group">
            <input v-model.trim="DocumnetFormData.expirationDate" @input="$v.DocumnetFormData.expirationDate.$touch()"
              class="form-control calender-date"
              aria-describedby="basic-addon2"
              type="text"
              placeholder="Expiration Date (MM/DD/YYYY) *"
              id="expirationDate"
              name="expirationDate"
            />
            <span class="input-group-addon cuorser" id="basic-addon2"><i class="fa fa-calendar" aria-hidden="true"></i></span>
          </div>

        </div>

        <div class="right-title margin-top spaceing">
          <h2>Vehicle Information </h2>
          <p>Is your vehicle reliable and does it receive regular maintenance?</p>
          <div class="chck-box">
            <label class="radio-inline">
              <input v-model.trim="DocumnetFormData.regularMaintenance" @change="$v.DocumnetFormData.regularMaintenance.$touch()"  id="inlineRadioOptions" value="Yes" type="radio"> Yes
            </label>
            <label class="radio-inline">
              <input v-model.trim="DocumnetFormData.regularMaintenance" @change="$v.DocumnetFormData.regularMaintenance.$touch()"  id="inlineRadioOptions" value="No" type="radio"> No
            </label>
          </div>
        </div>
        <div class="contact-form">
          <div class="form-group" v-if="DocumnetFormData.selectYearCar">
              <select class="form-control" v-model.trim="DocumnetFormData.selectYearCar">
                <option value="" disabled selected>Select Year of Car *</option>
                <option v-for="(yearCar, index) in DocumnetFormData.selectYearCar" :key="index" :value="yearCar">{{ yearCar }}</option>
              </select>
              <div v-if="$v.DocumnetFormData.selectYearCar.$dirty">
                <p class="text-danger" v-if="!$v.DocumnetFormData.selectYearCar.required">Please select year of manufacturer car.</p>
              </div>
          </div>
          <div class="form-group">
            <input v-model.trim="DocumnetFormData.vehicleCompany" @input="$v.DocumnetFormData.vehicleCompany.$touch()"
              class="form-control"
              placeholder="Make *"
              type="text"
            />
            <div v-if="$v.DocumnetFormData.vehicleCompany.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.vehicleCompany.required">Please enter valid Vehicle company.</p>
            </div>

          </div>
          <div class="form-group">

            <input v-model.trim="DocumnetFormData.vehicleModal" @input="$v.DocumnetFormData.vehicleModal.$touch()"
              class="form-control"
              placeholder="Model *"
              type="text"
            />
            <div v-if="$v.DocumnetFormData.vehicleModal.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.vehicleModal.required">Please enter valid Vehicle company.</p>
            </div>
          </div>
          <div class="form-group">
            <select class="form-control" v-model.trim="DocumnetFormData.lincensePlateState">
                <option value="">License Plate State *</option>
                <option v-for="(state, index) in getStateData" :key="index" :value="state">{{ state }}</option>
            </select>
            <div v-if="$v.DocumnetFormData.lincensePlateState.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.lincensePlateState.required">Please select your license plate state.</p>
            </div>
          </div>
          <div class="form-group">
            <input v-model.trim="DocumnetFormData.licensePlateNumber" @input="$v.DocumnetFormData.licensePlateNumber.$touch()"
              class="form-control"
              placeholder="License Plate Number *"
              type="text"
            />
            <div v-if="$v.DocumnetFormData.licensePlateNumber.$dirty">
              <p class="text-danger" v-if="!$v.DocumnetFormData.licensePlateNumber.required">Please enter valid license plate number.</p>
            </div>
          </div>

        </div>


        <div class="right-title margin-top spaceing">
          <h2>Upload Photos</h2>
        </div>
        <div class="contact-form">
          <div class="upload-text">
            <p>Photo of Drivers License. </p>
          </div>
          <p class="doct-content">
            Please upload a photo of your <span>Drivers License </span>. Please  make sure we can read all the letters and numbers on the license. *
          </p>

          <div class="upload-file">
            <div class="upload-photo">
              <a href="javascript:void(0)" @click="driverLicenseImage">
                <i class="fa fa-upload" aria-hidden="true"></i>
                CHOOSE FILE
              </a>
              <input type="file" accept="image/*" @change="onFileChangeDriverLicense" id="recommendationImageUpload" style="display:none" />
              <span id="fileNameDriverLicense">No File Chosen</span>
            </div>
          </div>

          <div class="upload-text">
            <p>PHOTO OF PROOF OF INSURANCE</p>
          </div>
          <p class="doct-content">
            Please upload a photo of your <span>Current car insurance policy</span>. Please <br class="hidden-xs"> make sure we can read all the letters and numbers on the license. *
          </p>
          <div class="upload-file">
            <div class="upload-photo">
              <a href="javascript:void(0)" @click="insuranceImage">
                <i class="fa fa-upload" aria-hidden="true"></i>
                CHOOSE FILE
              </a>
              <input type="file" accept="image/*" @change="onFileChangeInsuranceImagae" id="govermentIdImageUpload" style="display:none" />
              <span id="fileNameInsurance">No File Chosen</span>
            </div>
          </div>

          <div class="upload-text">
            <p>PHOTO OF REGISTRATION</p>
          </div>
          <p class="doct-content">
            Please upload a photo of your <span>current car registration</span>. Please <br class="hidden-xs"> make sure we can read all the letters and numbers on the license. *
          </p>
          <div class="upload-file">
            <div class="upload-photo">
              <a href="javascript:void(0)" @click="registrationImage">
                <i class="fa fa-upload" aria-hidden="true"></i>
                CHOOSE FILE
              </a>
              <input type="file" accept="image/*" @change="onFileChangeRegistration" id="deliveringImageUpload" style="display:none" />
              <span id="fileNameRegistration">No File Chosen</span>
            </div>
          </div>

          <div class="upload-text green-title-new" v-if="!$v.caregiversImageFile.required">
            <p>** FOR CAREGIVERS ONLY **</p>
          </div>
          <p class="doct-content" v-if="!$v.caregiversImageFile.required">
            Please upload a photo of proof of your <span>CURRENT CAREGIVER CERTIFICATE </span>
            (if applicable). Please make sure we can read all of the numbers/letters in the image.
          </p>
          <div class="upload-file" v-if="!$v.caregiversImageFile.required">
            <div class="upload-photo">
              <a  href="javascript:void(0)" @click="caregiversImage"> 
                <i class="fa fa-upload" aria-hidden="true"></i>
                CHOOSE FILE
              </a>
              <input type="file" accept="image/*" @change="onFileChangeCaregivers" id="caregiversImageUpload" style="display:none" />
                <span id="fileNameCaregivers">No File Chosen</span>
            </div>
          </div>
          <div class="border-bottom"></div>
        </div>
        <div class="text-center">
        <div v-if="error" v-html="error">{{error}}</div>
          <button class="btn send-btn save-btn" :disabled="$v.$invalid">
              <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>Save & Continue
            <i class="fa fa-long-arrow-right" aria-hidden="true"></i>
          </button>
        </div>
      </form>
      
      </div>
    </div>
  </div>
</template>

<script>
import { required, minLength } from "vuelidate/lib/validators";
export default {
  data() {
    return {
      getStateData: [],
      image:null,
      DocumnetFormData:{
        driverLicenseNo:null,
        issuingState:"",
        expirationDate:null,
        selectYearCar: "",
        regularMaintenance:"No",
        vehicleCompany:"",
        vehicleModal:null,
        lincensePlateState:"",
        licensePlateNumber:null,
      },
      driverLicenseFile:null,
      insuranceImageFile:null,
      registrationImageFile:null,
      caregiversImageFile:null,

      spinnerShow: false,
      error: null,
    };
  },
  validations: {
    DocumnetFormData: {
      driverLicenseNo: {
        required,
      },
      issuingState: {
        required,
      },
      expirationDate: {
        required,
      },
      selectYearCar: {
        required,
      },
      regularMaintenance: {
        required,
      },
      vehicleCompany: {
        required,
      },
      vehicleModal: {
        required,
      },
      lincensePlateState: {
        required,
      },
      licensePlateNumber: {
        required,
      },
    },
    driverLicenseFile: {
      required,
    },
    insuranceImageFile: {
      required,
    },
     registrationImageFile: {
      required,
    },
   /* caregiversImageFile: {
      required,
    },
    */
    caregiversImageFile: {
      required: function() {
        return this.$store.state.isCaregiverState ? !!this.caregiversImageFile : true;
      },
    },
    
  },
 
  mounted() {
    this.fbState();
    this.SelectedYearCar();
    var vm = this;
    $("input[name=expirationDate]").datepicker({ 
      changeMonth: true,
      changeYear: true,
      minDate: 0,
      onSelect: (dateForm) => {
        vm.DocumnetFormData.expirationDate = dateForm;
      },
    });
    $("input[name=recommendationDate]").datepicker({ 
      changeMonth: true,
      changeYear: true,
      minDate: 0,
      onSelect: (dateForm) => {
        vm.DocumnetFormData.recommendationDate = dateForm;
      },
    });
  },

  methods: {
    driverLicenseImage() {
        $("#recommendationImageUpload").trigger("click");
    },
    onFileChangeDriverLicense(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length) return;
        $('#fileNameDriverLicense').html(files[0].name);
        this.driverLicenseFile=files[0].name;
        this.createImage(files[0], 'driverLicenseImage');
    },
    createImage(file, ketName) {
        var image = new Image();
        var reader = new FileReader();
        var vm = this;

        reader.onload = (e) => {
            let image = e.target.result;
             Vue.set(this.$store.state, ketName, image); 
        };
        reader.readAsDataURL(file);
    },

    insuranceImage() {
        $("#govermentIdImageUpload").trigger("click");
    },
    onFileChangeInsuranceImagae(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length) return;
        $('#fileNameInsurance').html(files[0].name);
        this.insuranceImageFile=files[0].name;
        this.createImage(files[0], 'insuranceImage');
    },
    
    registrationImage() {
        $("#deliveringImageUpload").trigger("click");
    },
    onFileChangeRegistration(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length) return;
        $('#fileNameRegistration').html(files[0].name);
        this.registrationImageFile=files[0].name;
        this.createImage(files[0], 'registrationImage');
    },

    caregiversImage() {
        $("#caregiversImageUpload").trigger("click");
    },
    onFileChangeCaregivers(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length) return;
        $('#fileNameCaregivers').html(files[0].name);
        this.caregiversImageFile=files[0].name;
        this.createImage(files[0], 'caregiversImage');
    },

    async fbState() {
        await this.$store.dispatch("getAllState");
        this.getStateData = this.$store.state.GetState[0].state;
    },
    
    async SubmitDocumentForm() {
      this.error = null;
      $('.btn').prop("disabled", true);
      this.spinnerShow = true;

      if (!this.$v.$invalid) {
        localStorage.setItem('DocumnetFormData', JSON.stringify(this.DocumnetFormData))
        $(".nav-tabs li a[href='#messages']").tab("show");
      } else {
        this.spinnerShow = false;
        $('.btn').prop("disabled", false);
      }
    },

    async SelectedYearCar(){ 
      const currentYear = (new Date()).getFullYear();
      const range = (start, stop, step) => Array.from({ length: (stop - start) / step + 1}, (_, i) => start + (i * step));
      this.DocumnetFormData.selectYearCar = range(currentYear, currentYear - 15, -1)
    },
  },
};
</script>
