<script>
/*eslint-disable */
import firebase from "firebase";
import * as fb from "../../firebaseConfig";
export default {
    data() {
        return {
            loginUser: null,
            headerLogo: this.$store.state.API_URL_ROOT + "images/logo.png",
            role:null,
            totalCartCount:0,
        };
    },
    created() {
        this.loginUser = firebase.auth().currentUser || false;
        this.role = this.$store.state.userProfile.role;

        if(this.loginUser){ 
            if(this.$store.state.userProfile.role==null)
                this.checkExsitUserRole();
            else
                this.getCartCustomerData()    
        }
        
    },
    methods: {
        logout() {
            this.$store.dispatch("logout");
        },
        async checkExsitUserRole(){
            await this.$store.dispatch("fetchUserProfile")   
            this.role = await this.$store.state.userProfile.role;
            await this.getCartCustomerData()
        },
        async getCartCustomerData(){
            var vueInstance = this;
            if(this.role == "Customer"){
                let dataGet = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_orderList').collection('orderList');
                dataGet.onSnapshot((querySnapshot) =>{ 
				    vueInstance.totalCartCount = querySnapshot.size;
                });
            }
        },
    },
};
</script>>
