<template>
<div>
  <div class="row contact-form">
    <div class="contact-title border-bottom">
      <h3>Today's Update</h3>
    </div>
  </div>

  <div class="row contact-form">
   
    <div class="col-sm-4">
        <div class="dash-box">
      <h4>Customers</h4>
      <h2 class="text-green">{{ customerCount}}</h2>
      </div>
    </div>
    <div class="col-sm-4">
        <div class="dash-box">
      <h4>Drivers</h4>
      <h2 class="text-green">{{ driverCount}}</h2>
      </div>
    </div>
    <div class="col-sm-4">
        <div class="dash-box">
      <h4>Dispensaries</h4>
      <h2 class="text-green">{{ dispnesaryCount}}</h2>
      </div>
    </div>
  </div>

  <div class="row  contact-form">
    <div class="col-sm-2"></div>
    <div class="col-sm-4">
        <div class="dash-box">
      <h4>Number Of Orders</h4>
      <h2 class="text-green">{{ totalOrder}}</h2>
      </div>
    </div>
    <div class="col-sm-4">
        <div class="dash-box">
      <h4>Number of Cancelled Orders</h4>
      <h2 class="text-green">{{ cancelOrder}}</h2>
      </div>
    </div>

    <div class="col-sm-1"></div>
 
  </div>
  
</div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import orderStatus from "../../../order-status";

export default {
  data() {
    return {
     driverRole: "Driver",
     customerRole: "Customer",
     dispensaryRole: "Dispensary",
     driverCount: 0,
     customerCount: 0,
     dispnesaryCount: 0,
     totalOrder: 0,
     cancelOrder: 0,
     cancelOrderStatus:orderStatus.cancel,
    };
  },
  created() {
    this.todayUser();
  },
  methods: { 
    
    async todayUser() { 
      var vueInstance = this;
      let currentDate= new Date()
      let todayDate = firebase.firestore.Timestamp.fromDate(new Date(currentDate.getFullYear(), currentDate.getMonth(), currentDate.getDate(),0,0,0,1)).toMillis()
      let driverCountData = await fb.usersCollection.where("role", "==", vueInstance.driverRole).where("created_at", ">=", todayDate).get();
      vueInstance.driverCount = await driverCountData.size

      let customerCountData = await fb.usersCollection.where("role", "==", vueInstance.customerRole).where("created_at", ">=", todayDate).get();
      vueInstance.customerCount = await customerCountData.size

      let dispesnaryCountData = await fb.usersCollection.where("role", "==", vueInstance.dispensaryRole).where("created_at", ">=", todayDate).get();
      vueInstance.dispnesaryCount = await dispesnaryCountData.size

      let totalOrderCountData = await fb.orderCollection.where("created_at", ">=", todayDate).where("payment_success", "==", true).get();
      vueInstance.totalOrder = await totalOrderCountData.size

      let cancelOrderCountData = await fb.orderCollection.where("created_at", ">=", todayDate).where("status", "==", vueInstance.cancelOrderStatus).get();
      vueInstance.cancelOrder = await cancelOrderCountData.size
      

    },
  },

};
</script>

