<template>
  <div class="row">
    <div class="col-lg-10 col-lg-offset-1">
      <div class="tab-title">
        <h2>
          <span></span> Account Settings
        </h2>
      </div>
      <div class="row">
        <div class="col-sm-6">
          <form @submit.prevent="UpdateDispensaryData">
            <div class="account-form">
              <div class="acc-top-space">
                <div class="form-group">
                  <label>Business Name</label>
                  <input
                    class="form-control"
                    type="text"
                    v-model.trim="dispensaryBusinessName"
                    @input="$v.dispensaryBusinessName.$touch()"
                  />
                  <div v-if="$v.dispensaryBusinessName.$dirty">
                    <p
                      class="text-danger"
                      v-if="!$v.dispensaryBusinessName.required"
                    >Please enter your business name.</p>
                  </div>
                </div>
                <div class="form-group">
                  <label>Business License Number</label>
                  <input
                    class="form-control"
                    type="text"
                    v-model.trim="dispensaryLicenseNumber"
                    @input="$v.dispensaryLicenseNumber.$touch()"
                  />
                  <div v-if="$v.dispensaryLicenseNumber.$dirty">
                    <p
                      class="text-danger"
                      v-if="!$v.dispensaryLicenseNumber.required"
                    >Please enter your business license number.</p>
                  </div>
                </div>
              </div>
              <div class="border-bottom"></div>

              <div class="acc-top-space">
                <div class="form-group">
                  <label>First Name</label>
                  <input
                    class="form-control"
                    v-model.trim="dispensaryFirstName"
                    @input="$v.dispensaryFirstName.$touch()"
                    type="text"
                  />
                  <div v-if="$v.dispensaryFirstName.$dirty">
                    <p
                      class="text-danger"
                      v-if="!$v.dispensaryFirstName.required"
                    >Please enter your first name.</p>
                  </div>
                </div>
                <div class="form-group">
                  <label>Last Name</label>
                  <input
                    class="form-control"
                    v-model.trim="dispensaryLastName"
                    @input="$v.dispensaryLastName.$touch()"
                    type="text"
                  />
                  <div v-if="$v.dispensaryLastName.$dirty">
                    <p
                      class="text-danger"
                      v-if="!$v.dispensaryLastName.required"
                    >Please enter your last name.</p>
                  </div>
                </div>
              </div>
              <div class="border-bottom"></div>

              <div class="acc-top-space">
                <div class="form-group">
                  <label>Street Address</label>
                  <input
                    class="form-control"
                    v-model.trim="dispensaryStreetAddress"
                    @input="$v.dispensaryStreetAddress.$touch()"
                    type="text"
                  />
                  <div v-if="$v.dispensaryStreetAddress.$dirty">
                    <p
                      class="text-danger"
                      v-if="!$v.dispensaryStreetAddress.required"
                    >Please enter your street address.</p>
                  </div>
                </div>
                <div class="form-group">
                  <label>Street Address 2</label>
                  <input class="form-control" type="text" v-model.trim="dispensaryStreetAddress2" />
                </div>
                <div class="form-group">
                        <input class="form-control" placeholder="City*" type="text" v-model.trim="dispensaryCity" @input="$v.dispensaryCity.$touch()" />
                        <div v-if="$v.dispensaryCity.$dirty">
                            <p class="text-danger" v-if="!$v.dispensaryCity.required">Please enter your city.</p>
                            <p class="text-danger" v-if="!$v.dispensaryCity.alpha">Please enter your valid city name.</p>
                        </div>
                    </div>
                <div class="form-group">
                  <label>State</label>
                  <select class="form-control" v-model="dispensaryState">
                      
                    <option
                      v-for="(state, index) in getStateData"
                      :key="index"
                      :selected="state === dispensaryState? true : false"
                    >{{ state }}</option>
                  </select>
                </div>
                <div class="form-group">
                  <label>Zip Code</label>
                  <input
                    class="form-control"
                    type="text"
                    v-model.trim="dispensaryZipCode"
                    @input="$v.dispensaryZipCode.$touch()"
                  />
                  <div v-if="$v.dispensaryZipCode.$dirty">
                    <p
                      class="text-danger"
                      v-if="!$v.dispensaryZipCode.required"
                    >Please enter your business name.</p>
                    <p
                      class="text-danger"
                      v-if="!$v.dispensaryZipCode.numeric"
                    >Please enter valid zip code.</p>
                  </div>
                </div>
              </div>
              <div class="border-bottom"></div>

              <div class="acc-top-space">
                <div class="form-group">
                  <label>Mobile Phone Number</label>
                  <input
                    class="form-control"
                    type="text"
                    placeholder="XXX-XXX-XXXX"
                    @keyup="acceptNumber"
                    v-model.trim="dispensaryMobileNumber"
                    @input="$v.dispensaryMobileNumber.$touch()"
                  />
                  <div v-if="$v.dispensaryMobileNumber.$dirty">
                    <p
                      class="text-danger"
                      v-if="!$v.dispensaryMobileNumber.required"
                    >Please enter your valid phone no..</p>
                  </div>
                </div>
                <div class="checkbox">
                  <label>
                    <input type="checkbox" v-model="dispensaryTextNotifications" :checked="this.dispensaryTextNotifications"/> Send me text message notifications
                  </label>
                </div>

                <div class="form-group">
                  <label>Email Address</label>
                  <input class="form-control" readonly type="text" :value="this.dispensaryEmailCommunications"/>
                </div>
                <div class="checkbox">
                  <label>
                    <input type="checkbox" v-model="dispensarySubscribe" :checked="this.dispensarySubscribe" /> Subscribe to receive email communications from Grab-A-Gram
                  </label>
                </div>
              </div>
              <div class="border-bottom"></div>

              <div class="acc-top-space">
                <div class="input-group">
                  <label>Current Password</label>
                  <input
                    :type="currentPassword"
                    v-model.trim="dispensaryPassword"
                    class="form-control"
                    placeholder="Enter your Password"
                  />
                  <span class="input-group-btn show-pass">
                    <button
                      @click="showCurrentPass"
                      class="btn btn-default"
                      type="button"
                    >{{ btnText }}</button>
                  </span>
                </div>
                <div class="input-group top-spce">
                  <label>New Password</label>
                  <input
                    :type="newPassword"
                    v-model.trim="dispensaryNewPassword"
                    class="form-control"
                    placeholder="Enter new password"
                  />
                  <span class="input-group-btn show-pass">
                    <button
                      @click="showNewPass"
                      class="btn btn-default"
                      type="button"
                    >{{ btnTextNew }}</button>
                  </span>
                </div>
                <div v-if="error" v-html="error">{{error}}</div>
              </div>
              <div class="border-bottom"></div>
              <flash-message></flash-message>
              <div class="acc-top-space">
                 <div class="text-left">
                  <button class="btn send-btn save-btn" :disabled="$v.$invalid">
                    <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>SAVE INFORMATION
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
        <div class="col-sm-6"></div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  required,
  numeric,
  alpha,
  sameAs,
  requiredIf,
  helpers
} from "vuelidate/lib/validators";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import axios from "axios";
export default {
  data() {
    return {
      spinnerShow: false,
      dispensaryBusinessName: null,
      dispensaryFirstName: null,
      dispensaryLastName: null,
      dispensaryLicenseNumber: null,
      dispensaryStreetAddress: null,
      dispensaryStreetAddress2: null,
      dispensaryCity: null,
      dispensaryState: null,
      dispensaryZipCode: null,
      dispensaryMobileNumber: null,
      dispensaryMessagenotifications: null,
      dispensaryEmailCommunications: null,
      dispensaryPassword: null,
      dispensaryNewPassword: null,
      dispensarySubscribe: false,
      dispensaryTextNotifications: false,
      firebaseId: null,
      getStateData: [],
      error: null,
      currentPassword: "password",
      newPassword: "password",
      btnText: "Show Password",
      btnTextNew: "Show Password",
    };
  },
  
  validations: {
    
    dispensaryBusinessName: {
      required,
    },
    dispensaryFirstName: {
      required,
    },
    dispensaryLastName: {
      required,
    },
    dispensaryStreetAddress: {
      required,
    },
    dispensaryCity: {
        required,
        alpha:helpers.regex('alpha', /^[a-zA-Z ]*$/),
      },
    dispensaryZipCode: {
      required,
      numeric,
    },
    dispensaryLicenseNumber: {
      required,
    },
    dispensaryMobileNumber: {
      required,
    },
  },
  created() {
    this.dispensaryUser();
    this.fbState();
  },
  methods: {
   
async UpdateAllData(){
  let currentTime = firebase.firestore.Timestamp.now().toMillis();
  var address = this.dispensaryStreetAddress + this.dispensaryStreetAddress2 +
    this.dispensaryCity + this.dispensaryState + this.dispensaryZipCode
    
    var phoneNumber = this.dispensaryMobileNumber.replace(/[^\d]/g, "");
  var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);
           
  let { data } = await axios.get(this.$store.state.googleMapsUrl+`&address=${address}`);
  
  if(data.status=='OK'){
    var geocodedLocation = data.results[0];
    let dataUpdate = await fb.dispensaryCollection.doc(this.firebaseId);
    dataUpdate.update({
      updated_at: currentTime,
      busniess_name: this.dispensaryBusinessName,
      license_number: this.dispensaryLicenseNumber,
      subscribe: this.dispensarySubscribe == true ? true : false,
      text_notifications: this.dispensaryTextNotifications == true ? true : false,
      
      location:  new firebase.firestore.GeoPoint(geocodedLocation.geometry.location.lat, geocodedLocation.geometry.location.lng),  
                                   
      "address.first_name": this.dispensaryFirstName,
      "address.last_name": this.dispensaryLastName,
      "address.phone_number": phoneNo,
      "address.state": this.dispensaryState,
      "address.street1": this.dispensaryStreetAddress,
      "address.street2": this.dispensaryStreetAddress2,
      "address.city": this.dispensaryCity,
      "address.zip_code": this.dispensaryZipCode,
      "address.latitude": geocodedLocation.geometry.location.lat,
      "address.longitude": geocodedLocation.geometry.location.lng,
    }).then((success) => {
    /*  fb.usersCollection.doc(fb.auth.currentUser.uid).update({
        name: this.dispensaryFirstName+' '+this.dispensaryLastName,
      });
    */  
      this.flashSuccess("You have successfully update your profile.", {timeout: 10000});
      this.spinnerShow = false;
      $('.btn').prop("disabled", false);
    }).catch((err) => {
      console.log(err);
      this.flashError("There is some problem. Please try again later1.", {timeout: 10000});
      this.spinnerShow = false;
      $('.btn').prop("disabled", false);
    });
  }else{ 
    this.error ="<p class='alert alert-danger'>Plese enter valid Address .<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
    this.spinnerShow = false;
    $('.btn').prop("disabled", false);
    return false;
  }
}, 

    async UpdateDispensaryData() {
      $('.flash__wrapper').empty();
      this.error = null;
      this.spinnerShow = true;
      $('.btn').prop("disabled", true);
      if (!this.$v.$invalid) {
        if (
          this.dispensaryPassword !== "" &&
          this.dispensaryPassword !== null
        ) {
          if (
            this.dispensaryNewPassword !== "" &&
            this.dispensaryNewPassword !== null
          ) {
            if (this.dispensaryNewPassword.length > 5) {
              var vueInstance = this;
              var user = firebase.auth().currentUser;
              let newPassword = vueInstance.dispensaryNewPassword;
              let currentPassword = vueInstance.dispensaryPassword;
              var credential = await firebase.auth.EmailAuthProvider.credential(
                user.email,
                currentPassword
              );
          await user
                .reauthenticateAndRetrieveDataWithCredential(credential)
                .then(function () {
                  
                  user
                    .updatePassword(newPassword)
                    .then(function () {
                      // Update successful.
                    /*  fb.usersCollection.doc(fb.auth.currentUser.uid).update({
                        password: newPassword
                      });
                    */  
                      vueInstance.UpdateAllData();
                    })
                    .catch(function (error) {
                      vueInstance.flashError(
                        "There is some problem. Please try again later.",
                        {
                          timeout: 10000,
                        }
                      );
                      vueInstance.spinnerShow = false;
                       $('.btn').prop("disabled", false);
                      return false;
                    });
                })
                .catch(function (error) {
                  vueInstance.flashError(
                    "Your current password does not match with our record.",
                    {
                      timeout: 15000,
                    }
                  );
                  vueInstance.spinnerShow = false;
                   $('.btn').prop("disabled", false);
                  return false;
                });
            } else {
              this.error =
                "<p class='alert alert-danger'>Please enter new password minimum 6 lenght.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
              this.spinnerShow = false;
               $('.btn').prop("disabled", false);
              return false;
            }
          } else {
            this.error =
              "<p class='alert alert-danger'>Please enter your new password.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
            this.spinnerShow = false;
             $('.btn').prop("disabled", false);
            return false;
          }
        }else{
          this.UpdateAllData();
           $('.btn').prop("disabled", false);
        }

      }
    },
    showCurrentPass() {
      if (this.currentPassword === "password") {
        this.currentPassword = "text";
        this.btnText = "Hide Password!";
      } else {
        this.currentPassword = "password";
        this.btnText = "SHOW Password!";
      }
    },
    showNewPass() {
      if (this.newPassword === "password") {
        this.newPassword = "text";
        this.btnTextNew = "Hide Password!";
      } else {
        this.newPassword = "password";
        this.btnTextNew = "SHOW Password!";
      }
    },
    acceptNumber() {
      var x = this.dispensaryMobileNumber
        .replace(/\D/g, "")
        .match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
      this.dispensaryMobileNumber = !x[2]
        ? x[1]
        : "(" + x[1] + ") " + x[2] + (x[3] ? "-" + x[3] : "");
    },
    async dispensaryUser() {
      await this.$store.dispatch("getDispensaryUser");
      let dispensaryUserData = await this.$store.state.dispensaryUser;
      this.dispensaryBusinessName = dispensaryUserData.allData.busniess_name;
      this.dispensaryFirstName = dispensaryUserData.allData.address.first_name;
      this.dispensaryLastName = dispensaryUserData.allData.address.last_name;
      this.dispensaryLicenseNumber = dispensaryUserData.allData.license_number;
      this.dispensaryStreetAddress = dispensaryUserData.allData.address.street1;
      this.dispensaryStreetAddress2 =
        dispensaryUserData.allData.address.street2;
      this.dispensaryState = dispensaryUserData.allData.address.state;
      this.dispensaryCity = dispensaryUserData.allData.address.city;
      this.dispensaryZipCode = dispensaryUserData.allData.address.zip_code;
      var cleaned = ('' + dispensaryUserData.allData.address.phone_number).replace(/\D/g, '')
      var match= cleaned.match(/^(\d{3})(\d{3})(\d{4})$/)
      if (match) {
        this.dispensaryMobileNumber= '(' + match[1] + ') ' + match[2] + '-' + match[3]
      }
      //this.dispensaryMobileNumber =dispensaryUserData.allData.address.phone_number;
      this.dispensaryMessagenotifications = dispensaryUserData.allData.email;
      //this.dispensaryEmailCommunications = dispensaryUserData.allData.email;
      this.dispensarySubscribe =
        dispensaryUserData.allData.subscribe == true ? true : false;
      this.dispensaryTextNotifications =
        dispensaryUserData.allData.text_notifications == true ? true : false;
      this.firebaseId = dispensaryUserData.allData.id;
      this.dispensaryEmailCommunications = fb.auth.currentUser.email
    },
    async fbState() {
      await this.$store.dispatch("getAllState");
      this.getStateData = this.$store.state.GetState[0].state;
    },
  },
};
</script>
