<template>
  <div>
    <header-component />

    <section id="banner-section">
      <div class="container">
        <div class="row">
          <div class="col-lg-10 col-lg-offset-1 margin-col-space">
            <div class=" animatedParent" data-sequence='100'>
              <div class="main-page-title animated bounceIn go" data-id="1">
                <added-order-details v-if="showCartLayout" :productOrderListData="productOrderListData" />
              </div>

            </div>
          </div>
        </div>

      </div>
    </section>

    <div class="document-exp-popup">
      <div class="modal fade bs-example-modal-lg" tabindex="-1" role="dialog" aria-labelledby="myLargeModalLabel"
        id="document-exp-pop-up">
        <div class="modal-dialog modal-lg" role="document">
          <div class="modal-content">
            <div class="share-box">
              <div class="text-center">
                <div class="title">
                  <h2>Your Document has Expired</h2>
                </div>
                <div class="verify-title">Your medical recommendation has expired.</div>
                <div class="sent-email">Please <span>update your documents</span></div>
                <div class="sub-heading">
                  The review process takes approximately 48-72 hours.<br class="hidden-xs">
                  Once the new documents are approved, you may complete your order.
                  <div class="pop-up-btn">
                    <div class="row margin-top">
                      <div class="col-lg-10 col-lg-offset-1">
                        <div class="row">

                          <div class="col-sm-1">
                          </div>

                          <div class="col-sm-11">
                            <div class="send-btn btn">
                              <a href="#" @click.prevent="redirectToDocument">UPDATE DOCUMENTS</a>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <footer-component />
  </div>
</template>

<script>
/*eslint-disable */
const $ = window.jQuery = require('jquery');

import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import AddedOrderDetails from '../../../components/backEnd/customer/AddedOrderDetails.vue';
import evenBus from "../../../event-bus";
export default {

  data() {
    return {
      showCartLayout: false,
      productOrderListData: {}
    };
  },
  components: {
    AddedOrderDetails
  },
  created() {
    this.checkIsCartEmpty();
    evenBus.$on("openExipreDocumentModal", () => {
      $('#document-exp-pop-up').modal('show');
    });
  },

  methods: {

    redirectToDocument() {
      $('#document-exp-pop-up').modal('hide');
      this.$router.push({ name: "documentsSetting" })
    },

    async checkIsCartEmpty() {
      var vueInstance = this;
      $(".flash__wrapper").empty();
      let getAddedOrderData = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_orderList');

      getAddedOrderData.get().then((doc) => {
        if (doc.exists) {
          vueInstance.productOrderListData = doc.data()
          getAddedOrderData.collection('orderList').get().then((getProduct) => {
            var totalAddedProductCount = getProduct.size;
            if (totalAddedProductCount <= 0) {
              vueInstance.flashError("Your Cart is empty.Please add product into your cart.", {
                timeout: 10000,
              });
              vueInstance.showCartLayout = true;
              return false
            }
            vueInstance.showCartLayout = true;
          })

        } else {
          vueInstance.showCartLayout = true;
        }
      })

    },
  },
};
</script>
