<template>
  <div>
    <div id="invoice">
      <img :src="logo" width="140px" height="50px" />
      <h1 style="text-align:center;">INVOICE</h1>

      <h2>ORDER #{{ allData.order_no}}</h2>

      <table id="shipping-table" class="table-hide">
        <tbody
          v-for="(address, index ) in this.allData.addresses"
          :key="index"
         
        >
        <template  v-if="address.type!='BILLING'">
          <tr>
            <td>SHIPPING ADDRESS</td>
          </tr>
          <tr>
            <td>{{ allData.customer_name}}</td>
          </tr>
          <tr>
            <td>{{ address.street_1}}</td>
          </tr>
          <tr>
            <td>{{ address.street_2}}</td>
          </tr>
          <tr>
            <td>{{ address.city}}, {{ address.state}} {{ address.zip_code}}</td>
          </tr>
           </template>
        </tbody>
      </table>
      <div class="order-table">
        <div class="signature-section">ORDER DETAILS</div>

        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th style="width: 50%">PRODUCT</th>
                <th style="width: 50%" class="text-right">TOTAL</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(iteam, index) in this.allData.items" :key="index">
                <td>{{ iteam.name}}</td>
                <td>${{ iteam.total_count | currency}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="left-space">
        <div class="order-table">
          <div class="table-responsive">
            <table class="table">
              <thead>
                <tr>
                  <th style="width: 50%">SUBTOTAL</th>
                  <th style="width: 50%" class="text-right">${{ this.allData.sub_total | currency}}</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>DELIVERY FEE</td>
                  <td class="text-right">${{ this.allData.delivery_fee | currency}}</td>
                </tr>

                <tr>
                  <td>SALES TAX</td>
                  <td class="text-right">${{ this.allData.sales_tax | currency}}</td>
                </tr>

                <tr>
                  <td class="total-text">TOTAL</td>
                  <td class="total-text text-right">${{ this.allData.grand_total | currency}}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
   
  </div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import evenBus from "../../../event-bus";
export default {
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      error: null,
      dataId: {
        id: this.id,
      },
      allData: {},
      logo: this.$store.state.API_URL_ROOT + "images/logo.png",
    };
  },
  created() {
    this.getOrderDetail(this.dataId.id);
    evenBus.$on("getInvoice", () => {
      this.printInvoice();
    });
   
  },
 
  methods: {
    async getOrderDetail(id) {
      let dataValues = null;
      //let getDriverName = null;
      await fb.orderCollection
        .doc(id)
        .get()
        .then(function (doc) {
          if (doc.exists) {
            dataValues = doc.data(0);
          } else {
            console.log("No such document!");
          }
        })
        .catch(function (error) {
          console.log("Error getting document:", error);
        });
      this.allData = dataValues;
    },
    async printInvoice() {
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      var pdf = new jsPDF("p", "pt", "letter");
      var source = $("#invoice")[0];
      var specialElementHandlers = {
        "#bypassme": function (element, renderer) {
          return true;
        },
      };
      var margins = {
        top: 10,
        bottom: 60,
        left: 40,
        width: 522,
      };
      pdf.fromHTML(
        source, // HTML string or DOM elem ref.
        margins.left, // x coord
        margins.top,
        {
          // y coord
          width: margins.width, // max width of content on PDF
          elementHandlers: specialElementHandlers,
        },

        function (dispose) {

          pdf.save(currentTime + ".pdf");
        },
        margins
      );
    },
    
  },
};
</script>

<style scoped>
#invoice{
  visibility: hidden;
  height: 0;
  overflow: hidden;
}
</style>