<template>
  <div>
    <div class="tab-title">
      <h3 class="text-center"><span>Step 4:</span> Member Agreement</h3>
    </div>
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
        <div class="contact-form">
          By submitting this customer registration form and becoming a member of
          the Grab-A-Gram collective, I attest under penalty of law that the
          information provided herein is accurate and correct. I further attest
          under penalty of law that I am of legal age in this jurisdiction to
          register as a member of a medical marijuana delivery service, either
          as a patient or caregiver. I further attest under penalty of law that
          I will only use this service for purposes allowed by law and that I
          will not divert delivered medicine and/or any other product(s)
          intended for medicinal use for any non-medical use, use by a
          non-member, or any other illegal purpose. This includes but is not
          limited to allowing unauthorized, illegal use of medicine and resale,
          gifting, or otherwise diverting the medicine from its intended
          recipient. I attest under penalty of law that I am not a member of law
          enforcement ordering or using medicine for the purpose of criminal
          investigation or any other non-medicinal use. I have read and
          understand the above rules and hereby agree to follow these rules. I
          acknowledge that I have had an opportunity to review the Membership
          Rules and Policies, as stated above, and Terms and Conditions of
          delivery service. Failure to comply with Grab-A-Grams Membership
          Rules, Policies, Terms and Conditions and any additional laws, rules
          and regulations may result in membership suspension and/or membership
          termination. Additionally, I hereby authorize the release of my
          medical information concerning my diagnosis, condition or prognosis to
          Grab-A-Gram and its authorized representatives for the limited
          purposes of verifying the validity of my doctor recommendation and for
          purposes of verifying legal compliance by Grab-A-Gram in its
          operations. All such medical releases will be in compliance with all
          applicable laws, rules and regulations and all necessary steps will be
          taken to protect patient privacy.

          <div class="signature-section">E-Signature</div>
          <form @submit.prevent="SubmitSignatureForm">
            <div class="form-group">
              <input v-model.trim="signature" @input="$v.signature.$touch()" class="form-control"
                placeholder="Type Your Full Name to Agree" type="text" />
              <div v-if="$v.signature.$dirty">
                <p class="text-danger" v-if="!$v.signature.required">Please enter your E-Signature.</p>
                <p class="text-danger" v-if="!$v.signature.maxLength">E-Signature must have at most 35 letters.</p>
              </div>
            </div>
            <div v-if="error" v-html="error">{{error}}</div>
            <div class="text-center">
              <button class="btn send-btn save-btn" :disabled="$v.$invalid">
                <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>SUBMIT</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
const $ = window.jQuery = require('jquery');
import { required, maxLength } from "vuelidate/lib/validators";
import * as fb from "../../../firebaseConfig";
import addressType from "../../../address-type";
import firebase from "firebase";
export default {
  data() {
    return {
      signature: null,
      spinnerShow: false,
      error: null,
      deviceType: "Web",
      role: "Customer",
      type: addressType.personalAddressType,
    };
  },
  validations: {
    signature: {
      required,
      maxLength: maxLength(35),
    },
  },
  methods: {
    //Handle waiting to upload each file using promise
    async uploadImageAsPromise(imageDataUrl, userId) {
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      const type = imageDataUrl.split(';')[0].split('/')[1];
      return new Promise(function (resolve, reject) {
        var storageRef = firebase
          .storage()
          .ref(`customerDoc/${userId}/${currentTime}.` + type)
          .putString(imageDataUrl, "data_url", {
            contentType: type,
          });
        storageRef.then((data) => {
          data.ref.getDownloadURL().then((url) => {
            resolve(url);
          });
        });
      });
    },

    async SubmitSignatureForm() {
      var vm = this;
      vm.error = null;
      vm.spinnerShow = true;
      let fullName = JSON.parse(localStorage.getItem("InfoFormData")).FirstName +
        " " + JSON.parse(localStorage.getItem("InfoFormData")).LastName;
      let email = JSON.parse(localStorage.getItem("AccountFormData")).email;
      let password = JSON.parse(localStorage.getItem("AccountFormData")).password;

      if (!vm.$v.$invalid) {

        await vm.$store.dispatch("checkUserExit", {
          email: email,
        });
        let checkExist = vm.$store.state.existUser;
        if (checkExist == false) {
          $('.btn').prop("disabled", true);
          await vm.$store.dispatch("signUp", {
            email: email,
            password: password,
          });
          let signUpData = await vm.$store.state.getSignUpData;
          let userId = signUpData.user.uid;
          /* Logout */
          await fb.auth.signOut()
          let recommendationImage = vm.$store.state.recommendationImage;
          let govermentIdImage = vm.$store.state.govermentIdImage;
          let deliveringImage = vm.$store.state.deliveringImage;

          let dob = JSON.parse(localStorage.getItem("InfoFormData")).dob;
          var dobDate = await firebase.firestore.Timestamp.fromDate(new Date(dob)).toMillis();

          let expirationDate = JSON.parse(localStorage.getItem("DocumnetFormData")).expirationDate;
          var expireDate = await firebase.firestore.Timestamp.fromDate(new Date(expirationDate)).toMillis();

          let recommendationDate = JSON.parse(localStorage.getItem("DocumnetFormData")).recommendationDate;
          var recommendnDate = await firebase.firestore.Timestamp.fromDate(new Date(recommendationDate)).toMillis();

          let document_images = {};
          if (recommendationImage) {
            await vm.uploadImageAsPromise(recommendationImage, userId).then((url) => {
              document_images['doctor_recommendation_letter'] = url;
            });
          }
          if (govermentIdImage) {
            await vm.uploadImageAsPromise(govermentIdImage, userId).then((url) => {
              document_images['government_photo_id'] = url;
            });
          }
          if (deliveringImage) {
            await vm.uploadImageAsPromise(deliveringImage, userId).then((url) => {
              document_images['address_proof'] = url;
            });
          }

          let account = {
            date_of_birth: dobDate,
            e_signature: vm.signature,
            first_name: JSON.parse(localStorage.getItem("InfoFormData")).FirstName,
            last_name: JSON.parse(localStorage.getItem("InfoFormData")).LastName,
            message_notification: JSON.parse(localStorage.getItem("InfoFormData")).textMessage == true ? true : false,
          }
          let document_info = {
            document_type: JSON.parse(localStorage.getItem("DocumnetFormData")).identification,
            expiration_date: expireDate,
            identity_number: JSON.parse(localStorage.getItem("DocumnetFormData")).driverLicenseNo,
            issuing_state: JSON.parse(localStorage.getItem("DocumnetFormData")).issuingState,
            patient_id: JSON.parse(localStorage.getItem("DocumnetFormData")).patientId,
            recommendation_letter_expiration_date: recommendnDate,
          }
          let user = {
            email: email,
            email_communication: JSON.parse(localStorage.getItem("AccountFormData")).subscribe == true ? true : false,
            //    name:JSON.parse(localStorage.getItem("AccountFormData")).userName, 
            //    password:password, 
            //    role:vm.role, 
            user_id: userId,
          }
          var phoneNumber = JSON.parse(localStorage.getItem("InfoFormData")).phoneNo.replace(/[^\d]/g, "");
          var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6);

          let addresses = {
            city: JSON.parse(localStorage.getItem("InfoFormData")).city,
            latitude: JSON.parse(localStorage.getItem("InfoFormData")).latitude,
            longitude: JSON.parse(localStorage.getItem("InfoFormData")).longitude,
            phone_number: phoneNo,
            state: JSON.parse(localStorage.getItem("InfoFormData")).state,
            street_1: JSON.parse(localStorage.getItem("InfoFormData")).StreetAddress1,
            street_2: JSON.parse(localStorage.getItem("InfoFormData")).StreetAddress2,
            zip_code: JSON.parse(localStorage.getItem("InfoFormData")).ZipCode,
            type: vm.type,
          };
          let currentTime = firebase.firestore.Timestamp.now().toMillis();
          let dataInsert = await fb.customerCollection.doc(userId).collection('response');
          dataInsert.doc('data_account').set({
            addresses: addresses,
            account: account,
            document_images: document_images,
            document_info: document_info,
            user: user
          }).then(function () {

            dataInsert.doc('data_address').set({
              addresses: [addresses],
              isPersonAddress: true
            }).then(function () {
              vm.$store.dispatch("UserDataInsert", {
                activated_at: true,
                device_id: "",
                device_mac_id: "",
                device_type: vm.deviceType,
                email: email,
                //  name: fullName,
                password: password,
                role: vm.role,
                user_id: userId,
                created_at: currentTime
              });
              localStorage.removeItem('AccountFormData');
              localStorage.removeItem('InfoFormData');
              localStorage.removeItem('DocumnetFormData');
              Vue.set(vm.$store.state, 'recommendationImage', null);
              Vue.set(vm.$store.state, 'govermentIdImage', null);
              Vue.set(vm.$store.state, 'deliveringImage', null);
              localStorage.clear();
              $('.btn').prop("disabled", false);
              vm.flashSuccess('You are successfully registered. Please log in here.', {
                timeout: 15000,
              });
              return vm.$router.push({ name: 'login' })
            })
          }).catch((err) => {
            $('.btn').prop("disabled", false);
            vm.error = "<p class='alert alert-danger'>There is some went wrong. Please try again later .<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
            vm.spinnerShow = false;
          });
        } else {
          vm.error = "<p class='alert alert-danger'>This email id already exit.Please try other email id.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
          vm.spinnerShow = false;
        }
      } else {
        vm.spinnerShow = false;
      }
    },
  },
};
</script>
