<template>
  <div>
    <header-component />
    <section id="customer-form">
      <div class="container">
        <div class="row">
          <div
            class="col-lg-10 col-lg-offset-1 animatedParent"
            data-sequence="100"
          >
            <div class="tab-section animated bounceIn go" data-id="1">
              <!-- Tab panes -->
              <div class="tab-content page-tab bottom-space">
                <div role="tabpanel" class="tab-pane active" id="deals-ads">
                  <div class="form-menu contact-form">
                    <div class="back-menu">
                      <i class="fa fa-long-arrow-left" aria-hidden="true"></i
                      >
                      <router-link :to="{ name: 'ordersList' }">Back to ORDERS</router-link>
                    </div>
                  </div>
                  <div class="row" v-if="allData.order_no">
                    <div class="col-sm-6">
                      <div class="row">
                        <div class="col-sm-8">
                          <div class="title">
                            <h2>ORDER #{{ allData.order_no }}</h2>
                          </div>
                        </div>
                      </div>
                      <div class="order-number margin-top">
                        Order placed on:
                        <span>{{ allData.created_at | longDate }}</span>
                      </div>
                      <div class="order-number">
                        Order status:
                        <span>{{ allData.status.replace(/_/g, " ") }}</span>
                      </div>
                      <div
                        class="order-number"
                        v-show="allData.confirmation_code"
                      >
                        Confirmation Code:<span>{{
                          allData.confirmation_code
                        }}</span>
                      </div>
                      <div class="order-table">
                        <div class="signature-section">ORDER DETAILS</div>

                        <div class="table-responsive">
                          <table class="table">
                            <thead>
                              <tr>
                                <th style="width: 50%">PRODUCT</th>
                                <th style="width: 50%" class="text-right">
                                  TOTAL
                                </th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr
                                v-for="(iteam, index) in allData.items"
                                :key="index"
                              >
                                <td>{{ iteam.name }}</td>
                                <td class="text-right">
                                  ${{ iteam.total_count | currency }}
                                </td>
                              </tr>
                            </tbody>
                          </table>
                        </div>
                      </div>
                      <div class="left-space">
                        <div class="order-table">
                          <div class="table-responsive">
                            <table class="table">
                              <thead>
                                <tr>
                                  <th style="width: 50%">SUBTOTAL</th>
                                  <th style="width: 50%" class="text-right">
                                    ${{ allData.sub_total | currency }}
                                  </th>
                                </tr>
                              </thead>
                              <tbody>
                                <tr v-if="allData.discount_amount > 0">
                                  <td>DISCOUNT</td>
                                  <td class="text-right">
                                    - ${{ allData.discount_amount | currency }}
                                  </td>
                                </tr>
                                <tr>
                                  <td>DELIVERY FEE</td>
                                  <td class="text-right">
                                    ${{ allData.delivery_fee | currency }}
                                  </td>
                                </tr>
                                <tr v-if="allData.sales_tax > 0">
                                  <td>SALES TAX</td>
                                  <td class="text-right">
                                    ${{ allData.sales_tax | currency }}
                                  </td>
                                </tr>

                                <tr>
                                  <td class="total-text">TOTAL</td>
                                  <td class="text-right total-text">
                                    ${{ allData.grand_total | currency }}
                                  </td>
                                </tr>
                              </tbody>
                            </table>
                          </div>
                        </div>
                        <div class="row bottom-space">
                          <div class="col-sm-6">
                            <div class="type-sub-btn report-btn new-space">
                              <a href="#" @click.prevent="invoice"
                                >DOWNLOAD INVOICE</a
                              >
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="col-sm-1"></div>

                    <div class="col-sm-5">
                      <div class="order-box-section border-box">
                        <div class="signature-section">
                          DELIVERY INFORMATION
                        </div>
                        <div class="border-bottom"></div>
                        <div class="order-place-datails">
                          <span>ORDER PLACED ON: </span>
                          <p class="order-place-date">
                            {{ allData.updated_at | longDate }} at
                            {{ allData.updated_at | timeAmPm }}
                          </p>
                        </div>

                        <div class="order-place-datails">
                          <span>customer details </span>
                          <div class="row">
                            <div class="col-sm-4">
                              <div class="type-sub">Name:</div>
                            </div>
                            <div class="col-sm-8">
                              <div class="time-title">
                                {{ allData.customer_name }}
                              </div>
                            </div>
                          </div>

                          <div class="row">
                            <div class="col-sm-4">
                              <div class="type-sub">Email:</div>
                            </div>
                            <div class="col-sm-8">
                              <div class="time-title">
                                <a
                                  v-bind:href="
                                    'mailto:' + this.allData.customer_email
                                  "
                                  >{{ this.allData.customer_email }}</a
                                >
                              </div>
                            </div>
                          </div>

                          <div class="row">
                            <div class="col-sm-4">
                              <div class="type-sub">Phone:</div>
                            </div>
                            <div class="col-sm-8">
                              <div class="time-title">
                                {{ customerPhoneNo | phoneNoFormate }}
                              </div>
                            </div>
                          </div>

                          <div class="row">
                            <div class="col-sm-4">
                              <div class="type-sub">Patient ID #:</div>
                            </div>
                            <div class="col-sm-8">
                              <div class="time-title">
                                {{ allData.patient_id }}
                              </div>
                            </div>
                          </div>
                        </div>

                        <div
                          class="order-place-datails"
                          v-for="(customerAddress, index) in allData.addresses"
                          :key="index"
                        >
                          <span
                            v-if="customerAddress.type == billingAddressType"
                            >Billing ADDRESS
                          </span>
                          <span v-else>Shipping ADDRESS </span>
                          <p class="order-place-date">
                            {{ customerAddress.street_1 }}
                            {{ customerAddress.street_2 }},
                            {{ customerAddress.city }}
                            {{ customerAddress.state }}
                            {{ customerAddress.zip_code }}
                          </p>
                        </div>
                        <div v-show="htmlProcessingOder">
                          <div class="order-place-datails">
                            <div class="modify-section">
                              <!--<p>
                  Would YOU LIKE TO MODIFY OR CANCEL A
                  <a href="#"> PROCESSING ORDER?</a>
                </p>-->

                              <div class="row">
                                <!--<div class="col-sm-6">
                    <div class="cart-btn modify-btn">
                      <i class="fa fa-pencil" aria-hidden="true"></i>
                      <a href="#" @click.prevent="processingOrder('MODIFY')" class="btn">MODIFY ORDER</a>
                    </div>
                  </div>
                -->
                                <div class="col-sm-6">
                                  <div class="cart-btn cancel-order-btn">
                                    <i class="fa fa-ban" aria-hidden="true"></i>
                                    <a
                                      href="#"
                                      @click.prevent="processingOrder('CANCEL')"
                                      class="btn"
                                      >CANCEL ORDER</a
                                    >
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                          <div v-if="processingError" v-html="processingError">
                            {{ processingError }}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <footer-component />

    <orderInovice :id="this.dataId.id" />
  </div>
</template>

<script>
import orderStatus from "../../../order-status";
import * as fb from "../../../firebaseConfig";
import evenBus from "../../../event-bus";
import orderInovice from "../../../components/backEnd/admin/OrderInvoiceComponent";
import addressType from "../../../address-type";
export default {
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      billingAddressType: addressType.billingAddressType,
      error: null,
      spinnerShow: false,
      dataId: {
        id: this.id,
      },
      allData: {},
      customerPhoneNo: null,
      processingError: null,
      showProcessingOrders: [orderStatus.schedule, orderStatus.accept],
      complatedStatus: orderStatus.complate,
      htmlProcessingOder: false,
    };
  },

  mounted() {
    $(".flash__wrapper").empty();
    const plugin = document.createElement("script");
    plugin.setAttribute(
      "src",
      "//cdnjs.cloudflare.com/ajax/libs/jspdf/1.0.272/jspdf.debug.js"
    );
    plugin.async = true;
    document.head.appendChild(plugin);
  },
  components: {
    orderInovice,
  },
  created() {
    this.getOrderDetail(this.dataId.id);
  },
  methods: {
    async processingOrder(processgOrder) {
      let id = this.dataId.id;
      var vueInstance = this;
      await fb.orderCollection
        .doc(id)
        .get()
        .then(function (doc) {
          if (doc.exists) {
            let data = doc.data(0);
            if (data.status == orderStatus.schedule) {
              if (processgOrder == "CANCEL") {
                if (confirm("Do you really want to cancel this order?")) {
                  let currentTime =
                    firebase.firestore.Timestamp.now().toMillis();
                  var updateRef = fb.orderCollection.doc(id);
                  updateRef
                    .update({
                      status: orderStatus.cancel,
                      updated_at: currentTime,
                    })
                    .then((suucess) => {
                      vueInstance.flashSuccess(
                        `You have successfully canceled your order no. ${data.order_no}`,
                        { timeout: 7000 }
                      );
                      vueInstance.$router.push({name : 'ordersList'});
                    });
                }
              } else {
                //Redirect for modify order
              }
            } else {
              let errorMessage =
                processgOrder == "CANCEL" ? "cancel" : "modify";
              vueInstance.processingError = `<p class='alert alert-danger'>You can't ${errorMessage} order in this status.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>`;
            }
          } else {
            console.log("No such document!");
          }
        })
        .catch(function (error) {
          console.log("Error getting document:", error);
        });
    },

    async getOrderDetail(id) {
      let dataValues = null;
      var vueInstance = this;

      await fb.orderCollection
        .doc(id)
        .get()
        .then(function (doc) {
          if (doc.exists) {
            dataValues = doc.data(0);
          } else {
            console.log("No such document!");
          }
        })
        .catch(function (error) {
          console.log("Error getting document:", error);
        });
      vueInstance.allData = dataValues;

      var customerAddress = dataValues.addresses;
      var customerAddressIndex = customerAddress.findIndex(
        (item) => item.type != "BILLING"
      );
      vueInstance.customerPhoneNo =
        customerAddress[customerAddressIndex].phone_number;

      if (vueInstance.showProcessingOrders.includes(dataValues.status)) {
        vueInstance.htmlProcessingOder = true;
      } else if (dataValues.status == vueInstance.complatedStatus) {
        await fb.dispensaryCollection
          .doc(dataValues.dispensary_data.id)
          .collection("reviews")
          .where("dispensary_id", "==", dataValues.dispensary_data.id)
          .where("order_id", "==", dataValues.id)
          .get()
          .then(function (doc) {
            if (doc.empty) {
              vueInstance.$store.state.dispensaryReviewOpenModal = true;
            }
          });
      }
    },

    invoice() {
      evenBus.$emit("getInvoice");
    },
    productReview() {
      evenBus.$emit("productReviewModal", this.allData);
    },
    dispensaryReview() {
      evenBus.$emit("dispesaryReviewModal", this.allData);
    },
  },
};
</script>
