<template>
  <div>
    <div :class="{ loader:dataLoader }"></div>
      <div class="col-md-6">
        <div class="mb-5 row">
          <div class="col-lg-8 col-lg-offset-2">
              <!-- <form @submit.prevent="searchByProduct"> -->
                <div class="form-group">
                  <input list="searchProduct" @keyup="searchByProduct" id="searchProductName" v-model.trim="searchData" class="form-control search-input search-new" type="text" placeholder="Enter Product Name">
                  <datalist id="searchProduct">
                      <!-- <option v-for="productSearch in productDataSearch" :key="productSearch.id"
                        :value="productSearch.name" :data-value="productSearch.id">
                        {{productSearch.name}}
                      </option> -->
                      <option v-for="productSearch in productDataSearch" :key="productSearch.id"
                        :value="productSearch.name">
                        {{productSearch.name}}
                      </option>
                    </datalist>
                    <!-- <div v-if="$v.searchData.$dirty">
                      <p class="text-danger" v-if="!$v.searchData.required">
                        Please select any product name.
                      </p>
                  </div> -->
                </div>
              <!-- </form>   -->
          </div>
        </div>
        <div class="mb-5" v-if="couponsData.length > 0">
          
          <div class="row">
            <div class="col-md-6">
              <h5 class="float-left text-uppercase">Hot Deals</h5>
            </div>
            <div class="col-md-6">
              <span class="pull-right">
                <router-link :to="{ name: 'dispensaryCouponView', params: { dispesaryId: dispnesaryData.id }}" class="text-uppercase">View All</router-link>
              </span>
            </div>
          </div>  
          <div class="row">
              <div class="col-xs-6 col-sm-3" v-for="(couponData, index) in couponsData" :key="index">
                <div class="deal-text">
                    <span class="sm-text">{{couponData.coupon_code_name}}</span> <span class="per">{{couponData.percentage}}%</span>
                </div>
              </div>
          </div>
        </div>
        <div class="mb-5 row">
          <div class="text-right col-sm-12">
              <span class="font-weight-bold">SORT BY:</span>
              <div class="dropdown d-inline-block sort-dropdown">
                <button class="btn btn-default dropdown-toggle" type="button" id="dropdownMenu1" data-toggle="dropdown" aria-haspopup="true" aria-expanded="true">
                {{sortingText || 'Default'}}
                <span class="caret"></span>
                </button>
                <ul class="dropdown-menu" aria-labelledby="dropdownMenu1">
                    <li><a href="#" @click.prevent="sortingBy('default','Default')">Default</a></li>
                    <li><a href="#" @click.prevent="sortingBy('low','Price: Low to High')">Price: Low to High</a></li>
                    <li><a href="#" @click.prevent="sortingBy('high','Price: High to Low')">Price: High to Low</a></li>
                    <li><a href="#" @click.prevent="sortingBy('up','Rating: Up to Down')">Rating: Up to Down</a></li>
                    <li><a href="#" @click.prevent="sortingBy('down','Rating: Down to Up')">Rating: Down to Up</a></li>
                </ul>
              </div>
          </div>
        </div>
        <flash-message></flash-message>
        <div class="row" v-if="productsData.length>0">
          <div class="col-sm-6 col-md-4" v-for="(checkProductData, index) in productsDataList" :key="index" :index="index">
              <div class="pro-box cuorser" @click.prevent="productDetail(checkProductData.id)">
                <img :src="checkProductData.feature_image" class="mb-2 img-responsive resize-img">
                <div class="p-4 text-center">
                    <div class="green-title"><b>${{checkProductData.sale_price | currency}}</b></div>
                    <div class="rating">
                      <starRatingCount :max="5" :current=checkProductData.ratingCount />
                    </div>
                    <p>{{checkProductData.reviewCount}} {{checkProductData.sale_price}} Reviews</p>
                </div>
              </div>
          </div>
        </div>
        <div class="page top-spce"  v-if="productsData.length> 12">
          <nav aria-label="pagenavigation" class="text-right">
            <ul class="pagination">
              <li>
                <a href="#" v-if="page != 1" @click.prevent="page--" aria-label="Previous">
                  <span aria-hidden="true">&laquo;</span>
                </a>
              </li>
              <li>
                <a
                  href="#"
                  v-for="(pageNumber, index) in pages.slice(page-1, page+5)"
                  :key="index"
                  @click.prevent="page = pageNumber"
                >{{pageNumber}}</a>
              </li>

              <li>
                <a href="#" @click.prevent="page++" v-if="page < pages.length" aria-label="Next">
                  <span aria-hidden="true">&raquo;</span>
                </a>
              </li>
            </ul>
          </nav>
        </div>
    </div>
    <div class="col-md-3" v-if="dispnesaryData">
        <div class="right-sidebar">
          <img :src="dispnesaryData.profile_image" class="img-responsive">
          <div class="rightbox" v-if="dispnesaryData">
              <div class="row">
                <div class="col-sm-10">
                    <h2 class="mt-0 text-uppercase">{{dispnesaryData.busniess_name}}</h2>
                    <div class="mb-5 fw-medium para14">{{dispnesaryData.address.street1}} {{dispnesaryData.address.street2}},  
                          {{dispnesaryData.address.city}},  {{dispnesaryData.address.state}} {{dispnesaryData.address.zip_code}} </div>
                    <div class="row">
                      <div class="col-md-7">
                          <div class="rating">
                            <starRatingCount :max="5" :current="dispnesaryData.rating" />
                          </div>
                      </div>
                      <div class="text-right col-md-4">{{dispensaryTotalReview}} Reviews</div>
                    </div>
                </div>
                <!-- <div class="text-right col-sm-2">
                    <a href="#">
                      <svg xmlns="http://www.w3.org/2000/svg" width="21.143" height="18.5" viewBox="0 0 21.143 18.5">
                          <path id="Icon_awesome-share" data-name="Icon awesome-share" d="M20.8,8.768,13.532,2.492a.992.992,0,0,0-1.639.75V6.547C5.26,6.623,0,7.953,0,14.239A8.442,8.442,0,0,0,3.441,20.6.736.736,0,0,0,4.6,19.834c-1.872-5.988.888-7.578,7.292-7.67v3.63a.992.992,0,0,0,1.639.75L20.8,10.268A.991.991,0,0,0,20.8,8.768Z" transform="translate(0 -2.25)" fill="#a9cc69"/>
                      </svg>
                    </a>
                </div> -->
              </div>
          </div>

          
          <overView v-if="dispnesaryData" :dispnesaryData="dispnesaryData"/>

          
          <div class="border-0 rightbox" v-if="dispensaryReviewData.length > 0">
              <h4 class="text-uppercase font-weight-bold">Customer Reviews</h4>
              <div id="carousel-review" class="carousel slide" data-ride="carousel">
                <!-- Wrapper for slides -->
                <div class="carousel-inner" role="listbox">
                    <div :class="['item', (index === 0 ? 'active' : '')]" v-for="(customerReview, index) in dispensaryReviewData" :key="index">
                      <p class="para14"> {{customerReview.comment}}</p>
                    </div>
                </div>
                <!-- Indicators -->
                <ol class="carousel-indicators" v-if="dispensaryReviewData.length > 1">
                    <li data-target="#carousel-review" data-slide-to="0" class="active"></li>
                    <li data-target="#carousel-review" data-slide-to="1"></li>
                    <li v-if="dispensaryReviewData.length > 2" data-target="#carousel-review" data-slide-to="2"></li>
                </ol>
              </div>
              <span class="pull-right">
                <router-link :to="{ name: 'dispensaryReviewView', params: { dispesaryId: dispnesaryData.id }}" class="text-uppercase">View All</router-link>
              </span>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
//import { required, numeric } from "vuelidate/lib/validators";
import evenBus from "../../../../event-bus";
import starRatingCount from '../../../../components/backEnd/RatingComponent';
import * as fb from "../../../../firebaseConfig";
import firebase from "firebase";

import overView from "../../../../components/backEnd/customer/dispensaryData/OverViewComponent";

export default {
    props:['dispesaryId','dispnesaryData'],
    data() {
        return {
          dataLoader:true,
          page: 1,
          perPage: 12,
          dispensaryReviewData:[],
          dispensaryTotalReview: 0,
          productsData:[],
          backUpProductsData:[],
          productDataSearch:[],
          couponsData:[],
          searchData:'',
          sortingText:'Default',
        };
    },
    // validations: {
    //   searchData: {
    //     required,
    //   },
    // },
    components: {
        overView,
        starRatingCount,
    },
    computed: {
      // productsDataList() { alert(2);
      //     if(this.searchData === '') return this.productsData
      //     return this.productsData.filter(entry => {
      //         return entry.name.toLowerCase().includes(this.searchData.toLowerCase())
      //     })   
      // },
      productsDataList() {
        return this.paginate(this.productsData);
      },
    },
    mounted(){
      evenBus.$on("categoryFilter", (categoryData) => {
        this.dataLoader = false
        if(!categoryData.categoryId)
          categoryData = '';
        this.productDataCategory(categoryData)
      });
    },   

    watch: {
      productsData() {
        this.page = 1;
        this.perPage= 12;
        this.pages= [];
        this.setPages();
      },
    },
    created() {
        $('.flash__wrapper').empty();
      this.customreReviewList();
      this.productDataCategory();
      this.productSearchList();
      this.couponList();
      // evenBus.$on("dispensaryCategoryData", (categories) => {
      //   this.allProductCategories= categories;
      // });
    },
   
    methods: {

      async sortingBy(sortBy,sortingName){
        this.sortingText = sortingName;
      //  this.productDataCategory( '','',sortBy)
        if(sortBy == 'low')
          this.productsData = this.productsData.sort((prev, curr) => prev.sale_price - curr.sale_price);
        else if(sortBy == 'high')
          this.productsData = this.productsData.sort((prev, curr) => curr.sale_price - prev.sale_price );  
        else if(sortBy == 'down')
          this.productsData = this.productsData.sort((prev, curr) => prev.ratingCount - curr.ratingCount);
        else if(sortBy == 'up')
          this.productsData = this.productsData.sort((prev, curr) => curr.ratingCount - prev.ratingCount );  
        else
          this.productsData = this.productsData.sort((prev, curr) => curr.created_at - prev.created_at );  
      },
      
      async productSearchList(){
      
        var vueInstance = this;
        vueInstance.productDataSearch=[];
        var allProductName = [];

        let categoryCodeQuery =  fb.productCollection.where("dispensary_id", "==", this.dispnesaryData.user_id).where("isPublic", "==", true).where("isDeleted", "==", false)
        categoryCodeQuery.get().then(function (querySnapshot) {
    
          querySnapshot.forEach((doc) => {
            allProductName.push(doc.data());
          });
          vueInstance.productDataSearch =allProductName
          
          });

          setTimeout(function(){
          if(allProductName.length <= 0)
            this.emptyProductNameDataVal = 0;
          }, 15000);
      },

      async searchByProduct(){
        
        if(this.searchData === '')  this.productsData = this.backUpProductsData
          this.productsData =this.productsData.filter(entry => 
              entry.name.toLowerCase().includes(this.searchData.toLowerCase())
          ) 
// return false
//         if(this.searchData){
//           var searchingProductName = this.searchData;
//           this.dataLoader = false
//           this.productDataCategory( '',searchingProductName)
//         }
      },

  /*    async searchByProduct(){

      if(this.searchData){
        var searchingProductName = this.productDataSearch;
        if(searchingProductName.some(searchingProductName => searchingProductName.name === this.searchData)){
          var value = $('#searchProductName').val();
          
         var productId =$('#searchProduct [value="' + value + '"]').data('value');
         let categoriesData =  this.productDataSearch.find(x => x.id === productId).categories;
         console.log(categoriesData);
          this.productDataCategory( categoriesData)
         
        }else{
          this.flashError("Sorry, no results found!",{
              timeout: 4000,
            }
          );
        }
      }
    },
    */
      setPages() {
        let numberOfPages = Math.ceil(this.productsData.length / this.perPage);
        for (let index = 1; index <= numberOfPages; index++) {
          this.pages.push(index);
        }
      },
      paginate(posts) {
        let page = this.page;
        let perPage = this.perPage;
        let from = page * perPage - perPage;
        let to = page * perPage;
        return posts.slice(from, to);
      },
      
      async customreReviewList() {
        var vueInstance = this;
        let customreReview = await fb.dispensaryCollection.doc(vueInstance.dispesaryId).collection('reviews').orderBy('created_at', 'desc');
           

        customreReview.onSnapshot(function (querySnapshot) {
          vueInstance.dispensaryTotalReview = querySnapshot.size;
          querySnapshot.forEach((doc) => {
            vueInstance.dispensaryReviewData.push(doc.data());
          });
        
          // setTimeout(()=>{
          //   if(allReviewData.length <= 0)
          //     vueInstance.emptyDataVal = 0;
          // }, 6000);
        });
      },

      async productDataCategory(categoryData = "", productSearchByName = ""){
        $('.flash__wrapper').empty();
        var vueInstance = this;
        vueInstance.productsData=[]; 
        vueInstance.dataLoader = true

        let query = await fb.productCollection.where("dispensary_id", "==", vueInstance.dispnesaryData.user_id).where("isPublic", "==", true).where("isDeleted", "==", false)
        if(categoryData){ 
          vueInstance.sortingText = 'Default';
          query = await query.where("categories", "array-contains", {category_id:categoryData.categoryId,category_name:categoryData.categoryName})
        }
        if(productSearchByName)
          query = await query.where("name", "==", productSearchByName)

        // if(sortBy)
        //   query = await query.orderBy('sale_price', sortBy);
        // else
          query = await query.orderBy('created_at', 'desc');

        query.get().then(function (querySnapshot) {
          if(querySnapshot.size > 0){
            $('.flash__wrapper').empty();
            querySnapshot.forEach((doc) => {
              const data =doc.data();
    
              var id = data.id;
              var reviewData;
              var rating = 0;
              
              try {
                fb.productCollection.doc(id).collection('reviews').get().then(function (reviewQuerySnapshot) {
                  var totalCount = reviewQuerySnapshot.size;
                  if(totalCount >0){
                    
                    reviewQuerySnapshot.forEach(function(docreview) {
                      rating += parseInt(docreview.data().rating);
                    });
                    reviewData={
                      reviewCount:totalCount,
                      ratingCount:rating/totalCount
                    };
                    rating =0;
                  }else{
                    reviewData={
                      reviewCount:0,
                      ratingCount:rating
                    };
                  }
                  vueInstance.productsData.push({...data, ...reviewData });
                  vueInstance.backUpProductsData.push({...data, ...reviewData });
                })
                .catch(function(error) {
                  console.log("Error getting documents: ", error);
                });
                vueInstance.dataLoader = false
              } catch (error) {}
            });
            //  vueInstance.reviewCount  = reviewCountData;
            //  vueInstance.productData=allProducts
              
            //  vueInstance.productSearchList();
          }else{ 
            $('.flash__wrapper').empty();
            vueInstance.flashSuccess("SORRY! There no product available in this disapnesay.", { timeout: 4000 });
            vueInstance.dataLoader = false
          }
        });
      },

      async couponList() {
        var vueInstance = this;
        let currentTime = firebase.firestore.Timestamp.now().toMillis();
        let couponCodeQuery = await fb.couponCollection.
          where("dispensary_user_id","==",this.dispnesaryData.user_id).where("expiration_date",">=",currentTime).where("isPublic","==",true).orderBy("expiration_date").limit(4);

        couponCodeQuery.get().then(function (querySnapshot) {
          
          querySnapshot.forEach((doc) => {
            vueInstance.couponsData.push(doc.data());
          });
        });
      },

      productDetail(producAutotId){ 
        evenBus.$emit('dispensaryProductDetail',producAutotId);
      },

    /*  async productDataCategory(categories = ""){
      
        var vueInstance = this;
        vueInstance.productData=[]; 
      
            let query = await fb.productCollection.where("dispensary_id", "==", vueInstance.dispnesaryData.user_id).where("isPublic", "==", true).where("isDeleted", "==", false)
            if(categories)
              query = await query.where("categories", "array-contains", {category_id:categoryId,category_name:categoryData.category_name})
            
            query.get().then(function (querySnapshot) {
              
              var allProducts=[] ;
              var reviewCountData = [];
              querySnapshot.forEach((doc) => {
                const data =doc.data();
			
			          var id = data.id;
                var reviewData = {};
                var ratingCount = 0;
               
                
                try {
                  fb.productCollection.doc(id).collection('reviews').get().then(function (reviewQuerySnapshot) {
                    var totalCount = reviewQuerySnapshot.size;
                    if(totalCount >0){
                      
                      reviewQuerySnapshot.forEach(function(docreview) {
                        ratingCount += parseInt(docreview.data().rating);
                      });
                      reviewData[id]={
                        count:totalCount,
                        rating:ratingCount/totalCount
                      };
                      reviewCountData.push(reviewData);
                      ratingCount =0;
                    }else{
                      reviewData[id]={
                        count:0,
                        rating:ratingCount
                      };
                      vueInstance.reviewCount.push(reviewData);
                      vueInstance.productData.push(data);
                    }
                  })
                  .catch(function(error) {
                    console.log("Error getting documents: ", error);
                  });
                } catch (error) {}
               
                allProducts.push(data);
              });
            //  vueInstance.reviewCount  = reviewCountData;
            //  vueInstance.productData=allProducts
             
            //  vueInstance.productSearchList();
         
              setTimeout(function(){
              if(vueInstance.productData.length <= 0)
                this.emptyDataVal = 0;
            }, 15000);
        });
        
      },*/

    },
};
</script>
<style scoped>
.resize-img{
  min-height:190px;
  max-height: 190px;
}
</style>
