<template>
<div>
    <div class="tab-title">
        <h3 class="text-center">
            <span>Step 2:</span> Profile Photo
        </h3>
    </div>
    <div class="row">
        <div class="col-lg-8 col-lg-offset-2">
            <div class="user-details">
                <form @submit.prevent="SubmitProfileForm">
                    <div class="text-center">
                        <div class="user-admin">
                            <div id="my_camera"></div>
                            <img v-if="image" :src="image" class="img-circle" width="200px" height="200px" />
                            <img v-else :src="userPhoto" class="img-circle" />
                        </div>
                        <div class="add-photo">
                            <div class="upload-photo">
                                <a href="javascript:void(0)" @click="browserImage">
                                    <i class="fa fa-upload" aria-hidden="true"></i>
                                    UPLOAD PHOTO
                                    <input type="file" @change="onFileChange" id="profileImage" style="display:none" />
                                </a>
                            </div>
                            <!--<div class="dark-btn">
                                <a href="javascript:void(0)" @click.prevent="takeNapshot">
                                    <i class="fa fa-camera" aria-hidden="true"></i>Take A Photo
                                </a>
                            </div>
                            -->
                        </div>
                        <div v-if="error">{{error}}</div>
                        <div class="botton-border"></div>
                        <button class="btn send-btn save-btn" :disabled="$v.$invalid">
                            <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i> SAVE & CONTINUE
                            <i class="fa fa-long-arrow-right" aria-hidden="true"></i>
                        </button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>

<script>
const file_size_validation = (value, vm) => {
   
    if (!value) {
        return true;
    }
    let file = value;
    return file.size < 6291456;
};

import {
    required,
    minLength
} from "vuelidate/lib/validators";
export default {
    data() {
        return {
            userPhoto: this.$store.state.API_URL_ROOT + "images/user-photo.png",
            image: null,
            spinnerShow: false,
            error: null,
            imageData: null,
        };
    },

    validations: {
        image: {
            required,
        },
    },

    mounted() {
        // Webcam.set({
        // width: 320,
        // height: 240,
        // image_format: 'jpeg',
        // jpeg_quality: 90
        // });
        // Webcam.attach( '#my_camera' );
    },
   
    methods: {
        takeNapshot(){
            alert(0);
            Webcam.set({
                width: 320,
                height: 240,
                dest_width: 320, 
                dest_height: 240,
                image_format: 'jpeg',
                jpeg_quality: 90,
                force_flash: true
            });
            Webcam.attach( '#my_camera' );
            
        },
        onFileChange(e) {
            this.userPhoto = null;
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length) return;
            this.imageData = files[0];
            this.createImage(files[0]);
        },
        createImage(file) {
            var image = new Image();
            var reader = new FileReader();
            var vm = this;

            reader.onload = (e) => {
                vm.image = e.target.result;
                Vue.set(this.$store.state, 'dispensaryProfileImage', vm.image); 

            };
            reader.readAsDataURL(file);
        },

        browserImage() {
            $("#profileImage").trigger("click");
        },

        async SubmitProfileForm() {

            this.error = null;
            this.spinnerShow = true;

            if (!this.$v.$invalid) {
                $(".nav-tabs li a[href='#messages']").tab("show");

            } else {
                this.spinnerShow = false;
            }
        },
    },
};
</script>
