<template>
<div>
  <div class="row">
    <div class="col-lg-10 col-lg-offset-1">
      <div class="form-menu contact-form">
        <div class="back-menu">
          <i class="fa fa-long-arrow-left" aria-hidden="true"></i>
            <a href="#" @click.prevent="productListBack">Back to Menu</a>
        </div>
      </div>
      <div class="title">
        <h2>Add Product</h2>
      </div>
      <flash-message></flash-message>
      <div class="row">
        <div class="col-sm-7">
          <div class="gray-form-box">
            <div class="right-title">
              <h2>Product Name & Description</h2>
            </div>

            <div class="contact-form">
              <div class="form-group">
                <label>Product Name*</label>
                <input
                  @input="$v.productData.productName.$touch()"
                  v-model.trim="productData.productName"
                  class="form-control"
                  type="text"
                  placeholder="Blueberry Bomb"
                />
                <div v-if="$v.productData.productName.$dirty">
                  <p
                    class="text-danger"
                    v-if="!$v.productData.productName.required"
                  >Please enter product name.</p>
                </div>
              </div>
              <div class="form-group">
                <label>Product Description*</label>
                <textarea
                  @input="$v.productData.productDescription.$touch()"
                  v-model.trim="productData.productDescription"
                  class="form-control msg-height"
                  rows="8"
                  placeholder="From Palomar Craft Cannabis, the Blueberry Bomb is an indica dominant hybrid. It is a cross of Blueberry and Afghani. Blueberry Bomb tastes of berries while slightly citrus. It tests between 15-20% THC."
                ></textarea>
                <div v-if="$v.productData.productDescription.$dirty">
                  <p
                    class="text-danger"
                    v-if="!$v.productData.productDescription.required"
                  >Please enter product description.</p>
                </div>
              </div>
            </div>
          </div>
          <div class="gray-form-box">
            <div class="right-title">
              <h2>Product Data</h2>
            </div>

            <div class="contact-form">
              <div class="row">
                <div class="col-sm-3">
                  <div class="sku-title">SKU*:</div>
                </div>
                <div class="col-sm-9">
                  <div class="form-group">
                    <input
                      @input="$v.productData.productSKU.$touch()"
                      v-model.trim="productData.productSKU"
                      class="form-control"
                      type="text"
                    />
                    <div v-if="$v.productData.productSKU.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.productData.productSKU.maxLength"
                      >Product SKU must have at most {{$v.productData.productSKU.$params.maxLength.max}} letters.</p>
                      <p
                        class="text-danger"
                        v-if="!$v.productData.productSKU.required"
                      >Please enter product SKU.</p>
                    </div>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-3">
                  <div class="sku-title">Stock Quantity*:</div>
                </div>
                <div class="col-sm-9">
                  <div class="form-group">
                    <input
                      @input="$v.productData.productQuantity.$touch()"
                      v-model.trim="productData.productQuantity"
                      class="form-control"
                      type="text"
                    />
                    <div v-if="$v.productData.productQuantity.$dirty">
                    <p
                        class="text-danger"
                        v-if="!$v.productData.productQuantity.maxLength"
                      >Stock Quantity must have at most {{$v.productData.productQuantity.$params.maxLength.max}} digits.</p>
                      <p
                        class="text-danger"
                        v-if="!$v.productData.productQuantity.required"
                      >Please enter product quantity.</p>
                      <p
                        class="text-danger"
                        v-if="!$v.productData.productQuantity.numeric"
                      >Please enter numeric product quantity.</p>
                    </div>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-3">
                  <div class="sku-title">weights*:</div>
                </div>
                <div class="col-sm-9">
                  <div class="form-group">
                    <select class="form-control" v-model.trim="productData.productweight">
                    <option value="">Select weight*</option>
                    <option :value="{value: 20, name:'GRAM'}">20 GRAM</option>
                    <option :value="{value: 50, name:'GRAM'}">50 GRAM</option>
                    <option :value="{value: 100, name:'GRAM'}">100 GRAM</option>
                    <option :value="{value: 250, name:'GRAM'}">250 GRAM</option>
                    <option :value="{value: 500, name:'GRAM'}">500 GRAM</option>
                  </select>
                    <div v-if="$v.productData.productweight.$dirty">
                    <p
                        class="text-danger"
                        v-if="!$v.productData.productweight.maxLength"
                      >Please select product weight.</p>
                      
                    </div>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-3">
                  <div class="sku-title">Regular Price* ($):</div>
                </div>
                <div class="col-sm-9">
                  <div class="form-group">
                    <input
                      @input="$v.productData.productRegularPrice.$touch()"
                      v-model.trim="productData.productRegularPrice"
                      class="form-control"
                      type="text"
                    />
                    <p
                        class="text-danger"
                        v-if="!$v.productData.productRegularPrice.maxLength"
                      >Regular Price must have at most {{$v.productData.productRegularPrice.$params.maxLength.max}} digits.</p>
                    
                    <div v-if="$v.productData.productRegularPrice.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.productData.productRegularPrice.required"
                      >Please enter product regular price</p>
                      <p
                        class="text-danger"
                        v-if="!$v.productData.productRegularPrice.decimal"
                      >Please enter valid numeric product regular price</p>
                    </div>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-3">
                  <div class="sku-title">Sale Price* ($):</div>
                </div>
                <div class="col-sm-9">
                  <div class="form-group">
                    <input
                      @input="$v.productData.productSalePrice.$touch()"
                      v-model.trim="productData.productSalePrice"
                      class="form-control"
                      type="text"
                    />
                    <div v-if="$v.productData.productSalePrice.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.productData.productSalePrice.required"
                      >Please enter product sale price</p>
                      <p
                        class="text-danger"
                        v-if="!$v.productData.productSalePrice.decimal"
                      >Please enter valid numeric product sale price</p>
                     
                    </div>
                  </div>
                </div>
              </div>
             <!-- <div class="row">
                <div class="col-sm-3">
                  <div class="sku-title">Tax Rate*:</div>
                </div>
                <div class="col-sm-9">
                <div class="form-group">
                    <input
                      @input="$v.productData.productTaxRate.$touch()"
                      v-model.trim="productData.productTaxRate"
                      class="form-control"
                      type="text"
                    />
                    <div v-if="$v.productData.productTaxRate.$dirty">
                      <p
                        class="text-danger"
                        v-if="!$v.productData.productTaxRate.required"
                      >Please enter product tax rate.</p>
                      <p
                        class="text-danger"
                        v-if="!$v.productData.productTaxRate.decimal"
                      >Please enter valid numeric product tax rate.</p>
                    </div>
                  </div>
                </div>
              </div> -->
            </div>
          </div>
          <div class="gray-form-box">
            <div class="right-title">
              <h2>Stain Attributes</h2>
            </div>
            <ul
              class="nav nav-tabs nav-justified table-border-bottom inner-tabs"
              id="pills-tab"
              role="tablist"
            >
              <li class="nav-item active" role="presentation">
                <a
                  class="nav-link"
                  id="pills-home-tab"
                  data-toggle="pill"
                  href="#effects"
                  role="tab"
                  aria-controls="pills-home"
                  aria-selected="true"
                >Effects</a>
              </li>
              <li class="nav-item" role="presentation">
                <a
                  class="nav-link"
                  id="pills-profile-tab"
                  data-toggle="pill"
                  href="#benefits"
                  role="tab"
                  aria-controls="pills-profile"
                  aria-selected="false"
                >Medical benefits</a>
              </li>
              <li class="nav-item" role="presentation">
                <a
                  class="nav-link"
                  id="pills-contact-tab"
                  data-toggle="pill"
                  href="#negative"
                  role="tab"
                  aria-controls="pills-contact"
                  aria-selected="false"
                >Negative</a>
              </li>
            </ul>
            <div class="tab-content" id="pills-tabContent">
              <div
                class="tab-pane active"
                id="effects"
                role="tabpanel"
                aria-labelledby="pills-home-tab"
              >
                <div class="contact-form">
                  <div class="border-bottom pb-4">
                    <div class="row">
                      <div class="col-sm-5">
                        <div class="sku-title font-weight-bold text-uppercase">Attribute</div>
                      </div>
                      <div class="col-sm-7">
                        <div class="sku-title font-weight-bold text-uppercase">Rating 1 to 5</div>(5 = Strongle Agree and 1 = Strongly Diagree)
                      </div>
                    </div>
                  </div>
                  <div class="border-bottom pb-4">
                    <div class="row">
                      <div class="col-sm-5">
                        <div class="sku-title text-uppercase">Happy*</div>
                      </div>
                      <div class="col-sm-7">
                        <select class="form-control" v-model.trim="productData.productEffectHappy">
                          <option value="">Select Happy Rating*</option>
                          <option value=1>1</option>
                          <option value=2>2</option>
                          <option value=3>3</option>
                          <option value=4>4</option>
                          <option value=5>5</option>
                        </select>
                        <div v-if="$v.productData.productEffectHappy.$dirty">
                          <p
                            class="text-danger"
                            v-if="!$v.productData.productEffectHappy.required"
                          >Please enter product happy rate</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="border-bottom pb-4">
                    <div class="row">
                      <div class="col-sm-5">
                        <div class="sku-title text-uppercase">Uplifted*</div>
                      </div>
                      <div class="col-sm-7">
                        <select
                          class="form-control"
                          v-model.trim="productData.productEffectUplifted"
                        >
                          <option value="">Select Uplifted Rating*</option>
                          <option value=1>1</option>
                          <option value=2>2</option>
                          <option value=3>3</option>
                          <option value=4>4</option>
                          <option value=5>5</option>
                        </select>
                        <div v-if="$v.productData.productEffectUplifted.$dirty">
                          <p
                            class="text-danger"
                            v-if="!$v.productData.productEffectUplifted.required"
                          >Please enter product uplifted rate</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="border-bottom pb-4">
                    <div class="row">
                      <div class="col-sm-5">
                        <div class="sku-title text-uppercase">Relaxed*</div>
                      </div>
                      <div class="col-sm-7">
                        <select
                          class="form-control"
                          v-model.trim="productData.productEffectRelaxed"
                        >
                          <option value="">Select Relaxed Rating*</option>
                          <option value=1>1</option>
                          <option value=2>2</option>
                          <option value=3>3</option>
                          <option value=4>4</option>
                          <option value=5>5</option>
                        </select>
                        <div v-if="$v.productData.productEffectRelaxed.$dirty">
                          <p
                            class="text-danger"
                            v-if="!$v.productData.productEffectRelaxed.required"
                          >Please enter product relaxed rate.</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="border-bottom pb-4">
                    <div class="row">
                      <div class="col-sm-5">
                        <div class="sku-title text-uppercase">Euphoric*</div>
                      </div>
                      <div class="col-sm-7">
                        <select
                          class="form-control"
                          v-model.trim="productData.productEffectEuphoric"
                        >
                         <option value="">Select Euphoric Rating*</option>
                          <option value=1>1</option>
                          <option value=2>2</option>
                          <option value=3>3</option>
                          <option value=4>4</option>
                          <option value=5>5</option>
                        </select>
                        <div v-if="$v.productData.productEffectEuphoric.$dirty">
                          <p
                            class="text-danger"
                            v-if="!$v.productData.productEffectEuphoric.required"
                          >Please enter product euphoric rate.</p>
                        </div>
                      </div>
                    </div> 
                  </div>
                </div>
              </div>
              <div
                class="tab-pane"
                id="benefits"
                role="tabpanel"
                aria-labelledby="pills-profile-tab"
              >
                <div style="float: right; margin-top: 3px;margin-bottom: 30px;" class="col-12">
                  <button
                    class="btn send-btn send-btn2 text-right float-right"
                    v-on:click="addRow"
                  >More Add</button>
                </div>

                <div class="border-bottom pb-4">
                  <div class="row">
                    <div class="pb-4 row" v-for="(row,index) in inputs" :key="index">
                      <div class="col-sm-9 mb-5">
                        <input
                          class="form-control pb-2"
                          type="text"
                          :name="row.inputname"
                          v-model="row.value"
                        />
                      </div>
                      <div class="col-sm-3 cursor-pointer">
                        <i class="fa fa-minus-circle cursor-pointer" @click="removeMedical(index)" v-show="index || ( !inputs && inputs.length > 1)"> Remove</i>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div
                class="tab-pane"
                id="negative"
                role="tabpanel"
                aria-labelledby="pills-contact-tab"
              >
              <div style="float: right; margin-top: 3px;margin-bottom: 30px;" class="col-12">
                  <button
                    class="btn send-btn send-btn2 text-right float-right"
                    v-on:click="addRowNegative"
                  >More Add</button>
                </div>

                <div class="border-bottom pb-4">
                  <div class="row">
                    <div class="pb-4 row" v-for="(row,index) in negativeInputs" :key="index">
                      <div class="col-sm-9 mb-5">
                        <input
                          class="form-control pb-2"
                          type="text"
                          :name="row.inputname"
                          v-model="row.value"
                        />
                      </div>
                      <div class="col-sm-3 cursor-pointer">
                        <i class="fa fa-minus-circle cursor-pointer" @click="removeNegative(index)" v-show="index || ( !negativeInputs && negativeInputs.length > 1)"> Remove</i>
                      </div>
                    </div>
                  </div>
                </div>

              </div>
            </div>
          </div>
          <div class="gray-form-box">
            <div class="right-title">
              <h2>Related Products</h2>
            </div>

            <div class="contact-form">
              <div class="row">
                <div class="col-lg-12 mb-5">
                <div v-if="error" v-html="error">{{error}}</div>
                  <div class="input-group">
                    <input
                      v-model="realtedProducts.inputProduct"
                      type="text"
                      class="form-control"
                      placeholder="Add new product"
                      list="browsers"
                      id="realtedProductsId"
                    />
                    <span class="input-group-btn">
                      <button
                        class="btn send-btn send-btn2"
                        @click.prevent="relatedProduct"
                        type="button"
                      >Add</button>
                    </span>
                    <datalist id="browsers">
                <option
                  v-for="relatedproductName in productNameData"
                  :key="relatedproductName.id"
                :data-value="relatedproductName.id"
                :data-image="relatedproductName.feature_image"
                :value="relatedproductName.name"
                >{{relatedproductName.name}}</option> 
              </datalist>
                  </div>
                  <!-- /input-group -->
                </div>
                <!-- /.col-lg-6 -->
              </div>
              <!-- /.row -->
              <div class="row">
                <div
                  class="col-md-6 mb-5"
                  v-for="(realtedproductName, key) in realtedProducts.addProduct"
                  :key="key"
                >
                  <div class="bg-white border related-pro">
                    <i
                      @click.prevent="removeProduct(key)"
                      class="fa fa-times-circle cursor-pointer"
                      aria-hidden="true"
                    ></i>
                    {{realtedproductName.product_name}}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="col-sm-1"></div>

        <div class="col-sm-4">
          <div class="gray-form-box new-from-box">
            <div class="text-center">
              <div class="add-photo">
                <div class="text-center">
                  <button 
                    class="btn save-btn-new"
                    @click.prevent="submitPublish(false)"
                    :disabled="$v.$invalid"
                  >
                    <i aria-hidden="true" class="fa fa-floppy-o"></i>
                    <i v-if="spinnerDraftSave" class="fa fa-spinner fa-spin"></i>
                    SAVE DRAFT
                  </button>
                </div>
                <div class="border-bottom space-border"></div>
               
                <div class="text-center">
                  <button
                    class="btn send-btn save-btn"
                    @click.prevent="submitPublish(true)"
                    :disabled="$v.$invalid"
                  >
                    <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>PUBLISH
                  </button>
                </div>
              </div>
            </div>
          </div>

          <div class="gray-form-box new-from-box">
            <span class="featured-title">Featured Product Image</span>
            <div class="box-pro-img">
              <img
                v-if="featureProductImage.featureImage"
                :src="featureProductImage.featureImage"
                class="img-responsive"
              />
            </div>
            <div class="delete">
              <a href="javascript:void(0)" @click="browserImage" class="text-uppercase">
                ADD PRODUCT IMAGE
                <input
                  type="file"
                  @change="onFileChange"
                  accept="image/jpeg"
                  id="featureImage"
                  style="display:none"
                />
              </a>
            </div>
          </div>

          <div class="gray-form-box new-from-box">
            <span class="featured-title">Product Gallery</span>
            <div class="row">
              <div class="col-md-6" v-for="(image, key) in galleryImages.GallImage" :key="key">
                <div class="box-pro-img">
                  <img ref="image" class="img-responsive resize-img" />
                </div>
              </div>
            </div>
            <div class="delete">
              <a href="#" data-toggle="modal" data-target="#addProductGallery">ADD PRODUCT GALLERY IMAGES</a>
            </div>
          </div>

          <div class="gray-form-box new-from-box">
            <span class="featured-title">Categories</span>
           
           <span v-for="(category,index) in categories.allCategories" :key="index">
           <div class="checkbox">
              <label>
                <input type="checkbox" :value="category" v-model="categories.selectedCategory"/>
                {{category.category_name}} 
              </label>
            </div>
          </span>
          </div>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>

import { required, numeric, minLength, maxLength, decimal } from "vuelidate/lib/validators";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import evenBus from "../../../event-bus";
export default {
  data() {
    return {
      productNameData: [],
      categories:{
        allCategories:[],
        selectedCategory:[],
      },

      inputs: [
        {
          value: "",
        },
      ],
      negativeInputs: [
        {
          value: "",
        },
      ],
      realtedProducts: {
        inputProduct: null,
        addProduct: [],
      },
      featureProductImage: {
        featureImage: null,
        featureImageUpload: null,
      },
      galleryImages: {
        GallImage: [],
        addImage: true,
        removeImage: false,
      },
      productData: {
        productName: null,
        productDescription: null,
        productSKU: null,
        productQuantity: null,
        productweight: "",
        productRegularPrice: null,
        productSalePrice: null,
    //    productTaxRate: null,
        productEffectHappy: "",
        productEffectUplifted: "",
        productEffectRelaxed: "",
        productEffectEuphoric: "",
      },
      spinnerShow: false,
      spinnerDraftSave: false,
       error: null,
    };
  },
  validations: {
    productData: {
      productName: {
        required,
      },
      productDescription: {
        required,
      },
      productSKU: {
        required,
        maxLength: maxLength(6),
      },
      productQuantity: {
        required,
        numeric,
        maxLength: maxLength(6),
      },
      productweight: {
        required,
      },
      productSalePrice: {
        required,
        decimal
      },
    /*  productTaxRate: {
        required,
        decimal
      },
      */
      productRegularPrice: {
        required,
        decimal,
        maxLength: maxLength(6),
      },
      productEffectHappy: {
        required,
      },
      productEffectUplifted: {
        required,
      },
      productEffectRelaxed: {
        required,
      },
      productEffectEuphoric: {
        required,
      },
    },
    galleryImages: {
      GallImage: {
        required,
        minLength: minLength(1), 
      },
    },
    categories: {
      selectedCategory: {
        required,
        minLength: minLength(1),
      },
    },
    featureProductImage: {
      featureImageUpload: {
        required,
      },
    },
  },
  created() {
    this.realatedProductData();
    this.getAllCategories ();
    evenBus.$on('previewGalleryImages', (ModelgalleryImages) => {
      this.onFileChangeGallery(ModelgalleryImages);   
    });
  },
  
  methods: {
    productListBack() {
      evenBus.$emit("backToList");
    },
  
async getAllCategories() { 
      await this.$store.dispatch("fetchCreatedCategories");
      this.categories.allCategories = await this.$store.state.allCategories;
     
    },
    async realatedProductData() {
      await this.$store.dispatch("allrealatedProduct");
      this.productNameData = await this.$store.state.relatedproductName;
    },
    removeMedical(index) {
      this.inputs.splice(index, 1)
    },
    addRow: function () {
      this.inputs.push({
        value: "",
      });
    },
    removeNegative(index) {
      this.negativeInputs.splice(index, 1)
    },
    addRowNegative: function () {
      this.negativeInputs.push({
        value: "",
      });
    },
    browserImageGallery() {
      $("#galleryImage").trigger("click");
    },
    onFileChangeGallery(ModelgalleryImages) {
      let vm = this;
      var selectedFiles = ModelgalleryImages;
      for (let i = 0; i < selectedFiles.length; i++) {
        vm.galleryImages.GallImage.push(selectedFiles[i]);
        let reader = new FileReader();
        reader.onload = (e) => {
          vm.$refs.image[i].src = reader.result;
        };

        reader.readAsDataURL(selectedFiles[i]);
      }
      
    //   for (let i = 0; i < vm.galleryImages.GallImage.length; i++) {
    //     let reader = new FileReader();
    //     reader.onload = (e) => {
    //       vm.$refs.image[i].src = reader.result;
    //     };

    //     reader.readAsDataURL(vm.galleryImages.GallImage[i]);
    //   }
    },

      browserImage() {
      $("#featureImage").trigger("click");
    },
    onFileChange(e) {
      const file = e.target.files[0];
      this.featureProductImage.featureImageUpload = file;
      this.featureProductImage.featureImage = URL.createObjectURL(file);
    },

    relatedProduct() { 
      this.error = null;
      if(this.realtedProducts.inputProduct){
        var addProductName = this.productNameData;
        if(addProductName.some(addProductName => addProductName.name === this.realtedProducts.inputProduct)){
          var value = $('#realtedProductsId').val();
          if(this.realtedProducts.addProduct.some(checkExistProduct => checkExistProduct.product_id === $('#browsers [value="' + value + '"]').data('value'))){
              this.error =
                "<p class='alert alert-danger'>This related product already selected.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
              setTimeout(() => {
                this.error= null;
                this.realtedProducts.inputProduct = null;
              }, 5000); 
              return false

          }else{
            let obj={
              product_id:$('#browsers [value="' + value + '"]').data('value'),
              product_name:this.realtedProducts.inputProduct,
              product_image:$('#browsers [value="' + value + '"]').data('image'),
            }
            this.realtedProducts.addProduct.push(obj);
            this.realtedProducts.inputProduct = null;
          }  
        }else{
          this.error =
                "<p class='alert alert-danger'>Please enter only created product.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
          setTimeout(() => {
            this.error= null;
            this.realtedProducts.inputProduct = null;
          }, 8000); 
        }
      }
    },
    removeProduct: function (index) {
      this.realtedProducts.addProduct.splice(index, 1);
    },

  
    //Handle waiting to upload each file using promise
    async uploadImageAsPromise(imageFile) {
      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      let userId = fb.auth.currentUser.uid;
      return new Promise(function (resolve, reject) {
        var storageRef = firebase
          .storage()
          .ref(`productImages/${userId + currentTime}.jpg`);
        var task = storageRef.put(imageFile).then((data) => {
          data.ref.getDownloadURL().then((url) => {
            resolve(url);
          });
        });
      });
    },
    
    async submitPublish(isPublic) {
      let selectedCategories = [];
        await $.each(this.categories.selectedCategory, function (key, value) {
          if (value.id) {
              var jsonObj = {};
              jsonObj.category_id = value.id;
              jsonObj.category_name = value.category_name;
              selectedCategories.push(jsonObj);
          }
        });
        $('.btn').prop("disabled", true);
      var medicalBenefits=[];
      await $.each( this.inputs, function( key, val ) {
          if(val.value)
          medicalBenefits.push(val.value);
        });
      var negativeAtt=[];
      await $.each( this.negativeInputs, function( key, val ) {
          if(val.value)
          negativeAtt.push(val.value);
        });  
       
      if(isPublic == true){
        this.spinnerShow = true;
      }else{
        this.spinnerDraftSave = true;
      }
      var vueInstance = this;
      if (!this.$v.$invalid) {
        let featureImageUploadUrl = this.featureProductImage.featureImageUpload;
        let currentTime = firebase.firestore.Timestamp.now().toMillis();
        let userId = fb.auth.currentUser.uid;

        let allGallaryImages = [];
        for (var i = 0; i < this.galleryImages.GallImage.length; i++) {
          var imageFile = this.galleryImages.GallImage[i];
          await this.uploadImageAsPromise(imageFile).then((res) => {
            allGallaryImages.push(res);
          });
        }

        
        let dataInsert = await fb.productCollection.doc();
        dataInsert
          .set({ 
            isPublic :isPublic,
            isDeleted: false, 
            name: this.productData.productName,
            description: this.productData.productDescription,
            sku: this.productData.productSKU,
            stock_quantity: parseFloat(this.productData.productQuantity),
            regular_price: parseFloat(this.productData.productRegularPrice),
            sale_price: parseFloat(this.productData.productSalePrice),
        //    tax_rate: parseFloat(this.productData.productTaxRate),
            categories:selectedCategories,
            stain_attributes: {
              effects: {
                happy: this.productData.productEffectHappy,
                uplifted: this.productData.productEffectUplifted,
                relaxed: this.productData.productEffectRelaxed,
                euphoric: this.productData.productEffectEuphoric,
              },
              medical_benefits:medicalBenefits,
              negative: negativeAtt,
            },

          //  related_products: this.realtedProducts.addProduct,
            dispensary_id: userId,
            created_at: currentTime,
            updated_at: currentTime,
          })
          .then(function () { 
            dataInsert.update({
              id: dataInsert.id,
              product_gallery: allGallaryImages,
            });
            fb.productCollection.doc(dataInsert.id).collection('weights').doc().set({
              created_at:currentTime,
              name:vueInstance.productData.productweight.name,
              product_id:dataInsert.id,
              updated_at:currentTime,
              value:vueInstance.productData.productweight.value,
            });
            try{
              $.each(vueInstance.realtedProducts.addProduct, function (key,value) {
                fb.productCollection.doc(dataInsert.id).collection('related_products').doc().set({
                  product_id:value.product_id,
                  product_name:value.product_name,
                  id:dataInsert.id,
                  product_image:value.product_image,
                }); 
            });
            }catch (error) {}

           
            //Hard code review coolection.We need to delete it after create review functionality from Customer section
        /*    fb.productCollection.doc(dataInsert.id).collection('reviews').doc().set({
              created_at:currentTime,
              comment:'Comment web H.C',
              rating:4,
              product_id:dataInsert.id,
              updated_at:currentTime,
              user_id:1,
              user_name:'User Name web'
            });
        */    //End
            vueInstance
              .uploadImageAsPromise(featureImageUploadUrl)
              .then((url) => {
                dataInsert.update({ feature_image: url });

                vueInstance.flashSuccess(
                  "You have successfully created your product.",
                  {
                    timeout: 4000,
                  }
                );
                setTimeout(function () { 
                  evenBus.$emit("backToList");
                }, 5000);
                vueInstance.spinnerShow = false;
                vueInstance.spinnerDraftSave = false;
                $('.btn').prop("disabled", false);
              });
         
          })
          .catch(function (error) {
            $('.btn').prop("disabled", false);
            console.error("Error writing document: ", error);
          });
      }
    },
  },
};
</script>

