<template>
	<div>
		<header-component />
		<div :class="{ loader: dataLoader }"></div>
		<section id="banner-section">
			<div class="container">
				<div class="row">
					<div class="col-lg-10 col-lg-offset-1"><flash-message></flash-message>
						<div class="dispensaries-result-section">
							<div class="row">
								<div class="col-sm-4">
									<div class="title">
										<h2>Dispensaries Near You</h2>
										<div class="sub-heading">{{ customerAddressData }}</div>
									</div>

									<div class="location=map-scroll">

										<div class="search-location"
											v-for="(dispensaryListData, index) in nearDispensaryData" :key="index">
											<div class="address-box">
												<div class="right-title">
													<h2>{{ index + 1 }}.
														<span class="cursor-pointer" @click="dispensaryProfile">
															{{ dispensaryListData.busniess_name }}
														</span>
													</h2>

													<div class="row top-spce">
														<div class="col-sm-4">
															<div class="star cursor-pointer">
																<star-rating :max="5"
																	:current="reviewCount[dispensaryListData.count][dispensaryListData.id].rating"></star-rating>
																<!-- <p class="reviews">({{reviewCount[dispensaryListData.count][dispensaryListData.id].count}} Reviews)</p> -->
															</div>
														</div>
														<div class="col-sm-8">
															<div class="media add-title">
																<div class="media-left">
																	<a href="#">
																		<i class="fa fa-map-marker" aria-hidden="true"></i>
																	</a>
																</div>
																<div class="media-body">
																	<h4 class="media-heading cursor-pointer"
																		@click="getPositionInfo(dispensaryListData, dispensaryListData.count)">
																		{{ dispensaryListData.address.street1 }}
																		{{ dispensaryListData.address.street2 }},<br />
																		{{ dispensaryListData.address.city }},
																		{{ dispensaryListData.address.state }}
																		{{ dispensaryListData.address.zip_code }}
																	</h4>
																</div>
															</div>
														</div>
													</div>
													<p>
														{{ dispensaryListData.about_us }}
													</p>
												</div>
											</div>
										</div>

										<div class="pagination-bottom">
											<div class="row">
												<!--<div class="col-sm-4">
								<div class="page-of">Page 1 of 5</div>
								</div>-->

												<div class="col-sm-10">
													<nav aria-label="Page navigation" class="text-right">
														<ul class="pagination">
															<li>
																<a href="#" v-if="page != 1" @click.prevent="page--"
																	aria-label="Previous">
																	<span aria-hidden="true">&laquo;</span>
																</a>
															</li>
															<li>
																<a href="#"
																	v-for="(pageNumber, index) in pages.slice(page - 1, page + 5)"
																	:key="index"
																	@click.prevent="page = pageNumber">{{ pageNumber }}</a>
															</li>
															<li>
																<a href="#" @click.prevent="page++"
																	v-if="page < pages.length" aria-label="Next">
																	<span aria-hidden="true">&raquo;</span>
																</a>
															</li>
														</ul>
													</nav>
												</div>
											</div>
										</div>
									</div>
								</div>
								<div class="col-sm-8">
									<div class="result-map" v-if="nearDispensaryListData.length > 0">
										<GmapMap :center="startLocation" :zoom="9" map-type-id="terrain"
											style="width: 630px; height: 700px">
											<gmap-info-window :options="infoPosition" :position="infoPosition"
												:opened="infoOpened" @closeclick="infoOpened = false">
												{{ infoContent }}
											</gmap-info-window>

											<gmap-marker v-for="item in nearDispensaryData" :key="item.count"
												:position="getPosition(item)" :clickable="true" :draggable="true"
												@click="getPositionInfo(item, item.count)" />
										</GmapMap>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
		<footer-component />
	</div>
</template>
<script>
/*eslint-disable */
const $ = window.jQuery = require('jquery');

import starRating from "../../components/backEnd/RatingComponent";
import * as fb from "../../firebaseConfig";
import firebase from "firebase";
//import {gmapApi} from 'vue2-google-maps'
export default {
	data() {
		return {
			dataLoader: true,
			searchWithState: this.$route.query.st,
			searchWithCategoryName: this.$route.query.name,
			searchWithCategoryId: this.$route.query.id,
			page: 1,
			perPage: 8,
			pages: [],
			despensaryDistance: 200,
			nearDispensaryListData: [],
			reviewCount: [],
			customerAddressData: null,
			startLocation: {},
			/*		startLocation:{
						lat:39.9727076,
						lng:-82.9689318
					},
			*/
			infoPosition: null,
			infoContent: null,
			infoOpened: false,
			infoCurrentKey: null,
			infoOptions: {
				pixelOffset: {
					width: 0,
					height: -35
				}

			},
			indexCountNumber: 0,
			checkAddedId: [],
		};
	},
	components: {
		starRating,
	},
	computed: {
		nearDispensaryData() {
			return this.paginate(this.nearDispensaryListData);
		},

	},
	watch: {
		nearDispensaryListData() {
			this.page = 1;
			this.perPage = 8;
			this.pages = [];
			this.setPages();
		},
	},
	mounted() {
		$(".flash__wrapper").empty();
	},
	created() {
		this.nearDispensary();
	},
	methods: {

		getPosition: function (marker) {
			return {
				lat: parseFloat(marker.address.latitude),
				lng: parseFloat(marker.address.longitude)
			}
		},

		getPositionInfo: function (marker, key) {

			this.infoPosition = this.getPosition(marker);
			this.infoContent = marker.address.street1 + ' , ' + marker.address.city;

			if (marker.address.street2 != '' && marker.address.street2 != null)
				this.infoContent = marker.address.street1 + ' ' + marker.address.street2 + ', ' + marker.address.city;



			if (this.infoCurrentKey == key) {
				this.infoOpened = !this.infoOpened;
			} else {
				this.infoOpened = true;
				this.infoCurrentKey = key;
			}
		},

		setPages() {
			let numberOfPages = Math.ceil(this.nearDispensaryListData.length / this.perPage);
			for (let index = 1; index <= numberOfPages; index++) {
				this.pages.push(index);
			}
		},
		paginate(posts) {
			let page = this.page;
			let perPage = this.perPage;
			let from = page * perPage - perPage;
			let to = page * perPage;
			return posts.slice(from, to);
		},

		async nearDispensary() {

			var vueInstance = this;
			let searchWithState = vueInstance.searchWithState
			let searchWithCategoryName = vueInstance.searchWithCategoryName
			let searchWithCategoryId = vueInstance.searchWithCategoryId

			let searchData = {};
			searchData['productCategory'] = []
			if (searchWithCategoryName) {
				await vueInstance.$store.dispatch("getDespensaryProduct", {
					category_id: searchWithCategoryId,
					category_name: searchWithCategoryName,
				});
				await vueInstance.getAsPromiseData('fromCategory').then((searchCategoryData) => {

					if (vueInstance.$store.state.allProduct.length > 0) {
						searchData['productCategory'] = vueInstance.$store.state.allProduct;
					}
				});
			}

			await searchData.productCategory.map(function (data, key) {
				fb.dispensaryCollection.where("user_id", "==", data.dispensary_id).get().then(querySnapshot => {
					querySnapshot.forEach(function (doc) {
						let data = doc.data();
						//	if ((vueInstance.checkAddedId.some(addedDispensary => addedDispensary != data.user_id))){ alert(2);
						vueInstance.nearDispensaryList(data)
						let latLong = {
							lat: data.address.latitude,
							lng: data.address.longitude
						}
						vueInstance.startLocation = latLong;
						vueInstance.dataLoader = false
					});
				})

			});
			searchData['dispendaryState'] = []
			if (searchWithState) {
				await vueInstance.$store.dispatch("getDespensaryWithState", { stateName: searchWithState });
				await vueInstance.getAsPromiseData('fromState').then((searchStateData) => {
					//	console.log(searchStateData);
					//return false
					if (vueInstance.$store.state.despensaryWithStateData.length > 0) {
						searchData['dispendaryState'] = vueInstance.$store.state.despensaryWithStateData
						let latLong = {
							lat: searchData.dispendaryState[0].address.latitude,
							lng: searchData.dispendaryState[0].address.longitude
						}
						vueInstance.startLocation = latLong;
					}
				});
			}

			await searchData.dispendaryState.map(function (data, key) {
				vueInstance.nearDispensaryList(data)
				vueInstance.dataLoader = false
			});

			setTimeout(function () {
				if (vueInstance.$store.state.despensaryWithStateData.length < 1 && searchWithState) {
					vueInstance.flashError("No search result found. Please try again later.", { timeout: 20000, });
					vueInstance.dataLoader = false
				}
				if (vueInstance.$store.state.allProduct.length < 1 && searchWithCategoryName) {
					vueInstance.flashError("No search result found. Please try again later.", { timeout: 20000, });
					vueInstance.dataLoader = false
				}
			}, 5000);

		},

		async nearDispensaryList(dispendaryData) {

			var vueInstance = this;
			var id = dispendaryData.id;
			var dispensaryData = [];
			var reviewData = {};
			if (!vueInstance.checkAddedId.includes(dispendaryData.user_id)) { //alert(0);
				vueInstance.checkAddedId.push(dispendaryData.user_id)
				try {
					await fb.dispensaryCollection.doc(id).collection('reviews').get()
						.then(function (querySnapshot) {
							var totalCount = querySnapshot.size;
							if (totalCount > 0) {

								reviewData[id] = {
									count: totalCount,
									rating: dispendaryData.rating
								};
								vueInstance.reviewCount.push(reviewData);
							} else {
								reviewData[id] = {
									count: 0,
									rating: dispendaryData.rating
								};
								vueInstance.reviewCount.push(reviewData);
							}

						}).catch(function (error) {
							console.log("Error getting documents: ", error);
						});
				} catch (error) { }

				dispendaryData['count'] = vueInstance.indexCountNumber;
				vueInstance.nearDispensaryListData.push(dispendaryData);
				vueInstance.indexCountNumber++;


			}
			//	vueInstance.reviewCount  = reviewCountData;
			setTimeout(function () {
				if (dispensaryData.length <= 0)
					vueInstance.notAvailable = 0;
			}, 3000);

		},

		//Handle waiting to Get each file using promise
		async getAsPromiseData(from) {
			var vueInstance = this;
			return new Promise(function (resolve, reject) {
				if (from == 'fromCategory') {
					if (vueInstance.$store.state.allProduct)
						resolve(true);
					else
						reject(false)
				}

				if (from == 'fromState') {
					if (vueInstance.$store.state.despensaryWithStateData)
						resolve(true);
					else
						reject(false)
				}
			});
		},
		dispensaryProfile(id) {
			this.$router.push({ name: 'login' })
		}
	},
};
</script>
