<template>
  <div>
    <header-component />

    <section id="banner-section" class="section">
      <section id="customer-form">
        <div class="container">
          <div class="row">
            <!-- <div class="col-md-3"></div> -->
            <div class="form-menu ">
              <div class="back-menu"><i class="fa fa-long-arrow-left" aria-hidden="true"></i>
              <router-link :to="{ name: 'dispensaryPrfoileView', params: { dispesaryId: dispnesaryData.id }}">Back to dispensary Profile</router-link> </div>
            </div>
            <div class="col-md-8 col-md-offset-2">
                  <customerReviewTab v-if="dispnesaryData" :dispnesaryData="dispnesaryData"/>
                </div>
          </div>
        </div>
      </section>
    </section>
    <footer-component />
  </div>
</template>

<script>
import * as fb from "../../../../firebaseConfig";
import customerReviewTab from "../../../../components/backEnd/customer/dispensaryData/DispensaryCustomerReviewComponent";

export default {
    props:['dispesaryId'],
    data() {
        return {
            tab: this.$route.name,
            dispnesaryData:null,
        };
    },
    components: {
        customerReviewTab,
    },
    created() {
        this.dispenaryExist(this.dispesaryId);
    },
   
    methods: {
        
        async dispenaryExist(dispesary_id){ 
            var vueInstance = this;
            if(dispesary_id){ 
               //  let dispesary_id = 'KIdMgulXz0Ubaj01FFUe'
                let query = fb.dispensaryCollection.doc(dispesary_id)
                query.get().then(function(doc) {
                    if (doc.exists) {
                       vueInstance.dispnesaryData = doc.data();
                    } else {
                      vueInstance.flashError("This disapnesay is not available now. please try again.", {
                        timeout: 10000,
                      });
                      vueInstance.$router.push({ name: 'dispensarySearch' })
                    }
                }).catch(function(error) {
                     vueInstance.flashError("This disapnesay is not available now. please try again.", {
                        timeout: 10000,
                      });
                      vueInstance.$router.push({ name: 'dispensarySearch' })
                });
            }else{
                vueInstance.flashError("This disapnesay is not available now. please try again.", {
                  timeout: 10000,
                });
                vueInstance.$router.push({ name: 'dispensarySearch' })
            }
        },
    },
};
</script>
