<template>
  <div class="contact-form" id="driver-bacic-information">
    <flash-message></flash-message>
    <form @submit.prevent="SubmitDriverFrom">
      <div class="form-group">
        <input class="form-control" placeholder="First Name" type="text" v-model.trim="DriverFromData.firstName"
          @input="$v.DriverFromData.firstName.$touch()" />
        <div v-if="$v.DriverFromData.firstName.$dirty">
          <p class="text-danger" v-if="!$v.DriverFromData.firstName.required">Please enter your first name.</p>
        </div>
      </div>
      <div class="form-group">
        <input class="form-control" placeholder="Last Name" type="text" v-model.trim="DriverFromData.lastName"
          @input="$v.DriverFromData.lastName.$touch()" />
        <div v-if="$v.DriverFromData.lastName.$dirty">
          <p class="text-danger" v-if="!$v.DriverFromData.lastName.required">Please enter your last name.</p>
        </div>
      </div>
      <div class="form-group">
        <input class="form-control" placeholder="Phone Number" type="text" @keyup="acceptNumber"
          v-model.trim="DriverFromData.phoneNo" @input="$v.DriverFromData.phoneNo.$touch()" />
        <div v-if="$v.DriverFromData.phoneNo.$dirty">
          <p class="text-danger" v-if="!$v.DriverFromData.phoneNo.required">Please enter your valid phone no..</p>

        </div>
      </div>
      <div class="form-group">
        <input class="form-control" placeholder="Email Address" type="text" v-model.trim="DriverFromData.email"
          @input="$v.DriverFromData.email.$touch()" />
        <div v-if="$v.DriverFromData.email.$dirty">
          <p class="text-danger" v-if="!$v.DriverFromData.email.required">Please enter your email address.</p>
          <p class="text-danger" v-if="!$v.DriverFromData.email.email">Please enter valid email address.</p>
        </div>
      </div>
      <div class="checkbox">
        <label>
          <input type="checkbox" v-model.trim="DriverFromData.subscribe" /> Subscribe to receive email communications from
          Grab-A-Gram
        </label>
      </div>
      <div class="form-group">
        <textarea class="form-control text-msg" :maxlength="maxWord" rows="6"
          placeholder="Tell us a little about you and your company..." v-model.trim="DriverFromData.aboutYou"
          @input="$v.DriverFromData.aboutYou.$touch()"></textarea>
        <div>About you must be no longer than {{ (maxWord - DriverFromData.aboutYou.length) }} word</div>
        <div v-if="$v.DriverFromData.aboutYou.$dirty">
          <p class="text-danger" v-if="!$v.DriverFromData.aboutYou.required">Please enter tell us about you.</p>
        </div>
      </div>
      <div class="condtion-text">
        <p>
          By clicking on "Become a Driver", I agree that Grab-A- Gram or its
          representatives may contact me by email, phone or SMS at the email address or telephone number I have provided.
          I understand that Grab-A- Gram is a
          request platform and not a delivery service or carrier. I also confirm that I have read and understand
          Grab-A-Gram's
          <span>Driver Applicant Privacy Statement</span>.
        </p>
      </div>
      <div v-if="error" v-html="error">{{error}}</div>
      <div class="text-center">
        <button class="btn send-btn help-btn driver-btn" :disabled="$v.$invalid">
          <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>Apply to become a driver</button>
      </div>
    </form>
  </div>
</template>

<script>
/* eslint-disable */
const $ = window.jQuery = require('jquery');
import {
  required,
  numeric,
  email
} from "vuelidate/lib/validators";
import axios from "axios";

import * as fb from "../../firebaseConfig";
import firebase from "firebase";
export default {
  data() {
    return {
      text: '',
      DriverFromData: {
        firstName: null,
        lastName: null,
        phoneNo: "",
        email: null,
        subscribe: null,
        aboutYou: '',
        // linkString: null
      },
      maxWord: 250,
      spinnerShow: false,
      error: null,
      url: this.$store.state.API_URL_ROOT,
      phoneValue: 0,
      preventNextIteration: false
    };
  },
  validations: {
    DriverFromData: {
      firstName: {
        required
      },
      lastName: {
        required
      },
      phoneNo: {
        required,
      },
      email: {
        required,
        email
      },
      aboutYou: {
        required
      },
    },
  },

  methods: {
    acceptNumber() {
      var x = this.DriverFromData.phoneNo.replace(/\D/g, '').match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
      this.DriverFromData.phoneNo = !x[2] ? x[1] : '(' + x[1] + ') ' + x[2] + (x[3] ? '-' + x[3] : '');
    },

    async SubmitDriverFrom() {
      $('.btn').prop("disabled", true);
      var vueInstance = this;
      vueInstance.error = null;
      vueInstance.spinnerShow = true;

      if (!vueInstance.$v.$invalid) {
        await vueInstance.$store.dispatch("checkUserExit", {
          email: vueInstance.DriverFromData.email,
        });
        let checkExist = vueInstance.$store.state.existUser;
        if (checkExist == false) {

          var phoneNumber = vueInstance.DriverFromData.phoneNo.replace(/[^\d]/g, "");
          var phoneNo = phoneNumber.substr(0, 3) + phoneNumber.substr(3, 3) + phoneNumber.substr(6)

          let currentTime = firebase.firestore.Timestamp.now().toMillis();
          let dataInsert = await fb.tempRegistrationCollection.doc();
          dataInsert.set({
            role: "Driver",
            first_name: vueInstance.DriverFromData.firstName,
            last_name: vueInstance.DriverFromData.lastName,
            phone_no: vueInstance.DriverFromData.phoneNo,
            email: vueInstance.DriverFromData.email,
            about_you: vueInstance.DriverFromData.aboutYou,
            subscribe: vueInstance.DriverFromData.subscribe,
            created_at: currentTime,
            updated_at: currentTime,
          }).then(function () {
            dataInsert.update({
              id: dataInsert.id,
            });
            let randomString = dataInsert.id
            //  vueInstance.$router.push({ name: "verificationDriverRegistration", params: { randomString: randomString }})
            axios.get(`${vueInstance.url}sendDriverVerifyEmail/${vueInstance.DriverFromData.email}/${randomString}`)
              .then((response) => {

                if (response.data.status == 0)
                  return vueInstance.error = response.data.msg;

                // Store all form data into local storage
                vueInstance.$set(vueInstance.DriverFromData, 'linkString', randomString);
                localStorage.setItem('DriverForm', JSON.stringify(vueInstance.DriverFromData))
                vueInstance.$router.push({ name: "driverVerification" });
              });

          });

        } else {
          this.error = "<p class='alert alert-danger'>This email id already exit.Please try other email id.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
          this.spinnerShow = false;
          $('.btn').prop("disabled", false);
        }
      } else {
        this.spinnerShow = false;
        $('.btn').prop("disabled", false);
      }
    },
  },
};
</script>
