<template>
  <div>
    <div class="main-page-title animated bounceIn go" data-id="1">
      <div class="row">
        <div class="col-sm-7">
          <div class="row">
            <div class="col-sm-8">
              <div class="title">
                <h2>Review Your Order</h2>
              </div>
            </div>
            <div class="col-sm-4"></div>
          </div>
          <div class="row">
            <div v-if="error" v-html="error">{{error}}</div>
            <div class="col-sm-6">
              
              <div class="contact-titles">
                <div class="row">
                  <div class="col-sm-8">
                    <div class="signature-section">BILLING ADDRESS</div>
                  </div>

                  <div class="col-sm-3">

                      <div class="change-green" v-if="customerBillingAddress && indexOfBillingAddress"><a href="#" @click.prevent="editBillingAddress(billingAddressType,indexOfBillingAddress)">CHANGE</a></div>
                      <div class="change-green" v-else><a href="#" @click.prevent="addBillingAddress(billingAddressType)">ADD</a></div>

                  </div>
                </div>
              </div>
                <p v-if="customerBillingAddressData" v-html="customerBillingAddressData">
                  {{customerBillingAddressData}}
                </p>

              <div class="signature-section ">PAYMENT</div>
              <div class="card-details">
                <div class="row">
                  <div class="col-sm-5">
                    <div class="card-number">Card Number *</div>

                  </div>
                  <div class="col-sm-6">
                    <div class="text-right">
                      <img :src="paymentIcon">
                    </div>
                  </div>

                </div>
                <div class="row">
                  <div class="form-group">
                    <div class="col-sm-12">
                      <div class="contact-form">
                        <input maxlength="19" @keyup="formatCardNumber" @input="$v.cardPayment.cardNumber.$touch()" v-model.trim="cardPayment.cardNumber" class="form-control" type="text">
                        <div v-if="$v.cardPayment.cardNumber.$dirty">
                          <p class="text-danger" v-if="!$v.cardPayment.cardNumber.required">
                            Please enter valid card number.
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="row">
                  <div class="col-sm-6">
                    <div class="contact-form">
                      <div class="form-group">
                        <label>Expiration Date *</label>
                        <input autocomplete="off" v-on:keyup="formatCardExpirationDate" maxlength='5' @input="$v.cardPayment.cardExipreDate.$touch()" v-model.trim="cardPayment.cardExipreDate" class="form-control"  placeholder="MM/YY" type="text">
                        <div v-if="$v.cardPayment.cardExipreDate.$dirty">
                          <p class="text-danger" v-if="!$v.cardPayment.cardExipreDate.required">
                            Please enter valid card number.
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="col-sm-6">
                    <div class="contact-form">
                      <div class="form-group">
                        <label>CVV/CID *</label>
                        <input autocomplete="off" maxlength='4' @input="$v.cardPayment.cardCVV.$touch()" v-model.trim="cardPayment.cardCVV" class="form-control"  type="password">
                        <div v-if="$v.cardPayment.cardCVV.$dirty">
                          <p class="text-danger" v-if="!$v.cardPayment.cardCVV.required">
                            Please enter valid CVV number.
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

            </div>
            <div class="col-sm-6">
              <div class="contact-titles">
                <div class="row">
                  <div class="col-sm-8">
                    <div class="signature-section">SHIPPING ADDRESS</div>
                  </div>

                  <div class="col-sm-3">
                    <div class="change-green"><a href="#" data-toggle="modal" data-target="#shipping-address">CHANGE</a> </div>

                  </div>
                </div>
              </div>
              <p v-if="customerShippingAddressData" v-html="customerShippingAddressData">
                {{customerShippingAddressData}}
              </p>

            </div>
          </div>

          <div class="special-inst">
            <div class="signature-section">SPECIAL INSTRUCTIONS</div>
            <div class="contact-form">
              <textarea v-model.trim="specialInstruction" class="form-control text-msg"></textarea>

            </div>
          </div>
       
        </div>
        <div class="col-sm-1"></div>
        <div class="col-sm-4">
          <div class="order-box-section border-box">
            <h3>ORDER SUMMARY</h3>
            <div class="white-section-box">
              <div class="row">
                <div class="col-sm-8">
                  <div class="item-no">ITEMS</div>
                </div>
                <div class="col-sm-4">
                  <div class="item-no">${{subTotalAmount.toFixed(2) | currency}}</div>
                </div>
              </div>
              <div class="row" v-if="totalDiscount>0">
                <div class="col-sm-8">
                  <div class="item-no">DISCOUNT</div>
                </div>
                <div class="col-sm-4">
                  <div class="item-no">- ${{totalDiscount.toFixed(2) | currency}}</div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-8">
                  <div class="item-no">DELIVERY FEE</div>
                </div>
                <div class="col-sm-4">
                  <div class="item-no">${{deliveryFee.toFixed(2) | currency}}</div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-8">
                  <div class="item-no">SERVICE FEE</div>
                </div>
                <div class="col-sm-4">
                  <div class="item-no">${{serviceFee.toFixed(2) | currency}}</div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-8">
                  <div class="item-no">SALES TAX</div>
                </div>
                <div class="col-sm-4">
                  <div class="item-no">${{taxTotalAmount.toFixed(2) | currency}}</div>
                </div>
              </div>
              <div class="total-price">
                <div class="row">
                  <div class="col-sm-8">
                    <div class="total-item-no">TOTAL</div>
                  </div>
                  <div class="col-sm-4">
                    <div class="total-item-no">${{totalAmount.toFixed(2) | currency}}</div>
                  </div>
                </div>
                <button class="palce-order-btn btn app-btn" :disabled="$v.$invalid"  @click.prevent="placeOrder">
                  <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>PLACE MY ORDER
                </button>
                
              </div>

            </div>
            <div v-if="discountLimitError" v-html="discountLimitError">{{discountLimitError}}</div>

          </div>
        </div>

      </div>

      <div class="row padding-top">
        <div class="col-sm-10">
          <div class="tab-table">
            <div class="table-responsive">
              <table class="table">

                <thead>
                <tr class="table-title">
                  <th>PRODUCT</th>
                  <th></th>
                  <th>QUANTITY</th>
                  <th>TOTAL</th>

                </tr>
                </thead>

                <tbody>
                <tr v-for="(cartProduct, index) in cartProductData" :key="index">
                  <td style="width: 10%">
                    <img :src="cartProduct.images[0]" class="img-responsive productImg-cart">
                  </td>
                  <td class="top-margin-td">{{cartProduct.name}}</td>
                  <td class="top-margin-td">{{cartProduct.quantity}}</td>
                  <td class="top-margin-td"> ${{cartProduct.quantity * cartProduct.sale_price | currency}}</td>
                </tr>
                </tbody>

              </table>
            </div>
          </div>
        </div>
        <div class="col-sm-2"></div>
      </div>
    </div>

    <div class="shipping-address-popup">
      <div class="modal fade bs-example-modal-lg" tabindex="-1" role="dialog" aria-labelledby="myLargeModalLabel" id="shipping-address">
        <div class="modal-dialog modal-lg" role="document">
          <div class="modal-content">
            <div class="share-box">
              <div class="text-center">
                <div class="title">
                  <h2>Change shipping Address</h2>
                </div>
                <p class="text-left select-add">Select Address</p>
                <div class="address-pop-up-box">
                  <div class="text-left">
                  <select class="form-control" v-model.trim="changeShippingAddressType">
                    <option v-for="(shippingAddress, index) in allShippingAddress" :key="index" :value="shippingAddress.type">
                      {{ shippingAddress.street_1 }} {{ shippingAddress.street_2 }}, {{ shippingAddress.city }} {{ shippingAddress.state }}, {{ shippingAddress.zip_code }}
                    </option>
                  </select>
                  <button class="palce-order-btn btn app-btn" @click.prevent="changesShippingAddress">
                    Changes Shipping Address
                  </button>
                  </div>

                </div>
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>

  </div>  
</template>

<script>
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import evenBus from "../../../event-bus";
import { required  } from "vuelidate/lib/validators";
import orderStatus from "../../../order-status";
import addressType from "../../../address-type";
import axios from "axios";

export default {
  props:{
    productOrderListData:{
      type: Object,
      required: true,
    },
    dispnesaryData:{
      required: true,
    },
  },
  data() {
    return {
        personalAddressType: addressType.personalAddressType,
        primaryAddressType: addressType.primaryAddressType,
        secondaryAddressType: addressType.secondaryAddressType,
        billingAddressType: addressType.billingAddressType,
        
      url:this.$store.state.API_URL_ROOT,
      paymentIcon: this.$store.state.API_URL_ROOT + "images/payment-icon.png",
      url: this.$store.state.API_URL_ROOT,
      cartProductData:[],
      subTotalAmount:0,
      taxTotalAmount:0,
      totalAmount:0,
      totalDiscount:0,
      deliveryFee:0,
      serviceFee:7,
      deliveryFeeMinute:0,
      deliveryFeeMiles:0,
      distanceIntoMinutes:5,
      distanceIntoMiles:0,

      customerShippingAddress:{},
      customerShippingAddressData:null,
      customerBillingAddress:{},
      customerBillingAddressData:null,
      indexOfBillingAddress:null,
      changeShippingAddressData:[],
      changeShippingAddressType:null,
      changeedShippingType:null,

      specialInstruction:null,

      cardPayment:{
        cardNumber:"",
        cardExipreDate:"",
        cardCVV:"",
      },

      customerAccount:{},
      customerShippingName: null,
      scheduleStatus:orderStatus.schedule,
      driverDistance:200,
      driverStatus:"Online",
      spinnerShow:null,
      discountLimitError:null,
      error: null
    };
  },
 validations: {
   cardPayment:{
      cardNumber: {
        required,
      },
      cardExipreDate: {
        required,
      },
      cardCVV: {
        required,
      },
   },
   customerShippingAddressData: {
        required,
      },
  },
   mounted(){
	  $(".flash__wrapper").empty();
  },
  computed: { 
    allShippingAddress: function () { 
      return this.changeShippingAddressData.filter(addressData => addressData.type !=this.billingAddressType)
    },
  },
  created() {
    this.getShippingAddress();
   // this.getCartData()
  },
  methods: {

    async getAutoOrderNo() {
      let getOrderNoCollection = fb.orderNoCollection.doc("response")
      return new Promise(function (resolve, reject) {
        getOrderNoCollection.get().then(function(orderDoc) {
            let orderNo = parseInt(orderDoc.data().order_no)+ 1

            getOrderNoCollection.update({
                order_no: firebase.firestore.FieldValue.increment(1)
            });
            resolve(orderNo);
        })
      });
    },

    async placeOrder(){
      var vueInstance = this;
      $(".flash__wrapper").empty();
      //When Billing Address not fill up
      if(!vueInstance.customerBillingAddressData){
        vueInstance.error = "<p class='alert alert-danger'>Please fill up your billing address.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
        return false
      }
      //When coupon is not applied on pervious(Promo code) Screen
      if(vueInstance.productOrderListData.used_coupon_id === undefined){
        vueInstance.placeOrderSubmit()
        return false
      }
      vueInstance.spinnerShow = true;
      vueInstance.discountLimitError = null;
      $('.btn').prop("disabled", true);
      
      
      if (!this.$v.$invalid) {
        
        let currentTime = firebase.firestore.Timestamp.now().toMillis();
        
        await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_coupon').collection('usedCoupon').doc(vueInstance.productOrderListData.used_coupon_id).get().then((doc)=> {
          if(doc.exists){ 
            var usedLimit = doc.data()
            let getCouponCode = fb.couponCollection.where("id", "==", vueInstance.productOrderListData.used_coupon_id).where("expiration_date",">=",currentTime).where("isPublic","==",true);

            getCouponCode.get().then(function(doc)  {
              if (!doc.empty) { 
                  var data = doc.docs[0].data()

                  if(data.usage_limit_per_user <= usedLimit.used_limit){
                    $('.btn').prop("disabled", false);
                    vueInstance.spinnerShow = false;
                    vueInstance.discountLimitError ="<p class='alert alert-danger'>You have already used this coupon please try another with one.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
                    return false
                  }else{
                    vueInstance.placeOrderSubmit()
                  }
              }else{
                vueInstance.placeOrderSubmit()
              }
            })   
          }else{
            vueInstance.placeOrderSubmit()
          }   
        });
      }
    },

    async placeOrderSubmit(){
        
      var vueInstance = this;
      $(".flash__wrapper").empty();
      vueInstance.spinnerShow = true;
      vueInstance.discountLimitError = null;
      vueInstance.error = null;
      $('.btn').prop("disabled", true);

      let confirmationCode = Math.floor(Math.random() * (10000 - 1000) + 1000)
      let dataInsert = await fb.orderCollection.doc();
      
      let orderNo = await vueInstance.getAutoOrderNo()

      
      // let captureData = { 
      //   cardPaymentData: vueInstance.cardPayment,
      //   totalAmountData: (vueInstance.totalAmount).toFixed(2),
      //   billingData: vueInstance.customerBillingAddress, 
      //   customerAccountData: vueInstance.customerAccount,
      //   invoiceNumber: orderNo,
      // };
      // await axios.post(vueInstance.url + "capture-payment", captureData)
      // .then(response => {
      //   console.log(response);
      //   if(response.data.status == 1){
      //     let transationId = response.data.getTransId
      //     let paymentResponseCode = response.data.getResponseCode
      //     let paymentAuthCode = response.data.getAuthCode
      //     let paymentDescription = response.data.getDescription

      //     let paymentData = {
      //       transationId : transationId,
      //    //transationId :40058528412,
      //     }
      //   /*  axios.post(vueInstance.url + "amount-payment", paymentData)
      //     .then(response => 
      //       console.log(response)
      //       ).catch(error => {
      //       this.errorMessage = error.message;
      //       console.error("There was an error!", error);
      //     });
      //   */





      //   }else{
      //     let getErrorText = response.data.getErrorText
      //     vueInstance.error =`<p class='alert alert-danger'>${getErrorText} .<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>`;
      //     vueInstance.spinnerShow = false;
      //     $(".btn").prop("disabled", false);
      //   }
      // })
      //   .catch(error => {
      //     vueInstance.errorMessage = error.message;
      //     console.error("There was an error!", error);
      //   });



//return false















      let currentTime = firebase.firestore.Timestamp.now().toMillis();
      let address=[]
      address.push(vueInstance.customerShippingAddress)
      address.push(vueInstance.customerBillingAddress)

      let insertOrderData={
        addresses:address,
        assign_driver_id : "",
        busniess_name : vueInstance.dispnesaryData.busniess_name,
    // confirmation_code : Math.random().toString(36).substr(2, 6),
     //   confirmation_code : Array(6).fill(0).map(x => Math.random().toString(36).charAt(2)).join(''),
        confirmation_code: confirmationCode.toString(),
        created_at : currentTime,
        customer_email :  vueInstance.customerAccount.user.email,
        customer_name : vueInstance.customerShippingName ? vueInstance.customerShippingName : vueInstance.customerAccount.account.first_name+' '+vueInstance.customerAccount.account.last_name,
        special_instruction : vueInstance.specialInstruction,
      //  delivery_fee : vueInstance.productOrderListData.delivery_fee,
        delivery_fee : vueInstance.deliveryFee,
        delivery_fee_miles : parseFloat(vueInstance.deliveryFeeMiles),
        delivery_fee_minute : parseFloat(vueInstance.deliveryFeeMinute),
        delivery_minute : parseInt(vueInstance.distanceIntoMinutes.toFixed(0)),
        delivery_miles : parseInt(vueInstance.distanceIntoMiles),
        discount_amount : vueInstance.totalDiscount,
        dispensary_data : vueInstance.dispnesaryData,
        dispensary_location : vueInstance.dispnesaryData.location,
        grand_total : parseFloat(vueInstance.totalAmount.toFixed(2)),
        isSendNotification : false,
        items : vueInstance.cartProductData,
        items : vueInstance.cartProductData,
        patient_id : vueInstance.customerAccount.document_info.patient_id,
        sales_tax : vueInstance.taxTotalAmount,
        status : vueInstance.scheduleStatus,
        sub_total : parseFloat(vueInstance.subTotalAmount.toFixed(2)),
        updated_at : currentTime,
        user_id : fb.auth.currentUser.uid,
        order_no: orderNo.toString(),
        payment_success: false,
      }






      let captureData = { 
        cardPaymentData: vueInstance.cardPayment,
        totalAmountData: (vueInstance.totalAmount).toFixed(2),
        billingData: vueInstance.customerBillingAddress, 
        shippingData: vueInstance.customerShippingAddress, 
        customerAccountData: vueInstance.customerAccount,
        invoiceNumber: orderNo,
      };

      //Capture the payment/Check card is valid or not
      await axios.post(vueInstance.url + "capture-payment", captureData).then(response => {
          var retrunCaptureData = response.data
        if(retrunCaptureData.status == 1){
          let transationId = retrunCaptureData.getTransId
          let paymentResponseCode = retrunCaptureData.getResponseCode
          let paymentAuthCode = retrunCaptureData.getAuthCode
          let accountType = retrunCaptureData.accountType

          let paymentResponse = {
            transationId: transationId,
            responseCode: paymentResponseCode,
            authCode: paymentAuthCode,
            accountType : accountType
          }
          let paymentData = {
            transationId : transationId,
         //transationId :40058566560,
          }
        /*  axios.post(vueInstance.url + "amount-payment", paymentData)
          .then(response => 
            console.log(response)
            ).catch(error => {
            this.errorMessage = error.message;
            console.error("There was an error!", error);
          });
        */








    //  let getOrderNo = fb.orderNoCollection.doc("response")
        
        dataInsert.set(insertOrderData).then(function () {
            
          /*getOrderNo.get().then(function(orderDoc) {
            let orderNo = parseInt(orderDoc.data().order_no)+ 1

            getOrderNo.update({
                order_no: firebase.firestore.FieldValue.increment(1)
            });
            */
            dataInsert.update({
              id: dataInsert.id,
              payment_method : paymentResponse,
            });


            axios.post(vueInstance.url + "amount-payment", paymentData).then(response => {
            //  console.log(response)
            //  console.log(response.data)
            
              if(response.data.status == 1){
                dataInsert.update({
                  payment_success: true,
                });

              
                //Checking dispensary openning hours
                let openningHours = vueInstance.dispnesaryData.opening_hours

                let todayDay= new Date().toLocaleDateString('en-US', { weekday: 'long' });
                if(openningHours.some(openningDay => openningDay.day === todayDay)){
                  let index = openningHours.findIndex(openningDay => openningDay.day === todayDay);

                  var nowTime = new Date(+currentTime);

                  let inTime =openningHours[index].in_time.split(':')
                  let inTimehour = inTime[0]
                  let inTimeMinutes = inTime[1]
                  if(inTimeMinutes == '00')
                    inTimeMinutes = 0;

                  let outTime =openningHours[index].out_time.split(':')
                  let outTimehour = outTime[0]
                  let outTimeMinutes = outTime[1]
                  if(outTimeMinutes === '00')
                    outTimeMinutes = 0;

                  var openningInTime = new Date();
                  openningInTime.setHours(inTimehour,inTimeMinutes,0); // 5.30 pm

                  var openningOutTime = new Date();
                  openningOutTime.setHours(outTimehour,outTimeMinutes,0); // 6.30 pm
                  
                  if(nowTime >= openningInTime && nowTime < openningOutTime ){
                    var i = 0
                    // Find driver near by dispensary and send the notification to driver
                    var latitude = vueInstance.dispnesaryData.address.latitude;
                    var longitude = vueInstance.dispnesaryData.address.longitude;
                    var distance = vueInstance.driverDistance;
                    // ~1 mile of lat and lon in degrees
                    let lat = 0.0144927536231884
                    let lon = 0.0181818181818182

                    let lowerLat = latitude - (lat * distance)
                    let lowerLon = longitude - (lon * distance)

                    let greaterLat = latitude + (lat * distance)
                    let greaterLon = longitude + (lon * distance)

                    let lesserGeopoint = new firebase.firestore.GeoPoint(lowerLat, lowerLon)
                    let greaterGeopoint = new firebase.firestore.GeoPoint(greaterLat, greaterLon)

                    let query = fb.driverLocationCollection.where("location", '>' , lesserGeopoint).where("location", '<' , greaterGeopoint).where("driver_status", '==' ,vueInstance.driverStatus)
                    
                    query.get().then((querySnapshot) => { 
                      if(querySnapshot.size > 0){
                        querySnapshot.forEach((doc) => {
                            
                        //  fb.usersCollection.doc(doc.data().user_id).get().then((doc) => { 
                        //    if (doc.exists) {  
                              var userData = doc.data();
                              var deviceId = userData.device_id;
                              if(deviceId){ 
                                axios.post(`${vueInstance.url}send-notification`,{
                                  message : `New order`,
                                  device_token : deviceId,
                                  order_id : dataInsert.id,
                                  order_no : orderNo.toString(),
                                  dispensary_name : vueInstance.dispnesaryData.busniess_name,
                                  notification_type : 'new_order',
                                  order_user_id : fb.auth.currentUser.uid,
                                }).then((response) => {
                                  dataInsert.update({
                                    notification_drivers_id:firebase.firestore.FieldValue.arrayUnion(userData.user_id),
                                    isSendNotification: true,
                                  });
                                  
                                  if(querySnapshot.size === i){ 
                                    vueInstance.spinnerShow = true;
                                    vueInstance.$router.push({ name: "confiremOderThankYouView", params: { orderId: dataInsert.id }})
                                  }  
                                });
                            
                              }
                        //    }else {
                              vueInstance.spinnerShow = false;
                              $('.btn').prop("disabled", false);
                              vueInstance.$router.push({ name: "confiremOderThankYouView", params: { orderId: dataInsert.id }})
                        //    }
                            i++; 
                        //  })
                          
                        }).then(response => { 
                            vueInstance.spinnerShow = false;
                              $('.btn').prop("disabled", false);
                              vueInstance.$router.push({ name: "confiremOderThankYouView", params: { orderId: dataInsert.id }})
                        })
                      }else{
                        vueInstance.spinnerShow = false;
                        $('.btn').prop("disabled", false);
                        vueInstance.$router.push({ name: "confiremOderThankYouView", params: { orderId: dataInsert.id }})
                        return true;
                      }
                    });
                  }else{
                    vueInstance.spinnerShow = false;
                    $('.btn').prop("disabled", false);
                    vueInstance.$router.push({ name: "confiremOderThankYouView", params: { orderId: dataInsert.id }})
                    return true;
                  }
                }else{
                  vueInstance.spinnerShow = false;
                  $('.btn').prop("disabled", false);
                  vueInstance.$router.push({ name: "confiremOderThankYouView", params: { orderId: dataInsert.id }})
                  return true;
                }
              }else{
                let getErrorText = response.data.getErrorText
                vueInstance.error =`<p class='alert alert-danger'>${getErrorText} .<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>`;
                vueInstance.spinnerShow = false;
                $(".btn").prop("disabled", false);
              }
            }).catch(error => {
              this.errorMessage = error.message;
            //  console.error("There was an error!", error);
            });
          /*})*/
          vueInstance.spinnerShow = false;
          $('.btn').prop("disabled", false);
          return true;
