      async calculateDelivaryFee(customerLat, customerLong){ 
        var vueInstance = this;
        let delivaryFeeAmount = 0;
        let dispnesaryLat =  vueInstance.dispnesaryData.address.latitude
        let dispnesaryLong =  vueInstance.dispnesaryData.address.longitude
        var distanceIntoMinutesFee = vueInstance.distanceIntoMinutes * 0.15

        if ((customerLat == dispnesaryLat) && (customerLong == dispnesaryLong)) {
          vueInstance.getCartData()
          return 0;
        }
        else {

          var origin = new google.maps.LatLng(dispnesaryLat, dispnesaryLong ); // using google.maps.LatLng class
          var destination = customerLat + ', ' + customerLong; // using string

          var directionsService = new google.maps.DirectionsService();
          var request = {
              origin: origin, // LatLng|string
              destination: destination, // LatLng|string
              travelMode: google.maps.DirectionsTravelMode.DRIVING
          };

          directionsService.route( request, function( response, status ) {
            
            if ( status === 'OK' ) {
              var point = response.routes[ 0 ].legs[ 0 ];
              
              var seconds  = point.duration.value 
              vueInstance.distanceIntoMinutes = Math.floor(seconds / 60);
              distanceIntoMinutesFee = vueInstance.distanceIntoMinutes * 0.15
             
              var dist = point.distance.value * 0.000621371192
            }else{
              var radlat1 = Math.PI * customerLat/180;
              var radlat2 = Math.PI * dispnesaryLat/180;
              var theta = customerLong-dispnesaryLong;
              var radtheta = Math.PI * theta/180;
              var dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
              if (dist > 1) {
                  dist = 1;
              }
              dist = Math.acos(dist);
              dist = dist * 180/Math.PI;
            }
                
            let distance = dist.toFixed(0); 
            if(distance > 0 && distance <= 5){
              delivaryFeeAmount = 14
            }else if(distance > 5 && distance <= 10){
              delivaryFeeAmount = 24
            }else{
              let leftDistance = distance - 10;            
              delivaryFeeAmount = (leftDistance * 0.99) + 24
            }

            vueInstance.getCartData()
            vueInstance.deliveryFee = delivaryFeeAmount + distanceIntoMinutesFee
            vueInstance.deliveryFeeMiles = delivaryFeeAmount.toFixed(2);
            vueInstance.deliveryFeeMinute= distanceIntoMinutesFee.toFixed(2)
            vueInstance.distanceIntoMiles= distance
              
              //  $( '#travel_data' ).html( 'Estimated travel time: ' + point.duration.text + ' (' + point.distance.text + ')' );
            
          });
        }
      },

