            vueInstance.customerShippingAddressData +=vueInstance.customerShippingAddress.street_2+'<br/>'
          vueInstance.customerShippingAddressData += vueInstance.customerShippingAddress.city +', '+vueInstance.customerShippingAddress.state+', '+vueInstance.customerShippingAddress.zip_code;
            
          vueInstance.changeShippingAddressType=shippingData.type;
