<template>
<div>
    <header-component />
    <sectionBanner />

    <section id="customer-form">
        <div class="container">
            <div class="row">
                <div class="col-lg-10 col-lg-offset-1 animatedParent" data-sequence="100">
                    <div class="tab-section animated bounceIn go" data-id="1">
                        <!-- Nav tabs -->
                        <selectTab :tabPage="this.tab"/>

                        <!-- Tab panes -->
                        <div class="tab-content page-tab bottom-space">
                            <div role="tabpanel" class="tab-pane active" id="deals-ads">
                            
                            <component v-bind:is="currentComponent" :id="this.dataId"></component>
                           
                               
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>

    <footer-component />
</div>
</template>

<script>
import evenBus from '../../../event-bus'
import sectionBanner from '../../../components/backEnd/dispensary/SectionBannerComponent'
import selectTab from '../../../components/backEnd/dispensary/TabComponent'
import couponList from '../../../components/backEnd/dispensary/CouponListComponent'
import couponAdd from '../../../components/backEnd/dispensary/CouponAddComponent'
import couponReview from "../../../components/backEnd/dispensary/CouponReviewComponent";

export default {
    data() {
        return {
            currentComponent:couponList,
            dataId: null,
            tab:null
        }
    },
    components: {
        sectionBanner,
        couponList,
        couponAdd,
        selectTab,
        couponReview
    },
    created (){
    this.tab = this.$route.name;
    evenBus.$on('addCoupon', () => {
        this.currentComponent=couponAdd;
    });
    evenBus.$on('backToList', () => {
     this.currentComponent=couponList;
    });
    evenBus.$on("couponReview", (id) => {
      this.dataId = id;
      this.currentComponent = couponReview;
    });

  }
};
</script>
