<template>
  
 <div 
      class="modal fade bs-example-modal-lg"
      tabindex="-1"
      role="dialog"
      aria-labelledby="myLargeModalLabel"
      id="dispesary-rating-review"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content">
          <form @submit.prevent="SubmitDispensaryReview">
            <div class="share-box">
              <div class="title">
                <h2>{{orderDetailsData.busniess_name}}</h2>
              </div>
              <div class="select-rating"  @mouseleave="showCurrentRating(0)" style="display:inline-block;">
                <star-rating v-model="DispensaryReviewData.dispensaryRatingAdd"  @input="$v.DispensaryReviewData.dispensaryRatingAdd.$touch()" :animate="true" :show-rating="false"  @rating-selected="setCurrentSelectedRating" @current-rating="showCurrentRating" :star-size="20" active-color="#99dc3f" :border-width="3"/>
                <div style="margin-right:10px;margin-bottom:20px;font-weight:bold;">{{currentRating}}</div>
              </div>
              <div class="contact-form">
                <textarea v-model.trim="DispensaryReviewData.aboutDispensary" @input="$v.DispensaryReviewData.aboutDispensary.$touch()"
                  class="form-control text-msg"
                  rows="6"
                  placeholder="What do you think about this dispensary?"
                ></textarea>
              </div>

              <div class="share-btn-pop-up">
                <div class="row">
                  <div class="col-sm-4"></div>
                  <div class="col-sm-8">
                    <div class="row">
                      <flash-message></flash-message>
                      <div class="col-sm-6">
                        <div class="text-center">
                          <a href="#" data-dismiss="modal" aria-label="Close" class="cancel-review">CANCEL REVIEW</a>
                        </div>
                      </div>

                      <div class="col-sm-6">
                        <button class="btn send-btn" :disabled="$v.$invalid">
                          <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>POST REVIEW
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>

</template>

<script>
import evenBus from '../../../event-bus'
import {required,numeric} from "vuelidate/lib/validators";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import starRatingCount from '../../../components/backEnd/RatingComponent';
import StarRating from 'vue-star-rating'
export default {
	data() {
		return {
		  totalReview: null,
			totalRating: null,
			currentRating: "No Rating", 
			currentSelectedRating: "No Current Rating",
			
			DispensaryReviewData: {
				dispensaryRatingAdd:null,
				aboutDispensary: null,
      },
      orderDetailsData:{},
			spinnerShow: false,
		};
	},
	validations: {
		DispensaryReviewData: {
			dispensaryRatingAdd: {
				required,
				numeric
			},
			aboutDispensary: {
				required
			},
		},
	},
	
	components: {
        starRatingCount,
        StarRating,
  },
  created() {
     evenBus.$on("dispesaryReviewModal", (allProductDetailsData) => {
            this.DispensaryReviewData.dispensaryRatingAdd = null;
            this.DispensaryReviewData.aboutDispensary = null;
            this.orderDetailsData = allProductDetailsData;
            $('#dispesary-rating-review').modal('show')
        });
  },
  mounted(){
	  $(".flash__wrapper").empty();
  },
	methods: {
		
      showCurrentRating(rating) { 
        this.currentRating = (rating === 0) ? this.currentSelectedRating : "Click to select " + rating + " stars"
      },
      setCurrentSelectedRating: function(rating) {
        this.currentSelectedRating = "You have Selected: " + rating + " stars";
	  },
	  
	  async SubmitDispensaryReview(){
        $('.btn').prop("disabled", true);
        this.spinnerShow = true;
        var vueInstance = this;
        if (!this.$v.$invalid) {
          let currentTime = firebase.firestore.Timestamp.now().toMillis();
          let dataInsert = fb.dispensaryCollection.doc(vueInstance.orderDetailsData.dispensary_data.id);
          await vueInstance.$store.dispatch("getCustomerUser").then(response => {
                let userProfile = vueInstance.$store.state.customerData;
                dataInsert.collection('reviews').doc().set({
                created_at:currentTime,
                comment:vueInstance.DispensaryReviewData.aboutDispensary,
                rating:vueInstance.DispensaryReviewData.dispensaryRatingAdd,
                dispensary_id:vueInstance.orderDetailsData.dispensary_data.id,
                order_id:vueInstance.orderDetailsData.id,
                updated_at:currentTime,
                user_id:userProfile.user.user_id,
                user_name:userProfile.account.first_name+' '+ userProfile.account.last_name
              }).then((response) => { 
                var ratingCount = 0;
                dataInsert.collection('reviews').get().then(function(querySnapshot) { 
                  var totalCount = querySnapshot.size;
                  if(totalCount >0){
                    
                    querySnapshot.forEach(function(docreview) {
                      ratingCount += parseInt(docreview.data().rating);
                    });
                    dataInsert.update({
                      rating : +(ratingCount/totalCount).toFixed(2),
                    });
                  }
                  
                 }).then((sucess) => { 
                  vueInstance.$store.state.dispensaryReviewOpenModal= false
                    vueInstance.flashSuccess("You have given a successful dispensary review.",{
                      timeout: 4000,
                    });
                    setTimeout(function(){
                      $('#dispesary-rating-review').modal('hide')
                    }, 4500);
                          
                  vueInstance.spinnerShow = false;
                  $('.btn').prop("disabled", false);
                })
              });  
            }, error => {
              $('.btn').prop("disabled", false);
              vueInstance.spinnerShow = false;
              console.error("Got nothing from server. Prompt user to check internet connection and try again")
          })
        }
      },
	},
};
</script>
