<template>
  <div>
    <header-component />

    <section id="banner-section">
      <div class="container">
        <div class="row">
          <div class="col-lg-10 col-lg-offset-1">
              <div v-if="showCartLayout" class="loader"></div>
            <div class=" animatedParent" data-sequence='100'>
              
            <cart-added-order-details v-if="dispnesaryData" :dispnesaryData="dispnesaryData" :productOrderListData="productOrderListData"/>

            </div>
          </div>
        </div>
      </div>
    </section>
    <manage-billing-addresss-component/>
    <footer-component />
  </div>
</template>

<script>
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import CartAddedOrderDetails from '../../../components/backEnd/customer/CartAddedOrderDetails.vue';
import evenBus from "../../../event-bus";
import ManageBillingAddresssComponent from '../../../components/backEnd/customer/ManageBillingAddresssComponent.vue';
export default {
    
    data() {
        return {
          showCartLayout:true,
          productOrderListData:{},
          dispnesaryData:null,
        };
    },
    components: {
        CartAddedOrderDetails,
        ManageBillingAddresssComponent
    },
  /*  beforeCreate() {
      let recaptchaScript = document.createElement('script')
      recaptchaScript.setAttribute('src', this.$store.state.googleDistanceUrl)
      document.head.appendChild(recaptchaScript)
    },
    */
    created() { 
        this.checkIsCartEmpty();
        evenBus.$on("openExipreDocumentModal", () => {
            $('#document-exp-pop-up').modal('show');
        });
    },
   
    methods: {
      async checkIsCartEmpty(){ 
        var vueInstance = this; 
        
        let getAddedOrderData = await fb.customerCollection.doc(fb.auth.currentUser.uid).collection('response').doc('data_orderList');

        getAddedOrderData.get().then((doc) => {
           if (doc.exists) {
             vueInstance.productOrderListData = doc.data()
              getAddedOrderData.collection('orderList').get().then((getProduct)=> {
                var totalAddedProductCount = getProduct.size;
                if (totalAddedProductCount <= 0) {
                  vueInstance.flashError("Your Cart is empty.Please add product into your cart.", {
                    timeout: 10000,
                  });
                  vueInstance.showCartLayout=false;
                  return false
                }
                vueInstance.showCartLayout=false;
              });
              fb.dispensaryCollection.where("user_id", "==", vueInstance.productOrderListData.dispensary_user_id).get().then(function(doc) {
                if (!doc.empty) {
                    vueInstance.dispnesaryData = doc.docs[0].data();
                } else {
                  vueInstance.flashError("This disapnesay is not available now. please try again.", {
                    timeout: 10000,
                  });
                  vueInstance.$router.push({ name: 'dispensarySearch' })
                }
                vueInstance.showCartLayout=false;
              })
                
            } else { 
                vueInstance.flashError("Your Cart is empty.Please add product into your cart.", {
                  timeout: 10000,
                });
                vueInstance.$router.push({ name: 'dispensarySearch' })
                
            }
        })

      },
    },
};
</script>
