<template>
  <div>
    <header-component />
    <sectionBanner />

    <section id="customer-form">
      <div class="container">
        <div class="row">
          <div class="col-lg-10 col-lg-offset-1 animatedParent" data-sequence="100">
            <div class="tab-section animated bounceIn go" data-id="1">
              <!-- Nav tabs -->
              <selectTab :tabPage="this.tab" />

              <!-- Tab panes -->
              <div class="tab-content page-tab bottom-space">
                <div role="tabpanel" class="tab-pane active" id="home">
                  <component v-bind:is="currentComponent" :id="this.dataId"></component>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <div class="modal fade" id="nearDriver">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title">Driver Lists</h4>
          </div>
          <div class="modal-body">
            <table class="table table-striped table-hover">
              <thead>
                <tr>
                  <th>
                    <label class="form-checkbox">
                      <input id="allChecked" type="checkbox" v-model="selectAll" @click="select">
                      <i class="form-icon"></i>
                    </label>
                  </th>
                  <th>name</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(driverData, index) in driverListData" :key="index">
                  <td>
                    <label class="form-checkbox">
                      <input type="checkbox" :value="driverData.driverLocation.device_id" v-model="selected">
                      <i class="form-icon"></i>
                    </label>
                  </td>
                  <td>{{ driverData.name }}</td>
                </tr>
                <tr v-if="notAvailable == 0 && driverListData.length <= 0">
                  <td colspan="2">There are no driver active near you right now.</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <div v-if="checkedError" v-html="checkedError">{{checkedError}}</div>
            <button class="btn notification-btn order-search-btn" @click.prevent="sendNotification">
              <i v-if="spinnerShow" class="fa fa-spinner fa-spin"></i>SEND ORDERS</button>
            <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
          </div>
        </div>
      </div>
    </div>


    <footer-component />
  </div>
</template>

<script>
/*eslint-disable */
const $ = window.jQuery = require('jquery');

import evenBus from "../../../event-bus";
import * as fb from "../../../firebaseConfig";
import firebase from "firebase";
import sectionBanner from "../../../components/backEnd/dispensary/SectionBannerComponent";
import orderList from "../../../components/backEnd/dispensary/OrderListComponent";
import orderDetail from "../../../components/backEnd/dispensary/OrderDetailComponent";
import selectTab from "../../../components/backEnd/dispensary/TabComponent";
import axios from "axios";
export default {
  data() {
    return {
      currentComponent: orderList,
      dataId: null,
      tab: null,

      url: this.$store.state.API_URL_ROOT,
      dispensaryLatitude: null,
      dispensaryLongitude: null,
      dispensaryLocation: null,
      driverDistance: 2000,
      orderDetailData: {},
      notAvailable: null,
      driverListData: [],
      driverStatus: "Online",
      selected: [],
      selectAll: false,
      checkedError: null,
      spinnerShow: false,
    };
  },
  components: {
    sectionBanner,
    orderList,
    orderDetail,
    selectTab,
  },
  created() {
    localStorage.removeItem("DisRegistratonLoginForm");
    localStorage.removeItem("DispensaryForm");
    localStorage.removeItem("userImage");
    this.tab = this.$route.name;
    evenBus.$on("orderDetails", (id) => {
      this.dataId = id;
      this.currentComponent = orderDetail;
    });
    evenBus.$on("backToList", () => {
      this.currentComponent = orderList;
    });

    this.dispensaryUser()
    this.driverList()
  },
  methods: {

    async sendNotification() {
      this.checkedError = null;
      this.spinnerShow = true;
      $('.btn').prop("disabled", true);

      if (this.selected.length <= 0) {
        this.checkedError = "<p class='alert alert-danger'>Plese check atleast one driver.<a href='#' class='close' data-dismiss='alert' aria-label='close'>&times;</a></p>";
        this.spinnerShow = false;
        $('.btn').prop("disabled", false);
        return false;
      }
      var driverDataArray = [];
      let dataUpdate = fb.orderCollection.doc(this.orderDetailData.id);
      for (var i = 0; i < this.selected.length; i++) {

        var driverData = this.driverListData.filter(e => e.driverLocation.device_id.includes(this.selected[i]));
        var userId = driverData[0].driverLocation.user_id
        await axios.post(`${this.url}send-notification`, {
          message: `New order`,
          device_token: this.selected[i],
          order_id: this.orderDetailData.id,
          order_no: this.orderDetailData.order_no,
          dispensary_name: this.orderDetailData.dispensary_data.busniess_name,
          order_user_id: this.orderDetailData.user_id,
          notification_type: 'new_order'
        }).then((response) => {
          if (response.data.success == true) {
            dataUpdate.update({
              "isSendNotification": true
            })
            driverDataArray.push(userId);
            dataUpdate.set({ 'notification_drivers_id': driverDataArray }, { merge: true })
          }
          console.log(response);
        });

      }
      this.spinnerShow = false;
      $('.btn').prop("disabled", false);
      $("#nearDriver").modal("hide");
      return false;

    },

    async driverList() {
      evenBus.$on("driverLists", (orderDetail) => {
        this.selected = [];
        this.selectAll = false;
        this.orderDetailData = orderDetail;
        var latitude = this.dispensaryLatitude;
        var longitude = this.dispensaryLongitude;
        var distance = this.driverDistance;
        // ~1 mile of lat and lon in degrees
        let lat = 0.0144927536231884
        let lon = 0.0181818181818182

        let lowerLat = latitude - (lat * distance)
        let lowerLon = longitude - (lon * distance)

        let greaterLat = latitude + (lat * distance)
        let greaterLon = longitude + (lon * distance)

        let lesserGeopoint = new firebase.firestore.GeoPoint(lowerLat, lowerLon)
        let greaterGeopoint = new firebase.firestore.GeoPoint(greaterLat, greaterLon)

        var vueInstance = this;
        let query = fb.driverLocationCollection.where("location", '>', lesserGeopoint).where("location", '<', greaterGeopoint).where("driver_status", '==', this.driverStatus)

        query.onSnapshot(function (querySnapshot) {
          var driverData = [];

          querySnapshot.forEach((doc) => {
            fb.driverCollection.doc(doc.data().user_id).collection('response').doc('data_account').get().then((driverDataDoc) => {
              //  console.log(driverDataDoc.data());
              var driverAccountData = {
                driverFirstName: driverDataDoc.data().account.first_name,
                driverlastName: driverDataDoc.data().account.last_name
              }
              fb.usersCollection.doc(doc.data().user_id).get().then((doc) => {
                if (doc.exists) {
                  if (doc.data().device_id) {
                    let driverAllData = {
                      driverLocation: doc.data(),
                      name: driverDataDoc.data().account.first_name + ' ' + driverDataDoc.data().account.last_name
                    }

                    driverData.push(driverAllData);
                    //   driverData.push(driverAccountData);
                  }
                } else {
                  // snapshot.data() will be undefined in this case
                  console.log("No such document!");
                }
              })
            })
          });

          vueInstance.driverListData = driverData;
          setTimeout(function () {
            if (driverData.length <= 0)
              vueInstance.notAvailable = 0;
          }, 3000);
        });
        $("#nearDriver").modal();

      });
    },


    async dispensaryUser() {
      await this.$store.dispatch("getDispensaryUser");
      let dispensaryUserData = await this.$store.state.dispensaryUser;
      this.dispensaryLatitude = dispensaryUserData.allData.address.latitude;
      this.dispensaryLongitude = dispensaryUserData.allData.address.longitude;
      this.dispensaryLocation = dispensaryUserData.allData.location;
    },
    select() {
      this.selected = [];
      if (!this.selectAll) {
        for (let i in this.driverListData) {
          this.selected.push(this.driverListData[i].driverLocation.device_id);
        }
      }
    }
  }
};
</script>
<style scoped>
.notification-btn {
  background: #8dc73f;
  border-radius: 0;
  border: 0;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  padding: 8px 16px;
}
</style>